import Transform = Laya.Transform3D;
import Vector3 = Laya.Vector3;
import Vector2 = Laya.Vector2;
import Quaternion = Laya.Quaternion;
import GameObject = Laya.Sprite3D;
import TouchPhase = ZY.TouchPhase;
import { ZY } from "../ZY";
export default class CameraCtrl extends Laya.Script3D {
    constructor() { super(); }
    
    public gameObject:GameObject;
    public transform:Transform;
    private _touch:ZY.Touch;
    private _pos:Vector3 = new Vector3();
    private _dir:Vector3 = new Vector3(); //从target指向camera的一个向量
    private _quat:Quaternion = new Quaternion();
    private _oldPos:Vector3 = new Vector3();
    private _oldQuat:Quaternion = new Quaternion();
    private _yp:Vector2 = new Vector2();
    private _oDir:Vector3 = new Vector3();
    private _mouseDown:boolean = false;
    public onAwake():void {
        this.gameObject = this.owner as GameObject;
        this.transform = this.gameObject.transform;
        this._touch = ZY.touchManager.touch;
        this.enabled = false;
    }

    private _target:Transform;
    public lookAt(target:Transform, distance:number):void {
        // console.log(this.transform.localPosition, this.transform.localRotation);
        this._yp.setValue(-30*Math.PI/180,-30*Math.PI/180);
        this.transform.localRotation.cloneTo(this._oldQuat);
        this.transform.localPosition.cloneTo(this._oldPos);
        this._target = target;
        this.enabled = true;
        this._oDir.setValue(0,0,distance);
        this.updateThis();

        this._mouseDown = true;
    }

    private updateThis():void {
        let tPos = this._target.position;
        Quaternion.createFromYawPitchRoll(this._yp.x, this._yp.y, 0, this._quat);
        Vector3.transformQuat(this._oDir, this._quat, this._dir);
        this._pos.setValue(this._dir.x+tPos.x, this._dir.y+tPos.y, this._dir.z+tPos.z);
        this.transform.position = this._pos;
        this.transform.lookAt(tPos, ZY.Temp.UP, false);
    }

    public reset(pos:Vector3 = null, rotation:Quaternion = null):void {
        this._target = null;
        this.enabled = false;

        if(pos) {
            this.transform.localPosition = pos;
        } else {
            this._oldPos.cloneTo(this.transform.localPosition);
            this.transform.localPosition = this.transform.localPosition;
        }
        if(rotation) {
            this.transform.localRotation = rotation;
        } else {
            this._oldQuat.cloneTo(this.transform.localRotation);
            this.transform.localRotation = this.transform.localRotation;
        }
    }

    public onUpdate():void {
        if(this._target) {
            if(this._touch.phase == TouchPhase.Moved) {
                if(this._mouseDown) {
                    this._yp.x -= this._touch.deltaX*0.005;
                    this._yp.y -= this._touch.deltaY*0.005;

                    if(this._yp.y >-6*Math.PI/180) this._yp.y = -6*Math.PI/180;
                    else if(this._yp.y < -89*Math.PI/180) this._yp.y = -89*Math.PI/180;
                    if(this._yp.x > -30*Math.PI/180) this._yp.x = -30*Math.PI/180;
                    else if(this._yp.x < -150*Math.PI/180) this._yp.x = -150*Math.PI/180;
                    this.updateThis();
                }
            } else if(this._touch.phase == TouchPhase.Began) {
                this._mouseDown = true;
            }
        }
    }
}