export default class HttpLoader extends Laya.EventDispatcher{
    constructor() {super()}

    private _hr:Laya.HttpRequest;
    private _retryCnt:number;
    private _retryMaxCnt:number = 3;
    private _url:string;
    private _data:any;
    private _method:string;
    private _responseType:string;
    private _headers:any[]|null;
    public destroy():void {
        this.offAll(Laya.Event.COMPLETE);
        this.offAll(Laya.Event.ERROR);
        if(this._hr) {
            this._hr.offAllCaller(this);
            this._hr = null;
        }
        this._retryCnt = null;
        this._retryMaxCnt = null;
    }

    /**
     * 设置重试次数
     * @param retryCnt 重试次数，默认3次 
     */
    public setRetryInfo(retryCnt:number = 3):void {
        this._retryMaxCnt = retryCnt;
    }

    /**
    * 发送 HTTP 请求。
    * @param url 请求的地址。大多数浏览器实施了一个同源安全策略，并且要求这个 URL 与包含脚本的文本具有相同的主机名和端口。
    * @param data (default = null)发送的数据。
    * @param method (default = "get")用于请求的 HTTP 方法。值包括 "get"、"post"、"head"。
    * @param responseType (default = "text")Web 服务器的响应类型，可设置为 "text"、"json"、"xml"、"arraybuffer"。
    * @param headers (default = null) HTTP 请求的头部信息。参数形如key-value数组：key是头部的名称，不应该包括空白、冒号或换行；value是头部的值，不应该包括换行。比如["Content-Type", "application/json"]。
    */
   public send(url:string,data:any=null,method:string = "get",responseType:string="text",headers:any[]|null = null):void {
       this._retryCnt = 0;
       this._url = url;
       this._data = data;
       this._method = method;
       this._responseType = responseType;
       this._headers = headers;
       this.sendNow(url, data, method, responseType, headers);
   }

   private sendNow(url:string,data:any=null,method:string = "get",responseType:string="text",headers:any[]|null = null):void {
    if(this._hr) {
        this._hr.offAllCaller(this);
    }
    let hr = this._hr = new Laya.HttpRequest();
    hr.http.timeout = 5000;
    this._retryCnt++;
    hr.once(Laya.Event.ERROR, this, this.onError);
    hr.once(Laya.Event.COMPLETE, this, this.onComplete);
    console.log("http请求:"+url+", retry="+this._retryCnt);
    hr.send(url, data, method, responseType, headers);
} 

   private onError(res):void {  
        if(this._retryCnt < this._retryMaxCnt) {
            Laya.systemTimer.frameOnce(2, this, this.sendNow, [this._url, this._data, this._method, this._responseType, this._headers]);
        } else {
            this.event(Laya.Event.ERROR, res);
        }
   }

   private onComplete(res):void {
        this.event(Laya.Event.COMPLETE, res);
   }
}