import { ZY } from "../ZY";
import { ui } from "../ui/layaMaxUI";
import { moduleBridge } from "../Interface";
import ZooButton from "../script/ZooButton";
import { userData } from "../userData/UserData";


export default class FestivalActivityCtrl extends ZY.BaseCtrl {
    public static zjQuestion: number;

    constructor() { super(); }

    public start(): void {
        super.start();
        this.bindAssets("res/atlas/giftbag.atlas", Laya.Loader.ATLAS, "zoo");
    }

    protected beforeEnterScene(sceneName: string): void {
        if (this._sceneName == "zoo") {
            this._btnDouble.clear();
            this._btnDouble = null;
            this._btnClose.clear();
            this._btnClose = null;
            this._view.btnNormal.off(Laya.Event.CLICK, this, this.onBtnNormal);
            this._view.imgMask.off(Laya.Event.MOUSE_DOWN, this, this.onBtnMask);

            this._view.removeSelf();
            this._view.destroy(true);
            this._view = null;
        }
    }

    private _view: ui.main.FestivalActivityUI;
    private _btnDouble: ZooButton;
    private _btnClose: ZooButton;

    protected createChildren(sceneName: string): void {
        if (sceneName == "zoo") {
            if (!this._view) {
                this._view = new ui.main.FestivalActivityUI();

                this._btnDouble = new ZooButton(this._view.btnDouble, null, null, -41, -26);
                this._btnClose = new ZooButton(this._view.btnclose, null, null, -19, -10);
            }
            //this._view.on(Laya.Event.CLICK, this, this.onClick);
            this._btnDouble.on(Laya.Event.CLICK, this, this.onBtnDouble);
            this._btnClose.on(Laya.Event.CLICK, this, this.onBtnClose);
            this._view.btnNormal.on(Laya.Event.CLICK, this, this.onBtnNormal, [1]);
            this._view.imgMask.on(Laya.Event.MOUSE_DOWN, this, this.onBtnMask);
        }
    }

    private onBtnMask(e: Laya.Event): void {
        e.stopPropagation();
    }

    private onBtnDouble(): void {
        if (ZY.channel == "wx" || ZY.channel == "qq" || ZY.channel == "zj" || ZY.channel == "bd") {
            if (ZY.channel === "zj") {
                ZY.reportEvent("ad_luckyBag", {value: "播放"});
            }
            ZY.ad.showVideoAd(this, this.getReward);
        } else if (ZY.channel == "oppo") {
            ZY.ad.showOPPOVideo(this, this.getReward);
        } else if (ZY.channel == "vivo") {
            ZY.ad.showVivoVideo(this, this.getReward);
        } else {
            this.getReward(true);
        }
    }

    private getReward(isEnd: boolean): void {
        if (isEnd) {
            if (ZY.channel === "zj") {
                ZY.reportEvent("ad_luckyBag", {value: "成功"});
            }
            this.onBtnNormal(2);
        } 
    }

    private onBtnClose(): void {
        moduleBridge.shipCtrl.backToShip("festival");
        this._view.removeSelf();
    }

    private onBtnNormal(count: number = 1): void {
        let gold: number = Number(this._view.textGold.text) * count;
        let diamond: number = Number(this._view.textDiamond.text) * count;
        if (this._view.imgHat.visible) {
            for (let i = 0; i < count; i++) {
                let hat: string = userData.getHat();
                if (hat) {
                    this.updateHat();
                    ZY.msg.show("恭喜获得稀有帽子：" + userData.cfgHats[hat].name);
                }    
            }
        } else {
            ZY.msg.show("恭喜获得钻石：" + diamond + " 金币："+gold);
        }

        userData.addGold(gold, 1028);
        userData.addJew(diamond, 1016);

        let date: Date = new Date();
        let year: number = date.getFullYear();
        let month: number = date.getMonth() + 1;
        let day: number = date.getDate();
        
        let str:string = userData.get("getFdRewardDay", "");
        if (str != "") {
            str += "," + (year + "-" + month + "-" + day);
        } else {
            str = year + "-" + month + "-" + day;
        }

        userData.set("getFdRewardDay", str);

        this.onBtnClose();
    }

    private updateHat():void{
        if (this._sceneName == "zoo"){
            moduleBridge.shipCtrl.dealHatsArr()
            moduleBridge.shipCtrl.createHats()
        }
    }

    private updateView(): void {
        let date: Date = new Date();
        let month: number = date.getMonth() + 1;
        let day: number = date.getDate();
        if (month == 10 && day == 1) {
            this._view.textGold.text = 500 + "";
            this._view.textDiamond.text = 10 + "";
            this._view.imgHat.visible = true;
            this._view.boxReward.x = 166;
        } else {
            this._view.textGold.text = 200 + "";
            this._view.textDiamond.text = 5 + "";
            this._view.imgHat.visible = false;
            this._view.boxReward.x = 286;
        }
    }


    public showFestivalActivity(): void {
        if (ZY.channel === "zj") {
            ZY.reportEvent("ad_luckyBag", {value: "曝光"});
        }

        if (this._sceneName == "zoo") ZY.stage.addChild(this._view, ZY.Layer.MIDDLE);
        Laya.timer.callLater(this, this.updateView);
    }

}

// export const mapCtrl = new MapCtrl();
moduleBridge.festivalActivityCtrl = new FestivalActivityCtrl();