import { ZY } from "../ZY";
import { ui } from "../ui/layaMaxUI";
import ZooButton from "../script/ZooButton";
import { userData } from "../userData/UserData";
import { moduleBridge } from "../Interface";
import ShipCtrl from "../ship/ShipCtrl";
import Helper2D from "../Helper2D";
import { reporter, ReporterPoints } from "../Reporter";
import { nativeAd } from "../NativeAd";

export default class GiftBagCtrl extends ZY.BaseCtrl {
    constructor() { super(); }
    public start():void {
        super.start();
        this.bindAssets("res/atlas/giftbag.atlas", Laya.Loader.ATLAS, "zoo");
        this.bindAssets("res/atlas/giftbag.atlas", Laya.Loader.ATLAS, "game");
    }

    protected beforeEnterScene(sceneName:string):void {
        if(this._sceneName == "zoo") {
            Laya.timer.clearAll (this);
            this._btnMoney.off (Laya.Event.CLICK, moduleBridge.shopCtrl, moduleBridge.shopCtrl.buyGiftBag);

            ((this._view["money"] as Laya.Box).getChildByName("btn_thanks_money") as Laya.Button).off (Laya.Event.CLICK, this, this.btnThanks);
            ((this._view["try"] as Laya.Box).getChildByName("btn_thanks_try") as Laya.Button).off (Laya.Event.CLICK, this, this.btnTryNext);
            this._btnReceive.off(Laya.Event.CLICK,this,this.btnReceive)
            this._btnSkinAd.off(Laya.Event.CLICK,this,this.btnSkinAd)
            this._btnBack.off(Laya.Event.CLICK,this,this.btnThanks)
            this._view.btnLeft.off(Laya.Event.CLICK,this,this.btnChange)
            this._view.btnRight.off(Laya.Event.CLICK,this,this.btnChange)

            this._index = null
            this._val = null
            this._curSkin = null
            this._showList = null
            this._tryId = null
            if (ZY.channel == "oppo") {
                nativeAd.clearAd("207983");
            }else if (ZY.channel == "vivo") {
                nativeAd.clearAd("abea873d8eef4a7d904c27e005acebea");
                // nativeAd.clearAd("b1942178daf5444e9fafefca609c4b89");
            }
            this._begin = null
            this._view.removeSelf();
            this._view.destroy(true);
            this._view = null;
        }else if (this._sceneName == "game"){
            Laya.timer.clearAll (this);
            this._btnReborn.off (Laya.Event.CLICK, moduleBridge.shopCtrl, moduleBridge.shopCtrl.buyGiftBag);

            ((this._view["reborn"] as Laya.Box).getChildByName("btn_thanks_reborn") as Laya.Button).off (Laya.Event.CLICK, this, this.btnThanks);
            ((this._view["try"] as Laya.Box).getChildByName("btn_thanks_try") as Laya.Button).off (Laya.Event.CLICK, this, this.btnTryNext);
            this._tryId = null
            this._view.removeSelf();
            this._view.destroy(true);
            this._view = null;
        }
    }


    private _view:ui.main.GiftBagUI;
    private _btnMoney:ZooButton;
    private _btnReborn:ZooButton;
    private _btnReceive:ZooButton;
    private _btnSkinAd:ZooButton;
    private _btnBack:ZooButton;
    private _btnTry:ZooButton;
    protected createChildren(sceneName:string):void {
        if(sceneName == "zoo") {
            if(!this._view) {
                this._view = new ui.main.GiftBagUI();
                this._view.on (Laya.Event.CLICK, this, this.onClick);
                this._btnMoney = new ZooButton(this._view.btn_buyMoney, null, "main", -62.5, -50);
                this._btnReceive = new ZooButton(this._view.btn_receive, null, "main", -45, -32.5);
                this._btnSkinAd = new ZooButton(this._view.btn_skinAd);
                this._btnBack = new ZooButton(this._view.btnBack);
                this._btnTry = new ZooButton(this._view.btnTry, null, "main", -62.5, -50);
            }
            this._btnMoney.on (Laya.Event.CLICK, moduleBridge.shopCtrl, moduleBridge.shopCtrl.buyGiftBag,["10"]);
            this._btnReceive.on(Laya.Event.CLICK,this,this.btnReceive)
            this._btnSkinAd.on(Laya.Event.CLICK,this,this.btnSkinAd)
            this._btnBack.on(Laya.Event.CLICK,this,this.btnThanks)
            this._btnTry.on(Laya.Event.CLICK,this,this.btnTry)
            this._view.btnLeft.on(Laya.Event.CLICK,this,this.btnChange,[1])
            this._view.btnRight.on(Laya.Event.CLICK,this,this.btnChange,[2])
            this._view.btnWatchAd.on(Laya.Event.CLICK,this,this.btnWatchAd)
            this.stopMaoPao ();
        }else if (sceneName == "game"){
            if(!this._view) {
                this._view = new ui.main.GiftBagUI();
                this._view.on (Laya.Event.CLICK, this, this.onClick);
                this._btnTry = new ZooButton(this._view.btnTry, null, "main", -62.5, -50);
                this._btnReborn = new ZooButton(this._view.btn_buyReborn, null, "main", -62.5, -50);
            }
            this._btnTry.on(Laya.Event.CLICK,this,this.btnTry)
            this._btnReborn.on (Laya.Event.CLICK, moduleBridge.shopCtrl, moduleBridge.shopCtrl.buyGiftBag,["11"]);
            this._view.btnWatchAd.on(Laya.Event.CLICK,this,this.btnWatchAd)
            this.stopMaoPao ();
        }
    }

    private stopMaoPao():void {
        this._view.on (Laya.Event.MOUSE_DOWN, this, (e:Laya.Event)=>{
            e.stopPropagation ();
        })
        for (let i:number = 0; i < this._view.numChildren; i ++) {
            let node = this._view.getChildAt (i) as Laya.Sprite;
            node.on (Laya.Event.MOUSE_DOWN, this, (e:Laya.Event)=>{
                e.stopPropagation ();
            })
        }
    }

    private btnReceive():void{
        if (this._showList[this._curSkin]){
            let id = this._showList[this._curSkin][1]
            let time = userData.get("gift"+id,0)
            if (time >= 3){
                let cfg = userData.cfgAnimal[id];
                let res = cfg.res;
                // ZY.load ([res], this, ()=>{
                    userData.catchAnimal(id);
                    this.btnThanks();
                    ZY.msg.show("恭喜你，获得了专属皮肤福利！")
                // })
            }
        }
    }

    private btnSkinAd2(done:boolean):void{
        if (done){
            if (this._showList[this._curSkin]){
                let id = this._showList[this._curSkin][1]
                let time = userData.get("gift"+id,0)
                userData.set("gift"+id,time+1)
                if (ZY.channel == "wx" || ZY.channel == "qq"){
                    ZY.TmCtrl.instance.tmSendEvent("gm_rareplay01",{value:"完成"})
                }else if (ZY.channel == "zj"){
                    ZY.reportEvent("gm_rareplay01",{value:"完成"})
                }else if (ZY.channel == "oppo"){
                    // reporter.upload(ReporterPoints.ad_skin,4)
                }
            }
        }else{
            moduleBridge.videoTipCtrl.showTips();
        }
        this.btnChange(0)
    }

    private btnSkinAd():void{
        if (ZY.channel == "wx"){
            ZY.ad.showVideoAd(this, this.btnSkinAd2,ZY.wxAd.freejew,"gm_rareplay01");
        }else if (ZY.channel == "vivo"){
            ZY.ad.showVivoVideo(this, this.btnSkinAd2,ZY.vivoAd.freejew,"gm_rareplay01");
        }else if (ZY.channel == "oppo"){
            // reporter.upload(ReporterPoints.ad_skin,3)
            ZY.ad.showOPPOVideo(this, this.btnSkinAd2 ,ReporterPoints.ad_skin);
        }else{
            ZY.ad.showVideoAd(this, this.btnSkinAd2,0,"gm_rareplay01");
        }
    }

    //0.原地不动 1.左 2.右
    private btnChange(index:number):void{
        if (this._showList && this._showList.length > 0){
            if (index == 1){
                let index = (this._curSkin - 1)%this._showList.length
                this._curSkin = index < 0 ? this._showList.length + index : index 
            }else if(index == 2){
                this._curSkin = (this._curSkin + 1)%this._showList.length
            }

            let curId = this._showList[this._curSkin][1]
            let time = userData.get("gift"+curId,0)
            this._view.img_skin.skin = "giftbag/img_"+curId+".png"
            if (curId == "188"){
                this._view.title_skin.skin = "giftbag/word_s"+curId+".png"
            }else{
                this._view.title_skin.skin = "giftbag/word_"+curId+".png"
            }

            if (time < 3){
                this._view.watch_before.visible = true
                this._view.btn_receive.visible = false
                for (let i = 0; i < 3; i++) {
                    let img = (this._view.watch_before as Laya.Box).getChildByName("img_time"+i) as Laya.Image
                    if (time > i){
                        img.skin = "giftbag/img_pro2.png"
                    }else{
                        img.skin = "giftbag/img_pro1.png"
                    }
                }
            }else{
                this._view.watch_before.visible = false
                this._view.btn_receive.visible = true
            }
            if (ZY.channel == "wx" || ZY.channel == "qq"){
                ZY.TmCtrl.instance.tmSendEvent("gm_rareplay01",{value:"曝光"})
            }else if (ZY.channel == "zj"){
                ZY.reportEvent("gm_rareplay01",{value:"曝光"})
            }
        }else{
            this.btnThanks()
        }
    }

    public btnThanks():void{
        if (ZY.channel === "oppo") {
            ZY.ad.hideBanner();
        }

        if (this._begin){
            // let iphone:boolean = false
            // if ((ZY.channel == "wx" || ZY.channel == "qq" || ZY.channel == "zj" || ZY.channel == "bd") && !Laya.Browser.onAndroid){
                // iphone = true
            // }
            // if ((userData.getAnimals(1000) && userData.getAnimals(1000).indexOf(10) > -1) || iphone || ZY.channel == "zj") {
                moduleBridge.shipCtrl.openShop(101)
                this._view.removeSelf();
            // }else{
                // this.showGiftBag(1)
            // }
        }else{
            if (ZY.stage.sceneName == "zoo"){
                if (this._index == 3){
                    moduleBridge.shipCtrl.startGame2(this._val);
                }else{
                    moduleBridge.shipCtrl.backToShip("gift"+this._index);
                }
            }else if (ZY.stage.sceneName == "game"){
                if (this._index == 3){
                    moduleBridge.gameCtrl.restartGame()
                }else{
                    if (ZY.channel == "oppo" && moduleBridge.gameCtrl.firstTime){
                        // reporter.upload(ReporterPoints.reborncancel)
                    } else if (ZY.channel === "bd" && moduleBridge.gameCtrl.firstTime) {
                        ZY.reportEvent("reborncancel");
                    }
                    moduleBridge.gameCtrl.backToGame();
                }
            }
            this._view.removeSelf();
        }
    }

    private _curSkin:number;
    private _showList:any;
    private _giftIndex: number;
    public get giftIndex(): number {
        return this._giftIndex;
    }
    private init(index:number):void{
        if (ZY.channel == "oppo") {
            this._giftIndex = index;
            if (index == 1) {
                nativeAd.showAsBanner2("207983", this._view.oppoActive); 
            } else if (index == 2) {
                this._view.oppoActive.visible = false;
                ZY.ad.showQGbanner(0);
            } 
        } else if (ZY.channel == "vivo") {
            if (index == 1) {
                nativeAd.showAsBanner2("abea873d8eef4a7d904c27e005acebea", this._view.vivoActive); 
            } else if (index == 2) {
                ZY.ad.showInsertAd()
            } 
        } else if (ZY.channel == "bd") {
            //ZY.ad.showBDbanner(0);
        }
        
        let list = ["reborn","money","skin","try"];
        for (let i = 0; i < list.length; i++) {
            let view = this._view[list[i]] as Laya.Box
            if (index != i){
                view.visible = false
            }else{
                if (list[index] == "reborn" || list[index] == "money"){
                    if (ZY.channel == "wx" || ZY.channel == "qq"){
                        if (list[index] == "reborn"){
                            ZY.TmCtrl.instance.tmSendEvent("ys_package2",{value:"曝光"})
                        }else if (list[index] == "money"){
                            ZY.TmCtrl.instance.tmSendEvent("ys_package1",{value:"曝光"})
                        }
                    }
                    if (list[index] == "reborn"){
                        if (ZY.channel == "qq" || ZY.channel == "none"){
                            this._view.price.skin = "giftbag/qq_price.png"
                            this._view.price.width = 138;
                            this._view.price.height = 103;
                        }else{
                            this._view.price.skin = "giftbag/price.png"
                            this._view.price.width = 138;
                            this._view.price.height = 103;
                        }
                    }
                    view.visible = true
                    let thanks =  view.getChildByName("btn_thanks_"+list[index]) as Laya.Button
                    thanks.visible = false
                    Laya.timer.once(2*1000,this,()=>{
                        thanks.visible = true
                        thanks.on(Laya.Event.CLICK, this, this.btnThanks)
                    })
                }else if (list[index] == "skin"){
                    let Skinlist = [[1001,"108"],[1005,"188"],[1007,"229"]]
                    this._showList = []
                    for (let i = 0; i < Skinlist.length; i++) {
                        let id = Skinlist[i][1];
                        let encId = Skinlist[i][0];
                        if (userData.enclosure[encId] != null && (userData.getAnimals(Number(encId)).indexOf(Number(id)) < 0)){
                            this._showList.push(Skinlist[i])
                        }
                    }
                    if (this._showList && this._showList.length > 0){
                        view.visible = true
                        if (this._showList.length == 1){
                            this._view.btnLeft.visible = this._view.btnRight.visible = false
                        }else{
                            this._view.btnLeft.visible = this._view.btnRight.visible = true
                        }
                        let random = Helper2D.GetRandom(0,this._showList.length,true)
                        this._curSkin = random
                        let curId = this._showList[this._curSkin][1]
                        let time = userData.get("gift"+curId,0)
                        this._view.img_skin.skin = "giftbag/img_"+curId+".png"
                        if (curId == "188"){
                            this._view.title_skin.skin = "giftbag/word_s"+curId+".png"
                        }else{
                            this._view.title_skin.skin = "giftbag/word_"+curId+".png"
                        }
                        // let time = 3
                        if (time < 3){
                            this._view.watch_before.visible = true
                            this._view.btn_receive.visible = false
                            for (let i = 0; i < 3; i++) {
                                let img = (this._view.watch_before as Laya.Box).getChildByName("img_time"+i) as Laya.Image
                                if (time > i){
                                    img.skin = "giftbag/img_pro2.png"
                                }else{
                                    img.skin = "giftbag/img_pro1.png"
                                }
                            }
                        }else{
                            this._view.watch_before.visible = false
                            this._view.btn_receive.visible = true
                        }
                        if (ZY.channel == "wx" || ZY.channel == "qq"){
                            ZY.TmCtrl.instance.tmSendEvent("gm_rareplay01",{value:"曝光"})
                        }else if (ZY.channel == "oppo"){
                            // reporter.upload(ReporterPoints.ad_skin,2)
                        }
                    }else{
                        this.btnThanks()
                    }
                }else if (list[index] == "try"){
                    if (ZY.channel === "zj") {
                        ZY.reportEvent("ad_skinTry", {value: "曝光"});
                    }
                    this.randomTry()
                    view.visible = true
                    let thanks =  view.getChildByName("btn_thanks_"+list[index]) as Laya.Button
                    thanks.visible = false
                    Laya.timer.once(2*1000,this,()=>{
                        thanks.visible = true
                        thanks.on(Laya.Event.CLICK, this, this.btnTryNext)
                    })
                }
            }
        }
    }

    private btnTryNext():void{
        let gou = this._view.gou as Laya.Image;
        if (gou.visible){
            this.btnTry()
        }else{
            this.btnThanks()
        }
    }

    private _tryId:number;
    private randomTry():void{
        let list = [["img_panda","word_panda",901],["img_dragon","word_dragon",902],["img_monkey","word_monkey",903]]
        let number = Helper2D.GetRandom(0,3,true)
        this._view.title.skin = "giftbag/"+list[number][1]+".png";
        for (let i = 0; i < list.length; i++) {
            const element = list[i][0]
            this._view[element].visible = false
        }
        this._view[list[number][0]].visible = true
        this._tryId = Number(list[number][2])
        if (ZY.channel == "oppo"){
            (this._view.gou as Laya.Image).visible = false;
            (this._view.try.getChildByName("btn_thanks_try").getChildAt(0) as Laya.Image).skin = "giftbag/notry.png";
        }else{
            (this._view.try.getChildByName("btn_thanks_try").getChildAt(0) as Laya.Image).skin = "giftbag/try.png";
            (this._view.gou as Laya.Image).visible = true;
        }
    }

    private btnTry():void{
        if (ZY.channel == "wx") {
            ZY.ad.showVideoAd(this, this.onTry2, ZY.wxAd.trynew, "gm_rareplay")
        }else if (ZY.channel == "vivo"){
            ZY.ad.showVivoVideo(this, this.onTry2, ZY.vivoAd.trynew, "gm_rareplay")
        }else if (ZY.channel == "oppo"){
            // reporter.upload(ReporterPoints.ad_play,3)
            ZY.ad.showOPPOVideo(this, this.onTry2 ,ReporterPoints.ad_play);
        } else {
            if (ZY.channel === "zj") {
                ZY.reportEvent("ad_skinTry", {value: "播放"});
            }
            ZY.ad.showVideoAd(this, this.onTry2, 0, "gm_rareplay" )
        }
    }

    private onTry2(done: boolean): void {
        if (done) {
            if (ZY.channel == "wx" || ZY.channel == "qq"){
                ZY.TmCtrl.instance.tmSendEvent("ad_play", { value: "完成" })
            } else if (ZY.channel == "zj") {
                ZY.reportEvent("ad_play", { value: "完成" });
                ZY.reportEvent("ad_skinTry", {value: "成功"});
            } 
            else if (ZY.channel == "oppo"){
                // reporter.upload(ReporterPoints.ad_play,4)
            }
            moduleBridge.endangeredAnimals.curTryId = this._tryId;
            if (ZY.stage.sceneName == "zoo"){
                moduleBridge.shipCtrl.startGame2(this._val);
            }else if(ZY.stage.sceneName == "game"){
                moduleBridge.gameCtrl.restartGame()
            }
            this._view.removeSelf();
        }
    }

    private btnWatchAd():void{
        let gou = this._view.gou as Laya.Image;
        if (gou.visible){
            gou.visible = false;
            (this._view.try.getChildByName("btn_thanks_try").getChildAt(0) as Laya.Image).skin = "giftbag/notry.png";
        }else{
            (this._view.try.getChildByName("btn_thanks_try").getChildAt(0) as Laya.Image).skin = "giftbag/try.png";
            gou.visible = true
        }
    }

    private updateView(index:number):void{
        this.init(index)
    }

    private _index:number;
    private _val:number;
    private _begin:boolean;
    public showGiftBag(index:number,val ?:number):void {
        if(this._sceneName == "zoo" || this._sceneName == "game") ZY.stage.addChild(this._view, ZY.Layer.MIDDLE);
        if (index == 20){
            index = 2 
            this._begin = true
        }else{
            this._begin = false
        }
        this._index = index
        this._val = val
        Laya.timer.callLater(this, this.updateView,[index])
    }


    private onClick(e:Laya.Event):void {
        let target = e.target;
        // console.log (target);
        switch (target) {
            default:
                break;
        }
    }

}

