    private onBtnDouble(): void {
        if (ZY.channel == "zj") {
            RecordVideo.ttShareAppMessage("video", this, this.getZjReward);
        }
        else if (ZY.channel == "vivo") {
            ZY.ad.showVivoVideo(this, this.getReward);
        } else if (ZY.channel == "oppo") {
            ZY.ad.showOPPOVideo(this, this.getReward);
        } else {
            ZY.ad.showVideoAd(this, this.getReward);
        }
    }

    private getZjReward(data: any): void {
        if (data.code == 0) {
            this.addReward();
        } 
    }

    private getReward(isEnd: boolean): void {

        if (!isEnd) {
            //ZY.msg.show("广告未播完，无法获得奖励！");
            moduleBridge.videoTipCtrl.showTips();
            return;
        }

        this.addReward();
    }

    private addReward(): void {
        if (this._reward.type == 0) {
            userData.addGold(this._reward.count, 1022);

            this._view.gold1.text = userData.gold.toString();
            this._view.gold2.text = userData.gold.toString();
        } else if (this._reward.type == 1) {
            userData.addJew(this._reward.count, 1014);
        } else if (this._reward.type == 2) {
            //获得帽子
            let hat: string = userData.getHat();
            if (!hat) {
                let coin: number = Math.ceil(userData.touristTicket[3] * 0.05 + 200);
                userData.addGold(coin, 1022);
                this._view.gold1.text = userData.gold.toString();
                this._view.gold2.text = userData.gold.toString();
            } else {
                this.updateHat()
                ZY.msg.show("恭喜获得稀有帽子：" + userData.cfgHats[hat].name);
            }

        }
        this._view.boxResult.visible = false;
    }


