import { ZY } from "../ZY";
import { ui } from "../ui/layaMaxUI";
import ZooButton from "../script/ZooButton";
import { userData } from "../userData/UserData";
import CheckButton from "../script/CheckButton";
import { moduleBridge } from "../Interface";
import ShipCtrl from "../ship/ShipCtrl";

export default class TipsCtrl extends ZY.BaseCtrl {
    constructor() { super(); }
    
    public start():void {
        super.start();
        this.bindAssets("res/atlas/main.atlas", Laya.Loader.ATLAS, "zoo");
    }

    protected beforeEnterScene(sceneName:string):void {
        if(this._sceneName == "zoo") {
            this._view.off (Laya.Event.CLICK, this, this.onClick);
            this._view.off (Laya.Event.MOUSE_DOWN, this, this.onMouseDown);
            // this._view.off (Laya.Event.MOUSE_UP, this, this.onMouseUp);
            this._btnClose.off (Laya.Event.CLICK, this, this.onBack);
            this._btnShop.off (Laya.Event.CLICK, this, this.openShop);
            // this._view.imgMask.off (Laya.Event.MOUSE_DOWN, this, this.onStopEvent);
            // this._view.imgMask.off (Laya.Event.MOUSE_UP, this, this.onStopEvent);

            this._btnClose = null;
            this._btnShop = null;

            this._view.removeSelf();
            this._view.destroy(true);
            this._view = null;
        }
    }

    private _view:ui.main.TipsUI;
    private _btnClose:ZooButton;
    private _btnShop:ZooButton;
    protected createChildren(sceneName:string):void {
        if(sceneName == "zoo") {
            if(!this._view) {
                this._view = new ui.main.TipsUI();
                this._btnClose = new ZooButton(this._view.btnClose);
                this._btnShop = new ZooButton(this._view.btnShop);
            }
            this._view.on (Laya.Event.CLICK, this, this.onClick);
            this._view.on (Laya.Event.MOUSE_DOWN, this, this.onMouseDown);
            // this._view.on (Laya.Event.MOUSE_UP, this, this.onMouseUp);
            // this._view.imgMask.on (Laya.Event.MOUSE_DOWN, this, this.onStopEvent);
            // this._view.imgMask.on (Laya.Event.MOUSE_UP, this, this.onStopEvent);
            this._btnClose.on (Laya.Event.CLICK, this, this.onBack);
            this._btnShop.on (Laya.Event.CLICK, this, this.openShop);
        }
    }

    private onStopEvent(e:Laya.Event):void {
        e.stopPropagation ();
    }

    private openShop():void {
        this.onBack ();
        moduleBridge.shopCtrl.showShop (0);
    }

    private onChangeClothing(dir:string):void {
        moduleBridge.shipCtrl.changeClothes (dir);
    }

    private updateView():void {
        this._view.mouseThrough = false;
    }

    public showTips():void {
        if(this._sceneName == "zoo") ZY.stage.addChild(this._view, ZY.Layer.MIDDLE);
        Laya.timer.callLater(this, this.updateView);
    }

    private onBack():void {
        // moduleBridge.shipCtrl.backToShip();
        this._view.removeSelf();
    }

    private onClick(e:Laya.Event):void {
        let target = e.target;
        // console.log (target);
        switch (target) {
            case this._view.btnClose:
                this.onBack ();
                break;
            case this._view.btnShop:
                this.openShop ();
                break;
            default:
                e.stopPropagation ();
                break;
        }
    }

    private onMouseDown(e:Laya.Event):void {
        e.stopPropagation ();
        // let target = e.target;
        // switch (target) {
        //     case this._view.btnClose:
        //         break;
        //     case this._view.btnShop:
        //         break;
        //     default:
        //         e.stopPropagation ();
        //         break;
        // }
    }

    // private onMouseUp(e:Laya.Event):void {
    //     let target = e.target;
    //     switch (target) {
    //         case this._view.btnClose:
    //             break;
    //         case this._view.btnShop:
    //             break;
    //         default:
    //             e.stopPropagation ();
    //             break;
    //     }   
    // }

}

moduleBridge.tipsCtrl = new TipsCtrl();