import { ui } from "../ui/layaMaxUI";

export default class WarningInfo {
    constructor(){

    }

    private _heartBg:Laya.Sprite;
    private _warnCircle:Laya.Sprite;
    private _heart:Laya.Sprite;
    private _warnAni:Laya.Animation;
    private _progress:Laya.Sprite;
    private _mLeft:Laya.Sprite;
    private _mRight:Laya.Sprite;
    private _warnRoot:Laya.Sprite;
    public init(view:Laya.Sprite):void {
        // this._view = view;
        // this._proressMask = new Laya.Sprite();
        // this._progress.mask = this._proressMask;
        this._heartBg = view.getChildByName("bg") as Laya.Sprite;
        this._warnCircle = view.getChildByName("circle") as Laya.Sprite;
        this._heart = view.getChildByName("heart") as Laya.Sprite;
        this._warnAni = view.getChildByName("warn") as Laya.Animation;
        this._progress = view.getChildByName("progress") as Laya.Sprite;
        let node = this._progress.getChildByName("left") as Laya.Sprite;
        this._mLeft = node.getChildAt(0) as Laya.Sprite;

        node = this._progress.getChildByName("right") as Laya.Sprite;
        this._mRight = node.getChildAt(0) as Laya.Sprite;
        this._warnRoot = view;
        this.hideAll();
    }

    public showWarning():void {
        if(!this._warnAni.visible) this._warnAni.visible = true;
        this._warnAni.stop();
        this._warnAni.play(0);
        if(this._heartBg.visible) {
            this._heart.visible = false;
            this._warnCircle.visible = true;
        }
    }

    public showHeart(t:number, caller:any, callback:Function):void {
        this._heartBg.visible = true;
        this._heart.visible = true;
        this._progress.visible = true;

        this._mLeft.rotation = 0;
        this._mRight.rotation = 180;

        t = t*1000;
        let k = 0;
        let timer = Laya.timer;
        
        timer.frameLoop(1, this, ()=>{
            k+=timer.delta;
            if(k>=t) {
                timer.clearAll(this);
                k = null;
                t = null;
                timer = null;
                this._heartBg.visible = false;
                this._heart.visible = false;
                this._progress.visible = false;
                if(this._warnAni.visible) {
                    this._warnCircle.visible = false;
                }
                if(caller && callback) {
                    callback.call(caller);
                }
                caller = null;
                callback = null;
                return;
            }
            let r = k/t*360;

            if(r>180) {
                this._mLeft.rotation = 180;
                this._mRight.rotation = r;
            } else {
                this._mLeft.rotation = r;
                this._mRight.rotation = 180;
            }
        });
    }

    // public warningSpeed(speed:number):void {
    //     this._warnAni.interval = 1000/30/speed;
    //     // this._warnAni.play();
    // }

    public hideWarning():void {
        this._warnAni.stop();
        this._warnAni.visible = false;
        this._warnCircle.visible = false;

        if(this._heartBg.visible) {
            this._heart.visible = true;
        }
    }

    public get showed():boolean {
        return this._warnAni.visible || this._heartBg.visible;
    }
    
    public hideAll():void {
        this._warnAni.stop();
        this._heartBg.visible = false;
        this._warnCircle.visible = false;
        this._progress.visible = false;
        this._heart.visible = false;
        this._warnAni.visible = false;

        Laya.timer.clearAll(this);
    }
    public pos(x:number,y:number):void {
        this._warnRoot.pos(x,y,true);
    }
}