export default class ZooButton extends Laya.EventDispatcher{
    private _skin:Laya.Image;
    private _btn:any;
    private _light:Laya.Image;
    private _lightParent:Laya.Sprite;
    private _pack:string;
    private _upDistance:number;
    private _downDistance:number;
    constructor(btn:Laya.Button, light:Laya.Image=null, pack:string = "main", upDistance:number = -40, downDistance:number = -28) {
        super();
        this._pack = pack;
        this._upDistance = upDistance;
        this._downDistance = downDistance;
        btn.on(Laya.Event.MOUSE_DOWN, this, this.mouseDownHandler);
        btn.on(Laya.Event.MOUSE_OUT, this, this.mouseUpHandler);
        btn.on(Laya.Event.CLICK, this, (e:Laya.Event)=>{
            e.stopPropagation();
            this.event("click");
        });
        this._btn = btn;
        // btn.on(Laya.Event.MOUSE_UP, this, this.mouseUpHandler);

        Laya.stage.on(Laya.Event.MOUSE_UP, this, this.mouseUpHandler);

        this._skin = btn.getChildAt(0) as Laya.Image;
        this._light = light;
        if(light){
            light.visible = false;
            this._lightParent = light.parent as Laya.Sprite;
            this.showLight();
        }

        this._skin.on(Laya.Event.MOUSE_DOWN, this, this.mouseDownHandler);
        this._skin.on(Laya.Event.MOUSE_OUT, this, this.mouseUpHandler);
        this._skin.on(Laya.Event.CLICK, this, (e:Laya.Event)=>{
            e.stopPropagation();
            this.event("click");
        })

    }

    public clear():void {
        this.hideLight();

        this._skin.offAllCaller(this);
        this._btn.offAllCaller(this);
        Laya.stage.offAllCaller(this);
        // this.offAll("click");
        this._skin = null;
        this._btn = null;
        this._light = null;
        this._lightParent = null;
        this._pack = null;
    }

    public showLight():void {
        if(!this._light) return;
        this._light.visible = true;
        this._light.skin = null;
        this._light.sizeGrid = "24,24,24,24";
        let  i = -1;
        Laya.systemTimer.frameLoop(2, this, ()=>{
            if(!this._skin.displayedInStage) return;
            if(i == -1) {
                // this._light.size(this._lightParent.width-4,this._lightParent.width-4);
                this._light.skin = this._pack+"/button_patch.png";
                this._light.pos(0,0,true);
                this._light.size(this._lightParent.width,this._lightParent.height);
                // this._light.height = 118;
            } else if(i>19 && i<26){
                // this._light.size(720-4,92-4);
                this._light.skin = this._pack+"/button_patchHilight"+(i-20).toString()+".png";
                this._light.pos(0,0,true);
                this._light.size(this._lightParent.width,this._lightParent.height);
                // this._light.height = 92;
            } else if(i==0 || i == 26){
                this._light.skin = null;
            } else {
                if(i >= 30) {
                    i = -2;
                }
            }
            ++i;
        })
    }

    public hideLight():void {
        if(!this._light) return;
        this._light.visible = false;
        Laya.systemTimer.clearAll(this);
    }

    // private _touchID:number;
    public mouseDownHandler(e:Laya.Event):void {
        this._skin.y = this._downDistance;
        e.stopPropagation();
    }

    public mouseUpHandler(e:Laya.Event):void {
        this._skin.y = this._upDistance;
        e.stopPropagation();
    }
}