class Bridge {
    constructor() {

    }

    shipCtrl:ShipCtrl;
    gameCtrl:GameCtrl;
    upgradeCtrl:UpgradeCtrl;
    animalMoveCtrl:AnimalMoveCtrl;
    teachCtrl:TeachCtrl;
    mapCtrl:MapCtrl;
    touristMoveCtrl:TouristMoveCtrl;
    shopCtrl:ShopCtrl;
    hatCtrl:HatCtrl;
    settingCtrl:SettingCtrl;
    prizeCtrl:PrizeCtrl;
    freeJewCtrl:FreeJewCtrl;
    player:Player;
    worldCreater:WorldCreater;
    animalManager:AnimalManager;
    tipsCtrl:TipsCtrl;
    endangeredAnimals:EndangeredAnimals;
    taskCtrl:TaskCtrl;
    dayMissionCtrl:DayMissionCtrl;
    rankCtrl:RankCtrl
    attentionCtrl:AttentionCtrl;
    inviteCtrl:InviteCtrl;
    giftbagCtrl:GiftBagCtrl;
    onedollarctrl:oneDollarCtrl;
    videoTipCtrl: VideoTipCtrl;
    questionCtrl: QuestionCtrl;
    festivalActivityCtrl: FestivalActivityCtrl;

    public warning:WarningInfo;

    private _flash:Laya.Sprite;
    public flashScreen():void {
        if(!this._flash || this._flash.destroyed) this._flash = new Laya.Sprite();
        this._flash.graphics.clear();
        this._flash.graphics.drawRect(0,0, Laya.stage.width, Laya.stage.height, "#ffffff");
        this._flash.alpha = 0.8;
        ZY.stage.addChild(this._flash, ZY.Layer.TOP);

        ZY.Tween.to(this._flash, {alpha:0}, 300, Laya.Ease.linearInOut, Laya.Handler.create(this, ()=>{
            this._flash.removeSelf();
        }), 0, true, true, false);
    }

    private _paused:boolean = false;
    private _timerScale:number = 1;
    public get paused():boolean {
        return this._paused;
    }

    public set paused(val:boolean) {
        if(this._paused === val) return;
        this._paused = val;
        if(val) {
            this._timerScale = Laya.timer.scale;
            Laya.timer.scale = 0;
        } else {
            Laya.timer.scale = this._timerScale;
        }
    }

    Animal:any;
    AnimalFlyer:any;
    Player:any;
}

export const moduleBridge = new Bridge();