// import  Sprite3D:Laya.Sprite3D;
export default class CloudShooter {
    private _list:Array<any>;
    private _funLis:any;
    private _v:number = 0.02;
    private _scene:Laya.Scene3D;
    constructor() {
        this._list = [];
        this._funLis=[this.createCloudTet_0,this.createCloudTet_1,this.createCloudTet_2];
    }

    public clear():void {
        Laya.timer.clearAll(this);
        while (this._list.length > 0) {
            this.recoverItem(this._list.pop());
        }
    }

    public start(scene:Laya.Scene3D,time:number=100):void {
        this.clear();
        this._scene = scene;
        Laya.timer.frameLoop(1,this, this.loop);
        Laya.timer.loop(1500,this, this.shoot);
        if(time > 0) {
            for (let i = 0; i < time; i++) {
                this.shoot((time-i)*2);
            }
        }
    }

    private loop():void {
        let s = this._v * Laya.timer.delta;
        for (let i = 0; i < this._list.length; i++) {
            const tet = this._list[i];
            if(tet.transform.localPositionZ < -160) {
                this._list.splice(i,1);
                i--;
                this.recoverItem(tet);
            } else {
                tet.transform.localPositionZ -= s;
            }
        }
    }

    private recoverItem(tet:Laya.Sprite3D):void {
        tet.removeSelf();
        Laya.Pool.recover(tet.name,tet);
    }

    private shoot(time:number=0):void {
        let i = Math.floor(Math.random()*3);
        let tet:Laya.Sprite3D = Laya.Pool.getItemByCreateFun("CloudTet_"+i,this._funLis[i]);
        let x = Math.floor(Math.random()*1500) - 500;
        let y = -180-Math.floor(Math.random()*420);
        let z = 2200-this._v*time*1000;
        // x = 0;
        // y = 0;
        // z = 0;
        tet.transform.localPosition = new Laya.Vector3(x,y,z);
        this._list.push(tet);
        this._scene.addChild(tet);
    }
    

    private createCloudTet_0():Laya.Sprite3D {
        let tet = Laya.Sprite3D.instantiate(Laya.loader.getRes("ship/Conventional/cloudTet_0.lh"));
        tet.name = "CloudTet_0";
        return tet;
    }
    private createCloudTet_1():Laya.Sprite3D {
        let tet = Laya.Sprite3D.instantiate(Laya.loader.getRes("ship/Conventional/cloudTet_1.lh"));
        tet.name = "CloudTet_1";
        return tet;
    }
    private createCloudTet_2():Laya.Sprite3D {
        let tet = Laya.Sprite3D.instantiate(Laya.loader.getRes("ship/Conventional/cloudTet_2.lh"));
        tet.name = "CloudTet_2";
        return tet;
    }
}