import { ZY } from "../ZY";
import ZooButton from "../script/ZooButton";
import { ui } from "../ui/layaMaxUI";
import { moduleBridge } from "../Interface";


export default class VideoTipCtrl extends ZY.BaseCtrl {
    constructor() { super(); }
    
    public start():void {
        super.start();
        this.bindAssets("res/atlas/btn.atlas", Laya.Loader.ATLAS, "zoo");
        this.bindAssets("res/atlas/btn.atlas", Laya.Loader.ATLAS, "game");
    }

    protected beforeEnterScene(sceneName:string):void {
        if(this._sceneName == "zoo" || this._sceneName == "game") {
            this._btnClose.clear();
            this._btnClose = null;

            this._callBack = null;
            this._caller = null;

            this._view.removeSelf();
            this._view.destroy(true);
            this._view = null;
        }
    }

    private _view:ui.main.VideoTipsUI;
    private _btnClose:ZooButton;

    protected createChildren(sceneName:string):void {
        if(sceneName == "zoo" || sceneName == "game") {
            if(!this._view) {
                this._view = new ui.main.VideoTipsUI();
                this._btnClose = new ZooButton(this._view.btnClose, null, null, -23, -10);
            }
            this._btnClose.on(Laya.Event.CLICK, this, this.onBack);
        }
    }

    private updateView():void {
       
    }

    private static TIPS: Array<string> = [
        "很抱歉，系统识别您今日游戏时长已经超过90分钟，您的游戏将强制下线",
        "很抱歉，系统识别您今日游戏时长已经超过180分钟，您的游戏将强制下线",
        "休息时间到，系统规定未成年人在22:00-8:00不得提供游戏服务，游戏即将下线。",
        "很抱歉，本游戏适合玩家年龄8岁以上才能进行充值哦！",
        "很抱歉，系统识别到您这周累计付费已经达到50元，本周不能继续充值了。",
        "很抱歉，系统识别到您这周累计付费已经达到100元，本周不能继续充值了。",
        "很抱歉，系统识别到您本月累计付费已经达到200元，本月不能继续充值了。"
    ]
    
    private _caller: any;
    private _callBack: any;
    public showTips(caller: any = null, callback: any = null, scene: number = 0):void {
        if(this._sceneName == "zoo" || this._sceneName == "game") ZY.stage.addChild(this._view, ZY.Layer.MIDDLE);
        //Laya.timer.callLater(this, this.updateView);

        if (scene == 0) {
            this._view.imgVideo.visible = true;
            this._view.textTips.visible = false;
        } else {
            this._view.imgVideo.visible = false;
            this._view.textTips.visible = true;
            if (ZY.channel == "bd") {
                if (scene == 1) {
                    this._view.textTips.text = VideoTipCtrl.TIPS[0];
                } else if (scene == 2) {
                    this._view.textTips.text = VideoTipCtrl.TIPS[1];
                } else if (scene == 3) {
                    this._view.textTips.text = VideoTipCtrl.TIPS[2];
                }
            }
        }

        this._caller = caller;
        this._callBack = callback;
    }

    private onBack():void {
        this._view.removeSelf();

        if (this._caller && this._callBack) {
            this._callBack.call(this._caller);
        }
    }

}

moduleBridge.videoTipCtrl = new VideoTipCtrl();