		// 		if (moduleBridge.player.state == PlayerState.flying && moduleBridge.player.transform.position.z < this.transform.position.z && 0 > 0 && this.transform.position.z < moduleBridge.player.transform.position.z + 30) {
		// 			let y = this.transform.position.z - moduleBridge.player.jumpPoint.z;
		// 			let tanTheta = Math.tan(Math.PI/180 * moduleBridge.player.jumpAngle);
		// 			let x = y * tanTheta;
		// 			let dist = moduleBridge.player.jumpPoint.x + x - this.transform.position.x;
		// 			if (Math.abs(dist) > 1 && Math.abs(dist) < 15) {
        //                 tmp = new Vector2();
        //                 Vector2.normalize(ZY.Temp.getVector2(dist,0), tmp);
        //                 this._randomComponent.setValue(tmp.x*moveToLassoo*4, tmp.y*moveToLassoo*4);
		// 				// this._randomComponent = new Vector2(dist, 0).normalized * 4 * moveToLassoo;
		// 			}
		// 		} else {
		// 			this._randomComponent = new Vector2(-1*Math.sin(this._lifetime*2)*8,0);//Vector2.left * Mathf.Sin(lifetime * 2) * 8; // test code
		// 		}
		// 	} else {
		// 		this._randomComponent = new Vector2();
		// 	}
		// }

		//to see if flying animals need to halt swaying
		let changedCourse = false;

        //front obstacles
		let forwardThreat:Threat = this._animal.isClearForward(CollisionGroup.RAYCAST_OBSTACLE|CollisionGroup.WORLD_SIDE|CollisionGroup.PLAYER|CollisionGroup.OBSTACLE);

        if (forwardThreat.dist < 16) {
			if (this.dontAvoidAnimals && forwardThreat.transform != null && (forwardThreat.transform.owner as GameObject).getComponent(moduleBridge.Player)) {
				// intentionally left blank so TRex doesn't avoid player
			// } else if (this.dontAvoidObstacles && forwardThreat.transform.owner.parent && (forwardThreat.transform.owner.parent as GameObject).getComponent(Obstacle) != null && !forwardThreat.transform.parent.gameObject.GetComponent<Obstacle>().isLevelEdge && !forwardThreat.transform.parent.gameObject.GetComponent<Obstacle>().isCenterEdge) {
                // intentionally left blank so gorilla/TRex doesn't avoid obstacles
                // Debug.LogError("11111111");
            } else {
                if (forwardThreat.pointOfRayImpact.x < forwardThreat.transform.position.x) {
                    finalVector.setValue(finalVector.x-(16-forwardThreat.dist), finalVector.y, finalVector.z);
                    // finalVector += new Vector3((16 - forwardThreat.dist), 0, 0);
                } else {
                    // finalVector += new Vector3(-(16 - forwardThreat.dist), 0, 0);
                    finalVector.setValue(finalVector.x+(16-forwardThreat.dist), finalVector.y, finalVector.z);
                }
            }
			changedCourse = true;
		} else {
            finalVector.setValue(finalVector.x+this._randomComponent.x, finalVector.y+this._randomComponent.y, finalVector.z);
			// finalVector += new Vector3(randomComponent.x, randomComponent.y, 0);
		}


		//avoid sideobstacles
		//check if in side obstacle
		// GameObject sideObstacle = animal.CheckFromAbove();
		// if (sideObstacle != null) {
        //     Obstacle ob = sideObstacle.GetComponent<Obstacle>();
        //     if (ob != null && ob.isLevelEdge) {
        //         if (sideObstacle.transform.lossyScale.x < 0) {
        //             //right
        //             finalVector = new Vector3(Math.min(finalVector.x, -12), finalVector.y / 16, 3);
