        //             //left
        //             finalVector = new Vector3(Math.max(finalVector.x, 12), finalVector.y / 16, 3);
        //         }
        //     } else {
        //         if (!dontAvoidObstacles) {
        //             if (transform.position.z > Player.Instance.transform.position.z + AnimalSpawner.Instance.animalSpawnPosition - 10 && transform.position.z < Player.Instance.transform.position.z + AnimalSpawner.Instance.animalSpawnPosition + 10) {
        //                 animal.TeleportOutOfObstacle();
        //             } else {
        //                 if (transform.position.x < sideObstacle.transform.position.x) {
