import { ZY } from "../ZY";

// import Game from "./Game";

export default class PfxHelper {
    // static show(x:number,y:number,z:number,canBreak:boolean):void {
    //     let p = Laya.aaaaaa.getItemByClass("PfxTreeBreak",PfxTreeBreak);
    //     Game.gameStage.addChild(p);
    //     p.show(x,y,z,canBreak);
    //     Laya.timer.once(1000,p,()=>{
    //         Laya.aaaaaa.recover("PfxTreeBreak",p);
    //     });
    // }

    private static _instance:PfxHelper;
    public static get instance():PfxHelper {
        if(!this._instance) {
            this._instance = new PfxHelper();
        }
        return this._instance;
    }

    // private _cfg:any;
    // private _pfxTime:any;
    private _pfxInfo:any;
    constructor() {
        
    }
    public init():void {
        if(this._pfxInfo) return;
        let cfg = Laya.loader.getRes("cfg/cfgPfx.json");
        // this._cfg = cfg;
        this._pfxInfo = {};
        let list = [];
        if (cfg){
            for (let i = 0; i < cfg.length; i++) {
                const pinfo = cfg[i];
                this._pfxInfo[pinfo.name] = pinfo;
            }
        }
    }
    public clearAndDestroy():void {
        let p:Laya.Sprite3D;
        while(this._activePfx.length > 0) {
            p = this._activePfx.pop();
            if(p) {
                Laya.timer.clearAll(p);
                p.removeSelf();
                p.destroy(true);
            }
        }

        let pool:Array<Laya.Sprite3D>;
        for (const key in this._caches) {
            if (this._caches.hasOwnProperty(key)) {
                pool = this._caches[key];
                while(pool && pool.length > 0) {
                    p = pool.pop();
                    if(p) {
                        p.destroy(true);
                    }
                }
            }
        }
        p = null;
        pool = null;
    }
    public createCache(name:string, cnt:number):void {
        // console.log("创建粒子缓存:"+name+","+cnt);
        let tmp = [];
        for (let i = 0; i < cnt; i++) {
            tmp[i] = this.createPfx(name);
        }
        for (let i = 0; i < cnt; i++) {
            this.recoverPfx(name, tmp[i]);
        }
    }
    private createPfx(name:string):Laya.Sprite3D {
        let pool = this._caches[name] as Array<Laya.Sprite3D>;
        if(pool && pool.length > 0) return pool.pop();

        let pinfo = this._pfxInfo[name];
        if(pinfo) {
            let url;
            if(pinfo.scene && pinfo.scene[ZY.stage.sceneName]) {
                url = pinfo.scene[ZY.stage.sceneName];
            } else {
                url = pinfo.prefab;
            }
            return Laya.Sprite3D.instantiate(Laya.loader.getRes(url)); 
        }
    }
    private recoverPfx(name:string, pfx:Laya.Sprite3D):void {
        let pool = this._caches[name] as Array<Laya.Sprite3D>;
        if(!pool) {
            pool = [];
            this._caches[name] = pool;
        }

        if(pool.indexOf(pfx) === -1) {
            pool.push(pfx);
        }
    }
    private _caches:any = {};
    private _activePfx:Array<Laya.Sprite3D> = [];
    private create(name:string, time:number):any {
        if(!this._pfxInfo[name]) return;
        let pfx = this.createPfx(name);
        if(!pfx) return null;
        if(time === -1) {
            time = this._pfxInfo[name].time*1000;
        } else {
            time = time*1000;
        }
        if(time > 0) {
            this._activePfx.push(pfx);
            let me = this;
            Laya.timer.once(time, pfx, ()=>{
                if(pfx && !pfx.destroyed) {
                    me.stop(pfx);
                    pfx.removeSelf();
                    let i = me._activePfx.indexOf(pfx);
                    if(i > -1) me._activePfx.splice(i, 1);
                    me.recoverPfx(name, pfx);
                } else {
                    pfx = null;
                }
            });
        }
        return pfx;
    }
    private stop(pfx:any):void {
        if(pfx instanceof Laya.ShuriKenParticle3D) {
            pfx.particleSystem.stop();
        }
        for (let i = 0; i < pfx.numChildren; i++) {
            let child = pfx.getChildAt(i);
            this.stop(child);
        }
    }
    private play(pfx:any):void {
        if(pfx instanceof Laya.ShuriKenParticle3D) {
            pfx.particleSystem.play();
        }
        for (let i = 0; i < pfx.numChildren; i++) {
            let child = pfx.getChildAt(i);
            this.play(child);
        }
    }
    /**
     * 显示一个cfgPfx.json里面配置过的特效
     * @param pfxName 特效名字
     * @param position 特效的位置
     * @param parent 特效需要加入的容器，默认是null，则加入世界中
     * @param isLocal 是否是local坐标
     * @param time 粒子持续时间，单位是s，默认是-1表示根据配置来
     */
    public show(pfxName:string, position:Laya.Vector3,parent:Laya.Node=null,isLocal:boolean=true,time:number=-1):Laya.Sprite3D {
        let pfx = this.create(pfxName, time);
        if(!pfx) return;
        parent = parent || ZY.stage.scene3D;
        parent.addChild(pfx);
        if(isLocal) {
            position.cloneTo(pfx.transform.localPosition);
            pfx.transform.localPosition = pfx.transform.localPosition;
        }else {
            position.cloneTo(pfx.transform.position);
            pfx.transform.position = pfx.transform.position;
        }
        this.play(pfx);
        return pfx;
    }
}