    protected beforeEnterScene(sceneName:string):void {
        if(this._sceneName == "zoo") {
            this._btnBack.off("click", this, this.onBack);
            this._btnBack.clear();
            this._btnBack = null;
            this._btnTipClose.clear();
            this._btnTipClose = null;
            // ZY.eventBus.off("NATIVE_AD",this,this.initActive);
            // ZY.eventBus.off("NATIVE_AD",this,this.layoutNativeAd);
            this._view.btnDayTask.off(Laya.Event.CLICK, this, this.onTab);
            this._view.btnCollectProgress.off(Laya.Event.CLICK, this, this.onTab);

            if(ZY.channel === ZY.Channel.oppo) {
                nativeAd.clearAd("201533");
            }else if(ZY.channel === ZY.Channel.vivo){
                nativeAd.clearAd("abea873d8eef4a7d904c27e005acebea")
            }
            this._view.removeSelf();
            this._view.destroy(true);
            this._view = null;

        }
    }

