    private _currentSelectIndex: number;
    private onTab(index: number): void {
        if (index == this._currentSelectIndex) {
            return;
        }
        this._currentSelectIndex = index;
        if (index == 0) {
            this._view.btnDayTask.skin = "daymission/tab2.png";
            (this._view.btnDayTask.getChildByName("imgIcon") as Laya.Image).skin = "daymission/tabIcon2.png";
            (this._view.btnDayTask.getChildByName("imgText") as Laya.Image).skin = "daymission/rw2.png";

            this._view.btnCollectProgress.skin = "daymission/tab3.png";
            (this._view.btnCollectProgress.getChildByName("imgIcon") as Laya.Image).skin = "daymission/tabIcon3.png";
            (this._view.btnCollectProgress.getChildByName("imgText") as Laya.Image).skin = "daymission/jd1.png";

            this._view.boxMu.visible = true;
            this._view.boxCollectPro.visible = false;
        } else if (index == 1) {
            this._view.btnDayTask.skin = "daymission/tab1.png";
            (this._view.btnDayTask.getChildByName("imgIcon") as Laya.Image).skin = "daymission/tabIcon1.png";
            (this._view.btnDayTask.getChildByName("imgText") as Laya.Image).skin = "daymission/rw1.png";

            this._view.btnCollectProgress.skin = "daymission/tab4.png";
            (this._view.btnCollectProgress.getChildByName("imgIcon") as Laya.Image).skin = "daymission/tabIcon4.png";
            (this._view.btnCollectProgress.getChildByName("imgText") as Laya.Image).skin = "daymission/jd2.png";

            this._view.boxMu.visible = false;
            this._view.boxCollectPro.visible = true;
        }
    }

    private onCollectBtn(i, j): void {
        let data: any = this._collectData[i][j];
        if (data.status == 0) {
            ZY.msg.show("收集完成才能领取奖励");
        } else if (data.status == 1) {
            userData.addGold(data.reward, 1024);
            if (i == 0) {
                userData.set("collectSum" + data.id, 2);
            } else if (i == 1) {
                userData.set("collectMapOne" + data.id, 2);
            } else if (i == 2) {
                userData.set("collectMapTwo" + data.id, 2);
            }

            this.updateCollectPro();
        }
    }

    private onGainTwo():void {

        let img0 = this._view.btnGain.getChildAt (0) as Laya.Image;
        let imgSkin = img0.getChildAt (0) as Laya.Image;
        imgSkin.skin = "daymission/img_20.png"
        this._view.btnGain.disabled = true;
        userData.addGold (30, 1006);
        userData.addJew (5, 1007)
        userData.set ("hasGainDayMisAward", [1,1,1,1,1,1,1,1,1]);
    }

    private onCloseTip():void {
        this._view.boxTip.visible = false;   
    }

    private stopMaoPao():void {
        this._view.on (Laya.Event.MOUSE_DOWN, this, (e:Laya.Event)=>{
            e.stopPropagation ();
        })
        for (let i:number = 0; i < this._view.numChildren; i ++) {
            let node = this._view.getChildAt (i) as Laya.Sprite;
            node.on (Laya.Event.MOUSE_DOWN, this, (e:Laya.Event)=>{
                e.stopPropagation ();
            })
        }
    }

    private updatePanel():void {

        let arr: Array<any> = [];
        for (let i = 0; i < this._misArr.length; i++) {
            arr[i] = this._misArr[i];
        }
        arr.sort(this.sortFunc);


        let boxPanel = this._view.panel.getChildAt (0) as Laya.Box;
        let count = (ZY.channel == "hw" || ZY.channel == "none") ? 5 : boxPanel.numChildren
        for (let i:number = 0;i<count; i ++) {
            let data = arr[i];
            let cell = boxPanel.getChildAt (i) as Laya.Box;
            if (ZY.channel == "oppo"){
                if (cell.name){
                    boxPanel.height = 1070;
                    continue
                }
                if (i >= 2){
                    cell.y = (i+1)*120
                }
            }else if (cell.name){
                continue
            }else if (ZY.channel == "hw" || ZY.channel == "none"){
                boxPanel.height = 710;
            }else{
                boxPanel.height = 950;
            }
            let labelTip = cell.getChildByName ("labelTip") as Laya.Label;
            let btn = cell.getChildByName ("btn") as Laya.Button;
            let labelGold = cell.getChildByName ("labelGold") as Laya.Label;
            let btnFunc = (btn.getChildAt (0) as Laya.Image).getChildAt (0) as Laya.Image;
            labelTip.text = data.name;
            labelGold.text = data.award + "";
            let zooBtn = this["_btn" + i];
            if (!zooBtn) return;
            // let zooBtn = new ZooButton (btn);
            // 0未完成 1完成但未领奖 2完成且已领奖
            zooBtn.off (Laya.Event.CLICK, this, this.openPrize);
            zooBtn.off (Laya.Event.CLICK, this, this.openSignin);
            zooBtn.off (Laya.Event.CLICK, this, this.onGameStart);
            zooBtn.off (Laya.Event.CLICK, this, this.onClick);

            if (data.status == 2) {
                btnFunc.skin = "daymission/img_20.png";
                btn.disabled = true;
                btn.skin = "daymission/img_5.png";
                (btn.getChildAt (0) as Laya.Image).skin = "btn/img_6.png";
            } else if (data.status == 1) {
                btnFunc.skin = "daymission/img_15.png";
                btn.disabled = false;
                btn.mouseEnabled = true;
                btn.skin = "daymission/img_5.png";
                (btn.getChildAt (0) as Laya.Image).skin = "btn/img_6.png";
                
                zooBtn.on (Laya.Event.CLICK, this, this.onClick, [data.id]);
            } else if (data.status == 0) {
                // 未完成
                btn.skin = "btn/img_weiwan1.png";
                (btn.getChildAt (0) as Laya.Image).skin = "btn/img_purple_0.png";
                if (data.func > 0) {
                    btnFunc.skin = "daymission/img_16.png";
                    if (data.func == 1) {
                        zooBtn.on (Laya.Event.CLICK, this, this.onGameStart);
                    } else if (data.func == 2) {
                        zooBtn.on (Laya.Event.CLICK, this, this.openSignin, [btn]);
                    } else if (data.func == 3) {
                        zooBtn.on (Laya.Event.CLICK, this, this.openPrize);
                    }
                    btn.mouseEnabled = true;
                } else {
                    btnFunc.skin = "daymission/img_24.png";
                    btnFunc.height = 31
                    btnFunc.width = 93
                    // btn.disabled = true;
                    btn.mouseEnabled = true;
                    zooBtn.on (Laya.Event.CLICK, this, ()=>{
                        ZY.msg.show("当前任务尚未完成")
                    });
                }
            }
    
            // 1:去游戏 2:去签到 3:去转盘 
            // if (data.func == 1) {
            //     zooBtn.on (Laya.Event.CLICK, this, this.onGameStart);
            // } else if (data.func == 0) {
            //     zooBtn.on (Laya.Event.CLICK, this, this.onClick);
            // } else if (data.func == 2) {
            //     zooBtn.on (Laya.Event.CLICK, this, this.openSignin);
            // } else if (data.func == 3) {
            //     zooBtn.on (Laya.Event.CLICK, this, this.openPrize);
            // }
        }

        // 完成任务数量
        let account = 0;
        for (let i:number = 0; i < this._misArr.length; i ++) {
            let data = this._misArr[i];
            if (data.status > 0) account ++;
        }
        this.setSlider (account);
    }

    private _sliderWidth:number = 480;
    private setSlider(num:number):void {
        let img0 = this._view.btnGain.getChildAt (0) as Laya.Image;
        let imgSkin = img0.getChildAt (0) as Laya.Image;
        imgSkin.skin = "daymission/img_23.png"
        if (ZY.channel == "hw"){
            this._view.labelSlider.text = num + "/6";
        }else{
            this._view.labelSlider.text = num + "/8";
        }
        this._view.imgSlider.width = (this._sliderWidth-30) / 8 * num + 30;
        if (num >= 8) {
            //改按钮
            let flag = userData.get("hasGainDayMisAward",[0,0,0,0,0,0,0,0,0])
            if (flag[8] == 0){
                this._view.btnGain.disabled = false;
            }else{
                this._view.btnGain.disabled = true;
            }
        } else {
            this._view.btnGain.disabled = true;
        }
    }


