    // 未领取状态
    private updateMisArr():void {
        // console.log (userData.dailyTask);
        let freshTime = userData.get ("freshDayMisAwardTime", 0);
        let now = Laya.Browser.now ();
        let dateNow = new Date (now);
        let dateLast = new Date (freshTime);
        let temp = [0,0,0,0,0,0,0,0,0];
        if (dateNow.setHours (0,0,0,0) != dateLast.setHours (0,0,0,0)) {
            userData.set ("freshDayMisAwardTime", now);
            userData.set ("hasGainDayMisAward", temp);// 0没领  1领了
        }
        let dayMisAward = userData.get ("hasGainDayMisAward", temp);
        let dailyTask = userData.dailyTask;
        for (let i:number = 0;i<this._misArr.length;i++) {
            let data = dailyTask[i];
            if (data[1]) {
                // 完成 
                this._misArr[i].status = 1;
                if (dayMisAward[i] > 0) this._misArr[i].status = 2
            } else {
                this._misArr[i].status = 0;
            }
        }

        this.updatePanel();
    }

    private sortFunc(a: any, b: any): number {
        if (a.status == 2 && b.status == 2) {
            return 0;
        } else {
            if (a.status == 2) {
                return 1;
            } else if (b.status == 2) {
                return -1;
            } else {
                if (a.status == 1 && b.status == 1) {
                    return 0;
                } else {
                    if (a.status == 1) {
                        return -1;
                    } else if (b.status == 1) {
                        return 1;
                    } else {
                        return 0;     
                    }
                }
            }
        }
    }

    private updateCollectPanel(): void {
        
        for (let i = 0; i < 6; i++) {
            let txt: Laya.Label = this._view.imgAchievement.getChildByName("txt" + i) as Laya.Label;
            txt.text = this._achieveDesc[i].replace("X", this._achieveData[i] + ""); 
        }
         
        let panel: Laya.Panel = this._view.boxCollectPro.getChildByName("panel") as Laya.Panel;
        for (let i = 0; i < panel.numChildren; i++) {
            let box: Laya.Box = panel.getChildAt(i) as Laya.Box;
            for (let j = 1; j < box.numChildren; j++) {
                let child: Laya.Box = box.getChildAt(j) as Laya.Box;
                if (child) {
                      let labelTip: Laya.Label = child.getChildByName("labelTip") as Laya.Label;
                      let data: any = this._collectData[i][j-1];
                      if (i == 0) {
                         labelTip.text = this._collectItemDesc[j-1].replace("X", data.current + "");         
                      } else {
                         labelTip.text = this._collectItemDesc[j].replace("X", data.current + "/" + data.target + "");
                      }
                      
                      let labelTip2: Laya.Label = child.getChildByName("labelTip2") as Laya.Label;
                      if (labelTip2) {
                          let progress: number = (userData.stat_animals + userData.stat_upencs + userData.stat_hats + userData.mapLevel) / (565+612+269+50) * 100;
                          labelTip2.text = "完成进度" + progress.toFixed(0) + "%";
                      }

                      let labelGold: Laya.Label = child.getChildByName("labelGold") as Laya.Label;
                      if (labelGold) {
                          labelGold.text = data.reward + "";
                      }
                      
                      let imgProgress: Laya.Image = child.getChildByName("imgProgress") as Laya.Image;
                      if (imgProgress) {
                          imgProgress.width = Math.min(460, 460 * (data.current/data.target));
                      }
                      
                      let btn: Laya.Button = child.getChildByName("btn") as Laya.Button;
                      if (data.status == 0) {
                          btn.skin = "btn/img_weiwan1.png";
                          (btn.getChildAt(0) as Laya.Image).skin = "btn/img_purple_0.png";
                          (btn.getChildAt(0).getChildAt(0) as Laya.Image).skin = "daymission/jxz.png";
                          btn.disabled = false;
                      } else if (data.status == 1) {
                          btn.skin = "daymission/img_5.png";
                         (btn.getChildAt(0) as Laya.Image).skin = "btn/img_6.png";
                         (btn.getChildAt(0).getChildAt(0) as Laya.Image).skin = "daymission/img_15.png";
                         btn.disabled = false;
                      } else if (data.status == 2) {
                         btn.skin = "daymission/img_5.png";
                         (btn.getChildAt(0) as Laya.Image).skin = "btn/img_6.png";
                         (btn.getChildAt(0).getChildAt(0) as Laya.Image).skin = "daymission/img_20.png";
                         btn.disabled = true;
                      }

                }
            }
        }
    }
    
