import { ZY } from "./ZY";
import { reporter, ReporterPoints } from "./Reporter";

class ResLoadRule extends ZY.BaseCtrl {
    constructor() { super(); }
    public start():void {
        super.start();
        // ZY.event = new Laya.EventDispatcher();
    }

    private backProgress(p:number):void {
        console.log("后台加载进度:"+p);
        if(this._sceneName == "check") {
            ZY.stage.setLoadProgress(p);
        }
    }

    private backLoadError(err:string):void {
        console.log("后在加载错误:"+err);
    }

    private _completed:boolean;
    private backLoadComplete():void {
        console.log("后台加载完成");
        this._completed = true;
        if(this._sceneName == "check") {
            ZY.stage.enterScene("zoo");
        }
        if (ZY.channel == "oppo"){
            reporter.upload(ReporterPoints.backLoadComplete)
        } else if (ZY.channel === "bd") {
            ZY.reportEvent("backLoadComplete");
        }
    }

    public get audioFirstLoaded():boolean {
        return this._audioFirstLoaded;
    }

    private _audioFirstLoaded:boolean;
    // protected createChildren(sceneName:string):void {
        
    // }

    protected afterEnterScene(sceneName:string):void {
        if(sceneName == "teach") {
            if(ZY.channel === "wx" || ZY.channel === "zj" || ZY.channel === "qq" || ZY.channel === "oppo" || ZY.channel == "vivo" || ZY.channel === "bd") {
                this._completed = false;
                this._audioFirstLoaded = false;
                ZY.assetsManger.checkAndLoad(["audio_first"], this, (p)=>{

                },(err:any)=>{
                    console.log("load audio error:"+err);
                }, ()=>{
                    this._audioFirstLoaded = true;
                    ZY.eventBus.event("audio_first");
                });
                ZY.assetsManger.checkAndLoad(["game"], this, this.backProgress, this.backLoadError, this.backLoadComplete);
            } else {
                this._completed = true;
            }
        } else if(sceneName == "check") {
            if(this._completed) {
                Laya.systemTimer.callLater(ZY.stage, ZY.stage.enterScene, ["zoo"]);
            } else {
                ZY.stage.showLoading("");
            }
        }
    }
}

export const resLoadRule = new ResLoadRule();