import { ZY } from "./ZY";
import { moduleBridge } from "./Interface";

export default class NativeAd {
    constructor() {

    }

    private _view:any;
    private _urls:any;
    // private _adDataList:Array<any>;
    public init():void {
        ZY.eventBus.on("NATIVE_AD_LOADED", this, this.onAdInfoLoaded);
        this._view = {};
        this._urls = {};
    }

    private onAdInfoLoaded(id:string):void {
        this.updateView(id);
    }

    private updateView(id:string):void {
        if (ZY.channel === "oppo") {
            if (id === "207983" && moduleBridge.giftbagCtrl.giftIndex == 2) {
                return;
            }
        }
        
        let list = ZY.ad.getNativeAdList(id);
        let info = list[0];
        let p = this._view[id] as Laya.Sprite;
        if(p) {
            if(!p.visible) p.visible = true;
            if((p as Laya.Sprite).name === "banner") {
                // banner;
                let title = p.getChildAt(2) as Laya.Label
                if (title){
                    title.text = info.title
                    title.fontSize = 26
                    title.color = "#ffffff"
                    title.pos(25,27)
                    title.size(276,63);
                    title.align = "left";
                    title.valign = "middle";
                    title.bold = true
                    p.visible = true
                }
            } else if ((p as Laya.Sprite).name === "banner2") {
               let title: Laya.Label = p.getChildByName("title") as Laya.Label;
               if (title) {
                   title.text = info.title;
               } 

               let title2: Laya.Label = p.getChildByName("title2") as Laya.Label;
               if (title2) {
                   title2.text = info.desc;
               } 

               let icon: Laya.Image = p.getChildByName("imgIcon") as Laya.Image;
               if (icon && info.icon) {
                   icon.skin = info.icon;
               }

               let pictrue :Laya.Image = p.getChildByName("imgPicture") as Laya.Image;
               if (pictrue && info.imgUrlList[0]) {
                    pictrue.skin = info.imgUrlList[0];
                }
            } else if ((p as Laya.Sprite).name === "bigImage") {
                let icon: Laya.Image = p.getChildByName("imgIcon") as Laya.Image;
                if (icon && info.imgUrlList[0]) {
                   icon.skin = info.imgUrlList[0];
                }
            }
            else {
                // icon;
                let icon = p.getChildAt(0) as Laya.Image;
                if(icon) {
                    if(this._urls[id]) {
                        Laya.loader.cancelLoadByUrl(this._urls[id]);
                    }
                    this._urls[id] = info.icon;
                    Laya.loader.load(info.icon, Laya.Handler.create(this, ()=>{
                        if(this._urls[id] === info.icon) {
                            this._urls[id] = null;
                            icon.texture = Laya.loader.getRes(info.icon);
                            icon.size(p.width, p.height);
                            icon.pos(0,0)
                            p.visible = true
                            console.log("加载完成",icon)
                        }
                    }))
                }
            }
        }
    }

    public showAsBanner(adId:string, parent:Laya.Sprite):void {
        console.log(adId,parent)
        this._view[adId] = parent;
        parent.name = "banner";
        let title:Laya.Label = new Laya.Label;
        parent.addChild(title);

        parent.on("click", this, this.onClickNative, [adId]);
        let list = ZY.ad.getNativeAdList(adId);
        console.log(list)
        if(!list) {
            parent.visible = false;
        } else {
            this.updateView(adId);
        }
    }

    public showAsBanner2(adId: string, parent: Laya.Sprite): void {
        console.log(adId, parent);
        this._view[adId] = parent;
        parent.name = "banner2";

        let bg: Laya.Image = parent.getChildByName("imgbg") as Laya.Image;
        bg.on("click", this, this.onClickNative, [adId]);

        let close: Laya.Image = parent.getChildByName("imgClose") as Laya.Image;
        close.on("click", this, this.onCloseNativeAd, [adId]);
        console.log("==================="+adId)
        let list = ZY.ad.getNativeAdList(adId);
        console.log(list);
        if (!list) {
            parent.visible = false;
        } else {
            this.updateView(adId);
        }
    }

    public showAsBigImage(adId: string, parent: Laya.Sprite): void {
        console.log(adId, parent);
        this._view[adId] = parent;
        parent.name = "bigImage";

        let bg: Laya.Image = parent.getChildByName("imgbg") as Laya.Image;
        bg.on("click", this, this.onClickNative, [adId]);

        let close: Laya.Image = parent.getChildByName("imgClose") as Laya.Image;
        close.on("click", this, this.onCloseNativeAd, [adId]);

        let list = ZY.ad.getNativeAdList(adId);
        console.log(list);
        if (!list) {
            parent.visible = false;
        } else {
            this.updateView(adId);
        }
    }

    public showAsIcon(adId:string, parent:Laya.Sprite):void {
        console.log("显示native广告[icon]:"+adId+","+parent.numChildren);
        this._view[adId] = parent;
        parent.name = "icon";
        if(parent.numChildren > 0) {
            console.error("容器内有子项！！adid="+adId);
            return;
        }
        // while(parent.numChildren > 0)
        let icon:Laya.Image = new Laya.Image();
        parent.addChild(icon);
        icon.on("click", this, this.onClickNative, [adId]);

        let list = ZY.ad.getNativeAdList(adId);
        console.log(list)
        if(!list) {
            parent.visible = false;
        } else {
            this.updateView(adId);
        }
    }

    private onClickNative(adId):void {
        let list = ZY.ad.getNativeAdList(adId);
        if (list){
            ZY.ad.reportNativeClick(adId,list[0].adId)
        }
    }

    private onCloseNativeAd(adId: string): void {
        this._view[adId].visible = false;
        this.clearAd(adId);     
    }

    public clearAd(adId:string):void {
        console.log("清理native广告对象:"+adId);
        let p = this._view[adId];

        if(p) {
            if(p.name === "banner") {
                //banner;
                // let title = p.getChildAt(2) as Laya.Label;
                // title = null;
                (p as Laya.Sprite).offAllCaller(this)
            } else if (p.name === "banner2" || p.name === "bigImage") {
                let bg: Laya.Image = p.getChildByName("imgbg") as Laya.Image;
                bg.offAllCaller(this);
                let close: Laya.Image = p.getChildByName("imgClose") as Laya.Image;
                close.offAllCaller(this);
            } 
            else {
                //icon
                let icon = p.getChildAt(0) as Laya.Image;
                icon.offAllCaller(this);
            }
            this._view[adId] = null;
            delete this._view[adId];
            console.log("我被清理了")
        }

    }
}
export const nativeAd = new NativeAd()