import Animal from "./Animal";
import Player from "./Player";
import { moduleBridge, AnimalState } from "../Interface";
import { UpgradeKeys } from "../UpgradeKeys";
import GameObject = Laya.Sprite3D;
import AnimationCurve from "../AnimationCurve";
import Helper2D from "../Helper2D";

export default class AnimalCuddler extends Animal {
	public energyBonus:number;
	public energyBonusDecay:number;
	public energyBonusDecayMax:number;
	protected energyBonusDecayTotal:number = 0;
	protected energy:number = 0;
	public speedDropOffCurve;
	// public EffectsEvent cuddlingPfx;
	protected bumpCooldown:number = 0;
	public bumpCooldownTime:number;

    public init(id:string, obj:GameObject):void{
		super.init(id, obj)
        let info = Laya.loader.getRes("cfg/cfgAttribute.json")[this.encId];
        this.energyBonus = info.energyBonus
        this.energyBonusDecay = info.energyBonusDecay
        this.energyBonusDecayMax = info.energyBonusDecayMax
		let curve = new AnimationCurve();
		if (info.speedDropOffCurve && info.speedDropOffCurve.length > 0){
			for (let i = 0 ; i < info.speedDropOffCurve.length ; i ++) {
				curve.addKeyFrame(info.speedDropOffCurve[i][0],info.speedDropOffCurve[i][1],info.speedDropOffCurve[i][2],info.speedDropOffCurve[i][3],info.speedDropOffCurve[i][4],info.speedDropOffCurve[i][5])
			}
        }
		this.speedDropOffCurve = curve
    }

	protected getAngry():void {
        moduleBridge.player.eatenCrash();
        // Player.Instance.EatenCrash();
	}

	protected enterGoingWild():void {
        // AudioGlobal.Instance.PlayerSatOn();
        moduleBridge.player.blockJumping(this.goingWildLength + 1)
        super.enterGoingWild();
		// Player.Instance.rope.gameObject.SetActive(false);
		// AnimalCaptureController.Instance.CancelCapturing();
	}

	protected animalCrash(otherAnimal):void {
        let special = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.special)
        let specialCoins = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.specialCoins)
        let rideLength = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.rideLength)

		if(special > 0 && otherAnimal.smashStrength <= 5 && this.state == AnimalState.ridden && this.angerStage < 2) {

			if(this.bumpCooldown > 0) {
				return;
			}

			// push the panda away
			this.sizePop(1.2)

			// push the other animal away
			otherAnimal.Bounce(this.transform.localPosition, 2.4, 1.2)

			// reduce effectiveness
			this.energyBonusDecayTotal += this.energyBonusDecay;
			this.energyBonusDecayTotal = Math.min(this.energyBonusDecayTotal, this.energyBonusDecayMax + rideLength); //needs to add the modifier so the bottom value remains the same

			// get energy
			this._angerTimer += (this.energyBonus + rideLength / 10 - this.energyBonusDecayTotal);
			this.bumpCooldown = this.bumpCooldownTime;

			if(this.angerStage == 1) {
				if(this._angerTimer > this.warningLength) {
					this.angerStage--;
					this._angerTimer -= this.warningLength;
					//remove warning
					// anim.SetTrigger(animTriggerResetShaders);
				}
			} else {
				//clamp angerTimer
				this._angerTimer = Math.min(this._angerTimer, ((this.runLength - this.warningLength) + rideLength));
			}

			// play pfx
			// EffectsEvent e = cuddlingPfx;			
			// EffectsEvent.SpawnEffect(e, null, Vector3.Lerp(transform.position, otherAnimal.transform.position, 0.5f), this);

			// get coins
			if(specialCoins > 0 && Helper2D.GetRandom(0,10,true) > 4) {
				// CoinEffectsManager.Instance.SpawnCurrency(specialCoins.Value, transform.position + new Vector3(0, 1, 4), "animal" + animalVariantMeta.genusName.ToString());
			}
		} else {
			super.animalCrash(otherAnimal);
		}
	}

	public sizePop(bounceScaleMultiplier:number) {

		let currentScaleMultiplier:number = bounceScaleMultiplier;
		let currentScaleDecayMultiplier:number = 1.5;
		while(currentScaleMultiplier > 1) {
            this.transform.setWorldLossyScale(new Laya.Vector3(this._oldScale.x*currentScaleMultiplier,this._oldScale.y*currentScaleMultiplier,this._oldScale.z*currentScaleMultiplier))
			currentScaleMultiplier = Helper2D.MoveTowards(currentScaleMultiplier, 1, Laya.timer.delta/1000 * currentScaleMultiplier * currentScaleDecayMultiplier);
			return 0;
		}

	}

	public ride(startingAnimal:boolean):void {
		this.energy = 0;
		this.bumpCooldown = 0;
		super.ride(startingAnimal);
	}

	protected GetRunSpeedBeforeModifiers():number {
		let babyRideLengthMultiplier = 1
		let babyGlobalRideLengthMultiplier = 1
		let actualRunLength = (this.runLength + this.goingWildLength) * babyRideLengthMultiplier * babyGlobalRideLengthMultiplier;
		if(this.angerStage == 0) {
			return super.GetRunSpeedBeforeModifiers() * this.speedDropOffCurve.evaluate(Math.min(this._angerTimer + this.warningLength, actualRunLength) / actualRunLength);
		} else {
			return super.GetRunSpeedBeforeModifiers() * this.speedDropOffCurve.evaluate(Math.min(this._angerTimer, actualRunLength) / actualRunLength);
		}
	}

	public turn(a:number):void {
		if(this.energy <= 0) {
			super.turn(a);
		}
	}

	protected moveAnimal(speed:number):void {
		if(this.angerStage < 2) {
			super.moveAnimal(speed);
		}
	}


    public reset(){
		super.reset()
		this.energyBonusDecayTotal = 0;
		this.bumpCooldown = 0;
	}

	public onUpdate():void {
		super.onUpdate();

		if(this.bumpCooldown > 0) {
			this.bumpCooldown -= Math.floor(Laya.timer.delta/1000);
		}
	}

}
