    private onBtnCodeSure(): void {
        console.log("发放奖励");

        if (this._view.inputCDK.text.length != 8) {
            ZY.msg.show("兑换码输入不合法!");
        }

        let request: Laya.HttpRequest = new Laya.HttpRequest();
		request.http.timeout = 5000;
		request.once(Laya.Event.ERROR, this, (e: any) => {
            this._view.btnSure.mouseEnabled = true;
		});
		request.once(Laya.Event.COMPLETE, this, (data: any) => {
            console.log(data);
            this._view.btnSure.mouseEnabled = true;
            if (data.code == 0) {
                this._rewardData = JSON.parse(data.data);
                this.showReward();
            } else if (data.code == 1105) {
                ZY.msg.show("您已经领取过奖励！");
            } else {
                ZY.msg.show("兑换码不正确!");
            }
            
		});

        let client = Laya.LocalStorage.getItem("zoozyClient");
        let time = Math.floor(Laya.Browser.now() / 1000);
        let sha = ZY.md5(ZY.md5(ZY.key + ZY.noice) + time + ":" + client + "::" + ZY.noice);
        let url:string = "sha=" + sha + "&noice=" + ZY.noice + "&time=" + time + "&client=" + client;
        url = "useCdKey?" + url + "&channel=" + ZY.channel + "&cdKey=" +this._view.inputCDK.text + "&gid=" + ZY.gid
        url = "https://api.smartjoygames.com/zy/"+url
        // url = "http://192.168.170.240:9000/api/"+url
        console.log(url);
		request.send(url, "", "get", "json");
        this._view.btnSure.mouseEnabled = false;
    }

    private showReward(): void {
        if (!this._rewardData) {
            return;
        }

        if (this._rewardData.animal != "") {
            this._animalIds = this._rewardData.animal.split(",");
            this._animalIndex = 0;
        }
        if (this._animalIds) {
            for (let i = 0; i < this._animalIds.length; i++) {
                let id: number = Number(this._animalIds[i]);
                if (userData.getAnimals(userData.cfgAnimal[id].encId) && userData.getAnimals(userData.cfgAnimal[id].encId).indexOf(id) < 0) {
                    userData.catchAnimal(id);
                }
            } 
        }
 
        let boxTip = this._view.boxTip as Laya.Box;
        boxTip.visible = true;

        if (this._rewardData.gold > 0 && this._rewardData.jew > 0) {    
            userData.addGold(this._rewardData.gold, 1021);
            userData.addJew(this._rewardData.jew, 1013);

            let day7 = boxTip.getChildByName ("day7") as Laya.Box;
            day7.visible = true;
            let icon = boxTip.getChildByName ("icon") as Laya.Image;
            let label = boxTip.getChildByName ("label") as Laya.Label;
            icon.visible = label.visible = false;
            let label0 = day7.getChildByName ("label0") as Laya.Label;
            let label1 = day7.getChildByName ("label1") as Laya.Label;
            label0.text = "x" + this._rewardData.gold;
            label1.text = "x" + this._rewardData.jew;
        } else {
            let day7 = boxTip.getChildByName ("day7") as Laya.Box;
            day7.visible = false;
            let icon = boxTip.getChildByName ("icon") as Laya.Image;
            let label = boxTip.getChildByName ("label") as Laya.Label;
            icon.visible = label.visible = true;
            if (this._rewardData.gold > 0) {
                userData.addGold(this._rewardData.gold, 1021);

                icon.skin = "setting/img_icon_1.png";
                label.text = "x" + this._rewardData.gold;
            } else if (this._rewardData.jew > 0) {
                userData.addJew(this._rewardData.jew, 1013);

                icon.skin = "setting/img_icon_0.png";
                label.text = "x" + this._rewardData.jew;
            } else {
                if (this._animalIds && this._animalIds.length > 0) {
                    let id: number = Number(this._animalIds[0]);
                    let res = userData.cfgAnimal[id].res;
                    res = res.replace(".lh", "");
                    res = res.replace("game/Conventional/", "");
                    icon.skin = "icons/" + res + ".png";
                    label.visible = false;
                    this._animalIndex++;
                }
            }
        }
    }

