    //复制ID
    private btnCopyId():void{
        let power;
        if (ZY.channel === "wx") power = wx as any;
        else if (ZY.channel === "zj") power = (window as any).tt;
        else if (ZY.channel === "oppo" || ZY.channel == "vivo") power = (window as any).qg;
        else if (ZY.channel === "qq") power = (window as any).qq;
        else if (ZY.channel === "bd") power = (window as any).swan;
        let id = String(ZY.gid);
        power.setClipboardData({
            data: id,
            success (res) {
                ZY.msg.show("复制成功")
            }
        })
    }

    //复制qq群号
    private btnCopy():void{
        let power;
        if (ZY.channel === "wx") power = wx as any;
        else if (ZY.channel === "zj") power = (window as any).tt;
        else if (ZY.channel === "oppo" || ZY.channel == "vivo") power = (window as any).qg;
        else if (ZY.channel === "qq") power = (window as any).qq;
        else if (ZY.channel === "bd") power = (window as any).swan;

        if (ZY.channel === "wx" || ZY.channel === "zj" || ZY.channel == "qq" || ZY.channel == "bd") {
            power.setClipboardData({
                data: '770138159',
                success (res) {
                    ZY.msg.show("复制成功")
                }
            })
        }else if (ZY.channel == "oppo" || ZY.channel == "vivo"){
            power.setClipboardData({
                data: '3231388893',
                success (res) {
                    ZY.msg.show("复制成功")
                }
            })
        }
    }

