    private _signDay:number;
    private signGain():void {
        // console.log ("[for test]双倍", this._signDouble);
        let hasSignin = userData.get ("hasSi.gnin", 0);
        if (hasSignin == 0) {
            if (this._signDouble){
                if (ZY.channel == "wx"){
                    ZY.ad.showVideoAd(this, this.signGain2,ZY.wxAd.signin,"ad_signIn");
                }else if (ZY.channel == "vivo"){
                    ZY.ad.showVivoVideo(this, this.signGain2,ZY.vivoAd.signin,"ad_signIn");
                }else if (ZY.channel == "oppo"){
                    // reporter.upload(ReporterPoints.ad_signIn,3)
                    ZY.ad.showOPPOVideo(this, this.signGain2 ,ReporterPoints.ad_signIn);
                }else{
                    ZY.ad.showVideoAd(this, this.signGain2,0,"ad_signIn");
                }
            }else{
                userData.set ("hasSignin", 1);
                userData.setDailyTask(0, 1);
                this.setSignin ();
            }
        }
    }

    private signGain2(done:boolean):void{
        if (done){
            userData.setDailyTask(6,1)
            if (ZY.channel == "wx" || ZY.channel == "qq"){
                ZY.TmCtrl.instance.tmSendEvent("ad_signIn",{value:"完成"})
            }else if (ZY.channel == "zj"){
                ZY.reportEvent("ad_signIn",{value:"完成"})
            }else if (ZY.channel == "oppo"){
                // reporter.upload(ReporterPoints.ad_signIn,4)
            }
            userData.set ("hasSignin", 1);
            userData.setDailyTask(0, 1);
            this.setSignin ();
        }else{
            moduleBridge.videoTipCtrl.showTips();
        }
        if (ZY.channel == "vivo"){
            ZY.sound.playMusic("audioGame/Music_Zoo.mp3");
        }
    }

