    private _animal:string;
    private setSignin():void {
        let box = this._view.boxSignin as Laya.Box;
        let img = box.getChildAt (this._signDay) as Laya.Image;
        if (!img) return;
        let imgMask = img.getChildByName ("imgMask") as Laya.Image;
        imgMask.visible = true;
        // this._view.btnSignGain.gray = true;
        this._view.btnSignGain.disabled = true;
        // 增加金币
        if (this._signDay < 6) {
            let c = this._signCfg[this._signDay];
            let gold = c.amount;
            if (this._signDouble) gold = gold * 2;
            if (c.type == 1) {
                userData.addGold (gold, 1006);
            }
            if (c.type == 2) {
                userData.addJew(gold,1002)
            }
            if (c.type == 3) {
                // 判断是否拥有
                if (userData.getAnimals(1002).indexOf(128) < 0) {
                    this._animal = "setting/zebra8.png"
                    userData.catchAnimal(128)
                }
                userData.addGold (gold, 1006);
            }
            // 提示界面
            let boxTip = this._view.boxTip as Laya.Box;
            let day7 = boxTip.getChildByName ("day7") as Laya.Box;
            day7.visible = false;
            let icon = boxTip.getChildByName ("icon") as Laya.Image;
            let label = boxTip.getChildByName ("label") as Laya.Label;
            icon.skin = c.icon;
            if (this._signDouble) label.text = c.doubleLabel;
            else label.text = c.label;
            icon.visible = label.visible = true;
        } else {
            let c = this._day7;
            let gold = c.amount0;
            if (this._signDouble) gold = gold * 2;
            let jew = c.amount1;
            if (this._signDouble) jew = jew * 2;
            if (userData.getAnimals(1000).indexOf(13) < 0) {
                this._animal = "icons/zebra6.png"
                userData.catchAnimal(13)
            }
            userData.addGold (gold, 1006);
            userData.addJew(jew,1002)

            // 提示界面
            let boxTip = this._view.boxTip as Laya.Box;
            let day7 = boxTip.getChildByName ("day7") as Laya.Box;
            day7.visible = true;
            let icon = boxTip.getChildByName ("icon") as Laya.Image;
            let label = boxTip.getChildByName ("label") as Laya.Label;
            icon.visible = label.visible = false;
            let label0 = day7.getChildByName ("label0") as Laya.Label;
            let label1 = day7.getChildByName ("label1") as Laya.Label;
            if (this._signDouble) {
                label0.text = c.doubleLabel0;
                label1.text = c.doubleLabel1;
            } else {
                label0.text = c.label0;
                label1.text = c.label1;
            }
        }
        // this._view.boxTip.visible = false;
        // this._view.btnSign.visible = false
        // // this._view.vs.visible = false
        this._view.tipMask.visible = true;
        this._view.tipMask.width = Laya.stage.width / ZY.stageScale;
        this._view.tipMask.height = Laya.stage.height / ZY.stageScale;

        // this.updateView()
        this._signDay ++ ;
        this._view.boxTip.visible = true;
        if (this._signDay >= 7) this._signDay = 7;
        if (ZY.channel == "wx" || ZY.channel == "vivo"){
            ZY.ad.hideBanner()
        }
        if (userData.getAnimals(1002) && userData.getAnimals(1002).indexOf(128) < 0 ) {
            this._view.icon.skin = "setting/zebra8.png"
            this._view.iconname.skin = "setting/mingcheng.png"
            this._view.day.skin = "setting/leiji.png"
        }else{
            this._view.icon.skin = "icons/zebra6.png"
            this._view.iconname.skin = "setting/rwkl.png"
            this._view.day.skin = "setting/leiji2.png"
        }
        // 签到成功
        userData.set ("signDay", this._signDay);
        userData.set ("signinTime", Laya.Browser.now ());
    }

    private hideTip() {

        if (this._animal || (this._animalIds && this._animalIds[this._animalIndex])){
            // 提示界面
            let boxTip = this._view.boxTip as Laya.Box;
            (boxTip.getChildByName ("day7") as Laya.Box).visible = false;
            (boxTip.getChildByName ("label") as Laya.Label).visible = false;
            let icon = boxTip.getChildByName ("icon") as Laya.Image;
            if (this._animal) {
                icon.skin = this._animal;
                icon.visible = true;
                this._animal = null;
            }

            if (this._animalIds && this._animalIds[this._animalIndex]) {
                icon.visible = true;
                let id: number = Number(this._animalIds[this._animalIndex]);
                let res = userData.cfgAnimal[id].res;
                res = res.replace(".lh", "");
                res = res.replace("game/Conventional/", "");
                icon.skin = "icons/" + res + ".png";
                this._animalIndex++;
            }
            
        }else{
            moduleBridge.shipCtrl.clearSignMask()
            this._view.boxTip.visible = false;
            moduleBridge.shipCtrl.backToShip();
            this._view.removeSelf();

            if (this._animalIds) {
                this._animalIds.splice(0);
                this._animalIds = null;
            }
        }
    }

