    private sliderDown():void {
        this._view.btnSlider.startDrag (new Laya.Rectangle (0, 31, this.SLIDER_WID, 0), false, 0, 0, null, true, 0);
        this._view.btnSlider.on (Laya.Event.DRAG_MOVE, this, this.sliderMove);
        this._view.btnSlider.once (Laya.Event.MOUSE_UP, this, ()=>{
            this._view.btnSlider.stopDrag ();
            this._view.btnSlider.off (Laya.Event.DRAG_MOVE, this, this.sliderMove);
        })
    }

    private sliderMove():void {
        let x:number = this._view.btnSlider.x;
        let per:number = Math.floor(x / this.SLIDER_WID * 100);
        this.setCunSlider (per);
    }

    private SLIDER_WID:number = 627;
    private setCunSlider(value:number):void {
        // console.log ("slider", value)
        let x:number = value / 100 * this.SLIDER_WID;
        this._view.btnSlider.x = x;
        this._view.imgSlider.width = x;
        
        // Img.scrollRect = new Laya.Rectangle (0, 0, x, Img.height);
    }

    private onGameStart():void {
        this._view.visible = false;
        moduleBridge.shipCtrl.startGame ();
    }

    private onBack():void {
        this.gameClub && this.gameClub.hide();
        if (this._curIndex == 0) {
            moduleBridge.shipCtrl.backToShip();
            this._view.removeSelf();
        } else {
            this.updateView (0);
        }
        ZY.ad.hideBanner()
    }

    private vivoBack():void{
        moduleBridge.shipCtrl.clearSignMask()
        moduleBridge.shipCtrl.backToShip();
        this._view.removeSelf();
    }


    public showSetting(index:number = 0):void {
        // if (index == 4) return;
        if(this._sceneName == "zoo") ZY.stage.addChild(this._view, ZY.Layer.MIDDLE);
        Laya.timer.callLater(this, this.updateView, [index]);
    }

