class Reporter {
    private static _instance:Reporter;
    static getInstance():Reporter {
        if(!Reporter._instance) {
            Reporter._instance = new Reporter();
        }
        return Reporter._instance;
    }

    private _host = "https://api.smartjoygames.com/zy/"
    private _list:Array<any> = [];
    public init():void {
        let client = Laya.LocalStorage.getItem("zoozyClient");        
        let time = Laya.Browser.now();
        console.log(">>>:reporter inited");

        // if(this._uid && this._uid != "") {
        //     reporter.upload(ReporterPoints.APP_LOGIN,1);
        // } else {
        //     reporter.upload(ReporterPoints.APP_LOGIN_FAIL,1);
        // }

        if(!client) {
            client = ZY.noice;
            let sha = ZY.md5(ZY.md5(ZY.key + ZY.noice) + time + ":" + client + "::" + ZY.noice);
            let hr = new HttpLoader;
            hr.once(Laya.Event.ERROR,this,(e:any)=>{
                console.log(e);
                Laya.LocalStorage.setItem("zoozyClient",client);
                userData.client = client
            });
            hr.once(Laya.Event.COMPLETE, this, (data:any)=>{
                if(!data) return;
                console.log(data);
                if(data.code == 0 && data.data) {
                    Laya.LocalStorage.setItem("zoozyClient",data.data);
                    userData.client = data.data
                    this.nextUpload();
                } else {
                    Laya.timer.once(5000, reporter, reporter.init);
                }
            });
            hr.send(this._host+"newClient?"+"sha="+sha+"&noice="+ZY.noice+"&time="+time+"&client="+client,"","get","json");
        } else {
            // userData.client = client;
            userData.client = client
            console.log("client:"+client);
            this.nextUpload();
        }

        // console.log("noice="+ZY.noice);
    }

    public upload(event:number,value ?:number):void {
        let time = Laya.Browser.now();

        let info = {time:time,value:value,event:event};
        this._list.push(info);

        this.nextUpload();
    }

    private nextUpload():void {
        // if(!userData.client) return;
        let info = this._list.shift();
        if(info) {            
            let time = info.time;
            let event = info.event;
            let value = info.value || 1;
            let client = userData && userData.client || ZY.md5(Laya.Browser.now() + "," + Math.random())
            // let sha = ZY.md5(this._key+time+":"+userData.client+"::"+ZY.noice);
            let sha = ZY.md5(ZY.md5(ZY.key + ZY.noice) + time + ":" + client + "::" + ZY.noice);
            // appid       游戏的id
            // ch          游戏渠道
            // game        游戏名称
            // uid         用户id
            // client      设备指纹
            // time        该数据产生的时间点
            // point       数据埋点位置
            // value       埋点值
            let uid = ZY.uid == "-1" ? "-" : ZY.uid

            let url = "report?"+"sha="+sha+"&noice="+ZY.noice+"&time="+time+"&client="+client;
            url += "&appid=30291945&channel="+ZY.channel+"&game=zoo&uid="+uid+"&point="+event+"&value="+value;

            let hr:Laya.HttpRequest = new Laya.HttpRequest();
            hr.http.timeout = 10000;
            hr.once(Laya.Event.ERROR,this,(e:any)=>{
                // console.log(">>>:upload error:"+e);                
                this._list.unshift(info);
                Laya.timer.once(1000,reporter, reporter.nextUpload);
            });
            hr.once(Laya.Event.COMPLETE, this, (data:any)=>{
                console.log(">>>:upload response:"+JSON.stringify(data));                
                // reporter.nextUpload();
                if(data && data.code == 1108) {
                    this._list.unshift(info);
                }
                Laya.timer.once(1000,reporter, reporter.nextUpload);
            });
            // console.log(">>>:uploader:"+url);
            hr.send(this._host+url,"","get","json");
            // let sha = this._md5(this._key+time+":"+)
        }
    }
}

export const reporter = Reporter.getInstance();