import Animal from "./Animal";
import { AnimalState, moduleBridge } from "../Interface";
import Helper2D from "../Helper2D";
import GameObject = Laya.Sprite3D;
import { UpgradeValue, UpgradeManager } from "../UpgradeManager";
import { UpgradeKeys } from "../UpgradeKeys";
import PfxHelper from "../game/PfxHelper";
import { ZY } from "../ZY";

export default class AnimalCharger extends Animal {
	public alwaysCharge:boolean;
	public chargeDurationBonus:number;
	public chargeSpeedBonus:number;

	private _chargeDuration:number = 0.5;
	private _maxSpeedBoost:number = 30;
	private _minSpeedBoost:number = 0;
	private _gracePeriod:number = 0.3;
	private _anticpationLength:number = 0.12;

	private _anticipatingCharge:boolean;

    public init(id:string, obj:GameObject):void{
		super.init(id, obj)
        let info = Laya.loader.getRes("cfg/cfgAttribute.json")[this.encId];
        this.alwaysCharge = info.alwaysCharge
        this.chargeDurationBonus = info.chargeDurationBonus
        this.chargeSpeedBonus = info.chargeSpeedBonus
    }

	protected landingBoost (){
        // let specialUpgradeAmount = 0
        let specialUpgradeAmount = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.special)
		if (specialUpgradeAmount <= 0 && this.alwaysCharge == false) {
            super.landingBoost()
            return
		}

        // let landingBoostLengthModifier = 0
        let landingBoostLengthModifier = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.landingBoostLengthModifier)
        // let landingBoostLengthModifier:UpgradeValue = animalUpgrades.GetUpgrade(UpgradeKeys.landingBoostLengthModifier);
		let totalChargeDuration = (this._chargeDuration + this.chargeDurationBonus) * (1 + landingBoostLengthModifier);

		this._anticipatingCharge = true;
		this._landingBoost = -20;
		this.smashStrength *= 3;
		this.smashAnimals = true;
        Laya.timer.once(this._anticpationLength,this,() =>{
            this._anticipatingCharge = false;

            // anim.SetLayerWeight(animLayer3DOverride, 1);
            // chargeEffect.Spawn(transform, new Vector3(0, 1, 1), chargeEffect.transform.rotation).TriggerEffect(null, totalChargeDuration);
            // EffectsEvent trail = chargeTrailEffect.Spawn(transform, new Vector3(0, 1, -1), chargeEffect.transform.rotation);
            // trail.TriggerEffect(null, totalChargeDuration + 0.3f);
            let a = ZY.Temp.getVector3(this.transform.localPositionX,this.transform.localPositionY+2,this.transform.localPositionZ+5)
            let pfx = PfxHelper.instance.show("pfx_chargeSmashActual",a,this.gameObject,false,totalChargeDuration);
            // pfx.transform.localRotationEulerY = this.transform.localRotationEulerY
            // let defaultCameraLerp = CameraController.Instance.lerpSpeed;
            let timer = totalChargeDuration;
            Laya.timer.frameLoop(1,this, () => {
                if (timer > 0 && this.state == AnimalState.ridden) {
                    this._landingBoost = Helper2D.Map((timer / totalChargeDuration), 1, 0, this._maxSpeedBoost + this.chargeSpeedBonus, this._minSpeedBoost);
                    // CameraController.Instance.lerpSpeed = Helper2D.Map((timer / totalChargeDuration), 1, 0, 0.075f, defaultCameraLerp);
                    timer -= Laya.timer.delta/1000
                }else{
                    // trail.particleSystem.Stop();
    
                    this._landingBoost = 0;
                    // CameraController.Instance.lerpSpeed = defaultCameraLerp;
                    // anim.SetLayerWeight(animLayer3DOverride, 0);
                    Laya.timer.clearAll(this)
                    if (this.state == AnimalState.ridden) {
                        // yield return StartCoroutine(Auto.Wait(gracePeriod));
                        Laya.timer.once(this._gracePeriod,this,() =>{
                            this.smashStrength /= 3;
                            this.smashAnimals = false;
                        })
                    }else{
                        this.smashStrength /= 3;
                        this.smashAnimals = false;
                    }
                }
            })


        })
	}

	protected obstacleCrash ( obstacle, immune:boolean = false):void {
		super.obstacleCrash (obstacle, this.smashAnimals);
	}

	public turn (a:number):void{	
		if (!this._anticipatingCharge) {
			super.turn (a);
		}
	}

	protected calculateCurrentAngle ():void{
		if (!this._anticipatingCharge) {
			super.calculateCurrentAngle ();
		}
	}
}
