import { ZY } from "./ZY";
import { moduleBridge } from "./Interface";

export default class RecordVideo {
    public static videoPath: string = "";
    public static recording:boolean = false;
    private static _timeRecord: number = 0;
    private static _timeInThis: number = 0;
    private static tt = (window as any).tt;
    /**头条 录屏开始 */
    public static startRecordVideo() {
        if (ZY.channel !== "zj") return
        var recorder = RecordVideo.tt.getGameRecorderManager();
        if (recorder) {
            recorder.start({
                duration: 300,
            });

            recorder.onStart((res: any) => {
                console.log("回调 录屏开始");
                RecordVideo.videoPath = "";
                RecordVideo._timeRecord = Laya.Browser.now();
                RecordVideo._timeInThis = 0;
                RecordVideo.recording = true;
            });

            recorder.onStop((res: any) => {
                RecordVideo._timeInThis = Laya.Browser.now() - RecordVideo._timeRecord;
                RecordVideo.recording = false;
                console.log("回调 录屏结束" + res.videoPath);
                recorder.clipVideo({
                    path: res.videoPath,
                    timeRange: [30, 0],
                    success(res) {
                        console.log("回调 录屏结束且剪辑成功" + res.videoPath);
                        RecordVideo.videoPath = res.videoPath;
                        if (RecordVideo._photocaller && RecordVideo._photocallback && RecordVideo._photoparam) {
                            RecordVideo._photocallback.call(RecordVideo._photocaller,RecordVideo._photoparam[0],RecordVideo._photoparam[1]);
                            RecordVideo._photocaller = null
                            RecordVideo._photocallback = null
                            RecordVideo._photoparam = null
                        }
                    },

                    fail(e) {
                    }
                })
                if (RecordVideo.videoPath) {
                    RecordVideo.videoPath = res.videoPath;
                }
            })
        }

    }

    /**头条 录屏结束 */
    private static _photocaller:any;
    private static _photocallback:Function;
    private static _photoparam:Array<any>;
    public static stopRecordVideo(photocaller ?:any,photocallback ?:Function ,photoparam ? :Array<any>) {
        if (ZY.channel !== "zj"){
            if (photocaller && photocallback && photoparam) {
                photocallback.call(photocaller,photoparam[0],photoparam[1]);
                return
            }
        }
        var recorder = RecordVideo.tt.getGameRecorderManager();
        console.log("录屏结束1");
        if (recorder) {
            console.log("录屏结束2");
            if (photocallback && photocaller){
                RecordVideo._photocaller = photocaller
                RecordVideo._photocallback = photocallback
                RecordVideo._photoparam = photoparam
            }
            if (!RecordVideo.recording && RecordVideo.videoPath){
                if (RecordVideo._photocaller && RecordVideo._photocallback && RecordVideo._photoparam) {
                    RecordVideo._photocallback.call(RecordVideo._photocaller,RecordVideo._photoparam[0],RecordVideo._photoparam[1]);
                    RecordVideo._photocaller = null
                    RecordVideo._photocallback = null
                    RecordVideo._photoparam = null
                    return
                }
            }
            recorder.stop();
        }
    }

    /**头条 录屏暂停 */
    public static pauseRecordVideo() {
        var recorder = RecordVideo.tt.getGameRecorderManager();
        console.log("录屏暂停");
        if (recorder) {
            recorder.pause();
        }
    }

    /**头条 录屏继续 */
    public static resumeRecordVideo() {
        var recorder = RecordVideo.tt.getGameRecorderManager();
        console.log("录屏继续");
        if (recorder) {
            recorder.resume();
        }
    }

    /**头条 录屏分享 */
    public static ttShareAppMessage(channel: string = "video", caller ?: any, callback ?: Function , param ? :Array<any>) {
        if (RecordVideo.videoPath != "" && RecordVideo._timeInThis > 3000) {
            console.log("录屏分享",param)
            RecordVideo.tt.shareAppMessage({
                channel: channel,
                title:"",
                imageUrl:"",
                query:"",
                extra: {
                    videoPath: RecordVideo.videoPath,
                    hashtag_list: ["疯狂动物园","木鸡游戏"],
                    videoTopics: ["疯狂动物园","木鸡游戏"]
                },

                success() {
                    console.log("分享视频成功");
                    if (caller && callback) {
                        callback.call(caller, { code: 0 ,param : param});
                    }
                },

                fail(e) {
                    console.log("分享视频失败", e);
                    // if (e.errMsg.indexOf("fail video file is too short path") == -1) {// 录屏小于3秒时的e.errMsg
                    if (e.errMsg.indexOf("cancel") != -1) {// 录屏除去取消的，其余的都显示小于三秒
                        if (caller && callback) {
                            callback.call(caller, { code: -1 });
                        }
                    } else if (e.errMsg.indexOf("fail shareInfo is null") != -1) {
                        ZY.msg.show("分享视频信息不存在,分享失败!")
                        // if (caller && callback)
                        // callback.call(caller, { code: -3 });
                    } else if (e.errMsg.indexOf("fail Request token failed") != -1) {
                        //这个头条会有自己的提示文案
                    } else {
                        if (caller && callback)
                            callback.call(caller, { code: -2 });
                    }
                }
            })
        } else if (RecordVideo._timeInThis < 3000) {
            console.log("录屏时长小于3秒，分享失败");
            RecordVideo.tt.showToast({title:"录屏时长小于3秒，分享失败"})
            if (caller && callback){
                callback.call(caller, { code: -2 });
            }
        } else {
            // Tip.show("录屏时长小于3秒，分享失败!!")
            RecordVideo.tt.showToast({title:"录屏时长小于3秒，分享失败"})
            if (caller && callback){
                callback.call(caller, { code: -3 });//路径不存在
            }

        }
    }
}

new RecordVideo();