import Animal from "./Animal";
import Helper2D from "../Helper2D";
import GameObject = Laya.Sprite3D;
import { moduleBridge, AnimalState, PlayerState } from "../Interface";
import { ZY } from "../ZY";
import AnimationCurve from "../AnimationCurve";
import { AnimalEatCollider } from "./AnimalEatCollider";
import { UpgradeKeys } from "../UpgradeKeys";
import PfxHelper from "../game/PfxHelper";
import { userData } from "../userData/UserData";

export default class AnimalPredator extends Animal {

	public mouthPosition;
	public preyEatStartPos;
	public eatCollider;
	public eatTimerBonus:number;
	public eatTimerBonusDecay:number;
	public eatTimerBonusDecayMax:number;
	public eatCooldown:number;
	protected eatTimerBonusDecayTotal:number = 0;
	protected eatTimer:number = 0;
	private _biteAni:Laya.Animator;
    public speedDropOffCurve:AnimationCurve;
	// private animTriggerMouthAttack:string = "attack";


    public onAwake():void{
		super.onAwake();
        this.gameObject = this.owner as Laya.Sprite3D;
        this.mouthPosition = (this.gameObject.getChildByName("mouthPosition") as GameObject).transform;
        this.preyEatStartPos = (this.gameObject.getChildByName("preyEatStartPos") as GameObject).transform;
		this.eatCollider = (this.gameObject.getChildByName("eatCollider") as GameObject)
		this.eatCollider = this.eatCollider.addComponent(AnimalEatCollider);
		this.eatTimerBonusDecayTotal = 0;

	}

	public init(id:string, obj:GameObject):void {
		this._biteAni = (ZY.findChildByPath(obj, "offset.bite") as GameObject).getComponent(Laya.Animator);
		super.init(id, obj)
		let info = Laya.loader.getRes("cfg/cfgAttribute.json")[this.encId];
        this.eatTimerBonus = info.eatTimerBonus
        this.eatTimerBonusDecay = info.eatTimerBonusDecay
        this.eatTimerBonusDecayMax = info.eatTimerBonusDecayMax
        this.eatCooldown = info.eatCooldown
		let curve = new AnimationCurve();
		if (info.speedDropOffCurve && info.speedDropOffCurve.length > 0){
			for (let i = 0 ; i < info.speedDropOffCurve.length ; i ++) {
				curve.addKeyFrame(info.speedDropOffCurve[i][0],info.speedDropOffCurve[i][1],info.speedDropOffCurve[i][2],info.speedDropOffCurve[i][3],info.speedDropOffCurve[i][4],info.speedDropOffCurve[i][5])
			}
		}
		this.speedDropOffCurve = curve
	}
	
	public reset():void {
		super.reset();
		this._biteAni.play("off");
	}


	protected getAngry () {
        // this._ani.play("goingWild");
        moduleBridge.player.eatenCrash();
	}

	protected enterGoingWild () {
        // AudioGlobal.Instance.PlayerThrown();
        // if (animalVariantMeta.genusName == GenusName.lion) {
            // AudioGlobal.Instance.Bark(gameObject, animalVariantMeta.genusName, animalVariantMeta.id, 100);
		// }
		if (this.encId == "1007") {
			this.Bark(100)
        }
		moduleBridge.player.blockJumping(this.goingWildLength + 1);
		moduleBridge.player.beEat();
		super.enterGoingWild();
		// this.state = AnimalState.crashed;
        // moduleBridge.player.rope.gameObject.SetActive(false);
		// AnimalCaptureController.Instance.CancelCapturing();
    }

	protected animalCrash(other:Animal) {
		// let allowCannibalism = 0
		// let canEatEverything = 0
		// let special = 0
		// let rideLength = 0
		// let specialCoins = 0
		let allowCannibalism = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.allowCannibalism);
		let canEatEverything = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.canEatEverything);
		let special = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.special);
		let rideLength = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.rideLength);
		let specialCoins = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.specialCoins);
		let animalAffinityRunLengthModifier = 1

		if (this.angerStage <= 1) {

			if (((other.edible && (other.encId != this.encId || allowCannibalism > 0)) || canEatEverything > 0) && other != moduleBridge.player.steed && moduleBridge.player.state == PlayerState.riding) {

				let babyPredatorEfficiencyMultiplier = 1
				
				if (this.encId == "1013" && special > 0 && other.encId == "1008") {
					this._angerTimer += special * (this.eatTimerBonus + rideLength / 10 - this.eatTimerBonusDecayTotal) * babyPredatorEfficiencyMultiplier;
				} else {
					this._angerTimer += (this.eatTimerBonus + rideLength / 10 - this.eatTimerBonusDecayTotal) * babyPredatorEfficiencyMultiplier;
				}

				if (this.angerStage == 1) {
					if (this._angerTimer > this.warningLength) {
						this.angerStage--;
						this._angerTimer -= this.warningLength;
						//remove warning
						// anim.SetTrigger(animTriggerResetShaders);
					}
				} else {
					//clamp this._angerTimer
					this._angerTimer = Math.min(this._angerTimer, ((this.runLength - this.warningLength) + rideLength) * animalAffinityRunLengthModifier);
				}

				this.EatDash(other);

				this.eatCollider.switchOff()
				this.eatTimer = this.eatCooldown;

				if (specialCoins > 0 && Helper2D.GetRandom(0, 10 , true) > 4) {
					PfxHelper.instance.show("pfx_money_1",this.transform.position);
					moduleBridge.gameCtrl.addGold(specialCoins,1007)
				}

				this.EatAnimal(other);
			} else {
				super.animalCrash(other);
			}
		}
	}

	protected animalCrashOriginal(otherAnimal:Animal) 
	{
		super.animalCrash(otherAnimal);
	}


	protected EatDash(prey:Animal, lerpValue:number = 0.2) {

		// let rideLength = 0
		let rideLength = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.rideLength);

        Helper2D.SetPosX(this.transform,Helper2D.Lerp(this.transform.localPositionX, prey.transform.position.x, lerpValue))
        Helper2D.SetPosZ(this.transform,Helper2D.Lerp(this.transform.localPositionZ, prey.transform.position.z, lerpValue))


		this.eatTimerBonusDecayTotal += this.eatTimerBonusDecay;
		this.eatTimerBonusDecayTotal = Math.min(this.eatTimerBonusDecayTotal, this.eatTimerBonusDecayMax + rideLength); //needs to add the modifier so the bottom value remains the same
		// ZY.animatorTrigger.stopTrigger(this._ani)
		this.triggerAni(["attack","run"])
		this._biteAni.play("attack", 0, 0);
		this._biteAni.speed = 0.1;
		// ZY.animatorTrigger.stopTrigger(this._biteAni);
		// this._ani.play("attack")
		// anim.SetTrigger(animTriggerAttack);
		// attackAnimator.SetTrigger(animTriggerMouthAttack);
		// AudioGlobal.Instance.Bark(gameObject, animalVariantMeta.genusName, animalVariantMeta.id, 25);
		this.Bark(25)

	}

	public onUpdate ()
	{
		if (this.state == AnimalState.ridden) {
			if (this.eatTimer > 0) {
				this.eatTimer -= Laya.timer.delta/1000
			}

			if (this.eatTimer <= 0) {
				this.eatCollider.switchOn()
			}
		}
		super.onUpdate ();
    }
    
	protected UpdateOriginal ()
	{
		super.onUpdate ();
	}

	public ride(startingAnimal:boolean,fhtime ?:number,fhdis ?:number):void {
		this.eatTimer = 0;
		this.eatCollider.switchOn()
		super.ride(startingAnimal,fhtime,fhdis);
    }
    
	// public RideOriginal(startingAnimal:boolean)
	// {
	// 	super.ride(startingAnimal,fhtime,fhdis);
	// }


	public leaveAnimal(crashed:boolean, playCrashAnim:boolean = true)
	{
		this.eatCollider.switchOff()
		super.leaveAnimal(crashed, playCrashAnim);
    }
    
	public LeaveAnimalOriginal(crashed:boolean, playCrashAnim:boolean = true)
	{
		super.leaveAnimal(crashed, playCrashAnim);
	}

    protected EatAnimal (prey:Animal) {
		// console.log("eat~"+prey)
        this.eatAnimalSequence(prey)
    }

    protected GetRunSpeedBeforeModifiers() {
		let babyRideLengthMultiplier = 1
		let babyGlobalRideLengthMultiplier = 1
		let actualRunLength = (this.runLength + this.goingWildLength) * babyRideLengthMultiplier * babyGlobalRideLengthMultiplier;
        if (this.angerStage == 0) {
			return super.GetRunSpeedBeforeModifiers() * this.speedDropOffCurve.evaluate(Math.min(this._angerTimer + this.warningLength, actualRunLength) / actualRunLength);
        } else {
			return super.GetRunSpeedBeforeModifiers() * this.speedDropOffCurve.evaluate(Math.min(this._angerTimer, actualRunLength) / actualRunLength);
        }
    }

    protected eatAnimalSequence(prey:Animal) {
		prey.SetEaten();
		// yield return StartCoroutine(Auto.Wait(0.05));
		moduleBridge.player.lastAnimalEaten = prey;
		userData.add_stat_eatAnimals(1)
		// MissionManager.instance.AddMissionProgress(MissionTask.animalEaten, 1);
		// StatsManager.instance.animalsEaten.Value++;
	}

	public turn (a:number)
	{
		if (this.eatTimer <= 0) {
			super.turn (a);
		}
	}

	protected moveAnimal (speed:number)
	{
		if (this.angerStage < 2) {
			super.moveAnimal(speed);
		}
	}

    public SlowedBehindAnimal(animal:Animal) {
        // if (animal.edible && animal.animalVariantMeta.genusName != animalVariantMeta.genusName && animal != Player.Instance.steed && (Player.Instance.state != PlayerState.flying && Player.Instance.state != PlayerState.landing)) {
        //     this.EatDash(animal);
        //     this.EatAnimal(animal);
        // }
    }
}
