    export function init(ch: string = "none") {
        console.log(">>>:初始化ZY包，当前渠道:" + ch);
        channel = ch;

        key = "49c36198149c7366553160d5263d2cc9";
        noice = ZY.md5(Laya.Browser.now() + "," + Math.random());

        if (ch === "wx" || ch === "zj" || ch === "oppo" || ch === "vivo" || ch === "qq" || ch === "bd") {
            let power;
            if (ch === "wx") power = wx as any;
            else if (ch === "zj") power = (window as any).tt;
            else if (ch === "oppo" || ch == "vivo") power = (window as any).qg;
            else if (ch === "qq") power = (window as any).qq;
            else if (ch === "bd") power = (window as any).swan;
            if (power.setKeepScreenOn) {
                power.setKeepScreenOn({
                    keepScreenOn: true
                })
            }
            if (ch === "oppo") {
                power.setLoadingProgress({
                    progress: 0
                });
                power.reportMonitor('game_scene', 0)
            }
            if (ch === "wx" || ch === "zj" || ch == "qq" || ch === "bd") {
                let obj = power.getSystemInfoSync();
                console.log(JSON.stringify(obj));
            }

            if (ch === "wx" || ch === "vivo" || ch === "qq" || ch === "bd") {
                power.onAudioInterruptionBegin(() => {
                    console.log("失去焦点")
                    ZY.focus = false;
                    ZY.eventBus.event(Laya.Event.BLUR);
                });

                power.onAudioInterruptionEnd(() => {
                    console.log("获得焦点")
                    ZY.focus = true;
                    ZY.eventBus.event(Laya.Event.FOCUS);
                });
            }

            power.onShow(() => {
                console.log("获得焦点")
                ZY.focus = true;
                ZY.eventBus.event(Laya.Event.FOCUS);
            });
            power.onHide(() => {
                console.log("失去焦点")
                ZY.focus = false;
                ZY.eventBus.event(Laya.Event.BLUR);
            });
        } else {
            Laya.stage.on(Laya.Event.FOCUS, this, () => {
                ZY.eventBus.event(Laya.Event.FOCUS);
            });
            Laya.stage.on(Laya.Event.BLUR, this, () => {
                ZY.eventBus.event(Laya.Event.BLUR);
            });
        }

        stage.init();
        sound.init();
        touchManager.init();
        timerManager.init();
    }

