    export function requestLoginHw(game: string, param: any): void {
        let caller = {};
        Laya.systemTimer.once(5100, caller, () => {
            ZY.hasRequsetLogin = true;
            msg.show("登陆失败，数据将在下次登陆时保存");
            ZY.eventBus.event("LOGINFAIL");
        });
        let client = Laya.LocalStorage.getItem(game + "zyClient");
        if (!client || client == "") {
            client = ZY.md5(Laya.Browser.now() + "," + Math.random());
            Laya.LocalStorage.setItem(game + "zyClient", client);
        }
        let time =  Laya.Browser.now();
        let ts: number = Math.floor(time / 1000);
        console.log("noice=" + ZY.noice + ",client=" + client);
        let sha = ZY.md5(ZY.md5(ZY.key + ZY.noice) + time + ":" + client + "::" + ZY.noice);
        let url = "login?" + "sha=" + sha + "&noice=" + ZY.noice + "&time=" + time + "&client=" + client;
        url = url + "&token=" + param.token + "&game=" + game + "&channel=hw" + "&openid=" + param.openid + "&gameid=" + param.cpInfo.gameid + "&channelkey="
              + param.cpInfo.channelkey + "&tspan=" + ts;
        // let hr: Laya.HttpRequest = new Laya.HttpRequest();
        let hr = new HttpLoader;
        // hr.http.timeout = 5000;
        hr.once(Laya.Event.ERROR, this, (e: any) => {
            console.log(">>>:login error:" + JSON.stringify(e));
            Laya.systemTimer.clearAll(caller);
            ZY.hasRequsetLogin = true;
            msg.show("登陆失败，数据将在下次登陆时保存");
            ZY.eventBus.event("LOGINFAIL");
        });
        hr.once(Laya.Event.COMPLETE, this, (data: any) => {
            console.log(">>>:login response:" + JSON.stringify(data));
            Laya.systemTimer.clearAll(caller);
            ZY.hasRequsetLogin = true;
            if (data.code == 0 && data.data) {
                // {"msg":"success","data":{"anonymous_openid":"9Q6fXTRkJDDUKlvF","openid":"nmzGcwFHKuhtQD4r","session_key":"63wm1nVz6bXaxbxb/dYvfw==","error":0},"code":0}
                data = data.data;
                if (data.openid && data.openid != "") {
                    ZY.uid = data.openid;
                    ZY.logined = true;
                    ZY.loginData = data.dat;
                    ZY.gid = data.gid;
                    ZY.sec = data.sec;
                    if (data.session_key) {
                        ZY.sessionkey = data.session_key
                    }
                    ZY.msg.hide();
                    ZY.eventBus.event("LOGINED", [data]);
                } else {
                    console.log("登陆失败1");
                    ZY.uid = "-1";
                    if (data.anonymous_openid && data.anonymous_openid != "") {
                        msg.show("游客登录成功");
                    } else {
                        msg.show("登陆失败1，数据将在下次登陆时保存");
                    }
                    ZY.eventBus.event("LOGINFAIL");
                }
            } else {
                console.log("登陆失败2");
                msg.show("登陆失败2，数据将在下次登陆时保存");
                ZY.eventBus.event("LOGINFAIL");
            }
        });
        console.log(">>>:login:" + url);
        hr.send("http://192.168.170.240:9000/api/"+url,"","get","json");
        //hr.send("https://api.smartjoygames.com/zy/" + url, "", "get", "json");
    }

