    export function codeStr(str: string): string {
        str = str.replace(/:/g, "\r");
        str = str.replace(/ /g, "");
        str = str.replace(/"/g, "\n");
        str = str.replace(/'/g, "\n");
        str = str.replace(/,/g, " ");
        let len = str.length;
        let tmp = "";
        while (len > 10) {
            len -= 10;
            tmp += str.substr(len, 10);
        }
        if (len > 0) {
            tmp += str.substr(0, len);
        }
        len = tmp.length;
        let tmp2 = "";
        while (len > 3) {
            len -= 3;
            tmp2 += tmp.substr(len, 3);
        }
        if (len > 0) {
            tmp2 += tmp.substr(0, len);
        }
        return tmp2;
    }

    export function findChild(sp, name): any {
        function _findChild(spArr, spName) {
            var arr: Array<any> = [];
            for (var i = 0; i < spArr.length; i++) {
                var child = spArr[i];
                if (child.name == spName) {
                    return child;
                }
                else if (child.numChildren) {
                    arr = arr.concat(child._children);
                }
            }
            if (!arr.length)
                return null;
            return _findChild(arr, spName);
        }

        if (sp.name == name)
            return sp;
        else
            return _findChild(sp._children, name);
    }

    export function findParent(sp: Sprite3D, name: string): any {
        if (!sp.parent) return null;
        if (sp.parent.name == name) {
            return sp.parent;
        }
        return findParent(sp.parent as Sprite3D, name);
    }

    /**
     * 随机打乱数组
     * @param arr 
     */
    export function shuffle(arr) {
        var i = arr.length, t, j;
        while (i) {
            j = Math.floor(Math.random() * i--);
            t = arr[i];
            arr[i] = arr[j];
            arr[j] = t;
        }
    }

    /**
     * 根据相对路径获取节点
     * @param sp 需要寻找的节点
     * @param path 需要寻找的路径，用“.”隔开，“_”表示父级路径，例如“_.node1.node2”
     */
    export function findChildByPath(sp: Sprite3D, path: string): any {
        if (!sp) return null;
        let arr = path.split(".");
        let out = sp;
        for (let i = 0; i < arr.length; i++) {
            if (arr[i] == "_") {
                out = out.parent as Sprite3D;
            } else {
                out = out.getChildByName(arr[i]) as Sprite3D;
            }
            if (!out) return null;
        }
        return out;
    }

    export function addScript3D(sp: Sprite3D, path: string, cls: typeof Laya.Script3D): any {
        let node = findChildByPath(sp, path) as Sprite3D;
        if (node) {
            return node.addComponent(cls);
        }
        return null;
    }

    export function stopPart(p: Laya.ShuriKenParticle3D): void {
        p.particleSystem.stop();
        // p.particleSystem.pause();
        for (let i = 0; i < p.numChildren; i++) {
            (p.getChildAt(i) as Laya.ShuriKenParticle3D).particleSystem.stop();
        }
    }

    export function playPart(p: Laya.ShuriKenParticle3D): void {
        stopPart(p);
        p.particleSystem.play();
        for (let i = 0; i < p.numChildren; i++) {
            (p.getChildAt(i) as Laya.ShuriKenParticle3D).particleSystem.play();
        }
    }
    export function setCollisionGroup(sp: Sprite3D, group: number, collideWith: number = 0): void {
        // PhysicsCollider
        let py: Laya.PhysicsCollider;
        py = sp.getComponent(Laya.PhysicsCollider);
        if (py) {
            py.collisionGroup = group;
            if (collideWith > 0) {
                py.canCollideWith = collideWith;
            }
        } else {
            py = sp.getComponent(Laya.Rigidbody3D);
            if (py) {
                py.collisionGroup = group;
                if (collideWith > 0) {
                    py.canCollideWith = collideWith;
                }
            }
        }

        if (sp.numChildren > 0) {
            for (let i = 0; i < sp.numChildren; i++) {
                setCollisionGroup(sp.getChildAt(i) as Sprite3D, group);
            }
        }
    }

    export function getNodeByWeight(list: Array<any>, key: string = "weight"): any {
        let total = 0;
        for (let i = 0; i < list.length; i++) {
            // total = total + list[i]
            const node = list[i];
            if (node && node[key]) {
                total += node[key]
            }
        }

        let k = Math.random() * total;

        for (let i = 0; i < list.length; i++) {
            // total = total + list[i]
            const node = list[i];
            if (node && node[key]) {
                total -= node[key];
                if (k >= total) {
                    return node;
                }
            }
        }

        console.error("权重算法有问题，没有随机出合法值!!!!!!");
        return list[0];
    }

