    /**
     * 
     * @param gameObject 
     * @param path 如果path为null表示gameObject自身
     * @param group 
     * @param collideWith 
     */
    export function setCollisionGroupSingle(gameObject: Sprite3D, path: string, group: number, collideWith: number = 0): void {
        let obj;
        if (path) {
            obj = ZY.findChildByPath(gameObject, path);
        } else {
            obj = gameObject;
        }
        if (obj) {
            let py;
            py = obj.getComponent(Laya.PhysicsCollider);
            if (py) {
                py.collisionGroup = group;
                if (collideWith > 0) {
                    py.canCollideWith = collideWith;
                }
            } else {
                py = obj.getComponent(Laya.Rigidbody3D);
                if (py) {
                    py.collisionGroup = group;
                    if (collideWith > 0) {
                        py.canCollideWith = collideWith;
                    }
                }
            }
        }
    }

    export function disableCollider(sp: Sprite3D): void {
        let py: Laya.PhysicsCollider;
        py = sp.getComponent(Laya.PhysicsCollider);
        if (py) {
            py.enabled = false;
        } else {
            py = sp.getComponent(Laya.Rigidbody3D);
            if (py) {
                py.enabled = false;
            }
        }
        if (sp.numChildren > 0) {
            for (let i = 0; i < sp.numChildren; i++) {
                disableCollider(sp.getChildAt(i) as Sprite3D);
            }
        }
    }
    export function activeCollider(sp: Sprite3D): void {
        let py: Laya.PhysicsCollider;
        py = sp.getComponent(Laya.PhysicsCollider);
        if (py) {
            py.enabled = true;
        } else {
            py = sp.getComponent(Laya.Rigidbody3D);
            if (py) {
                py.enabled = true;
            }
        }
        if (sp.numChildren > 0) {
            for (let i = 0; i < sp.numChildren; i++) {
                activeCollider(sp.getChildAt(i) as Sprite3D);
            }
        }
    }

    export function clearArray(arr: Array<any>): void {
        if (arr && arr instanceof Array) {
            while (arr.length > 0) {
                arr.pop();
            }
        }
    }

    export function posSprite3D(obj: Sprite3D, x: number, y: number, z: number, islocal: boolean = true): void {
        if (islocal) {
            obj.transform.localPosition.setValue(x, y, z);
            obj.transform.localPosition = obj.transform.localPosition;
        } else {
            obj.transform.position.setValue(x, y, z);
            obj.transform.position = obj.transform.position;
        }
    }

