    /**
     * 开始加载预加载资源
     * @param preload 
     * @param caller 
     * @param callback 
     */
    export function preload(preload: Array<any>, caller: any, callback: Function, loadingUI: LoadingUI): void {
        if (channel === "wx" || channel === "zj") {
            Laya.MiniAdpter.nativefiles = preload;
        } else if (channel === "oppo") {
            Laya.QGMiniAdapter.nativefiles = preload;
        } else if (channel === "vivo") {
            Laya.VVMiniAdapter.nativefiles = preload;
        } else if (channel === "qq") {
            Laya.QQMiniAdapter.nativefiles = preload;
        } else if (channel === "bd") {
            Laya.BMiniAdapter.nativefiles = preload;
        }

        stage.setLoadingUI(loadingUI);
        if (preload && preload.length > 0) {
            Laya.loader.load(preload, Laya.Handler.create(null, () => {
                loadingUI.init();
                stage.showLoading("");
                if (callback) {
                    callback.call(caller);
                }
            }));
        } else {
            loadingUI.init();
            stage.showLoading("");
            // (this._loadingLayer.addChild(this._loadingUI.show(LoadingUIStyle.FULL, null, null)) as Sprite).size(Laya.stage.width, Laya.stage.height);
            Laya.systemTimer.frameOnce(1, this, () => {
                if (callback) {
                    callback.call(caller);
                }
            })

        }
    }

    export function load(reslist: Array<any>, caller: any, callback: Function): void {
        stage.load(reslist, caller, callback)
    }

    export function bindSceneUI(sceneName: string, uicls: any, res: Array<any>): void {
        stage.bindSceneUI(sceneName, uicls, res);
    }

