    export class BaseCtrl {
        protected _sceneName: string = "";
        constructor() { }
        public start(): void {
            stage.__addCtrl(this);
        }
        /**
         * 绑定一个资源
         * @param url 资源地址
         * @param type 资源类型
         * @param sceneName 使用该资源的场景
         * @param dynamic 是否是动态资源，默认为false
         */
        protected bindAssets(url: string, type: string, sceneName: string, dynamic: boolean = false): void {
            stage.bindAssets(url, type, sceneName, dynamic);
        }

        /**
         * 绑定一个3d资源
         * @param url 
         * @param sceneName 
         * @param dynamic 是否是动态资源，默认为false
         */
        protected bindAssets3D(url: string, sceneName: string, dynamic: boolean = false): void {
            stage.bindAssets3D(url, sceneName, dynamic);
        }

        protected createChildren(sceneName: string): void {
        }

        protected layoutChildren(): void {
        }

        protected beforeEnterScene(sceneName: string): void {

        }

        protected afterEnterScene(sceneName: string): void {

        }

        public __beforeEnter(sceneName: string): void {
            this.stopFrameLoop();
            this.beforeEnterScene(sceneName);
        }

        public __afterEnter(sceneName: string): void {
            this._sceneName = sceneName;
            this.createChildren(sceneName);
            this.layoutChildren();
            // this.afterEnterScene(sceneName);
        }

        public __layoutChildren(): void {
            this.layoutChildren();
        }

        public __afterAllEnter(sceneName: string): void {
            this.afterEnterScene(sceneName);
        }

        private _status: number = NaN;
        public get status(): number {
            return this._status;
        }
        protected get intStatus(): number {
            return Math.floor(this._status);
        }
        protected changeStatus(status: number): void {
            if (this._status !== status) {
                this._status = status;
                this.handleChangeStatus(status);
            }
        }

        protected handleChangeStatus(status: number): void {
        }

        protected frameLoop(): void {
        }

        protected startFrameLoop(): void {
            Laya.systemTimer.frameLoop(1, this, this.frameLoop);
        }

        protected stopFrameLoop(): void {
            Laya.systemTimer.clear(this, this.frameLoop);
        }
        protected loadAssets(resList: Array<any>, caller: any, complete: Function, cancel: Function): void {

        }
    }

    class FirstCtrl extends BaseCtrl {
        public static SCENE_NAME: string = "ZY_FIRST";
        constructor() {
            super();
        }
        public onCompleteCaller: any;
        public onComplete: Function;
        public loadList: Array<any>;
        public enterSceneName: string

        protected beforeEnterScene(sceneName: string): void {
            if (sceneName === FirstCtrl.SCENE_NAME) {
                ZY.stage.setLoadingStatus(0, 0.1, "正在加载配置", false);
                console.log(">>>:加载预加载资源：");
                console.log("   ---------------------------------");
                let res = this.loadList;
                for (let i = 0; i < res.length; i++) {
                    const info = res[i];
                    console.log("   #" + info);
                    if (typeof info === "string") {
                        if (info.lastIndexOf(".png") > 0 || info.lastIndexOf(".jpg") > 0) {
                            this.bindAssets(info, Laya.Loader.IMAGE, sceneName, false);
                        } else if (info.lastIndexOf(".atlas") > 0) {
                            this.bindAssets(info, Laya.Loader.ATLAS, sceneName, false);
                        } else if (info.lastIndexOf(".json") > 0 || info.lastIndexOf(".ani") > 0) {
                            this.bindAssets(info, Laya.Loader.JSON, sceneName, false);
                        }
                    } else if (info && info.url && info.type) {
                        this.bindAssets(info.url, info.type, sceneName, false);
                    }
                }
                console.log("   ---------------------------------");
            }
        }
        protected createChildren(sceneName: string): void {
            if (sceneName === FirstCtrl.SCENE_NAME) {
                if (this.onComplete && this.onCompleteCaller) {
                    this.onComplete.call(this.onCompleteCaller);
                }
            } else {
                ZY.stage.setLoadingStatus(0, 1);
            }
        }
        protected afterEnterScene(sceneName: string): void {
            if (sceneName === FirstCtrl.SCENE_NAME) {
                // if (this.onComplete && this.onCompleteCaller) {
                //     this.onComplete.call(this.onCompleteCaller);
                // }
                ZY.stage.setLoadingStatus(0.1, 0.9);
                ZY.stage.enterScene(this.enterSceneName);
            }
        }
    }

