    /////////////////////声音相关////////////////////////
    var __soundsID: number = 0;
    export interface ISoundChannel extends Laya.EventDispatcher {
        /**
         * 音频唯一id
         */
        audioID: number;
        /**
         *  根据一个地址播放音频
         * @param src 需要播放的音频地址
         * @param loops 循环次数，0表示无限循环
         */
        playSrc(src: string, loops: number): void;
        /**
         * 
         */
        play(): void;
        pause(): void;
        resume(): void;
        stop(): void;
        /**
         * 停止一个声音，并且清理所有侦听
         * @param recover 是否回收，默认值是true
         */
        clear(recover?: boolean): void;
        src: string;
        __canRecover(): boolean;
        volume: number;
    }
    // class SoundChannel {
    //     constructor() {}
    // }
    class AudioSoundChannel extends Laya.EventDispatcher {
        private _audio: any;
        private _onMusicReady: any;
        private _audioReady: boolean = false;
        private _curLoops: number;
        // private _curCaller:any;
        // private _callback:Function;
        private _playedTimes: number;
        private _playing: boolean = false;
        private _audioId: number;
        constructor() {
            super();
            this._audioId = ++__soundsID;
            this._audio = document.createElement("audio");
            this._audio.preload = true;
            this._audio.addEventListener("ended", this.onAudioEnded.bind(this));
            this._onMusicReady = this.onMusicReady.bind(this);
            document.addEventListener("mousedown", this._onMusicReady);
            document.addEventListener("touchstart", this._onMusicReady);
        }
        public get audioID(): number {
            return this._audioId;
        }
        public __canRecover(): boolean {
            return !this.hasListener("complete");
        }
        public clear(recover: boolean = true): void {

        }
        public get volume(): number {
            if (this._audio.hasOwnProperty("volume")) return this._audio.volume;
            return 1;
        }

        public set volume(v: number) {
            if (v < 0) v = 0;
            if (v > 1) v = 1;
            if (this._audio.hasOwnProperty("volume")) {
                this._audio.volume = v;
            }
        }
        private onAudioEnded(): void {
            console.log("audio ended!");
            this._playedTimes++;
            if (this._playedTimes >= this._curLoops && this._curLoops != 0) {
                // if(this._callback) {
                //     this._callback.call(this._curCaller);
                // }
                this.event("complete", this);
                this._playing = false;
                return;
            }
            this._audio.play();
        }
        private onMusicReady(): void {
            console.log("mousedown!");
            this._audioReady = true;
            document.removeEventListener("mousedown", this._onMusicReady);
            document.removeEventListener("touchstart", this._onMusicReady);
            if (!this._audio.src) {
                console.log("audio load");
                this._audio.src = "";
                this._audio.load();
            }
            else {
                console.log("play:" + this._audio.src);
                if (this._playing) this._audio.play();
            }
        }
        public get src(): string {
            return this._audio.src;
        }
        public playSrc(src: string, loops: number): void {
            this._audio.src = src;
            this._curLoops = loops;
            this._playedTimes = 0;
            // this._callback = callback;
            // this._curCaller = caller;
            if (loops === 0) {
                this._audio.loop = true;
                console.log("loop");
            } else {
                this._audio.loop = false;
            }
            if (this._audioReady) this._audio.play();
            this._playing = true;
        }

        public pause(): void {
            if (this._playing) {
                this._audio.pause();
                this._playing = false;
            }
        }

        public play(): void {
            if (!this._playing) {
                this._playing = true;
                if (this._audioReady) this._audio.play();
            }
        }

        public resume(): void {
            this.play();
        }

        public stop(): void {
            if (this._playing) {
                this._audio.pause();
                this._audio.currentTime = 0;
                this._playing = false;
            }
        }
    }

    class GameSoundChannel extends Laya.EventDispatcher {
        private _info: {
            src: string,
            loops: number,
            playCnt: number
        }
        private _audio: any;
        private _palying: boolean;
        private _audioId: number;
        // public completeHandler: Laya.Handler;
        /**
         * 小游戏声音轨道
         */
        constructor() {
            super();
            this._audioId = ++__soundsID;
            if (channel === "wx") {
                this._audio = wx.createInnerAudioContext();
            } else if (channel === "qq") {
                this._audio = (window as any).qq.createInnerAudioContext();
            } else if (channel === "zj") {
                this._audio = (window as any).tt.createInnerAudioContext();
            } else if (channel === "oppo" || channel == "vivo") {
                this._audio = (window as any).qg.createInnerAudioContext();
            } else if (channel === "bd") {
                this._audio = (window as any).swan.createInnerAudioContext();
            }

            if (this._audio.obeyMuteSwitch) {
                this._audio.obeyMuteSwitch = true;
            }
            // this._musicAudio.onCanplay(); //监听音频进入可以播放状态的事件
            this.onMusicEnded = this.onMusicEnded.bind(this);
            this.onMusicError = this.onMusicError.bind(this);
            this.onMusicPlay = this.onMusicPlay.bind(this);

            this._audio.onEnded(this.onMusicEnded); //监听音频自然播放至结束的事件
            this._audio.onError(this.onMusicError); //监听音频播放错误事件
            this._audio.onPlay(this.onMusicPlay); //监听音频播放错误事件
            this._info = { src: "", loops: 1, playCnt: 0 };
            this._palying = false;
        }

        public get volume(): number {
            if (this._audio.volume !== undefined) return this._audio.volume;
            return 1;
        }

        public set volume(v: number) {
            if (v < 0) v = 0;
            if (v > 1) v = 1;
            if (this._audio.volume !== undefined) {
                this._audio.volume = v;
            }
        }

        public get audioID(): number {
            return this._audioId;
        }

        public playSrc(src: string, loops: number): void {
            this._info.src = src;
            this._info.loops = loops;
            this._info.playCnt = 0;

            this._audio.src = src;
            this._audio.autoplay = true;
            this._palying = false;
            // console.log("audio playurl:"+this._info.url);
            // if(ZY.channel !=="wx") {
            this.play();
            // }
        }

        public play(): void {
            if (this._palying) return;
            // console.log("audio play:"+this._info.url);
            if (this._audio.src && this._audio.src != "") {
                this._palying = true;
                this._audio.play();
            } else {
                console.warn("ZY>>:没有指定声音地址，请先调用playSrc接口");
            }
        }

        private onMusicPlay(): void {
            // console.log("music play:"+this._info.src+","+this._audio.src);
            this._palying = true;
        }

        private onMusicEnded(): void {
            this._info.playCnt++;
            this._palying = false;
            // console.log("audio stop1:"+this._info.url);
            // console.log("audio end:"+this._info.url);
            if (this._info.playCnt < this._info.loops || this._info.loops == 0) {
                this._audio.src = this._info.src;
                this.play();
            } else {
                this.event("complete", this);
                if (this.__canRecover()) {
                    this.clear();
                }
            }
        }

        public pause(): void {
            this._palying = false;
            // console.log("audio stop2:"+this._info.url);
            this._audio.pause();
        }

        public resume(): void {
            this.play();
        }

        public stop(): void {
            this._palying = false;
            // console.log("audio stop3:"+this._info.url);
            this._audio.stop();
        }

        public clear(recover: boolean = true): void {
            this._palying = false;
            // console.log("audio stop4:"+this._info.url);
            // this._audio.src = "";
            this._audio.stop();
            this._info = { src: "", loops: 1, playCnt: 0 };
            this.offAll();

            if (recover) {
                // console.log("回收:" + this._audio.src);
                if (channel === "wx") {
                    console.log("销毁音轨:" + this._audioId);
                    this._info = null;
                    this._palying = null;
                    this._audioId = null;
                    this._audio.offEnded(this.onMusicEnded); //监听音频自然播放至结束的事件
                    this._audio.offError(this.onMusicError); //监听音频播放错误事件
                    this._audio.offPlay(this.onMusicPlay); //监听音频播放错误事件
                    this._audio.destroy();
                    this._audio = null;
                } else {
                    Laya.Pool.recover("__SOUND_CHANNEL", this);
                }
            }
        }

        private onMusicError(e): void {
            console.log("music error:" + JSON.stringify(e) + "," + this._info.src);
        }

        public get src(): string {
            return this._info.src;
        }

        public __canRecover(): boolean {
            return !this.hasListener("complete");
        }
    }

    class SoundManager extends BaseClass {
        public basePath: string = "";
        private _bMusic: boolean;
        public set bMusic(value: boolean) {
            this._bMusic = value;
        }
        private _bSound: boolean;
        public set bSound(value: boolean) {
            this._bSound = value;
        }
        private _audioSoundChannel: ISoundChannel;
        constructor() {
            super();
        }

        public init(): void {
            super.init();
            if (Laya.Utils.getQueryString("sound") == "false") return;
            if (channel === "wx" || channel === "zj" || channel === "oppo" || channel == "vivo" || channel == "qq" || channel === "bd") {
                this._audioSoundChannel = new GameSoundChannel();
            } else {
                this._audioSoundChannel = new AudioSoundChannel();
            }

            this._audioSoundChannel.on("complete", this, (a: any) => {
                // 增加一个侦听，防止被回收
            })

            this._onlySoundList = {};
        }

        private createSoundChannel(): ISoundChannel {
            if (Laya.Utils.getQueryString("sound") == "false") null;
            if (channel === "wx" || channel === "zj" || channel === "oppo" || channel == "vivo" || channel == "qq" || channel === "bd") {
                return Laya.Pool.getItemByClass("__SOUND_CHANNEL", GameSoundChannel);
            }
            return null;
        }

        /**
         * 播放一段背景音乐，注意背景音乐同时只能播放一段，当需要播放新的背景音乐时，上一段背景音乐会停止
         * @param src 音乐地址
         * @param loops 循环次数，如果是0则表示无限循环,默认是0
         * @param caller 音乐播放完成时的回调函数的caller
         * @param callback 音乐播发完成时的回调函数，如果无限循环，则不会触发该回调
         */
        public playMusic(src: string, loops: number = 0, caller: any = null, callback: Function = null): ISoundChannel {
            // console.log("play music:"+src);
            if (!this._bMusic || ZY.videoAdShow) return
            if (Laya.Utils.getQueryString("sound") == "false") return null;
            if (!this._audioSoundChannel) {
                console.log("ZY>>:playMusic->没有可用的 audioSoundChannel，检查渠道是否不支持，再不懂问一下公司技术");
                return;
            }
            if (callback != null && caller != null) {
                this._audioSoundChannel.once("complete", caller, callback);
            }
            this._audioSoundChannel.playSrc(this.basePath + src, loops);

            return this._audioSoundChannel;
        }

        public stopMusic(): void {
            if (this._audioSoundChannel) this._audioSoundChannel.stop();
        }

        public get musicSoundChannel(): ISoundChannel {
            return this._audioSoundChannel;
        }

        private _onlySoundList: any;

        /**
         * 播放一段音效，音效文件加载完成后才会开始播放。
         * @param src 音效地址
         * @param loops 循环次数，如果是0则表示无限循环,默认值是1
         * @param only 默认为false。如果为true，同一时间，同一个声音只能播放一个，例如xxx/xxx.mp3还没播放完的时候，再次播放该声音时，相当于重新播放该声音
         * @param caller 音效播放完成时的回调函数的caller
         * @param callback 音效播发完成时的回调函数，如果无限循环，则不会触发该回调
         */
        public playSound(src: string, loops: number = 1, only: boolean = false, caller: any = null, callback: Function = null): ISoundChannel {
            if (!this._bSound || ZY.videoAdShow) {
                if (caller && callback) {
                    callback.call(caller);
                }
                return
            }
            if (Laya.Utils.getQueryString("sound") == "false") return null;
            let sc;
            if (only) {
                sc = this._onlySoundList[src] || this.createSoundChannel();
                if (!sc) {
                    console.log("ZY>>:playSound->没有可用的 audioSoundChannel，检查渠道是否不支持，再不懂问一下公司技术");
                    return;
                }
                this._onlySoundList[src] = sc;
                sc.once("complete", this, () => {
                    this._onlySoundList[src] = null;
                    if (caller && callback) {
                        callback.call(caller, sc);
                    }
                })
                sc.stop();
                sc.playSrc(this.basePath + src, loops);
            } else {
                sc = this.createSoundChannel();
                if (!sc) {
                    console.log("ZY>>:playSound->没有可用的 audioSoundChannel，检查渠道是否不支持，再不懂问一下公司技术");
                    return;
                }
                if (caller && callback) sc.once("complete", caller, callback);
                sc.playSrc(this.basePath + src, loops);
            }

            return sc;
        }

        public stopSound(): void {
            if (this._onlySoundList && this._onlySoundList.length > 0) {
                for (let i = 0; i < this._onlySoundList.length; i++) {
                    const sc = this._onlySoundList[i];
                    sc.stop();
                }
            }
        }

    }
