        public getSizeStr(names: Array<string>): string {
            if (!names) return "0M";
            let s = 0;
            for (let i = 0; i < names.length; i++) {
                const n = names[i];
                const cfg = this._cfg[n];
                if (cfg && cfg.hasOwnProperty("size")) {
                    s += cfg.size;
                }
            }

            return Math.floor(s * 100) / 100 + "M";
        }

        public checkLoadZip(sceneName: string): Array<string> {
            if (this._cfg.set.channel.indexOf(channel) > -1) {
                // return true;
                let tmp = [];
                let cfg = this._cfg.set.any;
                if (cfg) {
                    for (let i = 0; i < cfg.length; i++) {
                        tmp.push(cfg[i]);
                    }
                }
                cfg = this._cfg.set[sceneName];
                if (cfg) {
                    for (let i = 0; i < cfg.length; i++) {
                        tmp.push(cfg[i]);
                    }
                }
                return tmp;
            };
            return null;
        }

        public checkAndLoad(names: Array<string>, caller: any, onProgress: Function, onError: Function, onComplete: Function): boolean {
            let compCnt = 0;


            let tmpNames = [];
            for (let i = 0; i < names.length; i++) {
                if (!this.check(names[i])) {
                    tmpNames.push(names[i]);
                }
            }

            if (tmpNames.length < 1) {
                onComplete.call(caller);
                console.log("无需下载任何zip资源")
                stage.setLoadingInfo("");
                return false;
            }

            // this.readAllFilesInfo();

            stage.setLoadingInfo("资源更新中[" + assetsManger.getSizeStr(names) + "],只需更新一次，请耐心等待...");

            let name = tmpNames[compCnt];

            let me = this;

            function progress(p): void {
                onProgress.call(caller, (compCnt + p) / tmpNames.length);
            }
            function error(msg): void {
                onError.call(caller, msg);
            }
            function complete(): void {
                ++compCnt;
                console.log("完成一个资源")
                if (compCnt >= tmpNames.length) {
                    onComplete.call(caller);
                } else {
                    name = tmpNames[compCnt];
                    me.download(name, this, progress, error, complete);
                }
            }

            this.download(name, this, progress, error, complete);

            return true;
        }

