        private reShowBanner(): void {
            if (this._bannerAd) {
                this._bannerAd.offClose(this.onBannerClose);
                this._bannerAd.destroy();
            }
            this._bannerAd = qg.createBannerAd({
                posId: this._bannerId,
                // style: {}
            }) as any;
            console.log("创建了vivo的banner:" + this._bannerId);
            this._bannerAd.onClose(this.onBannerClose);
            this._bannerShowed = false;
            this.showBannerNow();
        }

        private _vivoBannerActive: boolean;
        private showBannerNow(retry: boolean = false): void {
            Laya.systemTimer.clear(this, this.reShowBanner);
            if (!this._vivoBannerActive) return;
            if (this._bannerShowed) return;
            if (!this._bannerAd) {
                this._bannerAd = qg.createBannerAd({
                    posId: this._bannerId,
                    // style: {}
                }) as any;
                this._bannerAd.onClose(this.onBannerClose);
                this._bannerAd.onResize(this.onBannerResize)
                console.log("创建了vivo的banner:" + this._bannerId);
                if (this._bannerId != "86f86432c21f4d7ea2d139a996399c3e") {
                    console.log("!!!!!!!!banner id 错误了！！！！！！！！！！！");
                }
            }
            if (this._bannerAd) {
                let adshow = this._bannerAd.show();
                console.log("_bannerAd.show!!");
                console.log(adshow);
                // 调用then和catch之前需要对show的结果做下判空处理，防止出错（如果没有判空，在平台版本为1052以及以下的手机上将会出现错误）
                adshow && adshow.then(() => {
                    console.log("banner广告展示成功");
                    this._lastBannerShowTime = Laya.Browser.now();
                    this._bannerShowed = true;
                }).catch((err) => {
                    switch (err.code) {
                        case 30003:
                            console.log("新用户1天内不能曝光Banner，请将手机时间调整为1天后，退出游戏重新进入")
                            break;
                        case 30009:
                            console.log("10秒内调用广告次数超过1次，10秒后再调用")
                            this.onBannerClose(10100);
                            return;
                        case 30002:
                            console.log("加载广告失败，重新加载广告")
                            this.onBannerClose(10100);
                            return;
                        default:
                            console.log("banner广告展示失败")
                            console.log(JSON.stringify(err))
                            break;
                    }
                    if (!retry) {
                        if (this._bannerAd) {
                            this._bannerAd.offClose(this.onBannerClose);
                            this._bannerAd.destroy();
                        }
                        this._bannerAd = qg.createBannerAd({
                            posId: this._bannerId,
                            // style: {}
                        }) as any;
                        this._bannerAd.onClose(this.onBannerClose);
                        this._bannerAd.onResize(this.onBannerResize);
                        console.log("创建了vivo的banner:" + this._bannerId);
                        this.showBannerNow(true);
                    }
                });
            }
        }

        private onBannerClose(time: number = null): void {
            if (this._bannerAd) {
                this._bannerAd.offClose(this.onBannerClose);
                this._bannerAd.destroy();
            }
            this._bannerAd = null;
            console.log(">>>:vivo的banner被手动关闭，20s后再次创建")
            this._bannerShowed = false;
            this._lastBannerHideTime = Laya.Browser.now();
            time = time || 20 * 1000
            Laya.systemTimer.once(time, this, this.reShowBanner);
        }

