        /**开始加载banner,从x个开始 */
        public start(index: number): void {
            if (this._canBanner) {
                var timeIdx: number = -1;
                for (let i: number = index; i < ad._ids.length; i++) {
                    if (!ad.isPushed(ad._ids[i])) {
                        timeIdx++;
                        Laya.timer.once(1000 * timeIdx, this, () => {
                            ad.createBannerAd(ad._ids[i]);
                        }, null, false);
                    }
                }
            }
        }

        /**是否已经在队列里 */
        private isPushed(id: string): boolean {
            for (var i: number = 0; i < ad._banners.length; i++) {
                if (ad._banners[i].id == id)
                    return true;
            }
            return false;
        }

        /**创建banner */
        private createBannerAd(id: string) {
            let power
            if (ZY.channel == "zj") {
                power = (window as any).tt;
            } else if (ZY.channel == "wx") {
                power = wx
            } else if (ZY.channel == "vivo" || ZY.channel == "oppo") {
                power = (window as any).qg
            } else if (ZY.channel == "qq") {
                power = (window as any).qq
            } else if (ZY.channel === "bd") {
                power = (window as any).swan;
            } 
            if (power.createBannerAd && this._canBanner) {
                console.log('banner 创建:' + id);
                let obj = power.getSystemInfoSync();
                let w = Math.floor(obj.screenWidth * 0.8);
                if (w < 300) {
                    w = 300;
                }
                var bannerAd = power.createBannerAd({
                    adUnitId: id,
                    style: {
                        left: Math.floor((obj.screenWidth - w) / 2),
                        top: 100,
                        width: w
                    },
                    adIntervals: 40
                });
                bannerAd.onError((err) => {
                    console.log('banner 广告加载失败:' + err.errCode);
                    ad._hasBannerLoadError = true;
                    // Mc2Manager.evt(WXBanner.BANNER_LOAD_FAIL);

                    Laya.timer.once(60000, ad, ad.resetBannerLoadError);
                });

                bannerAd.onLoad(() => {
                    console.log('banner 广告加载成功: ');

                });
                var me = this;
                bannerAd.onResize((res) => {
                    me._adWidth = res.width;
                    me._adHeight = res.height;
                    if (me._adHeight < 2) {
                        me._adHeight = 89;
                    }
                });

                if (!ad.isPushed(id)) {
                    console.log('banner 广告PUSH成功:');
                    if (ad.isNoBanners) {
                        console.log('第一个广告加载成功');
                    }
                    ad.isNoBanners = false;
                    var bannerData: BannerData = new BannerData();
                    bannerData.id = id;
                    bannerData.banner = bannerAd;
                    ad._banners.push(bannerData);
                }
            }
            else {
                if (!ad._isNoAdsSend) {
                    ad._isNoAdsSend = true;
                }
            }
        }

