        private _videoCaller: any;
        private _videoCallback: Function;
        private _videoParam: Array<any>;
        public isADing: boolean = false;
        public showVideoAd(caller: any, callback: Function, index: number = 0, event?: string, param?: Array<any>, isFailCallball: boolean = false): void {
            if (ZY.channel == "wx") {
                index = index >= 3 ? 3 : index
            } else if (ZY.channel == "qq" || ZY.channel == "bd") {
                this.showQQVideoAd(caller, callback, event, param)
                return
            } else if (ZY.channel == "zj") {
                this.showZjVideoAd(caller, callback, event, param, isFailCallball)
                return
            }
            if (!this._videoAd || !this._videoAd[index]) {
                callback.call(caller, true, param);
                return;
            }
            this._videoCaller = caller;
            this._videoCallback = callback;
            this._videoParam = param;
            this.isADing = true;
            let videoAd = this._videoAd[index];
            let self = this;
            console.log(">>>:voide准备显示", videoAd, index);
            videoAd.show()
                .then(() => {
                    if (event) ZY.eventBus.event("CLICK_AD", event)
                    console.log(">>>:video广告显示成功");
                })
                .catch(err => {
                    console.log(">>>:voide广告组件出现问题", err);
                    // 可以手动加载一次
                    videoAd.load().then(() => {
                        console.log("手动加载成功");
                        // 加载成功后需要再显示广告
                        return videoAd.show();
                    })
                });
        }

