                    this._bannerAd.style.left = Math.floor((obj.screenWidth - this._adWidth) / 2);
                    this._bannerAd.style.top = obj.screenHeight - offsetY - (this._adHeight || Math.floor(210 * obj.screenHeight / Laya.stage.height));
