import Helper2D from "../Helper2D";
import AnimationCurve from "../AnimationCurve";
import GameObject = Laya.Sprite3D;
import AnimalPredator from "./AnimalPredator";
import { AnimalState, PlayerState, moduleBridge } from "../Interface";

export enum GriffinState {running, flying, diving, rising}

export default class AnimalGriffin extends AnimalPredator {

	private currentHeight:number;
	public flyHeight:number;
	public runTime:number;
	private runCounter:number;
	public risingCurve;
	public risingTime:number;
	private risingCounter:number;
	public diveRate:number;
	public diveCollider;
	private griffinState:GriffinState;

	protected clearAll():void {
		this.griffinState = null;
		this.diveCollider = null;
		super.clearAll();
	}

    public init(id:string, obj:GameObject):void{
        super.init(id, obj)
		let info = Laya.loader.getRes("cfg/cfgAttribute.json")[this.encId];
        this.flyHeight = info.flyHeight
        this.runTime = info.runTime
        this.risingTime = info.risingTime
		this.diveRate = info.diveRate
		this.currentHeight = 0;
        Helper2D.SetPosY(this.transform,this.currentHeight)
        this.griffinState = GriffinState.running;
        let curve = new AnimationCurve();
        if (info.risingCurve && info.risingCurve.length > 0){
			for (let i = 0 ; i < info.risingCurve.length ; i ++) {
				curve.addKeyFrame(info.risingCurve[i][0],info.risingCurve[i][1],info.risingCurve[i][2],info.risingCurve[i][3],info.risingCurve[i][4],info.risingCurve[i][5])
            }        
        }
        this.risingCurve = curve
	}

	public DivingCollision(){
		if (this.griffinState == GriffinState.flying || this.griffinState == GriffinState.rising) {
			this.griffinState = GriffinState.diving;
		} else if (this.griffinState == GriffinState.running) {
			this.runCounter = 0;
		}
	}

	public ride(startingAnimal:boolean,fhtime ?:number,fhdis ?:number):void {
		this.currentHeight = 0;
		this.runCounter = 0;
		this.risingCounter = 0;
		this.griffinState = GriffinState.running;
		// this.diveCollider.enabled = true;
		super.ride (startingAnimal,fhtime,fhdis);
	}

	public onUpdate () {

		switch (this.griffinState) {
		case GriffinState.diving:
			this.currentHeight -= this.diveRate * Laya.timer.delta/1000
			if (this.currentHeight <= 0) {
				this.currentHeight = 0;
				this.runCounter = 0;
				this.griffinState = GriffinState.running;
			}

			// anim.SetLayerWeight (animLayer3DOverride, currentHeight / flyHeight);

			break;

        case GriffinState.flying:
			if (this.state != AnimalState.ridden || this.angerStage >= 1) {
				this.griffinState = GriffinState.diving;
			}
			break;

		case GriffinState.rising:

			if (this.state != AnimalState.ridden) {
				this.griffinState = GriffinState.diving;
				break;
			}

			this.risingCounter += Laya.timer.delta

			this.currentHeight = this.flyHeight * this.risingCurve.evaluate (this.risingCounter / this.risingTime);
			let layerWeight = Helper2D.Clamp(-2 + 3 * this.currentHeight /this.flyHeight,0,1);
			// anim.SetLayerWeight (animLayer3DOverride, layerWeight);

			if (this.risingCounter > this.risingTime) {
				this.risingCounter = 0;
				this.griffinState = GriffinState.flying;
				this.currentHeight = this.flyHeight;
				// anim.ResetTrigger("forceRidden");
				// anim.SetTrigger("forceRidden");
			}

			break;

		case GriffinState.running:

			if (this.state == AnimalState.ridden && this.angerStage < 1) {
				this.runCounter += Laya.timer.delta/1000
				if (this.runCounter > this.runTime) {
					this.griffinState = GriffinState.rising;
					this.risingCounter = 0;
					this.runCounter = 0;
					// anim.ResetTrigger("forceBuck");
					// anim.SetTrigger("forceBuck");
				}
			}

			break;
		}

		super.onUpdate ();


		if (moduleBridge.player.state != PlayerState.waitingForStart && this.state != AnimalState.falling && this.griffinState != GriffinState.running /* && gliding == true*/) {
            Helper2D.SetPosY(this.transform,this.currentHeight)
			// transform.SetPosY (this.currentHeight);
		}
	}

	public leaveAnimal (crashed:boolean = false, playCrashAnim:boolean = true)
	{
		// this.diveCollider.enabled = false;
		super.leaveAnimal (crashed, playCrashAnim);
	}
}