    private hwPay(big: number, small: number): void {
        if (ZY.uid == "-1") {
            ZY.msg.show("当前为游客模式，请登录之后充值~")
            return
        }
        let list = {
            0: [[1, "1000金币和象伯爵", "萌新礼包", "fkdwy1"], [3, "5000金币和电子斑马", "回馈礼包", "fkdwy2"], [50, "50000金币和商人贩卖的普通动物", "动物之友", "fkdwy3"]],
            1: [[12, "开门营业双倍金币", "摇钱牛", "fkdwy7"], [3, "无限刷新任务", "任务狂骡", "fkdwy4"], [12, "每局复活一次", "复活渡鸦", "fkdwy5"], [12, "稀有动物出现概率更大", "幸运羔羊", "fkdwy6"]],
            2: [[6, "3000金币", "超经济环保型观光团", "fkdwy8"], [9, "业界精英旅行团", "6000金币", "fkdwy9"], [15, "超级巨星专场", "15000金币", "fkdwy10"]],
            3: [[6, "摇钱礼包", "摇钱大礼包", "fkdwy12"], [10, "复活礼包", "复活大礼包", "fkdwy11"]],
            4: [[1, "1元礼包", "1元礼包", "fkdwy13"]]
        }
        let self = this;
        let hr = new HttpLoader;
        hr.once(Laya.Event.ERROR, this, (e: any) => {
            // ZY.msg.hide();
            ZY.msg.show("拉起失败，请再试一次")
            console.log("请求失败~", e)

        });
        hr.once(Laya.Event.COMPLETE, this, (data: any) => {
            console.log("请求成功", data);
            // ZY.msg.hide();
            if (data.code == 0) {
                let tData = data.data;
                if (tData.prepayId) {
                    if (Laya.Browser.window.superSDK.doPay) {
                        let pay = Laya.Browser.window.superSDK.doPay({
                            orderid: tData.prepayId,
                            itemsid: goodid,
                            itemsname: list[big][small][2],
                            amount: amt,
                            uopenid: ZY.uid
                        });

                        pay.success(function(status) {
                            if (status === "SUCCESS") {
                                self.giveAward(list[big][small][1]);
                            } else {
                                ZY.msg.show("支付拉起失败。");
                            }
                        });

                        pay.error(function(status, msg, desc) {
                            ZY.msg.show("支付失败");
                        })
                    }
                }
            } else {
                if (data && data.msg) {
                    ZY.msg.show(data.msg)
                } else {
                    ZY.msg.show("支付拉起失败，请再试一次");
                }
            }
        });


        let openid = ZY.uid;
        let ts = Math.floor(Laya.Browser.now() / 1000);
        let amt = list[big][small][0];
        let goodid = list[big][small][3];
        let app_remark = ZY.gid+":"+list[big][small][1];
        let session_key = ZY.sessionkey;

        let client = Laya.LocalStorage.getItem("zoozyClient");
        let sha = ZY.md5(ZY.md5(ZY.key + ZY.noice) + ts + ":" + client + "::" + ZY.noice);
        let url = "sha=" + sha + "&noice=" + ZY.noice + "&time=" + ts + "&client=" + client;
        url = url + "&openid=" + openid + "&ts=" + ts + "&amt=" + amt + "&goodid=" + goodid + "&app_remark=" + app_remark + "&session_key=" + session_key
        // hr.send("http://192.168.170.240:9000/api/qqPay?"+url,"","get","json");
        // hr.send("http://192.168.170.240:9000/api/qqPay?"+"openid=" + openid + "&ts=" + ts + "&amt=" + amt + "&goodid=" + goodid + "&app_remark=" + app_remark + "&session_key=" + session_key,"","get","json");
        hr.send("https://api.smartjoygames.com/zy/qqPay?" + url, "", "get", "json");
        console.log("https://api.smartjoygames.com/zy/qqPay?" + url);
        ZY.msg.showFull2("正在拉起支付，请稍等", 10 * 1000)
    }

