    private getForestAnimals(): Array<number> {
        let idArr: Array<number> = [];
        let cfg = userData.cfgSpawn;
        for (const key in cfg) {
            if (Number(key) > 1007 && Number(key) <= 1014) {
                let arr: Array<any> = cfg[key].animal;
                for (let i = 0; i < arr.length; i++) {
                    idArr.push(arr[i].id);
                }
            }
        }
        return idArr;
    }

    private getGrasslandsAnimals(): Array<number> {
        let idArr: Array<number> = [];
        let cfg = userData.cfgSpawn;
        for (const key in cfg) {
            if (Number(key) <= 1007) {
                let arr: Array<any> = cfg[key].animal;
                for (let i = 0; i < arr.length; i++) {
                    idArr.push(arr[i].id);
                }
            }
        }
        return idArr;
    }

