            list.push("gift3")
            if (userData.getAnimals(1000) && userData.getAnimals(1000).indexOf("10") > -1) {
                userData.set("giftBull",1)
            }
            if (userData.getAnimals(1000) && userData.getAnimals(1000).indexOf("12") > -1) {
                userData.set("giftSheep",1)
            }
