    private onClick(e: Laya.Event): void {
        let target = e.target;
        // console.log (target);
        let index;
        switch (target) {
            case this._view.btnPrev:
                index = this._curTabSelect - 1;
                index = index < 0 ? 3 : index;
                // this._view.tab.selectedIndex = index;
                this.onTabSelect(index);
                break;
            case this._view.btnNext:
                index = this._curTabSelect + 1;
                index = index > 3 ? 0 : index;
                // this._view.tab.selectedIndex = index;
                this.onTabSelect(index);
                break;

            default:
                break;
        }
    }

