    private onPostAwardComplete(orderId: string, data: any): void {
        console.log("支付成功:orderId=" + orderId);
        console.log(data);
        if (data.code == 0 && data.data.produceName) {
            ZY.msg.hide();
            // this.giveAward(data.data.produceName,orderId);
            this.oppoPayComplete(orderId)
            this._checkComCnt = 0;
            return;
        } else if (data.code == 1001) {
            // ZY.msg.show("没有订单")
        } else if (data.code == 1002) {
            ZY.msg.show("你已经领取过了");
            return;
        } else if (data.code == 1003) {
            // ZY.msg.show("数据更新失败")
        } else if (data.code == 1004) {
            // ZY.msg.show("没有数据")
        } else if (data.code == 1005) {
            // ZY.msg.show("没有订单")
        } else {
            // ZY.msg.show("失败")
        }
        if (this._checkPayCnt < 8) {
            Laya.systemTimer.once(1000, this, this.postAward, [orderId])
        } else {
            ZY.msg.show("订单异常，请截图联系客服:" + data.code, 10000);
        }
    }

    private onPostAwardError(orderId: string, e: any): void {
        console.log("检查订单状态请求失败");
        if (this._checkPayCnt < 8) {
            this.postAward(orderId);
        } else {
            ZY.msg.show("充值未到账，请联系客服。");
        }
    }

    private postAward(orderId: string): void {
        // ZY.msg.showFull2("支付完成，查询订单中...");
        console.log("准备查询订单:" + orderId + ",retry=" + this._checkPayCnt);
        if (this._payResultHttp) {
            this._payResultHttp.offAllCaller(this);
        }
        let hr = this._payResultHttp = new Laya.HttpRequest();
        // let hr: Laya.HttpRequest = new Laya.HttpRequest();
        hr.http.timeout = 5000;
        hr.once(Laya.Event.ERROR, this, this.onPostAwardError, [orderId]);
        hr.once(Laya.Event.COMPLETE, this, this.onPostAwardComplete, [orderId]);

        let timestamp = Laya.Browser.now();
        let client = Laya.LocalStorage.getItem("zoozyClient");
        let sha = ZY.md5(ZY.md5(ZY.key + ZY.noice) + timestamp + ":" + client + "::" + ZY.noice);
        let url = "/oppoAward?sha=" + sha + "&noice=" + ZY.noice + "&time=" + timestamp + "&client=" + client;
        url += "&orderId=" + orderId;

        hr.send("https://api.smartjoygames.com/zy" + url, "", "get", "json");
        console.log("https://api.smartjoygames.com/zy" + url);
        this._checkPayCnt++;
    }

