import AnimalThrower from "./AnimalThrower";
import { VaultState, moduleBridge, AnimalState, PlayerState } from "../Interface";
import { UpgradeKeys } from "../UpgradeKeys";
import Helper2D from "../Helper2D";
import Vector3 = Laya.Vector3;
import GameObject = Laya.Sprite3D;
import AnimationCurve from "../AnimationCurve";

export default class AnimalSmallVaulter extends AnimalThrower {

	public vaultStrength:number;
	public jumpForce:number;
	public jumpForceToilet:number;
	public jumpForceUnridden:number;
	public jumpSpeedUnridden:number;
	public gravity:number;
	public shockwaveRadius:number;
	public shockwaveRadiusLarge:number;
	public smashOnLanding:boolean = true;
	protected vaultState:VaultState = VaultState.none;

	// [Header("Energy System")]
	public energyMax:number;
	protected energyMaxTrue:number;
	public energyDecayRate:number;
	public energyGain:number;
	public energyGainUpgrade:number;
	protected energy:number;

	public decayRateIncrease:number;
	public maxDecayRateMultiplier:number = 20;
	protected additionalDecayRate:number = 0;
	public speedDropOffCurve;

	private defaultRunSpeedModifier:number;

	protected yVel:number;

    public init(id:string, obj:GameObject):void{
        super.init(id, obj)
        let info = Laya.loader.getRes("cfg/cfgAttribute.json")[this.encId];
        this.vaultStrength = info.vaultStrength;
        this.jumpForce = info.jumpForce;
        this.jumpForceToilet = info.jumpForceToilet;
        this.jumpForceUnridden = info.jumpForceUnridden;
        this.jumpSpeedUnridden = info.jumpSpeedUnridden;
        this.gravity = info.gravity;
        this.shockwaveRadius = info.shockwaveRadius;
        this.shockwaveRadiusLarge = info.shockwaveRadiusLarge;
        this.smashOnLanding = info.smashOnLanding;
        this.energyMax = info.energyMax;
        this.energyDecayRate = info.energyDecayRate;
        this.energyGain = info.energyGain;
        this.energyGainUpgrade = info.energyGainUpgrade;
        this.decayRateIncrease = info.decayRateIncrease;
        this.maxDecayRateMultiplier = info.maxDecayRateMultiplier;
        let curve = new AnimationCurve();
		if (info.speedDropOffCurve && info.speedDropOffCurve.length > 0){
			for (let i = 0 ; i < info.speedDropOffCurve.length ; i ++) {
				curve.addKeyFrame(info.speedDropOffCurve[i][0],info.speedDropOffCurve[i][1],info.speedDropOffCurve[i][2],info.speedDropOffCurve[i][3],info.speedDropOffCurve[i][4],info.speedDropOffCurve[i][5])
			}
		}
        this.speedDropOffCurve = curve
        this.initValues()
    }

	/////////////// vaulter code
	public reset(resetPos:boolean = true):void {
        super.reset()
        this.initValues()
    }
    
    private initValues():void{
		this.vaultState = VaultState.none;
		this.additionalDecayRate = 0;
		this.setEnergyMax();
		this.energy = this.energyMaxTrue;
		this.yVel = 0;
    }

	protected setEnergyMax():void {
        let extraEnergy = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.rideLength)
		this.energyMaxTrue = this.energyMax + extraEnergy * this.energyDecayRate;
	}

    protected get energyDeclineRateMultiplier():number{
        let stopperEnergyDeclineRateMultiplier = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.stopperEnergyDeclineRateMultiplier)
        return stopperEnergyDeclineRateMultiplier > 0 ? 1 / stopperEnergyDeclineRateMultiplier : 1;
    }


	public onUpdate():void {
		if(this.state == AnimalState.ridden && moduleBridge.player.state != PlayerState.waitingForStart && this.vaultState != VaultState.jumping && this.vaultState != VaultState.preparing) {

			this.additionalDecayRate = Math.min(this.additionalDecayRate + this.decayRateIncrease * Math.floor(Laya.timer.delta/1000) * this.energyDeclineRateMultiplier, this.maxDecayRateMultiplier);

			this.energy = Helper2D.Clamp(this.energy - this.energyDecayRate * this.energyDeclineRateMultiplier * Math.floor(Laya.timer.delta/1000), 0, this.energyMaxTrue);
			this.energy = Helper2D.Clamp(this.energy - this.additionalDecayRate * Math.floor(Laya.timer.delta/1000) * moduleBridge.player.speedScale, 0, this.energyMaxTrue);
			this.playerJumpSpeedMultiplier = Math.max(0.8, this.energy);
		}

		super.onUpdate();
	}

	protected updateAngerTimer():void {
		this._angerTimer = this.energy / (this.energyDecayRate * this.energyDeclineRateMultiplier + this.additionalDecayRate * moduleBridge.player.speedScale);
		if(this.angerStage <= 0) {
			if(this._angerTimer < this.warningLength + this.goingWildLength) {
                this.angerStage = 1;
                this.PlayWarning();
				// anim.SetTrigger(animTriggerWarning);
			}
		} else if(this.angerStage == 1) {
			if(this._angerTimer > this.warningLength + this.goingWildLength) {
				this.angerStage = 0;
                // anim.SetTrigger(animTriggerResetShaders);
                this._warning.hideWarning();
			} else if(this._angerTimer < this.goingWildLength) {
				this.angerStage = 2;
                this.enterGoingWild();
                this._warning.hideWarning();
			}
		} else if(this.angerStage == 2) {
			if(this._angerTimer <= 0) {
				this.getAngry();
			}
		}
	}

	protected GetRunSpeedBeforeModifiers():number {
		return super.GetRunSpeedBeforeModifiers() * this.speedDropOffCurve.evaluate(this.energy/this.energyMaxTrue) * 1;
	}


    public ride(startingAnimal:boolean,fhtime ?:number,fhdis ?:number):void {
		super.ride(startingAnimal);
		this.yVel = 0;
	}

    protected obstacleCrash(obstacle, immuneToWalls:boolean = false):void {
		this.jumpOrSmash(obstacle, immuneToWalls);
	}

	protected animalCrash(otherAnimal):void {
		this.vaultState = VaultState.none;
		super.animalCrash(otherAnimal);
	}

	public jumpOrSmash(obstacle, immuneToWalls:boolean):void {
		if(this.state == AnimalState.ridden) {
			if(this.vaultState == VaultState.none) {
				if(obstacle.smashStrength <= this.vaultStrength && !obstacle.isLevelEdge && !obstacle.isCenterEdge && !obstacle.indestructable) {
					if(!obstacle.smashed) {
						this.VaultRoutine(obstacle);
						// obstacle.smashed = true;
					}
				} else {
					super.obstacleCrash(obstacle, immuneToWalls);
				}
			} else if(this.vaultState == VaultState.jumping) {
				if (obstacle.layer == 8) {
					if (this.transform.localPositionY > 3.5) {
						this.vaultState = VaultState.none;
                        moduleBridge.player.landingRange.updateLassoSize(0)
						super.obstacleCrashVerified(obstacle);
					} else {
						super.obstacleCrash(obstacle, true);
					}
				} else if (obstacle.indestructable) {
					super.obstacleCrash(obstacle, immuneToWalls);
				} else {
					super.smashObstacle(obstacle);
				}

                /*
				super.obstacleCrash(obstacle, false);

				if(obstacle.smashStrength <= this.smashStrength && !obstacle.isLevelEdge && !obstacle.isCenterEdge && !obstacle.indestructable) {
					// just smash it
				} else {
					if(!obstacle.smashed) {
						// stop vaulting and crash properly
						this.vaultState = VaultState.none;				
					}
                }
                */
			}
		} else {
			if(this.vaultState == VaultState.none) {
				if(obstacle.smashStrength <= this.vaultStrength && !obstacle.isLevelEdge && !obstacle.isCenterEdge && !obstacle.indestructable) {
					if(!obstacle.smashed) {
						this.VaultRoutine(obstacle);
					}
				}
			} else if(this.vaultState == VaultState.jumping) {
				super.obstacleCrash(obstacle, immuneToWalls);
			}
		}
	}

	protected additionalBounceCheck():boolean {
		if(this.state == AnimalState.ridden && this.vaultState == VaultState.jumping) {
			return false;
		} else {
			return super.additionalBounceCheck();
		}
	}

	public onTriggerEnter(other:Laya.PhysicsComponent):void {
        super.onTriggerEnter(other);
    }

	protected moveAnimal(speed:number):void {
		if(this.vaultState != VaultState.preparing) {
			if(this.state != AnimalState.ridden && this.vaultState == VaultState.jumping) {
				super.moveAnimal(3 * speed);
			} else {
				super.moveAnimal(speed);
			}
		}
	}

	protected calculateCurrentAngle():void {
		if(this.vaultState == VaultState.none) {
			super.calculateCurrentAngle();
		}
	}

	public dryOff(changeYPos:boolean):void {
		if(this.vaultState == VaultState.none) {
			super.dryOff(true);
		} else {
			super.dryOff(false);
		}

	}

	// public override void EnterSpecialObstacle(Collider other, bool changeYPos) {
	// 	if(this.vaultState == VaultState.none) {
	// 		super.EnterSpecialObstacle(other, true);
	// 	} else {
	// 		super.EnterSpecialObstacle(other, false);
	// 	}
	// }

	public leaveAnimal(crashed:boolean = false, playCrashAnim:boolean = true):void {
		if(crashed == true) {
			super.leaveAnimal(crashed, playCrashAnim);
			return;
		}

		if(this.vaultState != VaultState.jumping) {
			super.leaveAnimal(crashed, playCrashAnim);
			return;
		}

        let vaulterMidJumpLassoModifier = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.vaulterMidJumpLassoModifier)

        moduleBridge.player.landingRange.updateLassoSize(vaulterMidJumpLassoModifier)

		super.leaveAnimal(crashed, playCrashAnim);
	}

	protected VaultRoutine(obstacle) {
		this.vaultState = VaultState.preparing;

		if (obstacle) {
			if (obstacle.isArch) {
                Helper2D.SetLocalPosY(this.transform,2)
				// transform.SetPosY(2);
			} else {
				Helper2D.SetLocalPosX(this.transform,obstacle.transform.localPositionX)
                Helper2D.SetLocalPosY(this.transform,obstacle.boundsY)
			}
		}

		// this.playAni("buck")

        if (this.state == AnimalState.ridden) {
            // AudioGlobal.Instance.AnimalJump();
		}
        Laya.timer.once(80, this, this.jumpOver)

	}

    private jumpOver():void{
        let special = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.special)
		if(this.state == AnimalState.ridden && special > 0) {
			this.energy = Helper2D.Clamp(this.energy + this.energyGainUpgrade, 0, this.energyMaxTrue);
		} else {
			this.energy = Helper2D.Clamp(this.energy + this.energyGain, 0, this.energyMaxTrue);
		}

		if(this.vaultState == VaultState.none) {
			return
		}

		this.vaultState = VaultState.jumping;
		//jump loop

		if(this.state != AnimalState.ridden) {
			this.yVel = this.jumpForceUnridden;
		} else {
			this.yVel = this.jumpForce;
		}
        
        Laya.timer.frameLoop(1,this,this.jumploop)

    }

    private jumploop():void{
        let jumpHeight = this.transform.localPositionY;

        if (jumpHeight > 0 && this.vaultState != VaultState.none) {
			jumpHeight += this.yVel * Math.floor(Laya.timer.delta/1000);
			this.yVel -= this.gravity * Math.floor(Laya.timer.delta/1000);
            Helper2D.SetLocalPosY(this.transform,jumpHeight)
		}else{
            Laya.timer.clear(this,this.jumploop)

            if(this.vaultState == VaultState.none) {
                return
            }
    
            Helper2D.SetLocalPosY(this.transform,0)
    
            // anim.SetTrigger(animTriggerAttack);
    
            if(moduleBridge.player.state == PlayerState.riding && this.state == AnimalState.ridden) {
                if(!this.checkAndStartFall()) {
                    this.smashLanding();
                    this.yVel = 0;
                }
            }
            this.vaultState = VaultState.none;
        }
    }


	protected handleFall():void {
		if(this.fallTimer < 0.6) {
			//keep moving animal with air resistance
			this.fallSpeed *= 0.982;
			this.moveAnimal(this.fallSpeed);
		}

		//handle fall
		this.fallTimer += Math.floor(Laya.timer.delta/1000);
        // transform.SetPosY(Player.Instance.edgeFallCurve.Evaluate(this.fallTimer) + this.fallTimer * this.yVel);
        Helper2D.SetLocalPosY(this.transform,moduleBridge.player.edgeFallCurve.Evaluate(this.fallTimer) + this.fallTimer * this.yVel);
		if(this.fallTimer > 0.3 && moduleBridge.player.state != PlayerState.crashed && moduleBridge.player.state != PlayerState.UI) {
			moduleBridge.gameCtrl.crash();
		}

	}

	protected smashLanding():void {
        let vaulterLargerSmashArea = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.vaulterLargerSmashArea)
        let specialCoins = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.specialCoins)

		//get knockback targets
		let collisionHalfSize:Vector3 = new Vector3(0,0,0)
		let spawnPos = 0;
		if(vaulterLargerSmashArea > 0) {
			collisionHalfSize = new Vector3(this.shockwaveRadius, this.shockwaveRadius, this.shockwaveRadiusLarge);
			spawnPos = this.shockwaveRadiusLarge;
		} else {
			collisionHalfSize = new Vector3(this.shockwaveRadius, this.shockwaveRadius, this.shockwaveRadius);
			spawnPos = this.shockwaveRadius;
		}

		if(this.smashOnLanding == false) {
			return;
        }
        
        this.stampedeAI.smashLanding(this.state == AnimalState.ridden,specialCoins);
	}

}