
import Vector3 = Laya.Vector3;
// import Vector2 = Laya.Vector2;
import GameObject = Laya.Sprite3D;
import Transform3D = Laya.Transform3D;
import Helper2D from "../Helper2D";
import { ZY } from "../ZY";
import CollisionGroup from "../CollisionGroup";
import Animal from "./Animal";
import { IAnimal, AnimalState, moduleBridge } from "../Interface";
export default class LandingRange extends Laya.Script3D {

    public gameObject:GameObject;
    public transform:Transform3D;
    private _lassoRing:Transform3D;
    private _lassoPos:Transform3D;
    constructor() { super(); }

    public onAwake():void {
        this.gameObject = this.owner as GameObject;
        this.transform = this.gameObject.transform;
        // lassoRing
        ZY.setCollisionGroupSingle(this.gameObject,null, CollisionGroup.LANDING_RANGE, CollisionGroup.LANDING_COLLIDER);

        this._lassoRing = ZY.findChildByPath(this.gameObject,"Player_LassoPos.lassoRing").transform;
        this._lassoPos = ZY.findChildByPath(this.gameObject,"Player_LassoPos").transform;
    }

    public show():void {
        if(!this.gameObject.active)
        {
            this.gameObject.active = true;
            this.enabled = true;
        }
    }

    public hide():void {
        if(this.gameObject.active)
        {
            this.enabled = false;
            this.gameObject.active = false;
        }
    }

    public onLateUpdate():void {
        if(this.enabled) {
            let p = this._lassoPos.position;
            p.y = 0.1;
            this._lassoRing.position = p;
        }
    }

	private _possibleLandingTargets:Array<IAnimal> = [];
	public targetedAnimal:IAnimal;

	public getLandingTarget():IAnimal {

		//remove any unfit to ride animals from list first
		if (this._possibleLandingTargets.length > 0) {
            for (let i = this._possibleLandingTargets.length - 1; i >= 0; i--) {
                const animal = this._possibleLandingTargets[i];                
				if (!(animal.state == AnimalState.running || animal.state == AnimalState.idle || animal.state == AnimalState.knockback)) {
                    // possibleLandingTargets.RemoveAt(i);
                    this._possibleLandingTargets.splice(i,1);
				}
			}
		}

		if (this._possibleLandingTargets.length > 0) {

            let closest = this._possibleLandingTargets[0];
            // possibleLandingTargets[0].transform.GetPositionYZero() - transform.GetPositionYZero()
            let pos1 = Helper2D.GetPositionYZero(closest.transform);
            let pos2 = Helper2D.GetPositionYZero(this.transform);
            pos1.setValue(pos1.x-pos2.x, pos1.y-pos2.y, pos1.z-pos2.z);
            
			let closestDistSqrd:number = Helper2D.SqrMagnitude(pos1);
			let closestIsNotCaptured:boolean = this.isAnimalCapturable(closest);
			let closestIsSpecial = false;//SpecialEventManager.Instance.IsAnimalSpecial (possibleLandingTargets [0]);

			// for (int i = 1; i < possibleLandingTargets.Count; i++) {
            for(let i = 1; i< this._possibleLandingTargets.length; i++) {
                // possibleLandingTargets[i].transform.GetPositionYZero() - transform.GetPositionYZero()
                pos1 = Helper2D.GetPositionYZero(this._possibleLandingTargets[i].transform);
                pos2 = Helper2D.GetPositionYZero(this.transform);
                pos1.setValue(pos1.x-pos2.x, pos1.y-pos2.y, pos1.z-pos2.z);
                let newDistSqrd = Helper2D.SqrMagnitude(pos1);
                
				let isNotCaptured = this.isAnimalCapturable(this._possibleLandingTargets[i]);
				let isSpecial = false;//SpecialEventManager.Instance.IsAnimalSpecial (possibleLandingTargets [i]);
				if (((closestIsNotCaptured == isNotCaptured && ((closestIsSpecial == isSpecial && newDistSqrd < closestDistSqrd) || (isSpecial && !closestIsSpecial))) 
					|| (isNotCaptured && !closestIsNotCaptured)) && this.hasAnimalLOS(this._possibleLandingTargets[i])) {
					closest = this._possibleLandingTargets[i];
					closestDistSqrd = newDistSqrd;
					closestIsNotCaptured = isNotCaptured;
					closestIsSpecial = isSpecial;
				}
			}

			if (closest != this.targetedAnimal) {
				if (this.targetedAnimal) {
                    //变白，恢复正常
                    // targetedAnimal.UnTarget();
                    this.targetedAnimal.colorNormal();
                }
                // 变黄
                // closest.Target();
                closest.colorYellow();
				this.targetedAnimal = closest;
                // AudioGlobal.Instance.FXAnimalHover(closest.gameObject);
            }
            return closest;
		} else {
			if (this.targetedAnimal) {
                // targetedAnimal.UnTarget();
                // 变白
                this.targetedAnimal.colorNormal();
				this.targetedAnimal = null;
			}
			return null;
		}
	}

	private isAnimalCapturable(a:IAnimal):boolean {
        // let capturable = ZooManager.Instance.CanCaptureAnimal(a);
        let capturable = true;

		// //if event active check if animal has an active follower, if so mark it as capturable for sake of targetting
		// if (GM.Instance.activeWorldState == ActiveWorldState.Special_Event && a.followerChanceToEnable != null) {
		// 	capturable = capturable || a.followerChanceToEnable.gameObject.activeSelf;
		// }

		return capturable;
    }
    
    private hasAnimalLOS (targetAnimal:IAnimal):boolean {
        // 玩家跟动物之间有没有障碍物
		// if (Physics.Linecast(Player.Instance.transform.position, targetAnimal.transform.position, 1 << LayerMask.NameToLayer ("Obstacle"))) {
		// 	return false;
		// } else {
		// 	return true;
        // }
        
        return true;
	}

	public getLandingCount():number {
		return this._possibleLandingTargets.length;
    }
    
    public onEnable():void {
        while(this._possibleLandingTargets.length) {
            this._possibleLandingTargets.pop();
        }
    }

    public onDisable() {
        if(this.targetedAnimal) {
            this.targetedAnimal.colorNormal();
            this.targetedAnimal = null;
        }
    }

	// void OnDisable() {
	// 	if (targetedAnimal != null) {
	// 		targetedAnimal.UnTarget();
	// 		targetedAnimal = null;
	// 	}
    // }
    
    public onTriggerEnter(other:Laya.PhysicsComponent):void {
        // let a = other.owner
        // console.log(other.owner.name);
        if(other.owner.name == "landingCollider") {
            let obj = other.owner.parent;
            let a = obj.getComponent(Animal) as IAnimal;
            if(a && (a.state == AnimalState.running || a.state == AnimalState.idle) && a != moduleBridge.player.steed) {
                this._possibleLandingTargets.push(a);
            }
        }
    }

    public onTriggerExit(other:Laya.PhysicsComponent):void {
        if(other.owner.name == "landingCollider") {
            let obj = other.owner.parent;
            let a = obj.getComponent(Animal) as IAnimal;
            if(a) {
                // a.colorNormal();
                let i = this._possibleLandingTargets.indexOf(a);
                if(i > -1) {
                    this._possibleLandingTargets.splice(i,1);
                }
            }
        }
    }

	public updateLassoSize(lassoSizeModifier:number) {
		let globalLassoSize = 1.2
		let globalLassoStrength = 5
		let babyLassoStartSizeMultiplier = 1
		let babyLassoStrengthMultiplier = 1
		let babyGlobalLassoStartSizeMultiplier = 1
		let babyGlobalLassoStrengthMultiplier = 1

		let effectiveJumpCount = moduleBridge.player.jumpCount - moduleBridge.player.lassooNotEffectingJumpCount
		let currentSize = Math.max(0.35, ((globalLassoSize * babyLassoStartSizeMultiplier * babyGlobalLassoStartSizeMultiplier) - effectiveJumpCount * (0.05 - globalLassoStrength * babyLassoStrengthMultiplier * babyGlobalLassoStrengthMultiplier * 0.001)) * (moduleBridge.player.steed.playerLassooBoostMultiplier + lassoSizeModifier));

		moduleBridge.player.rope.flyingLoopSize = currentSize;
		this.transform.localScale = new Vector3(currentSize, 1, currentSize);
	}
}
