    public destroy():void {
        if(this.steed) {
            ZY.Tween.clearAll(this.steed.transform);
        }
        if(this.transform) {
            ZY.Tween.clearAll(this.transform);
            Laya.Tween.clearAll(this.transform);
        }
        Laya.timer.clearAll(this);
        Laya.systemTimer.clearAll(this);
        moduleBridge.taskCtrl.off("TASK_ADD",this,this.addTask)
        super.destroy();
    }

    public get speedScale():number{
        if(moduleBridge.teachCtrl.status > 0) {
            return 1-this.temporarySpeedScaleDecrease;
        }
        return this._speedScale - this.temporarySpeedScaleDecrease;
    }

    public onAwake(): void {
        let gameObject = (this.owner as Sprite3D);
        this.gameObject = gameObject;
        this._shadowQuad = (gameObject.getChildByName("shadowQuad") as Sprite3D).transform;
        this._ctrlHips = ZY.findChildByPath(gameObject, "offset.ctrl_main.ctrl_hips").transform;
        this.transform = gameObject.transform;
        this._touch = ZY.touchManager.touch;
        this._ani = gameObject.getComponent(Animator);
        // this._ani.play("riding");
        let obj = this.gameObject.getChildByName("LandingRange");
        this.landingRange = obj.addComponent(LandingRange);
        this.offset = (this.gameObject.getChildByName("offset") as Sprite3D).transform;
        
        this.rope = ZY.addScript3D(gameObject, "Lasso_poly", Rope);

        this._heart = gameObject.getChildByName("pfx_hearts") as Laya.ShuriKenParticle3D;
        this.startImmunityTimer = this._startImmunityTimerMax;

        let curve = new AnimationCurve();
		curve.addKeyFrame(0,0,1,-50,0,1)
		this.edgeFallCurve = curve

        ZY.stopPart(this._heart);
        // this._heart.active = false;
        ZY.setCollisionGroupSingle(this.gameObject, null, CollisionGroup.PLAYER, CollisionGroup.WORLD_SIDE|CollisionGroup.OBSTACLE);
        ZY.setCollisionGroupSingle(this.gameObject, "AIAvoid", CollisionGroup.RAYCAST_OBSTACLE, CollisionGroup.ANIMAL);
        // ZY.setCollisionGroup(this.gameObject, CollisionGroup.PLAYER, CollisionGroup.WORLD_SIDE|CollisionGroup.OBSTACLE);

        if (userData.curHatId !== "100"){
            // 人物帽子
            let roleHead = ZY.findChildByPath (gameObject, "offset.ctrl_main.ctrl_hips.HEAD 1.SKELETON 3.joint_head.joint_HAT.Hat_attach") as Laya.Sprite3D
            roleHead.removeChildren ();
            let res:string = userData.cfgHats[userData.curHatId].res;
            ZY.stage.load([res],this,()=>{
                let curHat = Laya.loader.getRes (res);
                curHat.transform.localPosition.setValue(0,0,0);
                curHat.transform.localPosition = curHat.transform.localPosition;
                curHat.transform.localRotationEulerY = 0;
                roleHead.addChild (curHat);
            })
        }


        let skin = ZY.findChildByPath(gameObject, "offset.MESHES.DO_NOT_BATCH");
        if(skin.getChildByName("cape")) skin.getChildByName("cape").active = false;
        if(skin.getChildByName("skirt")) skin.getChildByName("skirt").active = false;
        moduleBridge.taskCtrl.on("TASK_ADD",this,this.addTask)
    }

    private _taskTime:number = null;
    private addTask(t):void{
        if (t.type == "RIDE_IN_TIME" || t.type == "RUN_IN_TIME"){
            this._taskTime = Math.floor(Laya.Browser.now()/1000);
        }
    }


