    private target:IAnimal;
	public land() {
        this.target = this.landingRange.targetedAnimal;
        if (this.target != null) {
            this.LandingSequence(this.target)
        }
    }
    
	private LandingSequence(target:IAnimal) {

		let animalLandingOnDoesntHarmLassoo = 0

		target.beingLandedOn = true;
        this.willBeLanding = true;
        this.jumpCount++;
        if (animalLandingOnDoesntHarmLassoo > 0) {
            this.lassooNotEffectingJumpCount++;
        }
		this.coinsInAir = false;

		let transitionTime:number = 0.17;
        
        this.state = PlayerState.landing;

        // Laya.timer.scale = 0.1;

        // ZY.animatorTrigger.playTrigger(this._ani, ["castLasso","grappeling","landing","riding"]);

        // this.transform.lookAt(target.playerJoint.position,ZY.Temp.getVector3(0,1,0),true);
        // this.transform.localRotationEulerY+=180;
        let pos = ZY.Temp.getVector3(2*this.transform.position.x-target.playerJoint.position.x,2*this.transform.position.y-target.playerJoint.position.y,2*this.transform.position.z-target.playerJoint.position.z);
        this.transform.lookAt(pos, ZY.Temp.UP);

        this._canJumpTimer = this.canJumpDelay;
		
		if (this.landingRange.targetedAnimal != null) {
            this.landingRange.targetedAnimal.colorNormal();
            // Laya.systemTimer
			this.landingRange.targetedAnimal = null;
		}
        // (this.landingRange.owner as Sprite3D).active = false;
        this.landingRange.hide()
        // this.steed.anim.SetBool(Animal.animBoolIdle, false);
        this._ani.play("castLasso",0,0);
        // ZY.animatorTrigger.playTrigger(this._ani, ["castLasso","grappeling"]);
        Laya.systemTimer.once(30, this, () => {
            this.willBeLanding = false;
            // this._ani.play("castLasso",0,0);
            this._ani.speed = 2;
            this.rope.grabAnimal(target.playerJoint, 0.06, this, ()=>{
                this._ani.play("grappeling",0,0);
                // ZY.animatorTrigger.playTrigger(this._ani, ["grappeling"]);
                // yield return StartCoroutine(transform.MoveToTransform(target.playerJoint, 0.05f, EaseType.CubeIn));
                Laya.Tween.to(this.transform,{localPositionX:target.playerJoint.position.x,localPositionY:target.playerJoint.position.y,localPositionZ:target.playerJoint.position.z},
                50, Laya.Ease.cubicIn,Laya.Handler.create(this, ()=>{
                    if (this.state == PlayerState.crashed) {
                        return
                    }
        
                    this.transform.localRotation = new Laya.Quaternion(0,0,0,1)
                    this.ride(target, false);
                    this.rope.switchOffRope();
                    this.state = PlayerState.riding;
            
                    // AnimalVariantMetaBase animalVariantMeta = moduleBridge.player.steed.animalVariantMeta;
            
                    // anim.SetTrigger(animTriggerLanded);
                    this._ani.play("riding",0,0);
                    this._ani.speed = 1;
            
                    this.speed = 30;
                    // this.landingShockwave();
                    if ( moduleBridge.paused == false){
                        Laya.timer.scale = 1
                    }
            
                    let lassooExtraSizeModifier = moduleBridge.gameCtrl.upgradeValue(this.steed.encId,UpgradeKeys.lassooExtraSizeModifier)

                    this.landingRange.updateLassoSize(lassooExtraSizeModifier);
            
                    target.beingLandedOn = false;

                    this.steed.stampedeAI.bounce();
                }))
            });

		})
	}

	private landingStarted:boolean = false;
	public startRunLanding() {
        this.landingStarted = true;
        // this.state = PlayerState.waitingForStart;
        this.steed.transform.localPosition = new Vector3(0, 50, 9+userData.startPos);
        this.lassooNotEffectingJumpCount = 0
        this.jumpCount = 0

        if (this.steed.encId == "1006") {
            ZY.Tween.to(this.steed.transform,{localPositionX:0,localPositionY:(this.steed as any).cruiseHeight,localPositionZ:15}, 380, Laya.Ease.linearInOut,Laya.Handler.create(this, this.landEnd));
		} else {
            ZY.Tween.to(this.steed.transform,{localPositionX:0,localPositionY:0,localPositionZ:15+userData.startPos}, 380, Laya.Ease.linearInOut, Laya.Handler.create(this, this.landEnd));
        }
        let time = userData.get("dollar_start",0)
        if (time > 0){
            userData.set("dollar_start",time - 1)
        }
    }

