    private landEnd():void {
        this.startRunSetup();
        this.landingStarted = false;
        this.steed.Bark(100);
        if (this.steed.transform) this.steed.transform.position.cloneTo(this.safePosition)
        this.safePosition = this.safePosition
        let lassooExtraSizeModifier = moduleBridge.gameCtrl.upgradeValue(this.steed.encId,UpgradeKeys.lassooExtraSizeModifier)
                    
        this.landingRange.updateLassoSize(lassooExtraSizeModifier);

        this.safePosition.cloneTo(this._lastSafePosition)
        this._lastSafePosition = this._lastSafePosition

    }

