    public eatenCrash() {
        Helper2D.SetPosY(this.transform,-100)
        this.steed.leaveAnimal(true, false);
        ZY.animatorTrigger.stopTrigger(this._ani);
        // this.landingRange.hide()
        this.state = PlayerState.crashed;
        moduleBridge.gameCtrl.showRestart();
    }

    public mountedCrash(hitHighObstacle:boolean, szObstacleType:string, withoutCrashAnim:boolean = false) {
		if (this.steed.transform.localPositionZ > 25) {

            if (withoutCrashAnim) {
                this.steed.leaveAnimal(false, false);
            } else {
                this.steed.moveToGround();
                this.steed.leaveAnimal(true);
            }

			// let playerPos = new Vector3(this.transform.localPositionX,this.transform.localPositionY,this.transform.localPositionZ) ;
			if (this.transform.localPositionY > 0) {
                ZY.Tween.to(this.transform,{localPositionY:0}, 300, Laya.Ease.linearInOut);
            }
            if (hitHighObstacle) {
                this._ani.play("deadBack")
			} else {
                this._ani.play("dead")
            }
            ZY.animatorTrigger.stopTrigger(this._ani);
            this.state = PlayerState.crashed;
            // moduleBridge.gameCtrl.crash()
            this._yVelocity = 0;
            if (szObstacleType == "ZJ_CD"){
                moduleBridge.gameCtrl.zjcd()
            }else{
                if(moduleBridge.teachCtrl.status > 0) {
                    moduleBridge.teachCtrl.crash();
                } else {
                    moduleBridge.gameCtrl.crash()
                }
            }
		}
	}
    
