    public static GetDistance(p1:any,p2:any):number {
        let dx,dy,dz
        if(p1 instanceof Vector3 && p2 instanceof Vector3) {
            dx = p1.x-p2.x;
            dy = p1.y-p2.y;
            dz = p1.z-p2.z;
            return Math.sqrt(dx*dx+dy*dy+dz*dz);
        }
        if(p1 instanceof Vector2 && p2 instanceof Vector2) {
            dx = p1.x-p2.x;
            dy = p1.y-p2.y;
            return Math.sqrt(dx*dx+dy*dy);
        }

        if(p1 instanceof Laya.Point && p2 instanceof Laya.Point) {
            dx = p1.x-p2.x;
            dy = p1.y-p2.y;
            return Math.sqrt(dx*dx+dy*dy);
        }
	}
	
	public static GetLength(p:any):number {
		if(p instanceof Vector3) {
            return Math.sqrt(p.x*p.x+p.y*p.y+p.z*p.z);
        }
        if(p instanceof Vector2) {
            return Math.sqrt(p.x*p.x+p.y*p.y);
        }

        if(p instanceof Laya.Point) {
            return Math.sqrt(p.x*p.x+p.y*p.y);
		}
		return 0;
	}

