	public static GetPositionYZero(t:Transform3D):Vector3 {
		let v = t.position;
		v.y = 0;
		return v;
    }
    
    public static DeltaAngle(a:number, b:number):number {
        let d = b - a;
        d = d % 360;
        if(d > 180) {
            d = d - 360;
        } else if(d < -180) {
            d = 360 + d;
        }
        return d;
    }

    private static _tmpQuaternion1:Laya.Quaternion;
    private static _tmpQuaternion2:Laya.Quaternion;
    public static RotateEulerY(o:Vector3, r:number):Vector3 {
        let rr = r*Math.PI/180;
        if(!Helper2D._tmpQuaternion1) Helper2D._tmpQuaternion1 = new Laya.Quaternion();
        if(!Helper2D._tmpQuaternion2) Helper2D._tmpQuaternion2 = new Laya.Quaternion();

        Helper2D._tmpQuaternion1.x =Helper2D._tmpQuaternion1.y =Helper2D._tmpQuaternion1.z = 0;
        Helper2D._tmpQuaternion1.w = 1;
        Helper2D._tmpQuaternion1.rotateY(rr, Helper2D._tmpQuaternion2);
        let out = new Vector3();
        Vector3.transformQuat(o, Helper2D._tmpQuaternion2,out);
        return out;
    }

    public static LerpAngle(r1:number, r2:number, k:number):number {
        // 如果maxAngle-minAngle的值大于180度的话,那maxAngle将相当于360-maxAngle,所以会从minAngle逆向转到3
        // if(r2-r1>180) r2 = 360-r2;
        // let 
        let r = r1+(r2-r1)*k;
        r = r%360;
        if(r<-180) {
            r = 360 + r;
        } else if(r > 180) {
            r = r - 360;
        }

        return r;
	}
	
	public static Lerp(a:number, b:number, t:number):number {
		return a + (b-a)*t;
	}

	public static Clamp(value:number, min:number, max:number):number {
		if(value < min) return min;
		if(value > max) return max;
		return value;
	}

	public static MoveTowards(current:number, target:number, maxDelta:number):number {
		let out:number;
		if(maxDelta > 0) {
			//目标靠近target
			if(target > current) {
				out = current+maxDelta;
				if(out>target) out = target;
			} else {
				maxDelta = -maxDelta;
				out = current+maxDelta;
				if(out<target) out = target;
			}
		} else {
			//目标离开target
			if(target > current) {
				out = current + maxDelta;
			} else {
				maxDelta = -maxDelta;
				out = current + maxDelta;
			}
		}
		return out;
	}

	public static MoveTowardsAngle(current:number, target:number, maxDelta:number):number {
		let out:number;
		if(maxDelta > 0) {
			//目标靠近target
			if(target > current) {
				out = current+maxDelta;
				if(out>target) out = target;
			} else {
				maxDelta = -maxDelta;
				out = current+maxDelta;
				if(out<target) out = target;
			}
		} else {
			//目标离开target
			if(target > current) {
				out = current + maxDelta;
			} else {
				maxDelta = -maxDelta;
				out = current + maxDelta;
			}
		}
		out = out % 360;
		if(out<-180) {
            out = 360 + out;
        } else if(out > 180) {
            out = out - 360;
        }
		return out;
	}

	public static SqrMagnitude(v:Vector3):number {
		return v.x*v.x+v.y*v.y+v.z*v.z;
	}

	public static insideUnitCircle(r:number=1):Vector2 {
		let x = Math.random()*Math.PI*2;
		return new Vector2(Math.cos(x)*r, Math.sin(x)*r);
	}
	public static GetRandom(min:number,max:number,int:boolean){
        if (int) {
            return Math.floor(Math.random() * (max - min) + min)
        }
        else{
            return Math.random() * (max - min) + min
        }
    }
