// import { animalMoveCtrl } from "./AnimalMoveCtrl";
// import { shipCtrl } from "../ship/ShipCtrl";
import {ZY} from "../ZY";
import { userData } from "../userData/UserData";

export default class ZooAnimal extends Laya.Sprite3D {
    private _body:Laya.Sprite3D;
    private _aniCtrl:Laya.Animator;
    private _type:string;//动物种类
    private _vx:number; //x轴速度
    private _vz:number; //z轴速度
    private _aim:number;  //目的地
    private _i:number;  //第几目的地
    private _animalPath = {};
    private _mat:Laya.PBRSpecularMaterial;
    private _albedoColor:Laya.Vector4;
    // private _path;
    constructor() {
        super();
    }

    public animalId:number;
    public reset(id):void {
        let cfg = userData.cfgAnimal;
        this._type = cfg[id].encId;
        let obj = this._body = Laya.Sprite3D.instantiate(Laya.loader.getRes(cfg[id].res)) as Laya.Sprite3D;
        this.addChild(obj);
        this._aniCtrl = obj.getComponent(Laya.Animator) as Laya.Animator;
        this.animalId = id;

        obj.getChildByName("StampedeAI").active = false;
        obj.getChildByName("landingCollider").active = false;
        obj.name = id.toString(); //必须要设置一个id，不然选中动物的时候不知道选中的是什么

        this._albedoColor = new Laya.Vector4(1,1,1,1);
        if(!this._mat) {
            this._mat = (obj as Laya.SkinnedMeshSprite3D).skinnedMeshRenderer.material.clone();
            this._mat.tilingOffset = new Laya.Vector4(1,1,(cfg[id].skinIndex || 0)/16,0);
        }
        this._albedoColor.fromArray([0,0,0,0]);
        this._mat.emissionColor = this._albedoColor;
        this.setNodeMat(obj);
    }

    private setNodeMat(node:Laya.Sprite3D):void {
        if(node instanceof Laya.SkinnedMeshSprite3D && node.name !== "eye" && node.name !== "biteMesh") {
            (node as Laya.SkinnedMeshSprite3D).skinnedMeshRenderer.material = this._mat;
        }
        if(node.numChildren > 0) {
            for (let i = 0; i < node.numChildren; i++) {
                const element = node.getChildAt(i);
                this.setNodeMat(element as Laya.Sprite3D);
            }
        }
    }

    public get body():Laya.Sprite3D {
        return this._body;
    }

    public clear():void {
        Laya.timer.clearAll(this);

        if(this._body){
            this._body.removeSelf();
            this._body.destroy(true);
        }
        this._body = null;
        this._animalPath = null;
        this._mat = null;
        this._aniCtrl = null;
        this._type = null;
        this._vx = null;
        this._vz = null;
        this._i = null;
        this._albedoColor = null;

        this.destroy(true);
    }

    public dance():void{
        this._aniCtrl.play("happy")
    }

    public init(animalPath:any){
        this._animalPath = animalPath[this._type];
        //刷新在出生地
        this._body.transform.localPosition = ZY.Temp.getVector3();
        this.transform.localPositionY = 0
        this.transform.localPositionX = this._animalPath["born"][0]
        this.transform.localPositionZ = this._animalPath["born"][1]
        //随机目的地
        let tPosition = this.cloneT(this._animalPath["pos"])
        for (let i = 0 ; i< tPosition.length ; i++){
            if (tPosition[i][0]) {
                //这个地方不能用了
                this._i = i
                this._aim = tPosition[i];
                this._animalPath["pos"][i][0] = false
                this._aniCtrl.play("walk");
                this._aniCtrl.speed = 1
                this.checkMove(this._animalPath["born"])
                break
            }
        }
    }

    private checkMove(start_pos){
        let a,b,c;
        if ((start_pos[0] < this._aim[1]) && (start_pos[1] > this._aim[2])){    //三
            a = Math.abs(start_pos[1] - this._aim[2])
            b = Math.abs(start_pos[0] - this._aim[1])
            c = 90
        }
        else if ((start_pos[0] > this._aim[1]) && (start_pos[1] < this._aim[2])){   //一
            a = Math.abs(start_pos[1] - this._aim[2])
            b = Math.abs(start_pos[0] - this._aim[1])
            c = 270
        }
        else if ((start_pos[0] < this._aim[1]) && (start_pos[1] < this._aim[2])){   //二
            a = Math.abs(start_pos[0] - this._aim[1])
            b = Math.abs(start_pos[1] - this._aim[2])
            c = 0
        }
        else if ((start_pos[0] > this._aim[1]) && (start_pos[1] > this._aim[2])){   //四
            a = Math.abs(start_pos[0] - this._aim[1])
            b = Math.abs(start_pos[1] - this._aim[2])
            c = 180
        }
        // //身位改变
        let nAngle = Math.atan(a/b)/(Math.PI/180)
        // this._body.transform.localRotationEulerY = nAngle + c
        Laya.Tween.to(this._body.transform, {localRotationEulerY:nAngle + c}, 400, Laya.Ease.linearInOut);
        //正弦值
        let vSin = Math.round(Math.sin((nAngle * Math.PI/180)) * 1000000) / 1000000;
        //余弦值
        let vCos = Math.round(Math.cos((nAngle * Math.PI/180)) * 1000000) / 1000000;
        if (c == 0) this._vx = vSin * 0.001,this._vz = vCos * 0.001;
        else if (c == 90) this._vx = vCos * 0.001,this._vz = - vSin * 0.001;
        else if (c == 180) this._vx = - vSin * 0.001,this._vz = - vCos * 0.001;
        else if (c == 270) this._vx = - vCos * 0.001,this._vz = vSin * 0.001;
        // console.log(this._vx,this._vz,c)
        Laya.timer.clearAll(this);
        Laya.timer.frameLoop(1, this, this.step)
    }

    private stop() {
        //乖乖站好
        // console.log(this._type,"乖乖站好",this._body.transform.localPosition)
        this._aniCtrl.play("idle")
        this._aniCtrl.speed = 0.2
        //速度归0
        this._vx = 0;
        this._vz = 0;
        Laya.timer.clearAll(this);
        Laya.timer.frameLoop(1000, this, this.hangOut)
    }

    private step() {
        let sx = Laya.timer.delta * this._vx;
        let sz = Laya.timer.delta * this._vz;
        sx > 1 && (sx = 1),
        sx < -1 && (sx = -1);
        sz > 1 && (sz = 1),
        sz < -1 && (sz = -1);
        if (((sx > 0) && (this.transform.localPositionX + sx >= this._aim[1])) ||
            ((sx < 0) && (this.transform.localPositionX - sx <= this._aim[1])) ||
            ((sz > 0) && (this.transform.localPositionZ + sz >= this._aim[2])) ||
            ((sz < 0) && (this.transform.localPositionZ - sz <= this._aim[2]))){
            this.stop();
            return
        }
        this.transform.localPositionX += sx
        this.transform.localPositionZ += sz
    }

    private hangOut(){
        //随机目的地
        let tPosition = this.cloneT(this._animalPath["pos"])
        // console.log(tPosition)
        for (let i = 0 ; i< tPosition.length ; i++){
            if (tPosition[i][0]) {
                this._animalPath["pos"][this._i][0] = true
                //这个地方不能用了
                this._i = i;
                this._aim = tPosition[i];
                this._animalPath["pos"][i][0] = false
                this._aniCtrl.play("walk")
                this._aniCtrl.speed = 1
                this.checkMove([this.transform.localPositionX,this.transform.localPositionZ])
                break
            }
        }
    }


    //取随机数
    private getRandom(min,max,int){
        if (int) {
            return Math.floor(Math.random() * (max - min + 1) + min)
        }
        else{
            return Math.random() * (max - min) + min
        }
    }

    //克隆表
    private cloneT(t){
        let tab = []
        tab.concat();
        for (let i = 0 ; i < t.length ; i ++){
            tab[i] = t[i].slice(0)
        }
        return tab
    }

}