import Elephant from "./Elephant";
import { moduleBridge, PlayerState, AnimalState } from "../Interface";
import GameObject = Laya.Sprite3D;
import AnimationCurve from "../AnimationCurve";
import { UpgradeKeys } from "../UpgradeKeys";

export default class Rhino extends Elephant {
    public speedCurve;
    public rideTimer:number = 0;
    public smashTIncrease:number;

    // public Init(Vector3 startPos, float rotation = 0) {
    //     base.Init(startPos, rotation);
    // }

    // Update is called once per frame
    

    public init(id:string, obj:GameObject):void{
        super.init(id, obj)
        let info = Laya.loader.getRes("cfg/cfgAttribute.json")[this.encId];
        this.smashTIncrease = info.smashTIncrease
        let curve = new AnimationCurve();
		if (info.speedCurve && info.speedCurve.length > 0){
			for (let i = 0 ; i < info.speedCurve.length ; i ++) {
				curve.addKeyFrame(info.speedCurve[i][0],info.speedCurve[i][1],info.speedCurve[i][2],info.speedCurve[i][3],info.speedCurve[i][4],info.speedCurve[i][5])
			}
		}
		this.speedCurve = curve
    }

	public onUpdate() {
		if (moduleBridge.player.state == PlayerState.riding && this.state == AnimalState.ridden && this.angerStage < 2) {
            this.rideTimer += Laya.timer.delta/1000;

			// UpgradeValue special = animalUpgrades.GetUpgrade(UpgradeKeys.special);
            let special = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.special)

			if (special > 0) {
				this.runSpeedModifier = this.speedCurve.evaluate(Math.min((this.rideTimer / (this.runLength * 2)) + this.thingsSmashed * this.smashTIncrease, 1));
			} else {
				this.runSpeedModifier = this.speedCurve.evaluate(Math.min((this.rideTimer / (this.runLength * 2)), 1));
			}
		}
		super.onUpdate();
	}

	public ride(startingAnimal:boolean,fhtime ?:number,fhdis ?:number):void {
		this.rideTimer = 0;

		this.runSpeedModifier = this.speedCurve.evaluate(this.rideTimer/this.runLength);
        super.ride(startingAnimal,fhtime,fhdis);
    }
}
