import Animal from "./Animal";
import GameObject = Laya.Sprite3D;
import { AnimalState, moduleBridge } from "../Interface";
import { UpgradeKeys } from "../UpgradeKeys";
import PfxHelper from "../game/PfxHelper";

export default class AnimalZoomer extends Animal {

	public zoomSpeedStart = 1.1;
	public zoomAccelerationRate = 0.1;
	public zoomMax = 1.6;
	protected currentZoom; 

    public runAnimInStampede:boolean;

    protected coinsFromGoingMaxSpeed:boolean = false;

	public ignoreZoomAnimation:boolean = false;


    public init(id:string, obj:GameObject):void{
        super.init(id, obj)
        let info = Laya.loader.getRes("cfg/cfgAttribute.json")[this.encId];
        this.zoomSpeedStart = info.zoomSpeedStart
        this.zoomAccelerationRate = info.zoomAccelerationRate
        this.zoomMax = info.zoomMax
        this.runAnimInStampede = info.runAnimInStampede
        this.ignoreZoomAnimation = info.ignoreZoomAnimation
    }


    // public onAwake():void{
    //     super.onAwake();
    //     if (this.runAnimInStampede) {
    //         // this.BuildUpToRun()
    //     }
    // }

	protected getAngry () {
        super.getAngry();
        
		this.AngryLoop()
	}


    private BuildUpToRun () {
        Laya.timer.once(500, this, () => {
            // anim.SetTrigger(animTriggerLanded);
            // this._ani.play()
            Laya.timer.once(500, this, () => {
                // anim.SetTrigger(animTriggerGoingWild);
                Laya.timer.once(500, this, () => {
                    // anim.SetTrigger(animTriggerBucking);
                })
            })
		})
    }

    public ride(startingAnimal:boolean,fhtime ?:number,fhdis ?:number):void {
        if (this.runAnimInStampede) {
            // anim.SetLayerWeight(animLayer3DOverride, 0);
            // anim.ResetTrigger(animTriggerAttack);
            // anim.ResetTrigger(animTriggerCrashed);
            // anim.ResetTrigger(animTriggerLanded);
            // anim.ResetTrigger(animTriggerBucking);
            // anim.ResetTrigger(animTriggerWarning);
            // anim.ResetTrigger(animTriggerGoingWild);
            // anim.ResetTrigger(animTriggerReset);
            // anim.ResetTrigger(animTriggerResetIdle);
            // anim.ResetTrigger(animTriggerEaten);
        }
        super.ride(startingAnimal,fhtime,fhdis);
    }


    protected AngryLoop() {
		//CameraController.Instance.speedStreaks.SetActive(true);

		let specialCoins = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.specialCoins)
        // let zoomSpeedMultiplier = 1
        let zoomSpeedMultiplier = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.zoomSpeedMultiplier)

		let baseRunModifier = this.runSpeedModifier;
		this.currentZoom = this.zoomSpeedStart;
		this.runSpeedModifier = baseRunModifier * this.currentZoom;

		let coinTimerMax:number = 2;
		let coinTimer:number = coinTimerMax;


        Laya.timer.frameLoop(1, this, ()=>{
            if (this.state == AnimalState.ridden) {
                if (this.currentZoom < this.zoomMax) {
                    this.currentZoom += this.zoomAccelerationRate * Laya.timer.delta/1000
                }

                if (this.coinsFromGoingMaxSpeed && specialCoins > 0) {
                    coinTimer -= Laya.timer.delta/1000;
                    if (coinTimer < 0) {
                        coinTimer = coinTimerMax;
                        PfxHelper.instance.show("pfx_money_1",this.transform.position);
						moduleBridge.gameCtrl.addGold(specialCoins,1007)
                        // CoinEffectsManager.Instance.SpawnCurrency(specialCoins, transform.position + new Vector3(0,3,3), "animal" + animalVariantMeta.genusName.ToString());
                    }
                }
                
                let t = (this.currentZoom - this.zoomSpeedStart) / (this.zoomMax - this.zoomSpeedStart); 
                if (!this.ignoreZoomAnimation) {
                    // anim.SetLayerWeight(animLayer3DOverride, t);
                }
                this.runSpeedModifier = baseRunModifier + (this.currentZoom - 1)*baseRunModifier * zoomSpeedMultiplier;
            }else{
                this.runSpeedModifier = baseRunModifier;
                Laya.timer.clearAll(this)
            }
        })
		
		//CameraController.Instance.speedStreaks.SetActive(false);
		// anim.SetLayerWeight(animLayer3DOverride, 0);
		
	}
}
