    private stopMaoPao(): void {
        this._view.on(Laya.Event.MOUSE_DOWN, this, (e: Laya.Event) => {
            e.stopPropagation();
        })
        for (let i: number = 0; i < this._view.numChildren; i++) {
            let node = this._view.getChildAt(i) as Laya.Sprite;
            node.on(Laya.Event.MOUSE_DOWN, this, (e: Laya.Event) => {
                e.stopPropagation();
            })
        }
    }

    private onSureBuy(): void {
        this.onCloseTip();
        let idArr = userData.get("dangerArr", [0, 0, 0]);
        let id = idArr[this._curBuyIndex];
        let res = userData.cfgAnimal[id].res;
        if (this._selectCurrency == 1) {
            if (userData.gold < this._currencyArr[0]) {
                ZY.msg.show("金币不足");
            } else {
                userData.addGold(-this._currencyArr[0], 1010);
                ZY.msg.show("解锁成功");
                let btn = this._view["btn" + this._curBuyIndex] as Laya.Button;
                btn.disabled = true;
                // ZY.load([res], this, () => {
                    userData.catchAnimal(id);
                // });
            }
        } else if (this._selectCurrency == 2) {
            if (userData.jew < this._currencyArr[1]) {
                ZY.msg.show("钻石不足");
            } else {
                userData.addJew(-this._currencyArr[1], 1010);
                ZY.msg.show("解锁成功");
                let btn = this._view["btn" + this._curBuyIndex] as Laya.Button;
                btn.disabled = true;
                // ZY.load([res], this, () => {
                    userData.catchAnimal(id);
                // });
            }
        }
    }

    private onClostBuy(): void {
        this._view.boxBuy.removeSelf();
        this._view.boxBuy.visible = false;
    }

    private onCloseTip(): void {
        this._view.boxTip.removeSelf();
        this._view.boxTip.visible = false;
        this._view.boxBuy.removeSelf();
        this._view.boxBuy.visible = false;
    }

    private _selectCurrency: number;
    private onShowTip(index: number): void {
        this._selectCurrency = index;
        this._view.boxBuy.removeSelf();
        this._view.boxBuy.visible = false;
        Laya.stage.addChild(this._view.boxTip);
        this._view.boxTip.visible = true;
        let str;
        let idArr = userData.get("dangerArr", [0, 0, 0]);
        let id = idArr[this._curBuyIndex];
        let name = userData.cfgAnimal[id].name;
        if (id == 0) return;
        if (index == 1) {
            str = "将使用" + this._currencyArr[0] + "金币购买" + name;
        } else if (index == 2) {
            str = "将使用" + this._currencyArr[1] + "钻石购买" + name;
        }
        this._view.labelTip.text = str;
    }

