    private onBuy(index: number): void {
        this._curBuyIndex = index;
        let idArr = userData.get("dangerArr", [0, 0, 0]);
        let id = idArr[index];
        if (id == 0) return;
        Laya.stage.addChild(this._view.boxBuy);
        this._view.boxBuy.visible = true;
        let encId = userData.cfgAnimal[id].encId;
        this._currencyArr = this._arr[encId];
        this._view.labelGold.text = "X" + this._currencyArr[0];
        this._view.labelJew.text = "X" + this._currencyArr[1];
    }

    private onTry(index: number): void {
        var today = new Date()
        if (userData.today != today.getFullYear() + ":" + (today.getMonth() + 1) + ":" + today.getDate() + "") {
            userData.today = today.getFullYear() + ":" + (today.getMonth() + 1) + ":" + today.getDate() + ""
            userData.initTyetime()
        }

        if ((userData.tryTime[index] && userData.tryTime[index] == 0) || (!userData.tryTime[index])) {
            if (ZY.channel == "wx") {
                ZY.ad.showVideoAd(this, this.onTry2, ZY.wxAd.trynew, "ad_play" , [index])
            }else if (ZY.channel == "vivo"){
                ZY.ad.showVivoVideo(this, this.onTry2, ZY.vivoAd.trynew, "ad_play" , [index])
            }else if (ZY.channel == "oppo"){
                reporter.upload(ReporterPoints.ad_play,3)
                ZY.ad.showOPPOVideo(this, this.onTry2 ,ReporterPoints.ad_play, [index]);
            } else {
                ZY.ad.showVideoAd(this, this.onTry2, 0, "ad_play" ,[index])
            }
            // userData.addTryTime(index)
            // let idArr = userData.get ("dangerArr", [0,0,0]);
            // let id = idArr[index];
            // if (id == 0) return;
            // // console.log ("试玩id", id);
            // this.curTryId = id;
            // this.onBack ();
            // moduleBridge.shipCtrl.startGame ();
        } else {
            ZY.msg.show("今日这次动物已经试玩过了呢");
        }
    }

    private onTry2(done: boolean, index): void {
        if (done) {
            userData.setDailyTask(6, 1)
            if (ZY.channel == "wx" || ZY.channel == "qq"){
                ZY.TmCtrl.instance.tmSendEvent("ad_play", { value: "完成" })
            } else if (ZY.channel == "zj") {
                ZY.reportEvent("ad_play", { value: "完成" })
            }else if (ZY.channel == "oppo"){
                reporter.upload(ReporterPoints.ad_play,4)
            }
            userData.addTryTime(index[0])
            let idArr = userData.get("dangerArr", [0, 0, 0]);
            let id = idArr[index[0]];
            if (id == 0) return;
            // console.log ("试玩id", id);
            this.curTryId = id;
            this.onBack();
            moduleBridge.shipCtrl.startGame();
        } else {
            moduleBridge.videoTipCtrl.showTips();
        }
        if (ZY.channel == "vivo"){
            ZY.sound.playMusic("audioGame/Music_Zoo.mp3");
        }
    }

    private onStart(): void {
        this.onBack();
        moduleBridge.shipCtrl.startGame();
    }

    private initBig(): void {
        let str = userData.getTodayAotd();
        if (str[1]) {
            this._view.btnSearch.disabled = false;
            this._view.imgBtnZi.skin = "endangeredanimals/img_zi_3.png";
        } else {
            this._view.btnSearch.disabled = true;
            this._view.imgBtnZi.skin = "endangeredanimals/img_zi_4.png";
        }
        let name: string = str[0];
        name = name.replace(".lh", "");
        name = name.replace("game/Conventional/", "");
        this._view.iconBig.skin = "icons/" + name + ".png";
    }

    private initIcon(): void {
        let dangerArr = userData.get("dangerArr", [0, 0, 0]);
        let nameArr = [];
        let cfg = userData.cfgSpawn;
        for (let i: number = 0; i < dangerArr.length; i++) {
            let id = dangerArr[i];
            if (id == 0) break;
            for (const key in cfg) {
                if (cfg.hasOwnProperty(key)) {
                    let arr = cfg[key].animal;
                    for (let i: number = 0; i < arr.length; i++) {
                        let item = arr[i];
                        if (item.id == id) {
                            nameArr.push(item.name);
                            break;
                        }
                    }
                }
            }
        }
        for (let i = 0; i < 3; i++) {
            const name = nameArr[i];
            const id = dangerArr[i];
            let label = this._view["label" + i] as Laya.Label;
            if (name && id){
                let icon = this._view["icon" + i] as Laya.Image;
                icon.skin = "icons/" + name + ".png";
                let namestr = userData.cfgAnimal[id].name;
                label.text = namestr;
                if (userData.getAnimals(userData.cfgAnimal[id].encId).indexOf(id) > -1){
                    let labelgold1 = (this._view["btn"+i] as Laya.Button).getChildAt(0).getChildByName("labelGold") as Laya.Label;
                    let labelgold2 = labelgold1.getChildByName("labelGold2") as Laya.Label;
                    labelgold1.text = labelgold2.text = "已购买";
                    (this._view["btn"+i] as Laya.Button).disabled = true;
                }
            }else{
                label.text = "售罄";
                label.centerX = label.centerY = 0;
                (this._view["btn"+i] as Laya.Button).visible = false;
                (this._view["btnTry"+i] as Laya.Button).visible = false;
                
            }
        }
    }

