import { userData } from "../userData/UserData";
import { ZY } from "../ZY";

export default class Tourist extends Laya.Sprite3D {
    private _body:Laya.Sprite3D;
    private _aniCtrl;
    private _statue:number;
    private _attitude:string;
    private _destination:any; //目的地
    private _path:any; //路径
    private _area:number;//区域
    private _firstZ:number;
    private _v:number;
    private _livePos;
    private _aniWalk;
    private _cameraPFX;
    private _res;
    private _age;
    private _name;
    private _cam:boolean = false;
    private _defaltSpeed:number;
    constructor() {
        super();
        
    }

    public init(info){
        this._body = Laya.Sprite3D.instantiate(Laya.loader.getRes("game/Conventional/Punter.lh"));
        let model:Laya.MeshSprite3D = ZY.findChildByPath(this._body,"GameObject.model") as Laya.MeshSprite3D;
        model.meshFilter.sharedMesh = Laya.loader.getRes(info.res);
        this._res = info.res;
        this._age = info.age;
        this._cam = info.camera;
        this._name = info.name;
        this.addChild(this._body);
        this._aniCtrl = model.getComponent(Laya.Animator) as Laya.Animator;
        let ani = {1:"punter_walk_new",2:"punter_walkKid_new"}
        this._aniWalk = ani[info.age]
        this._cameraPFX = ZY.findChildByPath(this._body,"GameObject.model.pfx_cameraFlash")
        this._cameraPFX.active = false
        this._defaltSpeed = 0.005 + Math.random() * 0.002
    }

    public change(){
        let destination = {}
        if (this._destination && this._destination.length > 0){
            for (let i = this._destination.length - 1 ; i > this._destination.length - 3 ; i -- ){
                destination[this._destination[i][0]] = this._destination[i][1]
            }
            let list = {
                "res" : this._res,
                "pos" : destination,
                "age" : this._age,
                "name" : this._name,
                "area" : this._area,
                "live" : this._livePos,
            }
            return list
        }else{
            return null
        }
    }

    public relive(info){
        this._body.transform.localPosition = ZY.Temp.getVector3();
        this.transform.localPositionY = 0;
        this.transform.localPositionZ = info.pos.z
        this.transform.localPositionX = info.pos.x
        this._area = info.area
        this._livePos = info.live
        this._aniCtrl.play("punter_walk_new")
        if (this._cam) this._cameraPFX.active = true
        this._v = 0,
        Laya.timer.clearAll(this)
        Laya.timer.frameLoop(1000, this, this.hangout)
    }

    public clear():void {
        Laya.timer.clearAll(this);

        if(this._body){
            this._body.removeSelf();
            this._body.destroy(true);
        }
        this._body = null;
        this._aniCtrl = null;
        this._statue = null;
        this._attitude = null;
        this._destination = null;
        this._path = null;
        this._area = null;
        this._livePos = null;
        this._firstZ = null;
        this._age = null
        this._res = null
        this._v = null;
        this.destroy(true);
    }


    public path(areaNum,touristArea,livePos):void{
        this._body.transform.localPosition = ZY.Temp.getVector3();
        this.transform.localPositionY = 0;
        this.transform.localPositionZ = -70
        this.transform.localPositionX = this.getRandom(-2,2,false);  //-3到3之间的随机数
        // 终点
        this._area = this.getRandom(1,areaNum,true)
        let tRandom_Des = touristArea[this._area]                                          //随机目的地
        let nRandom_path = this.getRandom(1,tRandom_Des.how.length,true) - 1                //随机路线
        //闲逛活动范围
        this._livePos = livePos
        //目的地
        for (const k in tRandom_Des.pos[nRandom_path]){
            let t = tRandom_Des.pos[nRandom_path][k]
            this._destination = this._destination || []
            this._destination[k] = this._destination[k] || []
            this._destination[k][0] = t[0]
            this._destination[k][1] = this.getRandom(t[1],t[2],false)
        }
        this._path = tRandom_Des.how[nRandom_path]                                              //路径
        //阶段一、下车往前走
        this._statue = -1;
        this._v = this._defaltSpeed;
        this._attitude = "front";
        // this._aniCtrl.play("punter_walk")
        this._aniCtrl.play(this._aniWalk)
        this._firstZ = this.getRandom(-11,-5,false)
        Laya.timer.frameLoop(1, this, this.step)
    }

    checkMove(){
        if (this._path && this._path[this._statue + 1]) {
            //状态+1
            this._statue += 1
            //姿态改变
            this._attitude = this._path[this._statue]
            //速度改变
            this._v = this._attitude == "right" && - this._defaltSpeed || this._defaltSpeed
            let tAngle = {"right" : -90, "back" : 180 , "front" : 0 , "left" : 90}
            Laya.Tween.to(this._body.transform, {localRotationEulerY:tAngle[this._attitude]}, 200, Laya.Ease.linearInOut);
            Laya.timer.clearAll(this)
            Laya.timer.frameLoop(1, this, this.step)
        }
        else{
            //乖乖站好
            this._aniCtrl.play("punter_walk_new")
            //换方向this._area
            let list = {1:-90,2:90}
            if (list[this._area]){
                this._body.transform.localRotationEulerY = list[this._area]
            }else{
                this._body.transform.localRotationEulerY = Math.random() > 0.5 ? 0 : 180
            }
            if (this._cam) this._cameraPFX.active = true
            this._v = 0,
            Laya.timer.clearAll(this)
            Laya.timer.frameLoop(1000, this, this.hangout)
        }
    }
    
    step() {
        let i = Laya.timer.delta * this._v;
        i > 1 && (i = 1),
        i < -1 && (i = -1);
        if (this._statue == -1){
            this.moveZ(i,this._firstZ)
        }
        else {
            let a = this._destination[this._statue];
            if (a) {
                if (a[0] == "x") {
                    this.moveX(i,a[1])
                }
                else if (a[0] == "z") {
                    this.moveZ(i,a[1])
                }
            }
        }
    }

    //X轴移动
    moveX(i,limit){
        let s = this.transform.localPositionX
        if ((this._attitude == "left" && s + i >= limit) || (this._attitude == "right" && s - i <= limit)){
            this.checkMove();
            return
        }
        this.transform.localPositionX += i
    }
    //Z轴移动
    moveZ(i,limit){
        let s = this.transform.localPositionZ
        if ((this._attitude == "front" && s + i >= limit) || (this._attitude == "back" && s - i <= limit)){
            this.checkMove();
            return
        }
        this.transform.localPositionZ += i
    }

    hangout(){
        //上下左右的各个坐标（单位1）
        let around_pos = {
            "front" : [this.transform.localPositionX,      this.transform.localPositionZ + 1],
            "back"  : [this.transform.localPositionX,      this.transform.localPositionZ - 1],
            "left"  : [this.transform.localPositionX + 1,  this.transform.localPositionZ],
            "right" : [this.transform.localPositionX - 1,  this.transform.localPositionZ],
        }
        let tD = ["front","back","left","right"]
        let tSpeed = {"front" : 0.001,"back": -0.001,"left": 0.001,"right": -0.001}
        this.randomPos(tD)
        //依次判断
        for (let i = 0; i < tD.length; i ++){
            if (this.checkpark(this._area,around_pos[tD[i]])){
                this._attitude = tD[i];
                this._v = tSpeed[this._attitude];
                let tAngle = {"right" : 270, "back" : 180 , "front" : 0 , "left" : 90}
                this._body.transform.localRotationEulerY = tAngle[this._attitude]
                // this._aniCtrl.play("punter_walk")
                this._aniCtrl.play(this._aniWalk)
                Laya.timer.clearAll(this)
                Laya.timer.frameLoop(1, this, this.wander,[around_pos[tD[i]]])
                break
            }
        }
    }

    private wander(pos):void{
        let i = Laya.timer.delta * this._v;
        i > 1 && (i = 1),
        i < -1 && (i = -1);
        if (this._attitude == "front" || this._attitude == "back"){
            this.moveZ(i,pos[1])
        }
        else{
            this.moveX(i,pos[0])
        }
    }

     
    //判断是否在其范围
    private checkpark(area,t):boolean{
        if ((t[0] >= this._livePos[area][0][0]) && (t[0] <= this._livePos[area][0][1]) &&
            (t[1] >= this._livePos[area][1][0]) && (t[1] <= this._livePos[area][1][1]))
            return true
    }

    //打乱表
    private randomPos(t):void{
        let tmp,index;
        for (let i = 0; i < t.length; i++) {
            index = Math.floor(Math.random() * (t.length - 1 - i + 1) + i)
            if (i != index){
                tmp = t[i]
                t[i] = t[index]
                t[index] = tmp
            }
        }
    }

    //取随机数
    private getRandom(min,max,int):number{
        if (int) {
            return Math.floor(Math.random() * (max - min + 1) + min)
        }
        else{
            return Math.random() * (max - min) + min
        }
    }



}