    private onScene3dAdded(sceneName:string):void {
        let pfxCfg = Laya.loader.getRes("cfg/cfgPfx.json");
        if(pfxCfg) {
            let cnt;
            for (let i = 0; i < pfxCfg.length; i++) {
                const pInfo = pfxCfg[i];
                if(pInfo && pInfo.cache) {
                    cnt = pInfo.cache[sceneName];
                    if(cnt) {
                        PfxHelper.instance.createCache(pInfo.name, cnt);
                    }
                }
                // cache
            }
        }
    }

    private _showMuji:boolean = true;
    protected afterEnterScene(sceneName:string):void {
        if(sceneName !== "ZY_FIRST" && (ZY.channel == ZY.Channel.zj || ZY.channel == "vivo" || ZY.channel == "oppo" || ZY.channel == "none")) {
            if (ZY.channel == "oppo"){
                (window as any).qg.offError();
            }
            if(this._showMuji) {
                this._showMuji = false;
                let loading = ZY.stage.loadingUI as LoadingPage;
                if(loading) {
                    loading.showMuJi();
                }
            }
        }
        if (ZY.channel == "wx"){
            ZY.ad.hideCustomAd()
        }
    }

