import GameObject = Laya.Sprite3D;
import { moduleBridge ,IAnimal } from "../Interface";
import Transform = Laya.Transform3D;

export class AnimalEatCollider extends Laya.Script3D {
	private _animal:IAnimal;
    public transform:Transform;
    public gameObject:GameObject;
    private _collider:Laya.PhysicsCollider;

    public onAwake():void {
        this.gameObject = this.owner as GameObject;
        this.transform = this.gameObject.transform;
        this._animal = this.gameObject.parent.getComponent(moduleBridge.Animal);
        this._collider = this.gameObject.getComponent(Laya.PhysicsCollider) as Laya.PhysicsCollider;
    }

    public switchOff():void {
        if(this.enabled) this.enabled = false;
        if(this._collider.enabled) this._collider.enabled = false;
    }

    public switchOn():void {
        if(!this.enabled) this.enabled = true;
        if(!this._collider.enabled) this._collider.enabled = true;
    }

	public onTriggerEnter(other:Laya.PhysicsComponent) {
        let otherAnimal = other.owner.parent.getComponent(moduleBridge.Animal) as IAnimal;
		if (otherAnimal != null && otherAnimal.edible) { 
			this._animal.RidingCollision(otherAnimal);
		}
	}
}
