import { ZY } from "../ZY";
import { ui } from "../ui/layaMaxUI";
import ZooButton from "../script/ZooButton";
import { userData } from "../userData/UserData";
import { moduleBridge } from "../Interface";
import { nativeAd } from "../NativeAd";

export default class oneDollarCtrl extends ZY.BaseCtrl {
    constructor() { super(); }
    
    public start():void {
        super.start();
        this.bindAssets("res/atlas/setting.atlas", Laya.Loader.ATLAS, "zoo");
    }

    protected beforeEnterScene(sceneName:string):void {
        if(this._sceneName == "zoo") {
            this._view.btnBack.off("click", this, this.onBack);
            this._btnBuy.off("click", moduleBridge.shopCtrl, moduleBridge.shopCtrl.buyOneDollar);
            this._btnBuy.clear();
            this._btnBuy = null;

            this._from = null;
            this._index = null;
            this._view.removeSelf();
            this._view.destroy(true);
            this._view = null;      

        }
    }

    private _view:ui.main.OneDollarUI;
    private _btnBuy:ZooButton;
    protected createChildren(sceneName:string):void {
        if(sceneName == "zoo") {
            if(!this._view) {
                this._view = new ui.main.OneDollarUI();
                this._btnBuy = new ZooButton(this._view.btnBuy);
            }
            this._view.btnBack.on("click", this, this.onBack);
            this._btnBuy.on("click", moduleBridge.shopCtrl, moduleBridge.shopCtrl.buyOneDollar);
            this.stopMaoPao ();
        }
    }

    private stopMaoPao():void {
        this._view.on (Laya.Event.MOUSE_DOWN, this, (e:Laya.Event)=>{
            e.stopPropagation ();
        })
        for (let i:number = 0; i < this._view.numChildren; i ++) {
            let node = this._view.getChildAt (i) as Laya.Sprite;
            node.on (Laya.Event.MOUSE_DOWN, this, (e:Laya.Event)=>{
                e.stopPropagation ();
            })
        }
    }

    private updateView():void {
        userData.loop();
        if (userData.get("dollar",0) == 0){
            this._view.boxMain.visible = true;
            if (ZY.channel == "vivo"){
                ZY.ad.showInsertAd()
            }
        }
    }

    private _from:string;
    private _index:number;
    public showView(from ?:string,index ?:number):void {
        if(this._sceneName == "zoo") ZY.stage.addChild(this._view, ZY.Layer.MIDDLE);
        this._from = from;
        this._index = index;
        Laya.timer.callLater(this, this.updateView);
    }

    public onBack():void {
        if (ZY.stage.sceneName == "zoo"){
            this._view.removeSelf();
            if (!this._from){
                moduleBridge.shipCtrl.backToShip("dollar");
            }else if(this._from == "start"){
                moduleBridge.shipCtrl.backToShip("start",this._index);
            }
        }
    }

}

moduleBridge.onedollarctrl = new oneDollarCtrl();