import AnimalThrower from "./AnimalThrower";
import { AnimalState, moduleBridge, PlayerState, VaultState } from "../Interface";
import Animal from "./Animal";
import Helper2D from "../Helper2D";
import { ZY } from "../ZY";
import GameObject = Laya.Sprite3D;
import { UpgradeKeys } from "../UpgradeKeys";


export default class AnimalVaulter extends AnimalThrower {
	public vaultStrength:number;
	public jumpForce:number;
	public gravity:number;
	public landingShockwave;
	public shockwaveRadius:number;
	public landingShockwaveLarge;
	public shockwaveRadiusLarge:number;
	public shockwaveMask;
	public smashOnLanding:boolean = true;
	protected vaultState:VaultState = VaultState.none;


    public init(id:string, obj:GameObject):void{
        super.init(id, obj)
        let info = Laya.loader.getRes("cfg/cfgAttribute.json")[this.encId];
        this.vaultStrength = info.vaultStrength,
        this.jumpForce = info.jumpForce,
        this.gravity = info.gravity,
        this.shockwaveRadius = info.shockwaveRadius,
        this.shockwaveRadiusLarge = info.shockwaveRadiusLarge,
        this.smashOnLanding = info.smashOnLanding,
        this.vaultState = VaultState.none;
    }
	public reset(){
		super.reset()
		this.vaultState = VaultState.none;
	}

    protected obstacleCrash (obstacle, immuneToWalls:boolean = false):void {
        this.jumpOrSmash(obstacle, immuneToWalls);
	}

	public jumpOrSmash(obstacle, immuneToWalls:boolean):void {
		if (this.state == AnimalState.ridden) {
			if (this.vaultState == VaultState.none) {
				if (obstacle.smashStrength <= this.vaultStrength && !obstacle.isLevelEdge && !obstacle.isCenterEdge && !obstacle.indestructable) {
					if (!obstacle.smashed) {
                        this.vaultRoutine(obstacle)
						// obstacle.smashed = true;
					}
				} else {
					super.obstacleCrash(obstacle, immuneToWalls);
				}
			} else if (this.vaultState == VaultState.jumping) {
				// if (obstacle.isLevelEdge || obstacle.isCenterEdge) {
				if (obstacle.layer == 8) {
					if (this.transform.localPositionY > 3.5) {
						this.vaultState = VaultState.none;
                        moduleBridge.player.landingRange.updateLassoSize(0)
						super.obstacleCrashVerified(obstacle);
					} else {
						super.obstacleCrash(obstacle, true);
					}
				} else if (obstacle.indestructable) {
					super.obstacleCrash(obstacle, immuneToWalls);
				} else {
					super.smashObstacle(obstacle);
				}
			}
		} else {
			if (this.vaultState == VaultState.none) {
				if (obstacle.smashStrength <= this.vaultStrength && !obstacle.isLevelEdge && !obstacle.isCenterEdge && !obstacle.indestructable) {
					if (!obstacle.smashed) {
                        this.vaultRoutine(obstacle)
						// StartCoroutine(VaultRoutine(obstacle));
					}
				}
			} else if (this.vaultState == VaultState.jumping) {
				if (obstacle.indestructable || obstacle.isLevelEdge || obstacle.isCenterEdge) {
					super.obstacleCrash(obstacle, immuneToWalls);
				} else {
					super.smashObstacle(obstacle);
				}
			}
		}

	}

	public onTriggerEnter(other:Laya.PhysicsComponent):void {
        super.onTriggerEnter(other);
        // let otherObstacle = other.GetComponent<Obstacle>();
        // if (otherObstacle != null && otherObstacle.type != ObstacleType.Shrub && !otherObstacle.isLevelEdge && !otherObstacle.isCenterEdge && state == AnimalState.running && vaultState == VaultState.none) {
        // let a = other.owner.getComponent(Animal)
        // if (!other.owner.getComponent(Animal)){
            // this.jumpOrSmash(other, true);
        // }
        // }
    }

    protected animalCrash (otherAnimal:Animal):void{
		if (this.vaultState == VaultState.none) {
			super.animalCrash(otherAnimal);
		} else {
			super.smashAnimal(otherAnimal);
		}
	}

	protected moveAnimal (speed:number):void{
		if (this.vaultState != VaultState.preparing) {
            if (this.state != AnimalState.ridden && this.vaultState == VaultState.jumping) {
                super.moveAnimal(3*speed);
            } else {
                super.moveAnimal(speed);
            }
		}
	}

	protected calculateCurrentAngle ()
	{
		if (this.vaultState == VaultState.none) {
			super.calculateCurrentAngle();
		}
	}

    public dryOff(changeYPos:boolean) {
        if (this.vaultState == VaultState.none) {
            super.dryOff(true);
        } else {
            super.dryOff(false);
        }
        
    }

	// public EnterSpecialObstacle(Collider other, bool changeYPos) {
    //     if (vaultState == VaultState.none) {
	// 		super.EnterSpecialObstacle(other, true);
    //     } else {
	// 		super.EnterSpecialObstacle(other, false);
    //     }
    // }

    protected updateAngerTimer(){
		if (this.vaultState == VaultState.none) {
			super.updateAngerTimer();
		}
	}

	public leaveAnimal (crashed:boolean = false, playCrashAnim:boolean = true){
		if (crashed == true) {
			super.leaveAnimal(crashed, playCrashAnim);
			return;
		}

		if (this.vaultState != VaultState.jumping) {
			super.leaveAnimal(crashed, playCrashAnim);
			return;
		}

        let vaulterMidJumpLassoModifier = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.vaulterMidJumpLassoModifier)
        // UpgradeValue vaulterMidJumpLassoModifier = animalUpgrades.GetUpgrade(UpgradeKeys.vaulterMidJumpLassoModifier);
        // let vaulterMidJumpLassoModifier = 0
		
        moduleBridge.player.landingRange.updateLassoSize(vaulterMidJumpLassoModifier)
		super.leaveAnimal(crashed, playCrashAnim);
	}

	protected vaultRoutine(obstacle):void {
		this.vaultState = VaultState.preparing;

		if (obstacle) {
			if (obstacle.isArch) {
                Helper2D.SetLocalPosY(this.transform,2)
				// transform.SetPosY(2);
			} else {
				Helper2D.SetLocalPosX(this.transform,obstacle.transform.localPositionX)
                Helper2D.SetLocalPosY(this.transform,obstacle.boundsY)
			}
		}
		//play vault anim
		if (this.animalId !== "903"){
			this.playAni("buck")
		}
		// anim.ResetTrigger("forceBuck");
		// anim.SetTrigger("forceBuck");
        if (this.state == AnimalState.ridden) {
            // AudioGlobal.Instance.AnimalJump();
		}
        Laya.timer.once(80, this, this.jumpOver)
	}

	private jumpOver():void{
		this.vaultState = VaultState.jumping;
		//jump loop
		Laya.timer.frameLoop(1,this,this.jumploop)
	}

	private _yVel:number;
	private jumploop(yVel:number):void{
		let jumpHeight:number = this.transform.position.y
		if (jumpHeight > 0) {
			this._yVel = this._yVel || this.jumpForce
			jumpHeight += this._yVel * Laya.timer.delta/1000;
			this._yVel -= this.gravity * Laya.timer.delta/1000;
			Helper2D.SetLocalPosY(this.transform,jumpHeight)
		}else{
			this._yVel = null;
			// Laya.timer.clearAll(this)
			Laya.timer.clear(this,this.jumploop)
			Helper2D.SetLocalPosY(this.transform,0)

			// anim.SetTrigger(animTriggerAttack);
			if (this.animalId !== "903"){
				this.playAni("attack")
			}
	
			if (moduleBridge.player.state == PlayerState.riding && this.state == AnimalState.ridden) {
				if (!this.checkAndStartFall()) {
					this.stampedeAI.enabled = true;
					this.smashLanding();
					this.playAni("run")
				}
			}
			this.vaultState = VaultState.none;
		}
	}

	protected smashLanding():void {
		let vaulterLargerSmashArea = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.vaulterLargerSmashArea)
		let specialCoins = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.specialCoins)
		let special = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.special)
        // let vaulterLargerSmashArea = 0
        // let specialCoins = 0
        // let special = 0
		//get knockback targets
        let collisionHalfSize:Laya.Vector3 = ZY.Temp.getVector3(0,0,0)
		let spawnPos:number = 0;
		if (vaulterLargerSmashArea > 0.0) {
			collisionHalfSize = new Laya.Vector3(this.shockwaveRadius, this.shockwaveRadius, this.shockwaveRadiusLarge);
			spawnPos = this.shockwaveRadiusLarge;
		} else {
			collisionHalfSize = new Laya.Vector3(this.shockwaveRadius, this.shockwaveRadius, this.shockwaveRadius);
			spawnPos = this.shockwaveRadius;
		}

		// EffectsEvent e = null;
		// if (vaulterLargerSmashArea > 0) {
		// 	e = landingShockwaveLarge;
		// } else {
		// 	e = landingShockwave;
		// }
		// EffectsEvent.SpawnEffect(e, null, transform.position + (transform.rotation * new Vector3(0, 0, this.shockwaveRadius)), this);

		if(this.smashOnLanding == false) {
			return;
        }
        
        this.stampedeAI.smashLanding(this.state == AnimalState.ridden,specialCoins);
	}
}
