import { ZY } from "../ZY";
import Helper2D from "../Helper2D";
import Sprite3D = Laya.Sprite3D;
import { PlayerState, moduleBridge } from "../Interface";

export default class Rope extends Laya.Script3D {

    public meshObject:Sprite3D;

    public start:Laya.Transform3D;

    public jointHand:Laya.Transform3D;
    public jointLoop:Laya.Transform3D;
    private _defaultLoopOffset = new Laya.Vector3(0, 1.2, 0);
    private _loopWorldPos = new Laya.Vector3();

    public flyingLoopSize = 1;

    private _lassooSet = false;
    private _targetAnimal:Laya.Transform3D;
    // public oldLassoRing:Sprite3D;

    public lassooArc:Sprite3D;

    public get transform():Laya.Transform3D {
        return (this.owner as Sprite3D).transform;
    }

    public onAwake() {
        let obj = this.owner as Sprite3D;
        this.meshObject = obj.getChildByName("lasso_Poly 1") as Sprite3D;
        this.start = (ZY.findChildByPath(obj, "_.offset.ctrl_main.ctrl_hips.ARM_L.SKELETON.joint_Bottom") as Sprite3D).transform;
        this.jointHand = (ZY.findChildByPath(obj, "ROOT.joint_Hand") as Sprite3D).transform;
        this.jointLoop = (ZY.findChildByPath(obj, "ROOT.joint_Loop") as Sprite3D).transform;
        this.flyingLoopSize = 1;
        // this.oldLassoRing
        this.lassooArc = ZY.findChildByPath(obj, "ROOT.joint_Hand.GameObject");
    }

	// Use this for initialization
	public onStart () {
        this._loopWorldPos = this.jointLoop.position.clone();
	}

	// yodo1 zhaojun
	private _temp:Laya.Vector2 = new Laya.Vector2();

	// Update is called once per frame
	public onUpdate () {
        this.jointHand.position = this.start.position;
        if (moduleBridge.player.state == PlayerState.riding || moduleBridge.player.state == PlayerState.flying) {
            let x = this._loopWorldPos.x+(this.jointHand.position.x+this._defaultLoopOffset.x - this._loopWorldPos.x) * 15 * Laya.timer.delta/1000;
            let y = this._loopWorldPos.y+(this.jointHand.position.y+this._defaultLoopOffset.y - this._loopWorldPos.y) * 15 * Laya.timer.delta/1000;
            this._temp.setValue(x,y);
            this._loopWorldPos = new Laya.Vector3(this._temp.x, this._temp.y, this.jointHand.position.z);
            this.jointLoop.position = this._loopWorldPos;
		} else if (moduleBridge.player.state == PlayerState.waitingForStart) {
			this._temp.setValue(this.jointHand.position.x + this._defaultLoopOffset.x,this.jointHand.position.y + this._defaultLoopOffset.y);
			this._loopWorldPos = new Laya.Vector3(this._temp.x, this._temp.y, this.jointHand.position.z);
			this.jointLoop.position = this._loopWorldPos;
        } else if (moduleBridge.player.state == PlayerState.landing && this._lassooSet) {
            this.jointLoop.position = this._targetAnimal.position;
        } else if (moduleBridge.player.state == PlayerState.crashed) {
            if (this.meshObject.active) {
                this.meshObject.active = false;
            }
        }
	}

    public forceRopeToStandardPosition () {
        this._temp.setValue(this.jointHand.position.x + this._defaultLoopOffset.x,this.jointHand.position.y + this._defaultLoopOffset.y);
        this._loopWorldPos = new Laya.Vector3(this._temp.x, this._temp.y, this.jointHand.position.z);
        this.jointLoop.position = this._loopWorldPos;
    }

    public grabAnimal(newTarget:Laya.Transform3D, transitionLength:number, caller:any, callback:Function) {
        this.lassooArc.transform.lookAt(newTarget.position,ZY.Temp.getVector3(0,1,0),false);

        
        Helper2D.SetLocalEulerZ(this.lassooArc.transform, Laya.Vector3.distance(this.lassooArc.transform.position, newTarget.position)/6);
        // this.lassooArc.transform.SetLocalScaleZ(Vector3.Distance(lassooArc.transform.position, newTarget.transform.position)/6);
        // yield return StartCoroutine(jointLoop.MoveToTransform(newTarget, transitionLength, EaseType.Linear));
        let pos = new Laya.Vector3();
        let oPos = this.jointLoop.position.clone();
        let nPos = newTarget.position.clone();
        let t = 0;
        Laya.systemTimer.frameLoop(1, this, ()=>{
            t += Laya.systemTimer.delta/1000;
            if(t>=transitionLength) {
                pos = nPos;
                this.jointLoop.position = pos;
                this._targetAnimal = newTarget;
                this.jointLoop.localScale = new Laya.Vector3(0.1, 1, 0.1);
                this._lassooSet = true;
                Laya.systemTimer.clearAll(this);
                callback.call(caller);
                return;
            } else {
                pos.setValue(oPos.x+(nPos.x-oPos.x)*t/transitionLength,oPos.y+(nPos.y-oPos.y)*t/transitionLength,oPos.z+(nPos.z-oPos.z)*t/transitionLength);
            }
            this.jointLoop.position = pos;
        });
        //Debug.Break();
        //lassooArc.SetActive(false);
        
    }

    // public IEnumerator GrabAnimal(Transform newTarget, float transitionLength) {
    //     lassooArc.transform.LookAt(newTarget);
    //     //lassooArc.SetActive(true);
    //     //StartCoroutine(transform.ScaleTo(Vector3.one, transitionLength));
    //     lassooArc.transform.SetLocalScaleZ(Vector3.Distance(lassooArc.transform.position, newTarget.position)/6);
    //     yield return StartCoroutine(jointLoop.MoveToTransform(newTarget, transitionLength, EaseType.Linear));
    //     //Debug.Break();
    //     //lassooArc.SetActive(false);
    //     this.targetAnimal = newTarget;
    //     jointLoop.localScale = new Vector3(0.1f, 1, 0.1f);
    //     lassooSet = true;
    // }

    public switchOffRope() {
        this.meshObject.active = false;
    }

    public resetRope() {
        if (!this.meshObject.active) {
            this._lassooSet = false;
            this.transform.localScale = ZY.Temp.getVector3(1,1,1);
            this.jointLoop.localScale = ZY.Temp.getVector3(1,1,1);
            this.meshObject.active = true;
        }
    }
}
