import AnimalThrower from "./AnimalThrower";
import { moduleBridge } from "../Interface";
import { UpgradeKeys } from "../UpgradeKeys";
import PfxHelper from "../game/PfxHelper";

export default class Elephant extends AnimalThrower {

	protected thingsSmashed = 0;

	public ride(startingAnimal:boolean,fhtime ?:number,fhdis ?:number):void {
		super.ride(startingAnimal,fhtime,fhdis);
        this.thingsSmashed = 0;

	}

	protected obstacleCrash(other, immune:boolean = false) {
		super.obstacleCrash(other, true);
	}

	protected smashObstacle(obstacle) {
		let specialCoins = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.specialCoins)

		super.smashObstacle(obstacle);

		if (specialCoins > 0) {
			PfxHelper.instance.show("pfx_money_1",this.transform.position);
			moduleBridge.gameCtrl.addGold(specialCoins,1007,true)
			// CoinEffectsManager.Instance.SpawnCurrency(specialCoins, obstacle.transform.position + (Vector3.up * 2), "animal" + animalVariantMeta.genusName.ToString());
		}
		this.thingsSmashed++;
	}

	protected animalCrash(otherAnimal) {
		// let special = 0
		let special = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.special)

		if (otherAnimal.smashStrength < this.smashStrength || special > 0) {
			this.smashAnimal(otherAnimal);
			this.thingsSmashed++;
		} else {
			super.animalCrash(otherAnimal);
		}
	}
}