import { AlligatorState, AnimalState, moduleBridge, PlayerState } from "../Interface";
import AnimalPredator from "./AnimalPredator";
import Helper2D from "../Helper2D";
import Animal from "./Animal";
import { ZY } from "../ZY";
import GameObject = Laya.Sprite3D;
import { UpgradeKeys } from "../UpgradeKeys";

export default class Alligator extends AnimalPredator {

    // public GameObject AIAvoid;

    private alligatorState:AlligatorState;
    private alreadyAttempted:boolean = false;

    private rideTimer:number = 100;

    private vct1:Laya.Vector3 = new Laya.Vector3()
    private vct2:Laya.Vector3 = new Laya.Vector3()

    public onUpdate() {
        if (this.state == AnimalState.ridden) {
            this.rideTimer += Laya.timer.delta/1000;
        }
        super.onUpdate();
    }

    public init(id:string, obj:GameObject):void{
        super.init(id, obj)
        this.alreadyAttempted = false;
        this.alligatorState = AlligatorState.idle;
        this.state = AnimalState.idle;
        this.playAni("idle")
        this.playerJoint.localPosition = new Laya.Vector3(0,0,0)
        this.rideTimer = 100;
    }

	protected stampedeBehaviour() {
		if (this.alligatorState == AlligatorState.idle) {
			this.speed = 0;

			let special = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.special)
			// let special = 0

            let attackRadius = 35 - special;
            if (Helper2D.GetDistance(this.transform.localPosition, moduleBridge.player.transform.position) < attackRadius &&  moduleBridge.player.state == PlayerState.riding && (moduleBridge.player.steed.edible && moduleBridge.player.steed.encId != this.encId) && !this.alreadyAttempted) {
                this.alligatorState = AlligatorState.pursuit;
				this.currentAngle = this.transform.localRotationEulerY
                // anim.SetBool("isIdle", false);
                this.playAni("run")
                this.speed = 1;
			}
		} else if (this.alligatorState == AlligatorState.pursuit) {
			this.speed += 3 * Laya.timer.delta/1000;
            this.speed = Math.min(this.speed, 8);
			this.currentAngle = Helper2D.MoveTowardsAngle(this.currentAngle, 180 - 180/Math.PI * Math.atan2(moduleBridge.player.transform.localPositionX - this.mouthPosition.position.x, moduleBridge.player.transform.localPositionY - this.mouthPosition.position.y), 3);
            Helper2D.SetLocalEulerY(this.transform,this.currentAngle)
            // console.log(this.currentAngle,this.speed)
			if (this.alreadyAttempted || moduleBridge.player.state != PlayerState.riding || !moduleBridge.player.steed.edible) {
				this.alligatorState = AlligatorState.idle;
                // anim.SetBool("isIdle", true);
                this.playAni("idle")
			} else if (Helper2D.GetDistance(this.mouthPosition.position, moduleBridge.player.steed.transform.localPosition) < 4 && moduleBridge.player.state == PlayerState.riding && moduleBridge.player.steed.edible) {
                this.attackPlayer();
                
			}
		}
	}

    public reset():void{
        this.alreadyAttempted = false;
        super.reset()
        this.alligatorState = AlligatorState.idle;
        this.state = AnimalState.idle;
        this.playAni("idle")
        this.playerJoint.localPosition = new Laya.Vector3(0,0,0)
        this.rideTimer = 100;
    }

	private attackPlayer () {
		// Debug.Log("attack player");
        this.alreadyAttempted = true;

        this.attackPlayerSequence(moduleBridge.player.steed)
    }

    protected animalCrash( other:Animal) {
        super.animalCrash(other);
        // AudioGlobal.Instance.AnimalBite(gameObject);
    }

    protected attackPlayerSequence(prey) {

        // anim.SetTrigger(animTriggerAttack);
        // attackAnimator.SetTrigger("attack");
        this.playAni("attack")
        this.alligatorState = AlligatorState.idle;
        this.state = AnimalState.idle;
        // anim.SetBool("isIdle", true);

        let targetSpot = new Laya.Vector3(prey.transform.localPositionX,prey.transform.localPositionY,prey.transform.localPositionZ+2)
        if (this.wetness > 0.5 && this.lastWaterEntered != null) {
            // waterEnterEffectPrefab.Spawn(targetSpot, this.lastWaterEntered.enterEffectPrefab.transform.rotation).TriggerEffect();
        }

        this.currentAngle = 180 - 180/Math.PI * Math.atan2(targetSpot.x - this.transform.localPositionX, targetSpot.y - this.transform.localPositionY);
        Helper2D.SetLocalEulerY(this.transform,this.currentAngle)
        Laya.Vector3.lerp(this.transform.localPosition,targetSpot,0.4,this.vct1)
        Laya.Tween.to(this.transform,{localPositionX:this.vct1.x,localPositionY:this.vct1.y,localPositionZ:this.vct1.z},150 ,Laya.Ease.linearInOut ,Laya.Handler.create (this, ()=>{
            if (Helper2D.GetDistance(this.mouthPosition.position, moduleBridge.player.steed.transform.localPosition) < 2 && moduleBridge.player.state == PlayerState.riding) {
                this.attackSuccessSequence(prey)
            }
            Laya.Vector3.lerp(this.transform.localPosition,targetSpot,0.4,this.vct2)
            Laya.Tween.to(this.transform,{localPositionX:this.vct2.x,localPositionY:this.vct2.y,localPositionZ:this.vct2.z},50 ,Laya.Ease.linearInOut)
        }))


    }

    protected attackSuccessSequence(prey:Animal) {
        moduleBridge.player.eatenCrash();
        // AudioGlobal.Instance.AnimalBite(gameObject);
        prey.SetEaten();
    }
    
    protected EatDash (prey:Animal, lerpValue:number) {
        super.EatDash(prey, 0.6);
    }

    protected moveAnimal( speed:number) {
        if (this.rideTimer < 2) {
            super.moveAnimal((1 + this.rideTimer)*speed/3);
        } else {
            super.moveAnimal(speed);
        }
    }

    protected setAnimationSpeedScale(speed:number) {
        if (this.rideTimer < 2) {
            super.setAnimationSpeedScale((1 + this.rideTimer) * speed / 3);
        } else {
            super.setAnimationSpeedScale(speed);
        }
    }

    public ride(startingAnimal:boolean,fhtime ?:number,fhdis ?:number):void {
        if (startingAnimal) {
            this.rideTimer = 100;
        } else {
            this.rideTimer = 0;
        }
		// anim.SetBool(animBoolIdle, false);
        super.ride(startingAnimal,fhtime,fhdis);

		// anim.ResetTrigger("forceRidden");
		// anim.SetTrigger("forceRidden");
	}

}
