		this.bindAssets("preload/button_patchHilight5.png",Laya.Loader.IMAGE,"teach");
		this.bindAssets("preload/black.png",Laya.Loader.IMAGE,"teach");
		this.bindAssets("preload/red.png",Laya.Loader.IMAGE,"teach");
		this.bindAssets("preload/tap.ani",Laya.Loader.JSON,"teach");
		this.bindAssets("preload/untap.ani",Laya.Loader.JSON,"teach");
		this.bindAssets("preload/warnning.ani",Laya.Loader.JSON,"teach");
		this.bindAssets("preload/drage.ani",Laya.Loader.JSON,"teach");
