    protected beforeEnterScene(sceneName:string):void{
        if(sceneName === "zoo") {
            if(userData.enclosure[1001] === -1) {
                this.bindAssets("res/atlas/preload.atlas",Laya.Loader.ATLAS, "zoo");
                userData.showAllShip = false;
            }
        }
        if(this._sceneName === "teach") {
            this._view.page1.off(Laya.Event.MOUSE_DOWN, this, this.onPage1MouseDown);
            this._view.page4.off(Laya.Event.MOUSE_DOWN, this, this.onPage4MouseDown);
            this._view.page4.off(Laya.Event.MOUSE_UP, this, this.onPage4MouseUp);
            // this._playerScript.destroy();
            // this._playerScript = null;
            // this._buffaloAni = null;
            this._buffalo.destroy();
            this._buffalo = null;
            this._horse.destroy();
            this._horse = null;
            this._player.destroy();
            this._player = null;
            this._playerAni = null;
            this._cam = null;
            this._camAni = null;
            this._worldStartBlock = null;
            this._meterial = null;
            this._scene = null;
            this._camNode = null;
            this._cameraRig.destroy();
            this._cameraRig = null;
            this._mouseDown = null;
            this._playerV = null;
            this._circleV = null;
            this._camV = null;
            this._colliderPrefab.destroy(true);
            this._colliderPrefab = null;
            // this._groundMat = null;
            this._horseV = null;
            this._sex = null;
            this._skinIndex = null;
            this._chest = null;
            this._hair = null;
            this._changeHat = null;
            this._firstChangan = null;
            this._firstSongshou = null;
            this._firstJump = null;
            this._firstHua = null;
            this._secondSong = null;
            this._showGift = null;
            Laya.Tween.clearAll(this);
            while(this._prefabList && this._prefabList.length){
                const a = this._prefabList.pop();
                a.destroy(true);
            }
            while(this._obsPrefabList && this._obsPrefabList.length){
                const o = this._obsPrefabList.pop();
                o.destroy(true);
            }
            this._prefabList = null;
            this._obsPrefabList = null;

            this._btnChange.off("click", this, this.onChangeHandler);
            this._btnOK.off("click", this, this.onOKHandler);
            this._btnContinue.off("click", this, this.onContinue);
            if(this._btnContinue4) this._btnContinue4.off("click", this, this.onContinue);
            if(this._btnCool) this._btnCool.off("click", this, this.btnCool);
            this._btnShare.off("click",RecordVideo,RecordVideo.stopRecordVideo)
            this._view.btnPhotoShare.off("click",RecordVideo,RecordVideo.stopRecordVideo)
            this._view.btnRefresh.off("click",this,this.refreshName)
            this.clearWorld();
            this.clearWorldCache();

            this._tilingOffset = null;
            this._worldList = null;
            this._worldCache = null;
            this._prefabNameList = null;

            this._btnChange.clear();
            this._btnOK.clear();
            this._btnContinue.clear();
            this._btnShare.clear();

            this._btnChange = null;
            this._btnOK = null;
            this._btnContinue = null;
            this._btnShare = null;


            this._view.cirAni.mask = null;
            this._view.destroy(true);
            this._view = null;
            this._worldCollider = null;

            if(this._circleMask) this._circleMask.destroy();
            this._circleMask = null;
            this._fence = null;
            moduleBridge.warning = null;
            this.stopFrameLoop();

            if(this._bg) {
                // this._bg.removeSelf();
                this._bg.destroy(true);
            }
        } else if(this._sceneName === "zoo") {
            if(this._teachView) {
                this._teachView.b1.offAllCaller(this);
                this._teachView.b2.offAllCaller(this);
                this._teachView.b3.offAllCaller(this);
                this._teachView.b4.offAllCaller(this);
                this._teachView.c1.offAllCaller(this);
                this._teachView = null;
            }
        }
    }

