    private refreshName():void{
        this._buffalo.refreshName()
        let names = Laya.loader.getRes("cfg/cfgNickname.json")
        let name_sex = userData.nickName[101]
        let nickname:string;
        if (name_sex && name_sex[0] != null && name_sex[1] != null){
            nickname = names[name_sex[0]][name_sex[1]]
        }
        this._view.animalName0.text = this._view.animalName1.text = "披风野牛"
        this._view.animalPicName.text = nickname;
    }

