    private _worldList:Array<Sprite3D>;
    private _worldCache:Array<Sprite3D>;
    private _worldEndZ:number;
    private _worldCollider:Array<Sprite3D>;
    private createWorld():void {
        if(!this._worldList) this._worldList = [];
        //右边： 42 + 16， y = 180, x=-10
        //左边： 26 + 16， y = 0, 10
        if(!this._worldCache) this._worldCache = [];
        if(!this._worldCollider) this._worldCollider = [];
        // this.clearWorld(false);
        this._worldEndZ = 0;
        let r,l,c,o;
        for (let i = 0; i < 9; i++) {
            r = Sprite3D.instantiate(this._prefabList[Math.floor(Math.random()*this._prefabList.length)], this._scene);
            l = Sprite3D.instantiate(this._prefabList[Math.floor(Math.random()*this._prefabList.length)], this._scene);
            r.transform.localPosition = ZY.Temp.getVector3(-10,0, 26+this._worldEndZ*16+16);
            r.transform.localRotationEulerY = 180;
            l.transform.localPosition = ZY.Temp.getVector3(10,0, 10+this._worldEndZ*16+16);
            l.transform.localRotationEulerY = 0;

            c = Sprite3D.instantiate(this._colliderPrefab, this._scene);
            c.transform.localPosition = ZY.Temp.getVector3(0,0, 10+this._worldEndZ*16+16);
            ZY.setCollisionGroup(c, CollisionGroup.WORLD_SIDE, CollisionGroup.ANIMAL|CollisionGroup.PLAYER);
            o = Sprite3D.instantiate(this._obsPrefabList[Math.floor(Math.random()*this._obsPrefabList.length)], c);
            o.transform.localPosition = ZY.Temp.getVector3();
            ZY.setCollisionGroup(o, CollisionGroup.WORLD_SIDE, CollisionGroup.ANIMAL|CollisionGroup.PLAYER);

            // ZY.disableCollider(c);
            
            this._worldCollider.push(c);

            this._worldList.push(l);
            this._worldList.push(r);

            this._worldEndZ += 1;
        }
    }

    private clearWorld(destroy:boolean = true):void {
        while(this._worldList && this._worldList.length > 0) {
            const world = this._worldList.pop();
            if(world) {
                world.removeSelf();
                if(destroy) {
                    world.destroy(true);
                } else {
                    this._worldCache.push(world);
                }
            }
        }
        while(this._worldCollider && this._worldList.length>0) {
            const c = this._worldCollider.pop();
            if(c) {
                c.removeSelf();
                if(destroy) {
                    c.destroy(true);
                }
            }
        }
    }

    private resetWorld(worldz:number = -3):void {
        let r,l,c;
        this._worldEndZ = worldz;
        for (let i = 0; i < this._worldList.length/2; i++) {
            l = this._worldList[i*2];
            r = this._worldList[i*2+1];
            r.transform.localPosition = ZY.Temp.getVector3(-10,0, this._worldEndZ*16+26+16);
            l.transform.localPosition = ZY.Temp.getVector3(10,0, this._worldEndZ*16+10+16);
            c = this._worldCollider[i];
            c.transform.localPosition = ZY.Temp.getVector3(0,0, this._worldEndZ*16+10+16+7);
            (c.getChildAt(0) as Sprite3D).transform.localPositionX = 35;
            (c.getChildAt(1) as Sprite3D).transform.localPositionX = -35;
            (c.getChildAt(2) as Sprite3D).active = false;
            this._worldEndZ += 1;
        }
        if(!this._bg) {
            this._scene.addChild(this._bg);
        }
        (this._scene.getChildByName("FenceOnly") as Sprite3D).transform.localPosition = ZY.Temp.getVector3(200,0,0);

        // console.log("this._worldEndZ="+this._worldEndZ); //234
    }

    private clearWorldCache():void {
        while(this._worldCache && this._worldCache.length > 0) {
            const world = this._worldCache.pop();
            if(world) {
                world.destroy(true);
            }
        }
    }

    private checkCreateWorld():void {
        // 234
        let i = Math.floor(this._player.transform.localPositionZ/16)+9;
        if(i>this._worldEndZ+4) {
            let cnt = i - this._worldEndZ-4;
            // console.log("i="+i+",z="+this._worldEndZ+",cnt="+cnt);
            let j,r;
            for (let j = 0; j < cnt; j++) {
                let l = this._worldList.shift();
                let r = this._worldList.shift();
                let c = this._worldCollider.shift();
                // l.removeSelf();
                // r.removeSelf();
                this._worldList.push(l);
                this._worldList.push(r);
                this._worldCollider.push(c);
                if(this.status > 7) {
                    r.transform.localPosition = ZY.Temp.getVector3(-24,0, this._worldEndZ*16+26+16);
                    l.transform.localPosition = ZY.Temp.getVector3(24,0, this._worldEndZ*16+10+16);
                    (c.getChildAt(0) as Sprite3D).transform.localPositionX = 50;
                    (c.getChildAt(1) as Sprite3D).transform.localPositionX = -50;
                    (c.getChildAt(2) as Sprite3D).active = true;
                } else {
                    r.transform.localPosition = ZY.Temp.getVector3(-10,0, this._worldEndZ*16+26+16);
                    l.transform.localPosition = ZY.Temp.getVector3(10,0, this._worldEndZ*16+10+16);
                    (c.getChildAt(0) as Sprite3D).transform.localPositionX = 35;
                    (c.getChildAt(1) as Sprite3D).transform.localPositionX = -35;
                    (c.getChildAt(2) as Sprite3D).active = false;
                }
                c.transform.localPosition = ZY.Temp.getVector3(0,0, this._worldEndZ*16+10+16+7);
                this._worldEndZ += 1;
            }

            if(this._bg.parent) {
                this._bg.removeSelf();
            }
        }
    }

