    private _skinIndexList = [1,2,3,4,5];
    private _changeHat:boolean = false;
    private onChangeHandler(e:Laya.Event):void {
        if(this.status != 2) return;
        // 0.0625
        if (ZY.channel == "oppo" && !this._changeHat){
            this._changeHat = true
            // reporter.upload(ReporterPoints.click_changeRole)
        } else if (ZY.channel === "bd" && !this._changeHat) {
            this._changeHat = true;
            ZY.reportEvent("click_changeRole");
        }
        // let index = Math.floor(Math.random()*16);
        this._skinIndex = (this._skinIndex + 1)%this._skinIndexList.length;
        this._sex = this._sex%2 + 1
        this.changeSex(this._sex)
        // this._tilingOffset.z = this._skinIndex * 0.0625;
        // this._meterial.tilingOffset = this._tilingOffset;
        Laya.Tween.clearAll(this._player.transform);
        this._player.transform.localRotationEulerY = this._player.transform.localRotationEulerY%360;
        Laya.Tween.to(this._player.transform,{localRotationEulerY:720},1000,Laya.Ease.cubicOut, Laya.Handler.create(this, ()=>{
            this._player.transform.localRotationEulerY = 0;
            // Laya.timer.clearAll(this);
        }));
        Laya.timer.clearAll(this);
        let i = 0;
        Laya.timer.loop(200, this, ()=>{
            i++;
            if(i>=3) {
                this._tilingOffset.z = this._skinIndexList[this._skinIndex] * 0.0625;
                // console.log(this._skinIndex);
                Laya.timer.clearAll(this);
            } else {
                this._tilingOffset.z = this._skinIndexList[((this._skinIndex+i)%this._skinIndexList.length)] * 0.0625;
            }
            this._meterial.tilingOffset = this._tilingOffset;
        });

        ZY.sound.playSound("audio/UI_Randomize.mp3", 1, true);
    }

    private changeSex(sex:number):void {
        if (sex == 1) {    
            userData.sex = 1
            this._chest.active = false
            this._hair.active = false
        } else if (sex == 2) {
            userData.sex = 2
            this._chest.active = true
            this._hair.active = true
        }
    }

