    private deadSlow():void{
        Laya.timer.scale = 0.1
        Laya.timer.once(150, this, () => {
            Laya.timer.scale = 1;
            if (this.status == 8){
                Laya.timer.once(1500, this,() =>{
                    this._playerAni.play("riding");
                    this._player.state = PlayerState.riding;

                    this._camNode.localPosition = ZY.Temp.getVector3(-2,0,9.494545);
                    this._cam.fieldOfView = 35

                    this._buffalo.transform.localPosition = ZY.Temp.getVector3(0,0,-0.505455);
                    this._buffalo.ride(false)
                    this._buffalo.playRun();
                    this._buffalo.speed = 23

                    this._horse.destroy();

                    this._warnInfo.showHeart(8, this, this.catchOne);
                    this._player.transform.localRotationEulerY = this._buffalo.transform.localRotationEulerY = 0;
                    this.resetWorld();

                    this._view["page8"].visible = true;
                    this._view.ani8.pos(Math.floor(Laya.stage.width/ ZY.stageScale / 2), Laya.stage.height / ZY.stageScale -300,true);
                    this._view.tapCir8.pos(this._view.ani6.x, this._view.ani6.y);
                    (this._scene.getChildByName("FenceOnly") as Sprite3D).transform.localPosition = ZY.Temp.getVector3(0,0,this._worldEndZ*16+16+10);
                })
            }else{
                Laya.timer.once(1500, this, this.changeStatus, [4]);
            }

        })
    }

    public crash():void {
        if(this.status >0 && this.status < 10) {
            if (ZY.channel == "wx" || ZY.channel == "qq"){
                ZY.TmCtrl.instance.tmSendEvent("tutorial_dead",{value:this.status-5})
            }else if (ZY.channel == "zj"){
                ZY.reportEvent("tutorial_dead",{value:this.status-5})
            }
            if(this._view["page"+this.status]) this._view["page"+this.status].visible = false;
        }
        this.deadSlow();
    }

