    public ride():void {
        if(this.status == 6) {
            this.changeStatus(7);
            if (ZY.channel == "wx" || ZY.channel == "qq"){
                ZY.TmCtrl.instance.tmSendEvent("tutorial_1",{})
            }else if (ZY.channel == "zj"){
                ZY.reportEvent("tutorial_1",{})
            }else if (ZY.channel == "oppo"){
                reporter.upload(ReporterPoints.tutorial_1)
            }
        } else if(this.status == 7) {
            this._horse.speed = 0;
            this.changeStatus(8);
            this._warnInfo.showHeart(8, this, this.catchOne);
            if (ZY.channel == "wx" || ZY.channel == "qq"){
                ZY.TmCtrl.instance.tmSendEvent("tutorial_2",{})
            }else if (ZY.channel == "zj"){
                ZY.reportEvent("tutorial_2",{})
            }else if (ZY.channel == "oppo"){
                reporter.upload(ReporterPoints.tutorial_2)
            } else if (ZY.channel === "bd") {
                ZY.reportEvent("tutorial_2");
            }
            // console.log("status="+this.status);
        }else if(this.status == 8) {
            this._buffalo.transform.localRotationEulerY = 0
        }
    }

