import AnimalThrower from "./AnimalThrower";
import Helper2D from "../Helper2D";
import Animal from "./Animal";
import GameObject = Laya.Sprite3D;
import { moduleBridge } from "../Interface";
import { UpgradeKeys } from "../UpgradeKeys";

export default class Giraffe extends AnimalThrower {
	private stumbleTimer:number = 0;
	private stumbleTimerMax:number = 4;
	private baseRunSpeedModifier:number;

	// public EffectsEvent halfCrashEffectPrefab;

    public init(id:string, obj:GameObject):void{
		super.init(id, obj)
        this.baseRunSpeedModifier = this.runSpeedModifier;
		this.stumbleTimer = 0;
	}

	public onUpdate ()
	{
		super.onUpdate ();
		if (this.stumbleTimer > 0) {
			// if (anim.GetLayerWeight(animLayer3DOverride) != 1) {
			// 	anim.SetLayerWeight(animLayer3DOverride, 1);
			// }
			if (this.angerStage < 2) {
				if (this.stumbleTimer > this.stumbleTimerMax - 0.05) {
					this.runSpeedModifier = this.baseRunSpeedModifier * 1.75;
				} else {
                    this.runSpeedModifier = this.baseRunSpeedModifier * Helper2D.Map(this.stumbleTimer / this.stumbleTimerMax, 1, 0, 0.65, 1);
				}
			}
		} else {
			// if (anim.GetLayerWeight(animLayer3DOverride) != 0) {
			// 	anim.SetLayerWeight(animLayer3DOverride, 0);
			// }
			if (this.angerStage < 2) {
				this.runSpeedModifier = this.baseRunSpeedModifier;
			}
		}
		this.stumbleTimer -= Laya.timer.delta/1000

	}

	protected animalCrash (otherAnimal:Animal)
	{
		// let special = 0
		let special = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.special)

		if (special > 0 && this.stumbleTimer <= (this.stumbleTimerMax * 0.25) && otherAnimal.smashStrength <= this.smashStrength) {
			this.stumbleTimer = this.stumbleTimerMax;
			this.smashAnimal(otherAnimal);
			// if (halfCrashEffectPrefab != null) {
			// 	halfCrashEffectPrefab.Spawn(transform.position, halfCrashEffectPrefab.transform.rotation).TriggerEffect();
			// }
		} else {
			super.animalCrash(otherAnimal);
		}
	}
}
