import { ZY } from "./ZY";
import { ui } from "./ui/layaMaxUI";
import { moduleBridge } from "./Interface";
import { reporter, ReporterPoints } from "./Reporter";

export default class LoadingPage{
    constructor() {

    }
    private _loadingPage:ui.main.LoadingUI;
    private _mujiUi:Laya.Image;
    /**
     * 初始化界面，当preload加载完成后会调用该方法
     */
    public init():void {
        this._loadingPage = new ui.main.LoadingUI();
        if(ZY.channel == ZY.Channel.zj ) {
            // this._mujiUi = new Laya.Image();
            // this._mujiUi.skin = "preload/black.png";
            // this._mujiUi.sizeGrid = "1,1,1,1"
            // let img = new Laya.Image();
            // img.skin = "preload/muji.png";
            // img.centerX = 0
            // img.centerY = -200
            // this._mujiUi.addChild(img)
        }else if(ZY.channel == "vivo" || ZY.channel == "oppo" || ZY.channel == "none"){
            this._mujiUi = new Laya.Image();
            this._mujiUi.skin = "preload/white.png";
            this._mujiUi.sizeGrid = "1,1,1,1"
            let img = new Laya.Image();
            img.skin = "preload/oppo_vivo.png";
            img.centerX = 0
            img.centerY = -200
            this._mujiUi.addChild(img)
        }
    }
    public showMuJi():void{
        if ((ZY.channel === "zj" || ZY.channel == "vivo" || ZY.channel == "none") && this._mujiUi){
            this._mujiUi.mouseEnabled = true;
            this._mujiUi.on(Laya.Event.MOUSE_DOWN,this,(e:Laya.Event)=>{
                e.stopPropagation()
            })
            this._mujiUi.on("click",this,(e:Laya.Event)=>{
                e.stopPropagation()
            })
            Laya.timer.once(3*1000,this,()=>{
                this._mujiUi.removeSelf();
                this._mujiUi.destroy(true);
                this._mujiUi = null;
            })
            this._mujiUi.size(Laya.stage.width, Laya.stage.height);
            // if (ZY.channel == "oppo"){
            //     reporter.upload(ReporterPoints.yx_logo,1)
            // }
            Laya.stage.addChild(this._mujiUi);
        }
    }
    /**
     * 
     * @param style loading页面的样式。
     * @param caller 如果用户点击关闭取消的回调函数caller
     * @param cancelCallback 如果用户点击关闭取消的回调函数
     * @returns 返回该loading页面本身
     */
    public show(style:ZY.LoadingUIStyle, caller:any, cancelCallback:Function,sceneName:string):Laya.Sprite {
        this._loadingPage.pro.scaleX = 0;
        // this._loadingPage.txt.text = "";
        if (ZY.stage.sceneName == "zoo" && sceneName == "game"){
            let taskData = moduleBridge.taskCtrl.getTaskInfo ();
            if (taskData && taskData.length > 0){
                let viewList = []
                this._loadingPage.dec0.text = taskData[0].dec;
                (this._loadingPage.complet0.getChildByName("difficult00") as Laya.Image).visible = false;
                (this._loadingPage.complet0.getChildByName("difficult01") as Laya.Image).visible = false;
                (this._loadingPage.complet0.getChildByName("difficult02") as Laya.Image).visible = false;
                let element = this._loadingPage.complet0.getChildByName("difficult0"+taskData[0].difficulty) as Laya.Image
                element.visible = true
                viewList.push(this._loadingPage.complet0)
                let pos = [null,[450],[600,750],[500,650,800]]
                for (let i = 0 ; i < viewList.length ; i ++){
                    viewList[i].visible = true
                    viewList[i].bottom = pos[viewList.length][i]
                }
                this._loadingPage.tishi.visible = true
            }
            this._loadingPage.pro1.centerY = NaN
            this._loadingPage.pro1.bottom = 150
            this._loadingPage.first.visible = true
            this._loadingPage.txt.visible = false
        }else {
            this._loadingPage.pro1.centerY = 0
            this._loadingPage.txt.visible = true
            this._loadingPage.tishi.visible = false
            this._loadingPage.complet0.visible =  false
        }
        this._loadingPage.checkTishi.visible = false
        console.log("scene name:"+ZY.stage.sceneName);
        if (ZY.stage.sceneName == "game" && sceneName == "zoo"){
            this._loadingPage.first.visible = true
            if (ZY.channel == "oppo"){
                ZY.ad.showQGbanner(0);
                this._loadingPage.pro1.bottom = ZY.ad.bannerHeight + 20
            } else if (ZY.channel == "zj") {
                ZY.ad.showTTBanner(0);
                this._loadingPage.pro1.bottom = ZY.ad.bannerHeight + 20;
            }
        }else if((ZY.stage.sceneName == "check" && sceneName == "zoo") || (ZY.stage.sceneName == "teach" && sceneName == "check")){
            this._loadingPage.first.visible = true
            this._loadingPage.checkTishi.visible = true
        }
        if (ZY.stage.sceneName !== "" && ZY.stage.sceneName !== "ZY_FIRST"){
            this._loadingPage.banhao.visible = false
            this._loadingPage.fcm.visible = false
            this._loadingPage.kfqq.visible = false
        }else{
            if (ZY.channel == "zj"){
                this._loadingPage.kfqq.visible = false
            }else{
                this._loadingPage.kfqq.y = ZY.stage.notchHeight;
            }

        }
        if (ZY.channel == "wx" && !this._first){
            ZY.ad.showCustomAd()
        }
        return this._loadingPage;
    }

    public setInfo(txt:string):void {
        this._loadingPage.txt.text=txt;
    }
    private _first:boolean = false;
    public setProgress(p:number): void {
        this._loadingPage.pro.scaleX = p;
        if (!this._first){
            if (p == 1){
                this._first = true
            }
            if (ZY.channel == "oppo"){
                qg.setLoadingProgress({
                    progress: p*100
                });
            }
        }
        if (this._loadingPage.txt.visible){
            p = Math.floor(p*100)
            if (ZY.channel == "oppo" || ZY.channel == "vivo"){
                this._loadingPage.txt.text = "警告：清除缓存会导致游戏数据全部丢失（"+ p +"/100）"
            }else{
                this._loadingPage.txt.text = "动物园开门营业的第356天，正在开门中（"+ p +"/100）"
            }
        }
    }

}