import { ZY } from "../ZY";
import { ui } from "../ui/layaMaxUI";
import ZooButton from "../script/ZooButton";
// import { shipCtrl } from "../ship/ShipCtrl";
import { userData } from "../userData/UserData";
import { moduleBridge } from "../Interface";
import { reporter, ReporterPoints } from "../Reporter";
import { nativeAd } from "../NativeAd";

export default class PrizeCtrl extends ZY.BaseCtrl {
    constructor() { super(); }
    
    public start():void {
        super.start();
        // this.bindAssets("res/atlas/prize.atlas", Laya.Loader.ATLAS, "zoo");
    }

    protected beforeEnterScene(sceneName:string):void {
        if(this._sceneName == "zoo") {
            Laya.timer.clearAll (this);
            Laya.Tween.clearAll (this);
            this._btnStart.off("click", this, this.startRing);
            this._btnBack.off("click", this, this.onBack);
            this._btnGift.off("click", this, this.showGift);
            this._btnClose.off("click", this, this.closeGift);
            this._btnQues.off("click", this, this.showQues);
            this._btnSure.off("click", this, this.onSure);
            this._btnSure2.off("click", this, this.onSure);
            this._btnRedraw.off("click", this, this.reDraw);

            this._view.off (Laya.Event.CLICK, this, this.onClick);

            this._btnStart.clear();
            this._btnStart = null;
            this._btnBack.clear();
            this._btnBack = null;
            this._btnGift.clear();
            this._btnGift = null;
            this._btnClose.clear();
            this._btnClose = null;
            this._btnQues.clear();
            this._btnQues = null;
            if (ZY.channel == "oppo") {
                nativeAd.clearAd("207985");
            }else if (ZY.channel == "vivo"){
                nativeAd.clearAd("abea873d8eef4a7d904c27e005acebea");
            }
            this._view.removeSelf();
            this._view.destroy(true);
            this._view = null;
            this._flag = null;

            
        }
    }

    private _view:ui.main.PrizeUI
    private _btnStart:ZooButton;
    private _btnBack:ZooButton;
    private _btnQues:ZooButton;
    private _btnGift:ZooButton;
    private _btnClose:ZooButton;
    private _btnSure:ZooButton;
    private _btnSure2:ZooButton;
    private _btnRedraw:ZooButton;
    protected createChildren(sceneName:string):void {
        if(sceneName == "zoo") {
            if(!this._view) {
                this._view = new ui.main.PrizeUI();
                this._btnBack = new ZooButton(this._view.btnBack);
                this._btnClose = new ZooButton(this._view.btnClose);
                this._btnStart = new ZooButton(this._view.btnStart);
                this._btnSure = new ZooButton(this._view.btnSure);
                this._btnSure2 = new ZooButton(this._view.btnSure2);
                this._btnRedraw = new ZooButton(this._view.btnRedraw);
                this._btnGift = new ZooButton(this._view.btnGift, null, "main", -40, -30);
                this._btnQues = new ZooButton(this._view.btnQues, null, "main", -20, -10);
            }
            this._btnStart.on("click", this, this.startRing,[0]);
            this._btnBack.on("click", this, this.onBack);
            this._btnGift.on("click", this, this.showGift);
            this._btnClose.on("click", this, this.closeGift);
            this._btnQues.on("click", this, this.showQues);
            this._btnSure.on("click", this, this.onSure);
            this._btnSure2.on("click", this, this.onSure);
            this._btnRedraw.on("click", this, this.reDraw);
            this._view.on (Laya.Event.CLICK, this, this.onClick);

            if (ZY.channel == "vivo") {
                this._view.btnRedraw.visible = false;
                this._view.btnSure.left = 218;
            }

            
        }
    }


    private closeGift():void {
        this._view.box0.visible = true;
        this._view.box1.visible = false;
    }

    private showQues():void {
        if (this._view.imgGiftTip.visible) {
            this._view.imgGiftTip.visible = false;
            return;
        }
        this._view.box1.visible = true;
        this._view.box1.bottom = ZY.ad.bannerHeight + 5
        this._view.box0.visible = false;
    }

    private showGift():void {
        this._view.imgGiftTip.visible = !this._view.imgGiftTip.visible;
    }
    
    private startPointer():void {
        let pointer = this._view.imgPointer as Laya.Image;
        let random:number = Math.floor(Math.random () * 30);
        // pointer.rotation = random;
        Laya.Tween.to (pointer, {rotation: random}, 50, null, null, 0, false);
    }

    private _isRing:boolean = false;
    private startRing2(done:boolean,param ?:Array<any>):void{
        if (done){
            userData.setDailyTask(6,1)
            if (this._isRing) return;

            this.initTimes ();
    
            this._isRing = true;
    
            this._view.box2.visible = false;
            
            this._data = null;
            // 指针动画
            let pointer = this._view.imgPointer as Laya.Image;
            Laya.Tween.to (pointer, {rotation: 35}, 300, Laya.Ease.quartIn, null, 300, false);
            Laya.timer.once (300, this, ()=>{ Laya.timer.loop (100, this, this.startPointer); });
            
            let img = this._view.img_ring as Laya.Image;
            let r = img.rotation;
            // test
            let random = Math.random ();
            let t;
            if (random < 0.03) {
                t = 0;
            } else if (random < 0.11) {
                t = 1;
            } else if (random < 0.19) {
                t = 2;
            } else if (random < 0.47) {
                t = 3;
            } else if (random < 0.73) {
                t = 4;
            } else {
                t = 5;
            } 
    
            let test = t * 60;
            let add:number = 6 * 360 + test;
            let newR = r - add;
            Laya.timer.once (2700, this, ()=>{ Laya.timer.clear (this, this.startPointer); })
            Laya.Tween.to (pointer, {rotation: 0}, 300, Laya.Ease.quartIn, null, 2500, true);
            Laya.Tween.to (img, {rotation:newR * 1}, 3000, Laya.Ease.quartInOut, Laya.Handler.create (this, ()=>{
                this._isRing = false;
                userData.setDailyTask(5, 1);
                // let times = userData.get ("prizeTimes", 0);
                // times ++;
                // if (times >= 5) {
                //     times = 0;
                //     // ZY.msg.show ("获得礼包");
                //     userData.addJew (100, 1005);
                //     let addGold = userData.touristTicket[3] * 2;
                //     if (addGold < 500) addGold = 500;
                //     userData.addGold (addGold, 1011);
                // }
                // userData.set ("prizeTimes", times);
    
                this.updateBox2 ();
                this.updateTimes ();
            }), 200, false);
            if (param && param[0] == 0) {
                if (ZY.channel == "wx" || ZY.channel == "qq"){
                    ZY.TmCtrl.instance.tmSendEvent("ad_wheel",{value:"完成"})
                }else if (ZY.channel == "zj"){
                    ZY.reportEvent("ad_wheel",{value:"完成"})
                }else if(ZY.channel == "oppo"){
                    // reporter.upload(ReporterPoints.ad_wheel,4)
                }
            }
            if (param && param[0] == 1) {
                if (ZY.channel == "wx" || ZY.channel == "qq"){
                    ZY.TmCtrl.instance.tmSendEvent("ad_wheelAgain",{value:"完成"})
                }else if (ZY.channel == "zj"){
                    ZY.reportEvent("ad_wheelAgain",{value:"完成"})
                }else if (ZY.channel == "oppo"){
                    // reporter.upload(ReporterPoints.ad_wheelAgain,4)
                }
            }
        } 
        else {
           moduleBridge.videoTipCtrl.showTips();
        }
        if (ZY.channel == "vivo"){
            ZY.sound.playMusic("audioGame/Music_Zoo.mp3");
        }
    }

    private startRing(index):void {
        if (ZY.channel == "wx"){
            if (index == 0){
                ZY.ad.showVideoAd(this, this.startRing2,ZY.wxAd.prize,"ad_wheel",[index]);
            }else if(index == 1){
                ZY.ad.showVideoAd(this, this.startRing2,ZY.wxAd.redraw,"ad_wheelAgain",[index]);
            }
        }else if (ZY.channel == "zj"){
            if (index == 0){
                ZY.ad.showVideoAd(this, this.startRing2,0,"ad_wheel",[index]);
            }else if(index == 1){
                ZY.ad.showVideoAd(this, this.startRing2,0,"ad_wheelAgain",[index]);
            }
        }else if (ZY.channel == "vivo"){
            if (index == 0){
                ZY.ad.showVivoVideo(this, this.startRing2,ZY.vivoAd.prize,"ad_wheel",[index]);
            }else if(index == 1){
                ZY.ad.showVivoVideo(this, this.startRing2,ZY.vivoAd.redraw,"ad_wheelAgain",[index]);
            }
        }else if(ZY.channel == "oppo"){
            if (index == 0){
                // reporter.upload(ReporterPoints.ad_wheel,3)
                ZY.ad.showOPPOVideo(this, this.startRing2 ,ReporterPoints.ad_wheel,[index]);
            }else if(index == 1){
                // reporter.upload(ReporterPoints.ad_wheelAgain,3)
                ZY.ad.showOPPOVideo(this, this.startRing2 ,ReporterPoints.ad_wheelAgain,[index]);
            }
        }else if (ZY.channel == "qq" || ZY.channel == "bd"){
            if (index == 0){
                ZY.ad.showVideoAd(this, this.startRing2,0,"ad_wheel",[index]);
            }else if(index == 1){
                ZY.ad.showVideoAd(this, this.startRing2,0,"ad_wheelAgain",[index]);
            }
        } 
    }

    private _resultData = {
        "0": { "type":0, label:"大礼包", time:0, "during":0, icon:"prize/img_logo_1.png" },
        "-60": { "type":1, label:"从800米处开始起跑", time:600, "during":60, icon:"prize/img_logo_4.png" },
        "-120":{ "type":2, label:"奔跑中可免费复活一次", time:300, "during":300, icon:"prize/img_logo_5.png" },
        "-180":{ "type":3, label:"驯服动物时间减少30%", time:600, "during":600, icon:"prize/img_logo_3.png" },
        "-240":{ "type":4, label:"奔跑中拾取2倍金币", time:300, "during":300, icon:"prize/img_logo_2.png" },
        "-300":{ "type":5, label:"稀有动物出现几率提高300%", time:600, "during":600, icon:"prize/img_logo_0.png" }
    }
    private _time:number;
    private _data:any;
    private updateBox2():void {
        if (!this._view) return;
        this._view.imgMask.height = Laya.stage.height / ZY.stageScale;
        let img = this._view.img_ring as Laya.Image;
        // console.log ("[for test]rotation", img.rotation);
        let s = (img.rotation % 360).toString ();
        // this._data = this._resultData[s];
        this._data = this._resultData[s]
        this._view.labelResult.text = this._data.label;
        if (this._data.time > 0) {
            this._time = this._data.during;
            this.timeOutLoop ();
            Laya.timer.loop (1000, this, this.timeOutLoop);
            this._data.time = Laya.Browser.now () + this._data.during * 1000;
            this._view.labelTime.visible = true;
        }else{
            this._view.labelTime.visible = false;
        }
        this._view.imgIcon.skin = this._data.icon;

        this._view.box2.visible = true;
        if (this._flag){
            this._view.youcant.visible = true
            this._view.youCan.visible = false
        }else{
            this._view.youcant.visible = false
            this._view.youCan.visible = true
            if (ZY.channel == "wx" || ZY.channel == "qq"){
                ZY.TmCtrl.instance.tmSendEvent("ad_wheelAgain",{value:"曝光"})
            }else if (ZY.channel == "zj"){
                ZY.reportEvent("ad_wheelAgain",{value:"曝光"})
            }else if (ZY.channel == "oppo"){
                // reporter.upload(ReporterPoints.ad_wheelAgain,2)
            }
        }
    }

    private _flag:boolean = false;
    private reDraw():void{
        this._flag = true
        this.startRing(1)
    }



    private onSure():void{
        // 数据变化
        let todayTimes = userData.get ("todayPrizeTimes", 3);
        if (todayTimes <= 0) {
            ZY.msg.show ("今日次数已用尽");
            return;
        }
        todayTimes --;
        userData.set ("todayPrizeTimes", todayTimes);
        if (this._data.type == 0){
            userData.addJew(100,1005)
        }else{
            userData.buff = this._data;
        }

        moduleBridge.shipCtrl.backToShip("prize");
        this._view.box2.visible = false;
        this._view.removeSelf();
        ZY.ad.hideBanner()

    }

    private timeOutLoop() {
        let label = this._view.labelTime as Laya.Label;
        let m = Math.floor (this._time / 60).toString ();
        let s = Math.floor (this._time % 60).toString ();
        m = m.length > 1 ? m : "0" + m;
        s = s.length > 1 ? s : "0" + s;
        label.text = "持续时间：" + m + ":" + s;
        this._time --;
        if (this._time < 0) {
            Laya.timer.clear (this, this.timeOutLoop);
        }
        
    }

    private updateTimes():void {
        let curNum:number = userData.get ("prizeTimes", 0);
        let maxNum:number = 5;
        this._view.imgGift.skin = "prize/lucky_gift_5.png";
        this._view.imgGiftBar.width = 475 / 5 * curNum;
        this._view.labelGift.text = curNum + "/" + maxNum;
    }

    private updateView():void {
        if (ZY.channel == "oppo") {
            nativeAd.showAsBanner2("207985", this._view.oppoActive);
        } else if (ZY.channel == "vivo") {
            nativeAd.showAsBanner2("abea873d8eef4a7d904c27e005acebea", this._view.oppoActive);
        }

        
        let curNum:number = userData.get ("prizeTimes", 0);
        let maxNum:number = 5;
        this._view.imgGift.skin = "prize/lucky_gift_5.png";
        this._view.imgGiftBar.width = 475 / 5 * curNum;
        this._view.labelGift.text = curNum + "/" + maxNum;
        this._view.imgGiftTip.visible = false;
        this._view.box0.visible = true;
        this._view.box1.visible = false;
        this.ringLiang();
        let img = this._view.img_ring as Laya.Image;
        img.rotation = 0;
        if (ZY.channel == "wx" || ZY.channel == "qq"){
            ZY.TmCtrl.instance.tmSendEvent("ad_wheel",{value:"曝光"})
        }else if (ZY.channel == "zj"){
            ZY.reportEvent("ad_wheel",{value:"曝光"})
        }else if(ZY.channel == "oppo"){
            // reporter.upload(ReporterPoints.ad_wheel,2)
        }
        this.initTimes ();

        if (ZY.channel !== "oppo"){
            if (ZY.channel == "vivo"){
                //ZY.ad.showQGbanner(ZY.vivoBannerAd.prize)
            }else{
                if (ZY.channel == "zj"){
                    if ((window as any).tt.getSystemInfoSync().appName == "Douyin"){
                        this._view.box0.centerY = 0
                    }else{
                        ZY.ad.showTTBanner(0)
                        this._view.box0.bottom = ZY.ad.bannerHeight + 5
                    }
                }else{
                    ZY.ad.showTTBanner(0)
                    this._view.box0.bottom = ZY.ad.bannerHeight + 5
                }
            }
        }
    }

    private initTimes():void {
        let lastTime = userData.get ("lastFreshPrizeTime", 0);
        let now = Laya.Browser.now ();
        let dateNow = new Date (now);
        let dateLast = new Date (lastTime);
        let times = userData.get ("todayPrizeTimes", 3);
        if (dateNow.setHours (0,0,0,0) != dateLast.setHours (0,0,0,0)) {
            userData.set ("lastFreshPrizeTime", now);
            userData.set ("todayPrizeTimes", 3);
        }
        this._view.labelTimes.text = times + "";
    }

    private ringLiang():void {
        for (let i:number = 0; i < 6; i++) {
            let img = this._view["imgGuang" + i] as Laya.Image;
            img.rotation = 0;
            Laya.Tween.to (img, {rotation: -360}, 12000, null, null, 0, false);
        }
        Laya.timer.once (12000, this, this.ringLiang);
    }
    
    private onClick(e:Laya.Event):void {
        let target = e.target;
        // console.log (target);
        if (this._view.imgGiftTip.visible) {
            this._view.imgGiftTip.visible = false;
            return;
        }
    }

    private onGameStart():void {
        this._view.visible = false;
        moduleBridge.shipCtrl.startGame ();
    }

    private onBack():void {
        ZY.ad.hideBanner()

        moduleBridge.shipCtrl.backToShip("prize");
        this._view.box2.visible = false;
        this._view.removeSelf();
    }

    public showPrize():void {
        if(this._sceneName == "zoo") ZY.stage.addChild(this._view, ZY.Layer.MIDDLE);
        this._flag = false;
        Laya.timer.callLater(this, this.updateView)
    }

}

// export const mapCtrl = new MapCtrl();
moduleBridge.prizeCtrl = new PrizeCtrl();