import { userData } from "../userData/UserData";
import ZooAnimal from "./ZooAnimal";
// import { shipCtrl } from "../ship/ShipCtrl";
import { ZY } from "../ZY";
import { moduleBridge } from "../Interface";

export default class AnimalMoveCtrl extends ZY.BaseCtrl {    
    constructor() {
        super();
    }

    public start():void {
        super.start();
        this.bindAssets3D("game/Conventional/buffalo6.lh", "zoo");
        this.bindAssets3D("game/Conventional/reviveBird.lh", "zoo");
        this.bindAssets3D("game/Conventional/luckyLamb.lh", "zoo");
        this.bindAssets3D("game/Conventional/zebra6.lh", "zoo");
    }

    protected beforeEnterScene(sceneName:string):void {
        // this._animalList[key].push(animal);
        
        if(sceneName == "zoo") {
            let enclosure = userData.enclosure;
            for (const encID in enclosure) {
                if (enclosure.hasOwnProperty(encID)) {
                    const animals = userData.getAnimals(parseInt(encID));
                    if(animals) {
                        for (let i = 0; i < animals.length; i++) {
                            const info = userData.cfgAnimal[animals[i]];
                            if(info && info.res) {
                                this.bindAssets3D(info.res, "zoo");
                            }
                        }
                    }
                }
            }
        }
        if(this._sceneName == "zoo") {
            if(this._animalList) {
                for (const key in this._animalList) {
                    if (this._animalList.hasOwnProperty(key)) {
                        const arr = this._animalList[key];
                        while(arr.length > 0) {
                            const animal = arr.pop() as ZooAnimal;
                            if(animal) {
                                animal.clear();
                            }
                        }
                    }
                }
                this._animalList = null;;
            }
            this._json = null
            this._animalPath = null
            Laya.timer.clearAll(this);
            ZY.eventBus.off("TASK_COMPLETE",this,this.newBuyanimal)
        }
    }

    private _animalList;
    private _json = {}
    protected createChildren(sceneName:string):void {
        if(sceneName == "zoo") {
            let enclosure = userData.enclosure;
            this._animalList = [];
            this._json = {}
            for (const key in enclosure){
                if(enclosure.hasOwnProperty(key)) { //写for in的时候，取得的key必须加这个判断，不然可能取到的key不定义是合法的。
                    const animals = userData.getAnimals(parseInt(key)); //for循环内的变量都用const，可以防止每次都申请内存，或者把变量申明放for外面。
                    //注意：const声明的常量是不能再次赋值或者修改的
                    if(animals.length > 0) {
                        this._json[key] = animals.concat(); //不能直接赋值，必须拷贝一份，防止用户数据的内容被更改。
                    }
                }
            }
            //动物们都滚出来吧
            for (const key in this._json){
                if(this._json.hasOwnProperty(key)) {
                    for (let i = 0 ; i < this._json[key].length ; i ++){
                        let cfg = userData.cfgAnimal[this._json[key][i]];
                        ZY.load ([cfg.res], this, ()=>{
                            let animal = new ZooAnimal();
                            animal.reset(this._json[key][i]);
                            this._animalList[key] = this._animalList[key] || []
                            this._animalList[key].push(animal);
                        })
                    }
                }
            }
            ZY.eventBus.on("CATCH_ANIMAL",this,this.newBuyanimal)
        }
    }

    public animalDance(animalId:number):void{
        // (this._animalList[1001][1] as ZooAnimal).dance()
        for (const key in this._animalList) {
            if (this._animalList.hasOwnProperty(key)) {
                const animals = this._animalList[key];
                for (let i = 0; i < animals.length; i++) {
                    const animal = animals[i] as ZooAnimal
                    if (animal.animalId == animalId){
                        animal.dance()
                        break
                    }
                }
            }
        }
    }

    private newBuyanimal(id:number):void{
        if (this._sceneName != "zoo") return
        let cfg = userData.cfgAnimal[id];
        let key = cfg.encId
        this._animalPath[key] = this._animalPath[key] || {}
        this._animalPath[key]["num"] = userData.getAnimals(key).length
        if (moduleBridge.shipCtrl.getEnclosure(Number(key)) == null) return
        if ((moduleBridge.shipCtrl.getEnclosure(Number(key)).getChildByName("zoo_homes") as Laya.Sprite3D) == null) return;
        let bornP = (moduleBridge.shipCtrl.getEnclosure(Number(key)).getChildByName("zoo_homes") as Laya.Sprite3D).transform
        this._animalPath[key]["born"] = [bornP.localPositionX,bornP.localPositionZ]
        let b = []
        let a = moduleBridge.shipCtrl.getEnclosure(Number(key)).getChildByName("spawnPoints") != null ? moduleBridge.shipCtrl.getEnclosure(Number(key)).getChildByName("spawnPoints") :moduleBridge.shipCtrl.getEnclosure(Number(key)).getChildByName("SpawnPoints")
        let daddy = (a as Laya.Sprite3D).transform.localPosition
        for (let i = 0; i < a.numChildren ; i ++){
            b.push(a.getChildAt(i))
        }
        this._animalPath[key]["pos"] = []
        for (let i = 0 ; i < b.length ; i ++){
            let x = b[i].transform.localPositionX + daddy.x
            let z = b[i].transform.localPositionZ + daddy.z
            this._animalPath[key]["pos"].push([true,x,z])
        }

        ZY.load ([cfg.res], this, ()=>{
            let animal = new ZooAnimal();
            animal.reset(id);
            this._animalList[key] = this._animalList[key] || []
            this._animalList[key].push(animal);
            let enclosure = moduleBridge.shipCtrl.getEnclosure(key);
            enclosure.addChild(animal);
            // animal.init(this._animalPath);
            for (const i in this._animalList[key]) {
                if (this._animalList[key].hasOwnProperty(i)) {
                    const element = this._animalList[key][i];
                    element.init(this._animalPath)
                }
            }
        })
    }

    public getAnimals(id:any):Array<ZooAnimal> {
        return this._animalList[id];
    }

    protected afterEnterScene(sceneName:string):void {
        if(sceneName == "zoo") {
            //let scene = shipCtrl.scene
            this.updatePath();
            for (let key in this._animalList){
                if (this._animalList.hasOwnProperty(key)){
                    for (let i = 0 ; i < this._animalList[key].length ; i ++){
                        Laya.timer.once(i*2000,this,()=>{
                            let enclosure = moduleBridge.shipCtrl.getEnclosure(Number(key));
                            let animal = this._animalList[key][i] as ZooAnimal;

                            enclosure.addChild(animal);
                            animal.init(this._animalPath);
                        }, null, false );
                    }
                }
            }
        }
    }

    private _animalPath:any;
    private updatePath():void{
        this._animalPath = {}
        for (let key in this._json){
            // let json = animalMoveCtrl[this._json][key]
            this._animalPath[key] = {}
            this._animalPath[key]["num"] = this._json[key].length
            let bornP = (moduleBridge.shipCtrl.getEnclosure(Number(key)).getChildByName("zoo_homes") as Laya.Sprite3D).transform
            this._animalPath[key]["born"] = [bornP.localPositionX,bornP.localPositionZ]
            let b = []
            let a = moduleBridge.shipCtrl.getEnclosure(Number(key)).getChildByName("spawnPoints") != null ? moduleBridge.shipCtrl.getEnclosure(Number(key)).getChildByName("spawnPoints") :moduleBridge.shipCtrl.getEnclosure(Number(key)).getChildByName("SpawnPoints")
            let daddy = (a as Laya.Sprite3D).transform.localPosition
            for (let i = 0; i < a.numChildren ; i ++){
                b.push(a.getChildAt(i))
            }
            this._animalPath[key]["pos"] = []
            for (let i = 0 ; i < b.length ; i ++){
                let x = b[i].transform.localPositionX + daddy.x
                let z = b[i].transform.localPositionZ + daddy.z
                this._animalPath[key]["pos"].push([true,x,z])
            }
        }
    }
}


// export const animalMoveCtrl = new AnimalMoveCtrl();
moduleBridge.animalMoveCtrl = new AnimalMoveCtrl();