export enum UpgradeOperations {
	add,
	subtract,
	multiply,
	divide,
	equals
}

export class UpgradeValue {
	protected modifiers:Array<UpgradeValueModifier> = [];

	protected _value:number;
	protected _defaultValue:number;
	protected _operation:UpgradeOperations;

	public constructor(operation:UpgradeOperations, defaultValue:number) {
		this._operation = operation;
		this._defaultValue = defaultValue;
		this._value = this._defaultValue;
    }

    public get defaultValue():number {
        return this._defaultValue;
    }

    public get operation():UpgradeOperations {
        return this._operation;
    }

    public get value():number {
        return this._value;
    }

    public apply (modifier:UpgradeValueModifier):void {
		if (modifier == null) {
			return;
        }

		switch (this._operation) {
			case UpgradeOperations.add:
				this._value += modifier.value;
				break;

			case UpgradeOperations.subtract:
				this._value -= modifier.value;
				break;

			case UpgradeOperations.multiply:
				this._value *= modifier.value;
				break;

			case UpgradeOperations.divide:
				this._value /= modifier.value;
				break;

			case UpgradeOperations.equals:
				{
					//Warn that values have been overwritten and lost
					if (this.modifiers.length > 0) {
						console.log(modifier.key.toString()+"has been overwritten with a new value");
					}
                    this.clearModifiers()
					this._value = modifier.value;
				}
				break;
		}

        // modifiers.Push(modifier);
        this.modifiers.push(modifier);
    }
    
    public remove(modifier:UpgradeValueModifier):void {
		if (modifier == null) {
			return;
		}

		if (this.modifiers.indexOf(modifier) == -1) {
			return;
		}

		let top = this.modifiers[this.modifiers.length-1];
		if (top == modifier) {
			this.removeInternal(modifier);
			this.modifiers.pop();
			return;
		}

        let history:Array<UpgradeValueModifier> = [];

		do {
			this.removeInternal(top);
			history.push(top);
			this.modifiers.pop();

			top = this.modifiers[this.modifiers.length-1];
		} while (top != modifier);

        this.removeInternal(top);
        
		this.modifiers.pop();

		while (history.length > 0) {
			top = history[history.length-1];
			this.apply(top);
			history.pop();
		}

	}

	private removeInternal(modifier:UpgradeValueModifier):void {
		if (modifier == null) {
			return;
		}

		switch (this._operation) {
			case UpgradeOperations.add:
				this._value -= modifier.value;
				break;

			case UpgradeOperations.subtract:
                this._value += modifier.value;
				break;

			case UpgradeOperations.multiply:
                this._value /= modifier.value;
				break;

			case UpgradeOperations.divide:
                this._value *= modifier.value;
				break;

			case UpgradeOperations.equals:
				{
                    // this.modifiers.Clear();
                    this.clearModifiers()
					this._value = this._defaultValue;
				}
				break;
		}
    }
    
    private clearModifiers():void {
        while(this.modifiers.length > 0) this.modifiers.pop();
    }

    public set(value:number):void {
        this._value = value;
        this.clearModifiers();
    }

	public reset():void {
		this._value = this._defaultValue;
		this.clearModifiers();
	}
}

