export class UpgradeContainer {
	// protected Dictionary<UpgradeKeys, UpgradeValue> upgrades = new Dictionary<UpgradeKeys, UpgradeValue>();
    private _upgrades:ZY.Dictionary = new ZY.Dictionary();
	public  getUpgrade(key:UpgradeKeys):UpgradeValue {
        if(!this._upgrades.has(key)) {
            let upgradeAdded = this.addMissingUpgrade(key);
            if(upgradeAdded == false) {
                return null;
            }
        }
        return this._upgrades.get(key);
		// if (upgrades.ContainsKey(key) == false) {
		// 	bool upgradeAdded = AddMissingUpgrade(key);
		// 	if (upgradeAdded == false) {
		// 		return null;
		// 	}
		// }

		// return upgrades[key];
	}

	public applyModifier(modifier:UpgradeValueModifier):void {
        let key = modifier.key;
        if(!this._upgrades.has(key)) {
            let upgradeAdded = this.addMissingUpgrade(key);
            if(upgradeAdded == false) return;
        }
        (this._upgrades.get(key) as UpgradeValue).apply(modifier);
		// if (upgrades.ContainsKey(key) == false) {
		// 	bool upgradeAdded = AddMissingUpgrade(key);
		// 	if (upgradeAdded == false) {
		// 		return;
		// 	}
		// }

		// upgrades[key].Apply(modifier);
	}

	public removeModifier(modifier:UpgradeValueModifier):void {
        let key = modifier.key;
        if(!this._upgrades.has(key)) {
            return;
        }
        (this._upgrades.get(key) as UpgradeValue).remove(modifier);
		// if (upgrades.ContainsKey(key) == false) {
		// 	return;
		// }

		// upgrades[key].Remove(modifier);
	}

	public clear():void {
		if (!this._upgrades) {
			return;
		}
		this._upgrades.clear();
	}

	//yodo1 zhaojun
	private _AMU_globalDefaultValue:UpgradeValue;
	private _AMU_localValue:UpgradeValue;
	//end
	private addMissingUpgrade(key:UpgradeKeys):boolean {
		this._AMU_globalDefaultValue = UpgradeManager.instance.getDefaultValue(key);
		if (!this._AMU_globalDefaultValue) {
			return false;
		}

		this._AMU_localValue = new UpgradeValue(this._AMU_globalDefaultValue.operation, this._AMU_globalDefaultValue.defaultValue);
		this._upgrades.add(key, this._AMU_localValue);

		return true;
	}
}
