    private qgData():void{
        if (ZY.channel == "oppo" || ZY.channel == "vivo"){
            console.log("oppo数据情况!!");
            let verList = [];
            let items = Laya.Browser.window.localStorage;
            for (let i = 0; i < items.length; i++) {
                console.log(items.key(i), items.getItem(items.key(i)));
                if (items.key(i).indexOf(this.DAT_NAME) > -1){
                    verList.push([items.key(i),items.getItem(items.key(i))])
                }
            }
            if (verList && verList.length == 1){
                Laya.LocalStorage.clear();
                Laya.LocalStorage.setItem(this.DAT_NAME, verList[0][1]);
            }else if (verList && verList.length == 2){
                let str1 = verList[0][1]
                let str2 = verList[1][1]
                let json1,json2;
    
                try {
                    json1 = JSON.parse(str1);
                } catch (error) {
                    json1 = null;
                }
    
                try {
                    json2 = JSON.parse(str2);
                } catch (error) {
                    json2 = null;
                }
                Laya.LocalStorage.clear();
                if (json1 && json2){
                    if (json1.dataVer && json2.dataVer){
                        if (json1.dataVer > json2.dataVer){
                            Laya.LocalStorage.setItem(this.DAT_NAME, str1);
                        }else{
                            Laya.LocalStorage.setItem(this.DAT_NAME, str2);
                        }
                    }else if (json1.dataVer && !json2.dataVer){
                        Laya.LocalStorage.setItem(this.DAT_NAME, str1);
                    }else if (!json1.dataVer && json2.dataVer){
                        Laya.LocalStorage.setItem(this.DAT_NAME, str2);
                    }
                }else if (json1 && !json2){
                    Laya.LocalStorage.setItem(this.DAT_NAME, str1);
                }else if (!json1 && json2){
                    Laya.LocalStorage.setItem(this.DAT_NAME, str2);
                }
            }
        }
    }



