    private _dailyTask = [1,1,2,1,100,2,3,1]
    public setDailyTask(key:number,val:number):void{
        var today = new Date()
        if (this._value.today && this._value.today != today.getFullYear()+":"+(today.getMonth()+1)+":"+today.getDate()+"") {
            this._value.today = today.getFullYear()+":"+(today.getMonth()+1)+":"+today.getDate()+""
            this._value.dailyTask = {}
        }
        if (this._value && this._value.dailyTask && this._value.dailyTask[key] && this._value.dailyTask[key][1]) return
        this._value.dailyTask = this._value.dailyTask || {}
        this._value.dailyTask[key] = this._value.dailyTask[key] || []
        this._value.dailyTask[key][0] = this._value.dailyTask[key][0] || 0
        this._value.dailyTask[key][1] = this._value.dailyTask[key][1] || false
        this._value.dailyTask[key][0] += val
        if (this._value.dailyTask[key][0] >= this._dailyTask[key]){
            this._value.dailyTask[key] = [this._dailyTask[key] , true]
            ZY.eventBus.event("DAILY_TASK");
        }
        this.saveData()
    }

