    /**
     * 获取栖息地升级带来的技能效果
     * @param encID 
     * @param key 
     * @returns 返回计算后的值,-1表示无效值
     */
    public getEncUpgradeValue(encID:number, key:number):number {
        if (this.cfgEnclosure[encID]){
            let encName = this.cfgEnclosure[encID].res;
            let v = -1;
            if(encName) {
                let cfg = this.cfgUpgrade[encName];
                let cfgKey = this.cfgUpgradeKey[key]; //{"name" : "spawnChance","opt" : 1 , "default" : 0},
                if(cfg && cfgKey) {
                    let encLvl = this.enclosure[encID] || -1;
                    let levels = cfg.level;
                    for (let i = 0; i < encLvl; i++) {
                        const node = levels[i]; //{"key" : 16  ,"value" : 2},
                        if(node.key && node.key == key) {
                            if(cfgKey.opt === 1) {
                                v = cfgKey.default + node.value;
                            } else if(cfgKey.opt === 2) {
                                v = node.value;
                            }
                        }
                    }
    
                    if(v === -1) {
                        v = cfgKey.default;
                    }
                }
            }
            return v;
        }
    }

