    private _value:any;
    private loadData():void {
        let str;
        let localStr;
        let fromServer:boolean;

        if(ZY.logined && ZY.loginData && ZY.loginData.length > 1) {
            if (ZY.channel !== "oppo" && ZY.channel !== "vivo"){
                str = ZY.loginData;
            }
            fromServer = true;

            let str1 = Laya.LocalStorage.getItem(this.DAT_NAME+ZY.uid) || "";
            let str2 = Laya.LocalStorage.getItem(this.DAT_NAME);
            let json1,json2;

            try {
                json1 = JSON.parse(str1);
            } catch (error) {
                json1 = null;
            }

            try {
                json2 = JSON.parse(str2);
            } catch (error) {
                json2 = null;
            }
            if(!json2 || (json2 && !json2.dataVer) || (json2 && json2.dataVer && json1 && json1.dataVer && json2.dataVer< json1.dataVer)) {
                if(json1) {
                    Laya.LocalStorage.clear();
                    Laya.LocalStorage.setItem(this.DAT_NAME, str1);
                }
                localStr = str1;
            } else {
                localStr = str2;
            }
            // localStr = Laya.LocalStorage.getItem(this.DAT_NAME);
        } else {
            str = Laya.LocalStorage.getItem(this.DAT_NAME);
            fromServer = false;
        }

        if(str == "{}") {
            str = null;
            localStr = null;
            this._jsonStr = "";
        } else {
            this._jsonStr = str || "{}";
        }
        let json = null;
        if(str) {
            try {
                json = JSON.parse(str);
            } catch (error) {
                json = null;
            }
        }

        if(fromServer && localStr) {
            let localJson;
            try {
                localJson = JSON.parse(localStr);
            } catch (error) {
                localJson = null;
            }
            
            let localVer;
            let serverVer;
            if(localJson) {
                localVer = localJson.dataVer || 1;
            } else {
                localVer = 1;
            }
            if(json) {
                serverVer = json.dataVer || 1;
            } else {
                serverVer = 1;
            }

            console.log(">>>:本地用户数据版本:"+localVer+",服务器用户数据版本:"+serverVer);

            if(serverVer < localVer) {
                json = localJson;
            }
        }

        // json = null;

        if(!json || !json.ver) {
            json = null;
        }

        if(json) {
            if(json.ver != ZY.ver) {
                json.ver = ZY.ver;
                
            }
        }
        
        //json = null;
        this._value = json;
        // this._value = null;
        //首次游戏，初始化操作
        if(!this._value) {
            let json:any = {};
            json.mapLevel = 0;
            json.enclosure = {};//{id:level}
            json.gold = 300;
            json.jew = 0;
            // json.animals = {"1001":[101],"1002":[121],"1003":[141],"1004":[161],"1005":[181],"1006":[201],"1007":[221]};
            json.animals = {"1001":[101]};
            json.animalNames = {}
            json.hatExp = {}
            json.hatLevel = {}
            json.hats = ["100","101"]
            json.curHatId = "100"
            json.sex = 1
            json.clothes = 0
            json.todayAd = 0
            json.completeTask = 0
            json.buff = {}
            json.changeTask = 0
            json.tryTime = {}
            // var today = new Date()
            // json.today = today.getFullYear()+":"+(today.getMonth()+1)+":"+today.getDate()+""
            json.dailyTask = {}
            json.photo = {}
            if (ZY.channel == ZY.Channel.wx || ZY.channel == ZY.Channel.qq){
                json.freeJewGainTimes = 3;
            }else if (ZY.channel == ZY.Channel.zj || ZY.channel == ZY.Channel.vivo ||ZY.channel == ZY.Channel.oppo ){
                json.freeJewGainTimes = 9999;
            }else{
                json.freeJewGainTimes = 3;
            }

            let first = this.cfgWorld[0].maps[0].animals;
            for (let i = 0; i < first.length; i++) {
                const node = first[i];
                json.enclosure[node] = -1;
            }
            json.enclosure[1000] = -1;
            json.startAnimal = 101; //初始动物是水牛

            json.aotdAnimal = {}
            json.todayAotd = []

            json.farest = {}

            json.openCount = 0

            this._value = json;

            // for test
            // json.animals["1000"] = [10,11,12,13];
            // for (let i = 1001; i < 1015; i++) {
            //     json.enclosure[i] = -1;
            // }
            json["CURRENT_WORLD"] = 0;
            json.ver = ZY.ver;
            json.dataVer = 1;
            ZY.assetsManger.mkdirFile(ZY.assetsManger.fileRoot+"/photo");
        }
        this._value.dataVer = this._value.dataVer || 1;
        this.saveData();
    }

