    //栖息地升级次数
    public get uplvlTime():any{
        let list = [[1001,1002,1003,1004,1005,1006,1007],[1008,1009,1010,1011,1012,1013,1014]]
        let t = []
        for (const key in list) {
            if (list.hasOwnProperty(key)) {
                for (let i = 0; i < list[key].length; i++) {
                    const id = list[key][i];
                    if (this._value.enclosure[id] && this._value.enclosure[id] > 0){
                        t[key] = t[key] || 0
                        t[key] += this._value.enclosure[id]
                    }
                }
            }
        }
        return t || []
    }

    
