    public upmapLevel():void {
        this._value.mapLevel = this._value.mapLevel || 0
        this._value.mapLevel += 1

        if(this.worldLevel < 2 && this.mapLevel < 5) {
            let list = this.cfgWorld[this.worldLevel].maps[this.mapLevel].animals;
            for (let i = 0; i < list.length; i++) {
                const node = list[i];
                if(!this.enclosure.hasOwnProperty(node)) this.enclosure[node] = -1;
            }
        }

        this.saveData();
    }


