    /**
     * 乘客數量
     */
    private _ticket = {
        "1000":[2.5,1],
        "1001":[2.5,1],"1002":[2.5,1],"1003":[5.5,2],"1004":[2.5,1],"1005":[2.5,1],"1006":[2.5,1],"1007":[2.5,1],
        "1008":[2.5,1],"1009":[2.5,1],"1010":[2.5,1],"1011":[2.5,1],"1012":[5.5,2],"1013":[2.5,1],"1014":[2.5,1]
    }
    public get touristTicket():Array<number>{
        let touristNum:number = 0;//游客數量
        let familyNum:number = 0;//家庭數量
        let ticketValuesTotal:number = 0;// 栖息地产生的票价
        let TotalTicketCost:number = 0;// 家庭票
        let IapCoinBase:number = 0;// 开门营业收入
        for (const key in this._value.enclosure){
            if (this._value.enclosure.hasOwnProperty(key)){
                if (this._value.enclosure[key] >= 0 && this._ticket[key]){
                    // let ticketValueMultiplier = moduleBridge.upgradeCtrl(key,UpgradeKeys.ticketValueMultiplier)
                    let ticketValueMultiplier = this.getEncUpgradeValue(Number(key),15)
                    touristNum += this._ticket[key][0]
                    ticketValuesTotal += (this._value.animals[key] && this._value.animals[key].length || 0) * this._ticket[key][1] * ticketValueMultiplier
                }
            }
        }
        let legend:boolean = false;
        if (userData.getAnimals(1000) && userData.getAnimals(1000).indexOf(10) > -1) legend = true
        // touristNum = Math.max(Math.ceil(touristNum),3)
        touristNum = Math.ceil(touristNum)
        familyNum = Math.max(Math.ceil(touristNum/2),2)
        ticketValuesTotal = Math.ceil(ticketValuesTotal)
        TotalTicketCost = legend ? Math.floor(10 + ticketValuesTotal) * 2 : Math.floor(10 + ticketValuesTotal)
        IapCoinBase = TotalTicketCost * familyNum
        return [touristNum , TotalTicketCost , familyNum , IapCoinBase]
    }

