    public setHatExp(encId:string,exp:number):void{
        if (this._value.hatExp){
            if (!this._value.hatExp[encId]) this._value.hatExp[encId] = 0
            this._value.hatExp[encId] += exp
            this.checkHatLevel(encId)
            this.saveData()
        }
    }

    private checkHatLevel(encId:string):void{
        let levelExp = [25,150,600]
        if (this._value.hatExp[encId]){
            if (!this._value.hatLevel[encId]) this._value.hatLevel[encId] = 0
            if (this._value.hatExp[encId] >= levelExp[this._value.hatLevel[encId]] && this._value.hatLevel[encId] < 3){
                this._value.hatLevel[encId] += 1
                if (this._value.hatLevel[encId] >= 3){
                    this._value.hatLevel[encId] = 3
                    this._value.hatExp[encId] = 600
                }else{
                    this._value.hatExp[encId] -= levelExp[this._value.hatLevel[encId] - 1]
                }
                this.getHat()
            }else if(this._value.hatLevel[encId] && this._value.hatLevel[encId] == 3){
                this._value.hatExp[encId] = 600
            }
        }
    }

    public getHat():string{
        let list = []
        for (const key in userData.cfgHats) {
            if (userData.cfgHats.hasOwnProperty(key)) {
                if (this._value.hats.indexOf(key) < 0){
                    list.push(key)
                }
            }
        }
        if (list && list.length > 0){
            let rand = Math.floor(Math.random()*list.length)
            if (list[rand]){
                this._value.hats.push(list[rand])
                this.saveData()
                return list[rand]
            }
        }
        return null;
    }

