    public setAotdAnimal(encId,id:number) {
        if(!this.cfgSpawn) return;
        if (this.cfgSpawn[encId] && this.cfgSpawn[encId].animal && this.cfgSpawn[encId].animal.length){
            for (let i = 0 ; i < this.cfgSpawn[encId].animal.length ; i ++){
                if (this.cfgSpawn[encId].animal[i].id == id){
                    if (!this.cfgSpawn[encId].animal[i].usedInAOTD) {
                        return;
                    }
                    break
                }
            }
            this._value.aotdAnimal = this._value.aotdAnimal || {}
            this._value.aotdAnimal[id] = true
            this.saveData();
        }
    }

