	public isAvailableForCapture(animalMeta):boolean {
		if (animalMeta == null) {
			return false;
		}
        let encId = this.cfgAnimal[animalMeta.id].encId
		let hasBeenBred:boolean = false
		if ((this.getAnimals(encId).indexOf(animalMeta.id) > -1) && hasBeenBred) {
			return true;
        }

		if (!animalMeta.usedInAOTD) {
			return false;
        }
        
        if (this._value.todayAotd[0] != animalMeta.id){
            return false
        }

		if (this.getAotdAnimal(animalMeta.id)) {
			return false;
        }
		if (this._value.enclosure[encId] < 9) {
			return false;
		}
		return true;
	}

    public aotdInit():void{
        let aotdAnimals = {}
        for (const key in userData.cfgSpawn){
            if (userData.cfgSpawn.hasOwnProperty(key)){
                let animals = userData.cfgSpawn[Number(key)].animal
                if (animals && animals.length > 0){
                    for (let i = 0 ; i < animals.length ; i ++){
                        if (animals[i].usedInAOTD){
                            aotdAnimals[key] = animals[i].id
                        }
                    }
                }
            }
        }

		let _OnTimeElapsed_availableAnimals = []
		for (const animalVariantMeta in aotdAnimals) {
			if (aotdAnimals[animalVariantMeta] == null) {
				continue;
			}
			_OnTimeElapsed_availableAnimals.push(aotdAnimals[animalVariantMeta]);
		}
		if (_OnTimeElapsed_availableAnimals == null || _OnTimeElapsed_availableAnimals.length <= 0) {
			return;
		}

        let randIdx = Math.floor(Math.random()*_OnTimeElapsed_availableAnimals.length)
        let selectedAOTD = _OnTimeElapsed_availableAnimals[randIdx];
        
        userData.setTodayAotd(selectedAOTD)

    }

