    private _needSave:boolean = false;
    public saveData():void {
        console.log("save data!!");
        this._needSave = true;
        Laya.systemTimer.frameOnce(2, this, this.saveDataNow);
    }
    private _uploadErrorCnt = 0;
    private saveDataNow():void {
        if(!this._needSave) return;
        this._needSave = false;
        let str = JSON.stringify(this._value);
        if(str != this._jsonStr) {
            console.log("save !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!");

            this._value.dataVer = this._value.dataVer || 1;
            this._value.dataVer ++;
            str = JSON.stringify(this._value);
            Laya.LocalStorage.setItem(this.DAT_NAME, str);

            this._jsonStr = str;
            this._uploadErrorCnt = 0;
            this.uploadData();
        }
    }

    private uploadData():void {
        if(!ZY.logined || ZY.channel == "oppo" || ZY.channel == "vivo") return;
        if(!ZY.uid || ZY.uid == "" || ZY.uid =="-1") return;
        let time = Laya.Browser.now();
        console.log("noice=" + ZY.noice + ",client=" + this.client);
        let sha = ZY.md5(ZY.md5(ZY.key + ZY.noice) + time + ":" + this.client + "::" + ZY.noice);
        let url = "set?" + "sha=" + sha + "&noice=" + ZY.noice + "&time=" + time + "&client=" + this.client;
        url = url + "&channel=" + ZY.channel + "&sec=" + ZY.sec + "&game=zoo&gid=" + ZY.gid+"&uid="+ZY.uid;
        let hr: Laya.HttpRequest = new Laya.HttpRequest();
        hr.http.timeout = 10000;
        hr.once(Laya.Event.ERROR, this, (e: any) => {
            console.log(">>>:login error:" + JSON.stringify(e));
            this._uploadErrorCnt ++;
            if(this._uploadErrorCnt < 3) {
                Laya.systemTimer.once(1000, this, this.uploadData);
            }
        });
        hr.once(Laya.Event.COMPLETE, this, (data: any) => {
            if(data && data.code == 0) {
                console.log(">>>:上传数据成功");
                this._uploadErrorCnt = 0;
            } else {
                console.log(">>>:上传数据失败，重试");
                this._uploadErrorCnt ++;
                if(this._uploadErrorCnt < 3) {
                    Laya.systemTimer.once(1000, this, this.uploadData);
                }
            }
        });
        console.log(">>>:set:https://api.smartjoygames.com/zy/" + url);
        hr.send("https://api.smartjoygames.com/zy/" + url, ZY.codeStr(this._jsonStr), "post", "json");
        // hr.send("http://192.168.170.244:9000/api/" + url, ZY.codeStr(this._jsonStr), "post", "json");
    }

