import { ZY } from "../ZY";
import { userData } from "../userData/UserData";
import { moduleBridge } from "../Interface";
import { reporter, ReporterPoints } from "../Reporter";

export class Task{
    // {"id":0,"difficulty":0,"type":"ANIMAL","target":1002,"value":1,"area":"AFR","dec":"驯服一只斑马"},
    public dec:string;
    public area:string;
    public id:number;
    public type:string;
    public target:number;
    public value:number;
    public difficulty:number;
    public progress:number;
    public clearProgress():void {
        this.progress = 0;
    }
    public reset(id:number, progress:number):void {
        let cfg = userData.cfgMission[id];
        if(cfg) {
            this.dec = cfg.dec;
            this.id = id;
            this.area = cfg.area;
            this.type = cfg.type;
            this.target = cfg.target;
            this.value = cfg.value;
            this.difficulty = cfg.difficulty;
            this.progress = progress;
        }
    }
    public destroy():void {
        this.dec = null;
        this.id = null;
        this.area = null;
        this.type = null;
        this.target = null;
        this.value = null;
        this.difficulty = null;
        this.progress = null;
    }
}

export default class TaskCtrl extends Laya.EventDispatcher {
    private _taskIndex:number;
    private _taskInfoList:Array<any>;
    private _nextTaskTime:number;
    private _taskList:Array<Task>;
    public init():void {
        this._taskInfoList = userData.get("task", []);
        this._taskIndex = userData.get("taskIndex", -1);
        this._nextTaskTime = userData.get("nextTaskTime",0);

        this._taskList = [];
        this._completeList = [];
        for (let i = 0; i < this._taskInfoList.length && i < 1; i++) {
            const info = this._taskInfoList[i];
            const t = new Task();
            t.reset(info.id, info.progress);
            this._taskList.push(t);
        }
        this.checkChangeTask()

        ZY.eventBus.on("AFTER_ENTER_SCENE", this, this.changeScene);
        ZY.eventBus.on(Laya.Event.BLUR, this, this.save);
        ZY.eventBus.on("TASK_UPDATE", this, this.onTaskUpdate);
        ZY.eventBus.on("CLICK_AD",this,this.log_point);
        ZY.eventBus.on("FAILED_AD",this,this.fail_ad);
        this.on("TASK_COMPLETE",this,this.task_complete);
        // Laya.systemTimer.loop(2000, this, this.loop);
        this.loop();
    }

    private fail_ad(event:number):void{
        if (ZY.channel == "oppo"){
            reporter.upload(event,5)
        }
    }


    private task_complete():void{
        Laya.timer.once(2*1000,this,()=>{
            for (let i = 0; i < 5; i++) {
                this._taskIndex += 1;
                let t:Task =new Task();
                t.reset(this._taskIndex, 0);
                if (t.type == "ANIMAL") {
                    if (t.target > 999){
                        if(userData.getAnimals(t.target).length > 0){
                            continue
                        }
                    }else{
                        if (userData.getAnimals(userData.cfgAnimal[t.target].encId).indexOf(t.target) > -1 ){
                            continue
                        }
                    }
                }
                this._taskInfoList.push({id:this._taskIndex,progress:0});
                this._taskList.push(t);
                if (ZY.stage.sceneName == "game") moduleBridge.gameCtrl.displayTarget(true)
                userData.set("nextTaskTime",this._nextTaskTime, false);
                userData.set("task", this._taskInfoList, false);
                userData.set("taskIndex", this._taskIndex, true);
                this.event("TASK_ADD", t);
                break
            }
        })
    }

    private checkChangeTask():void{
        if (this._taskList && this._taskList[0]){
            while (((this._taskList[0].target > 999 && userData.getAnimals(this._taskList[0].target).length > 0 ) || (this._taskList[0].target < 1000 && userData.cfgAnimal[this._taskList[0].target] && userData.cfgAnimal[this._taskList[0].target].encId && userData.getAnimals(userData.cfgAnimal[this._taskList[0].target].encId).indexOf(this._taskList[0].target) > -1)) && this._taskList[0].type == "ANIMAL"){
                this._taskList.splice(0, 1);
                this._taskInfoList.splice(0, 1);
                this._taskIndex += 1;
                let t = new Task();
                t.reset(this._taskIndex, 0);
                this._taskInfoList.push({id:this._taskIndex,progress:0});
                this._taskList.push(t);
                userData.set("nextTaskTime",this._nextTaskTime,false);
                userData.set("task", this._taskInfoList, false);
                userData.set("taskIndex", this._taskIndex, false);
                this.event("TASK_ADD", t);
            }
        }
    }


    private _resetTypeList = ["RIDE_CNT","CRASH_IN_ONE","RUN_NO_JUMP","JUMP_IN_ONE","JUMP_LONG_IN_ONE","CRASH_ANIMAL_IN_ONE","CRASH_ANIMAL_IN_ONE_WITH","RUN_IN_ONE","RIDE_DIF_IN_ONE"];
    // private _resetOnceTypeList = ["RIDE_STAY","RUN_NO_JUMP",""];
    private _completeList:Array<Task>;
    public changeScene(sceneName:string):void {
        // userData.set("")
        for (let i = 0; i < this._taskList.length; i++) {
            const t = this._taskList[i];
            if(this._resetTypeList.indexOf(t.type)  !== -1) {
                t.clearProgress();
                this._taskInfoList[i].progress = 0;
            }
        }
        userData.set("task", this._taskInfoList, false);

        while(this._completeList.length > 0) {
            const t = this._completeList.pop();
            t.destroy();
        }

        this.save();
    }

    private log_point(event:string):void{
        if (ZY.channel == "wx" || ZY.channel == "qq"){
            ZY.TmCtrl.instance.tmSendEvent(event,{value:"点击"})
        }else if (ZY.channel == "zj"){
            ZY.reportEvent(event,{value:"点击"})
        }
    }

    private changeTask2(done:boolean,param ? :Array<any>):void{
        if (done){
            userData.setDailyTask(6,1)
            if (this._taskInfoList && this._taskInfoList.length > 0){
                for (let i = 0; i < this._taskList.length; i++) {
                    const t = this._taskList[i];
                    this._taskList.splice(i, 1);
                    this._taskInfoList.splice(i, 1);
                    this._completeList.push(t);
                    for (let i = 0; i < 5; i++) {
                        this._taskIndex += 1;
                        let flag:boolean = false
                        flag = this._taskIndex%5 == 0 ? true : false
                        let t=new Task();
                        t.reset(this._taskIndex, 0);
                        if (t.type == "ANIMAL") {
                            if (t.target > 999){
                                if(userData.getAnimals(t.target).length > 0){
                                    continue
                                }
                            }else{
                                if (userData.getAnimals(userData.cfgAnimal[t.target].encId).indexOf(t.target) > -1 ){
                                    continue
                                }
                            }
                        }
                        this._taskInfoList.push({id:this._taskIndex,progress:0});
                        this._taskList.push(t);
                        if (flag){
                            if (ZY.channel == "wx"){
                                this._nextTaskTime = Laya.Browser.now()+20*60*1000;
                            }
                        }
                        // this._nextTaskTime = now+10*1000;
                        userData.set("nextTaskTime",this._nextTaskTime,false);
                        userData.set("task", this._taskInfoList, false);
                        userData.set("taskIndex", this._taskIndex, false);
                        this.event("TASK_ADD", t);
                        userData.addChangeTask();
                        if (ZY.channel == "wx" || ZY.channel == "qq"){
                            ZY.TmCtrl.instance.tmSendEvent("ad_changeTask",{value:"完成"})
                        }else if (ZY.channel == "zj"){
                            ZY.reportEvent("ad_changeTask",{value:"完成"})
                        }else if (ZY.channel == "oppo"){
                            reporter.upload(ReporterPoints.ad_changeTask,4)
                        }
                        if (param){
                            if (param[0] == 1){
                                moduleBridge.gameCtrl.pauseChangeTask()
                            }else if (param[0] == 2){
                                moduleBridge.gameCtrl.restartGame()
                            }else if (param[0] == 3){
                                moduleBridge.settingCtrl.updateTask()
                            }
                        }
                        break
                    }
                }
            }
        }else{
            moduleBridge.videoTipCtrl.showTips();
        }
        if (ZY.channel == "vivo"){
            ZY.sound.playMusic("audioGame/Music_Zoo.mp3");
        }
    }

    //1.暂停换任务
    //2.重开换任务
    public changeTask(index:number):boolean{
        if (userData.changeTask >= 3 ) return false
        if (ZY.channel == "wx"){
            if (index == 1){
                ZY.ad.showVideoAd(this,this.changeTask2,ZY.wxAd.taskpause,"ad_changeTask",[index])
            }else if(index == 2){
                ZY.ad.showVideoAd(this,this.changeTask2,ZY.wxAd.taskrestart,"ad_changeTask",[index])
            }else if(index == 3){
                ZY.ad.showVideoAd(this,this.changeTask2,ZY.wxAd.tasksetting,"ad_changeTask",[index])
            }
        }else if (ZY.channel == "vivo"){
            ZY.ad.showVivoVideo(this,this.changeTask2,ZY.vivoAd.taskpause,"ad_changeTask",[index])
        }else if (ZY.channel == "oppo"){
            reporter.upload(ReporterPoints.ad_changeTask,3)
            ZY.ad.showOPPOVideo(this, this.changeTask2 ,ReporterPoints.ad_changeTask,[index]);
        }else if (ZY.channel == "hw"){
            if (userData.gold >= 50){
                userData.addGold(-50,1025)
                this.changeTask2(true)
            }else{
                ZY.msg.show("金币不够")
            }
        }else{
            ZY.ad.showVideoAd(this,this.changeTask2,0,"ad_changeTask",[index])
        }
        return true
    }

    private save():void {
        userData.saveData();
    }

    private loop():void {
        let now = Laya.Browser.now();
        let t:Task;
        let legend:boolean = false;
        if (userData.getAnimals(1000) && userData.getAnimals(1000).indexOf(13) > -1) legend = true
        if((legend || now >= this._nextTaskTime) && this._taskInfoList.length < 1 ) {
            this._taskIndex += 1;
            t=new Task();
            t.reset(this._taskIndex, 0);
            if (t.type == "ANIMAL") {
                if (t.target > 999){
                    if(userData.getAnimals(t.target).length > 0){
                        return
                    }
                }else{
                    if (userData.getAnimals(userData.cfgAnimal[t.target].encId).indexOf(t.target) > -1 ){
                        return
                    }
                }
            }
            this._taskInfoList.push({id:this._taskIndex,progress:0});
            this._taskList.push(t);
            if (ZY.stage.sceneName == "game") moduleBridge.gameCtrl.displayTarget(true)
            if (this._taskIndex != 0 && this._taskIndex%5 == 0 && ZY.channel == "wx"){
                this._nextTaskTime = Laya.Browser.now()+20*60*1000;
            }
            // this._nextTaskTime = now+10*1000;
            userData.set("nextTaskTime",this._nextTaskTime, false);
            userData.set("task", this._taskInfoList, false);
            userData.set("taskIndex", this._taskIndex, true);
            this.event("TASK_ADD", t);
        }
    }

    public getTaskInfo():Array<Task> {
        return this._taskList;
    }

    public getCompleteTaskInfo():Array<Task> {
        return this._completeList;
    }

    private onTaskUpdate(type:string, target:number, value:number):void {
        for (let i = 0; i < this._taskList.length; i++) {
            const t = this._taskList[i];
            if(t.type === type && t.area == userData.get("CURRENT_WORLD",0)) {
                if(t.type === "ANIMAL") {
                    if(t.target > 999) {
                        if(userData.cfgAnimal[target]) {
                            let encId = userData.cfgAnimal[target].encId;
                            if(encId == t.target) {
                                this._taskList.splice(i, 1);
                                this._taskInfoList.splice(i, 1);
                                t.progress = t.value;
                                --i;
                                this._completeList.push(t);
                                userData.set("task", this._taskInfoList, false);
                                userData.completeTask()
                                // moduleBridge.gameCtrl.displayTarget(true,1)
                                this.event("TASK_COMPLETE", t);
                            }
                        }
                    } else {
                        if(target == t.target) {
                            this._taskList.splice(i, 1);
                            this._taskInfoList.splice(i, 1);
                            t.progress = t.value;
                            --i;
                            this._completeList.push(t);
                            userData.set("task", this._taskInfoList, false);
                            userData.completeTask()
                            // moduleBridge.gameCtrl.displayTarget(true,1)
                            this.event("TASK_COMPLETE", t);
                        }
                    }
                // } else if(t.type === "RIDE_STAY" || t.type === "RIDE_SAME_CNT") {
                } else if(t.type == "RIDE_STAY" || t.type === "RIDE_SAME_CNT" || t.type == "RUN_NO_JUMP") {
                    if(target == t.target) {
                        if(value >= t.value) {
                            t.progress = t.value;
                            this._taskList.splice(i, 1);
                            this._taskInfoList.splice(i, 1);
                            --i;
                            this._completeList.push(t);
                            userData.set("task", this._taskInfoList, false);
                            userData.completeTask()
                            // moduleBridge.gameCtrl.displayTarget(true,t.progress)
                            if ((t.type == "RIDE_STAY" || t.type == "RUN_NO_JUMP" ) && moduleBridge.player && moduleBridge.player.steed && moduleBridge.player.steed.rideTime){
                                moduleBridge.player.steed.rideTime = Math.floor(Laya.Browser.now()/1000);
                                moduleBridge.player.steed.stayTime = 0;
                            }
                            this.event("TASK_COMPLETE", t);
                        }else{
                            moduleBridge.gameCtrl.displayTarget(true,value)
                        }
                    }else{
                        if (t.type === "RIDE_SAME_CNT"){
                            moduleBridge.gameCtrl.displayTarget(true,0)
                        }
                    }
                } else if(t.type == "JUMP_LONG_IN_ONE" || t.type == "JUMP_LONG"){
                    if(target >= t.target) {
                        t.progress+=value;
                        moduleBridge.player.flyDis = null
                        if(t.progress >= t.value) {
                            t.progress = t.value;
                            this._taskList.splice(i, 1);
                            this._taskInfoList.splice(i, 1);
                            --i;
                            this._completeList.push(t);
                            userData.set("task", this._taskInfoList, false);
                            userData.completeTask()
                            // moduleBridge.gameCtrl.displayTarget(true,t.progress)
                            this.event("TASK_COMPLETE", t);
                        } else {
                            moduleBridge.gameCtrl.displayTarget(true,t.progress)
                            this.event("TASK_UPDATE", t);                            
                        }
                    }
                } else {
                    // RIDE_CNT,
                    if(t.target == target) {
                        t.progress+=value;
                        if(t.progress >= t.value) {
                            t.progress = t.value;
                            this._taskList.splice(i, 1);
                            this._taskInfoList.splice(i, 1);
                            --i;
                            this._completeList.push(t);
                            userData.set("task", this._taskInfoList, false);
                            userData.completeTask()
                            // moduleBridge.gameCtrl.displayTarget(true,t.progress)
                            this.event("TASK_COMPLETE", t);
                        } else {
                            moduleBridge.gameCtrl.displayTarget(true,t.progress)
                            this.event("TASK_UPDATE", t);                            
                        }
                    }
                }
            }
        }
    }
}