import { ZY } from "../ZY";
import { ui } from "../ui/layaMaxUI";
import ZooButton from "../script/ZooButton";
import { userData } from "../userData/UserData";
import CheckButton from "../script/CheckButton";
import { moduleBridge } from "../Interface";
import ShipCtrl from "../ship/ShipCtrl";
import { nativeAd } from "../NativeAd";

export default class HatCtrl extends ZY.BaseCtrl {
    constructor() { super(); }
    
    public start():void {
        super.start();
        this.bindAssets("res/atlas/hat.atlas", Laya.Loader.ATLAS, "zoo");
    }

    protected beforeEnterScene(sceneName:string):void {
        if(this._sceneName == "zoo") {
            // this._view.destroy(true);
            // this._view = null;
            // this._aaa = null;
            // this._btnStart.off("click", this, this.onGameStart);
            this._btnSure.off("click", this, this.onBack);

            this._btnSure.clear();
            this._btnClothDown.clear();
            this._btnClothUp.clear();
            this._btnColorDown.clear();
            this._btnColorUp.clear();
            this._btnDesignPlayer.clear();
            this._btnGear.clear();
            this._btnHat.clear();
            this._btnMan.clear();
            this._btnWoman.clear();

            this._btnSure = null;
            this._btnClothDown = null;
            this._btnClothUp = null;
            this._btnColorDown = null;
            this._btnColorUp = null;
            this._btnDesignPlayer = null;
            this._btnGear = null;
            this._btnHat = null;
            this._btnMan = null;
            this._btnWoman = null;
            if (ZY.channel == "vivo"){
                nativeAd.clearAd("abea873d8eef4a7d904c27e005acebea")
            }
            this._view.removeSelf();
            this._view.destroy(true);
            this._view = null;
        }
    }

    private _view:ui.main.DesignPlayerUI;
    private _btnSure:ZooButton;
    private _btnClothDown:ZooButton;
    private _btnClothUp:ZooButton;
    private _btnColorDown:ZooButton;
    private _btnColorUp:ZooButton;
    private _btnDesignPlayer:ZooButton;
    private _btnGear:ZooButton;
    private _btnHat:CheckButton;
    private _btnMan:CheckButton;
    private _btnWoman:CheckButton;
    protected createChildren(sceneName:string):void {
        if(sceneName == "zoo") {
            if(!this._view) {
                this._view = new ui.main.DesignPlayerUI();
                this._btnSure = new ZooButton(this._view.btnSure);
                this._btnClothDown = new ZooButton(this._view.btnClothDown);
                this._btnClothUp = new ZooButton(this._view.btnClothUp);
                this._btnColorDown = new ZooButton(this._view.btnClothDown);
                this._btnColorUp = new ZooButton(this._view.btnColorUp);
                this._btnDesignPlayer = new ZooButton(this._view.btnDesignPlayer);
                this._btnGear = new ZooButton(this._view.btnGear);
                this._btnHat = new CheckButton(this._view.btnHat);
                this._btnMan = new CheckButton(this._view.btnMan);
                this._btnWoman = new CheckButton(this._view.btnWoman);
            }
            this._view.on (Laya.Event.CLICK, this, this.onClick);
            this._btnSure.on (Laya.Event.CLICK, this, this.openBox0);
            this._btnDesignPlayer.on (Laya.Event.CLICK, this, this.openBox1);
            this._btnGear.on (Laya.Event.CLICK, this, this.onBack);
            this._btnHat.on (Laya.Event.CLICK, this, this.onClickHat);
            this._btnMan.on (Laya.Event.CLICK, this, this.onChangeSex, [1]);
            this._btnWoman.on (Laya.Event.CLICK, this, this.onChangeSex, [2]);
            this._btnClothUp.on (Laya.Event.CLICK, this, this.onChangeClothing, ["up"]);
            this._btnColorDown.on (Laya.Event.CLICK, this, this.onChangeClothing, ["down"]);
        }
    }

    private onChangeClothing(dir:string):void {
        moduleBridge.shipCtrl.changeClothes (dir);
    }

    /** 玩家当前性别 */
    private _curSex:number;
    private onChangeSex(sex:number):void {
        // 1 男     2 女
        this._curSex = sex;
        let imgMan = this._view.btnMan.getChildAt (0) as Laya.Image;
        let imgWoman = this._view.btnWoman.getChildAt (0) as Laya.Image;
        if (sex == 1) {
            imgMan.y = -28;
            imgWoman.y = -40;
        } else if (sex == 2) {
            imgMan.y = -40;
            imgWoman.y = -28;
        }
        moduleBridge.shipCtrl.changeSex (sex);
    }

    private _showHat:boolean = false;
    private onClickHat():void {
        let hatImg:Laya.Image = this._view.btnHat.getChildAt (0) as Laya.Image;
        if (this._showHat) {
            this._showHat = false;
            hatImg.y = -40;
            this._view.imgHat.skin = "hat/img_topup.png";
            moduleBridge.shipCtrl.showHat (false);
        } else {
            this._showHat = true;
            hatImg.y = -28;
            this._view.imgHat.skin = "hat/img_topdown.png";
            moduleBridge.shipCtrl.showHat (true);
        }
    }

    private openBox0():void {
        this._view.box0.visible = true;
        this._view.box1.visible = false;
        moduleBridge.shipCtrl.camMoveHat (0);
        this._view.imgBtnHat.visible = true;
        this._view.imgBtnHat.y = 30 + ZY.stage.notchHeight;
        if (ZY.channel == "oppo"){
            ZY.ad.showQGbanner(0);
            this._view.box2.bottom = ZY.ad.bannerHeight;
        }
    }

    private openBox1():void {
        if (ZY.channel == "oppo" || ZY.channel == "zj"){
            ZY.ad.hideBanner()
        }
        this._view.box0.visible = false;
        this._view.box1.visible = true;
        if (this._view.imgBtnHat.visible == true && this._showHat) moduleBridge.shipCtrl.camMoveHat (1, true);
        else moduleBridge.shipCtrl.camMoveHat (1, false);
    }
    
    private updateView():void {
        this._view.box0.visible = true;
        this._view.box1.visible = false;
        // 测试  默认男
        this.onChangeSex (1);
        moduleBridge.shipCtrl.addEventHats ();
        //--- 
        let showArr = userData.hats
        let cfg = userData.cfgHats // 配置
        let len = Object.keys (cfg).length;
        this._view.labelAmount.text = showArr.length + "/" + len;
        this._view.labelAmount.y = 30 + ZY.stage.notchHeight;
        if (ZY.channel == "oppo"){
            ZY.ad.showQGbanner(0);
            this._view.box2.bottom = ZY.ad.bannerHeight;
        } else if (ZY.channel == "zj") {
            ZY.ad.showTTBanner(0);
            this._view.box2.bottom = ZY.ad.bannerHeight;
        }
    }

    public showHatBtn(index:number):void {
        if (index > 0){
            this._view.imgBtnHat.visible = true;
            this._view.imgBtnHat.y = 30 + ZY.stage.notchHeight;
        }
        else{
            this._view.imgBtnHat.visible = false;
        }
    }

    public changeName(name:string, id:string):void {
        //test
        let showArr = userData.hats
        // console.log(showArr)
        if (showArr.indexOf (id) < 0 && id != "100") {
            name = "???";
            this._view.labelTip.visible = true;
            if (ZY.channel == "zj"){
                this._view.labelName.on(Laya.Event.CLICK,this,this.zjCd)
            }
        } else {
            this._view.labelName.off(Laya.Event.CLICK,this,this.zjCd)
            this._view.labelTip.visible = false;
        }
        this._view.labelName.text = name;
    }

    private _clickNum:number;
    private _clickTime:number;
    private zjCd(e:Laya.Event):void{
        if(!this._clickNum) this._clickNum = 0;
        this._clickNum += 1
        if (this._clickNum == 1){ 
            this._clickTime = Laya.Browser.now()
            Laya.timer.once(600,this,()=>{
                this._clickNum = null
                this._clickTime = null
            })
        }else if (this._clickNum == 3){
            if (Laya.Browser.now() - this._clickTime <= 600){
                userData.zjCdHat()
                moduleBridge.shipCtrl.zjCdHat()
                this._view.labelName.off(Laya.Event.CLICK,this,this.zjCd)
                ZY.msg.show("surprised!")
            }
        }

    }

    public showHat():void {
        if(this._sceneName == "zoo") ZY.stage.addChild(this._view, ZY.Layer.MIDDLE);
        Laya.timer.callLater(this, this.updateView);
        Laya.timer.once(1000,this,()=>{
            if (ZY.channel == "qq" || ZY.channel == "wx"){
                ZY.ad.showInsertAd()
            }else if (ZY.channel == "zj"){
                ZY.ad.showZjInsertAd()
            }else if (ZY.channel == "vivo"){
                nativeAd.showAsBanner2("abea873d8eef4a7d904c27e005acebea",this._view.vivoActive)
            }
        })
    }

    private onBack():void {
        if (ZY.channel == "oppo" || ZY.channel == "zj"){
            ZY.ad.hideBanner()
        }
        moduleBridge.shipCtrl.backToShip("hat");
        this._view.removeSelf();
    }

    private onClick(e:Laya.Event):void {
        let target = e.target;
        // console.log (target);
        let index;
        switch (target) {
            default:
                break;
        }
    }

}

// export const hatCtrl = new HatCtrl();
moduleBridge.hatCtrl = new HatCtrl();