import AnimalThrower from "./AnimalThrower";
import { moduleBridge } from "../Interface";
import { UpgradeKeys } from "../UpgradeKeys";
import PfxHelper from "../game/PfxHelper";

export default class AnimalBuckingSmasher extends AnimalThrower {
	protected turnOrBuck() {
		if (this.angerStage < 2) {

			this.buckFunction();
		}
	}

	protected updateAngerTimer():void {
		this._angerTimer -= Laya.timer.delta/1000
		if (this._angerTimer < 0 && this.angerStage < 3) {
			this.angerStage++;
			if (this.angerStage == 1) { //start warning
                this._angerTimer = this.warningLength;
                this.warningTimer = 1;
                this.PlayWarning();

			}
			if (this.angerStage == 2 && this.angryBuckTimer - Laya.timer.delta/1000 <= 0) { //start getting wild
                this.enterGoingWild();
                this._warning.hideWarning();
			}
			if (this.angerStage == 3) { //start wild
				this.getAngry();
			}
		}
	}

	protected smashObstacle(obstacle):void {
		super.smashObstacle(obstacle);

        let specialCoins = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.specialCoins)
		if (specialCoins <= 0) {
			return;
        }

        PfxHelper.instance.show("pfx_money_1",this.transform.position);
        moduleBridge.gameCtrl.addGold(specialCoins,1007)
		// CoinEffectsManager.Instance.SpawnCurrency(specialCoins.Value, obstacle.transform.position + (Vector3.up * 2), "animal" + animalVariantMeta.genusName.ToString());
	}
}
