import { ZY } from "../ZY";
import { ui } from "../ui/layaMaxUI";
import ZooButton from "../script/ZooButton";
import { userData } from "../userData/UserData";
import { moduleBridge } from "../Interface";
import { reporter, ReporterPoints } from "../Reporter";
import { nativeAd } from "../NativeAd";

export default class FreeJewCtrl extends ZY.BaseCtrl {
    constructor() { super(); }
    
    public start():void {
        super.start();
        this.bindAssets("res/atlas/freejew.atlas", Laya.Loader.ATLAS, "zoo");
    }

    protected beforeEnterScene(sceneName:string):void {
        if(this._sceneName == "zoo") {
            this._btnBack.off("click", this, this.onBack);
            this._btnBack.clear();
            this._btnBack = null;
            if (ZY.channel == "vivo"){
                nativeAd.clearAd("abea873d8eef4a7d904c27e005acebea")
            }
            this._view.removeSelf();
            this._view.destroy(true);
            this._view = null;       
        }
    }

    private _view:ui.main.FreeJewUI;
    private _btnBack:ZooButton;
    private _btnGain:ZooButton;
    private _btnClos:ZooButton;
    protected createChildren(sceneName:string):void {
        if(sceneName == "zoo") {
            if(!this._view) {
                this._view = new ui.main.FreeJewUI();
                this._btnBack = new ZooButton(this._view.btnBack);
                this._btnGain = new ZooButton(this._view.btnGain);
                this._btnClos = new ZooButton(this._view.btnCloseTip);
            }
            this._btnBack.on("click", this, this.onBack);
            this._btnGain.on("click", this, this.onGain);
            this._btnClos.on("click", this, this.showView);
            this.stopMaoPao ();
        }
    }

    private onGain2(done:boolean):void{
        if (done){
            userData.setDailyTask(6,1)
            userData.addFreeJewGainTimes()
            this._view.boxMain.visible = false;
            this._view.boxTip.visible = true;
            userData.addJew(6,1001)
            moduleBridge.shipCtrl.showFreeJewBtn()
            if (ZY.channel == "wx" || ZY.channel == "qq"){
                ZY.TmCtrl.instance.tmSendEvent("ad_freeDiamond",{value:"完成"})
            }else if (ZY.channel == "zj"){
                ZY.reportEvent("ad_freeDiamond",{value:"完成"})
            }else if(ZY.channel == "oppo"){
                // reporter.upload(ReporterPoints.ad_freeDiamond,4)
            }
            if (ZY.channel == "vivo"){
                //ZY.ad.showQGbanner(ZY.vivoBannerAd.freejew)
            }else if (ZY.channel == "oppo"){
                ZY.ad.showQGbanner(ZY.oppoBannerAd.freejew)
            } else if (ZY.channel == "bd") {
                //ZY.ad.showBDbanner(0);
            } else{
                ZY.ad.showTTBanner(0)
            }
        }else{
            moduleBridge.videoTipCtrl.showTips();
        }
        if (ZY.channel == "vivo"){
            ZY.sound.playMusic("audioGame/Music_Zoo.mp3");
        }
    }

    private onGain():void {
        var today = new Date()
        if (userData.today != today.getFullYear()+":"+(today.getMonth()+1)+":"+today.getDate()+"") {
            userData.today = today.getFullYear()+":"+(today.getMonth()+1)+":"+today.getDate()+""
            userData.initFreeJewGain()
        }
        // 判断能否领取
        if (userData.freeJewGainTimes <= 0) {
            ZY.msg.show ("次数已用尽，明再来");
            this.onBack ();
            moduleBridge.shipCtrl.showFreeJewBtn ();
            return;
        }else{
            if (ZY.channel == "wx"){
                ZY.ad.showVideoAd(this, this.onGain2,ZY.wxAd.freejew,"ad_freeDiamond");
            }else if (ZY.channel == "vivo"){
                ZY.ad.showVivoVideo(this, this.onGain2,ZY.vivoAd.freejew,"ad_freeDiamond");
            }else if(ZY.channel == "oppo"){
                // reporter.upload(ReporterPoints.ad_freeDiamond,3)
                ZY.ad.showOPPOVideo(this, this.onGain2 ,ReporterPoints.ad_freeDiamond);
            }else{
                ZY.ad.showVideoAd(this, this.onGain2,0,"ad_freeDiamond");
            }
        }
    }

    private stopMaoPao():void {
        this._view.on (Laya.Event.MOUSE_DOWN, this, (e:Laya.Event)=>{
            e.stopPropagation ();
        })
        for (let i:number = 0; i < this._view.numChildren; i ++) {
            let node = this._view.getChildAt (i) as Laya.Sprite;
            node.on (Laya.Event.MOUSE_DOWN, this, (e:Laya.Event)=>{
                e.stopPropagation ();
            })
        }
    }

    private init():void {
        
    }

    private updateView():void {
        if (userData.freeJewGainTimes <= 0) {
            this._view.boxTip.visible = false;
            ZY.msg.show ("次数已用尽，明日再来");
            this.onBack()
            return
        }

        if (ZY.channel == "vivo") {
            nativeAd.showAsBanner2("abea873d8eef4a7d904c27e005acebea", this._view.vivoActive);
        }
        
        this.init ();
        this._view.boxMain.visible = true;
        this._view.boxTip.visible = false;

        var today = new Date()
        if (userData.today != today.getFullYear()+":"+(today.getMonth()+1)+":"+today.getDate()+"") {
            userData.today = today.getFullYear()+":"+(today.getMonth()+1)+":"+today.getDate()+""
            userData.initFreeJewGain()
        }

        if (ZY.channel == ZY.Channel.zj || ZY.channel == "oppo" || ZY.channel == "vivo"){
            this._view.wxOnly.visible = false;
            this._view.wxExcept.visible = true;
            this._view.wxExcept.centerY = -30;
        }else{
            this._view.wxOnly.visible = true;
            this._view.wxExcept.visible = true;
            this._view.wxExcept.centerY = -65;
            this._view.wxOnly.centerY = 140;
            this._view.labelShengyu.text = userData.freeJewGainTimes + "";
        }
        if (ZY.channel == "wx" || ZY.channel == "qq"){
            ZY.TmCtrl.instance.tmSendEvent("ad_freeDiamond",{value:"曝光"})
        }else if (ZY.channel == "zj"){
            ZY.reportEvent("ad_freeDiamond",{value:"曝光"})
        }else if(ZY.channel == "oppo"){
            // reporter.upload(ReporterPoints.ad_freeDiamond,2)
        }
        this._view.labelGainjew0.text = this._view.labelGainjew1.text = "6";

         
        if (ZY.channel == "zj") {
            ZY.ad.showTTBanner(0);
        } else {
            ZY.ad.hideBanner();
        }   
    }

    public showView():void {
        if(this._sceneName == "zoo") ZY.stage.addChild(this._view, ZY.Layer.MIDDLE);
        Laya.timer.callLater(this, this.updateView);
    }

    private onBack():void {
        ZY.ad.hideBanner()
        this._view.removeSelf();
    }

}

moduleBridge.freeJewCtrl = new FreeJewCtrl();