import GameConfig from "./GameConfig";
import { ZY } from "./ZY";
import LoadingPage from "./LoadingPage";
import { ui } from "./ui/layaMaxUI";
import { userData } from "./userData/UserData";
import { resLoadRule } from "./ResLoadRule";
import { soundStation } from "./SoundStation";
import { UpgradeManager } from "./UpgradeManager";
import { moduleBridge } from "./Interface";
import ShipCtrl from "./ship/ShipCtrl";
import GameCtrl from "./game/GameCtrl";
import UpgradeCtrl from "./zooUpgrade/UpgradeCtrl";
import AnimalMoveCtrl from "./animalMove/AnimalMoveCtrl";
import TeachCtrl from "./teach/TeachCtrl";
import MapCtrl from "./map/MapCtrl";
import TouristMoveCtrl from "./TouristMove/TouristMoveCtrl";
import ShopCtrl from "./shop/ShopCtrl";
import HatCtrl from "./hat/HatCtrl";
import SettingCtrl from "./setting/SettingCtrl";
import PrizeCtrl from "./prize/PrizeCtrl";
import { systemCtrl } from "./SystemCtrl";
import TipsCtrl from "./tip/TipCtrl";
import EndangeredAnimals from "./endangeredAnimals/EndangeredAnimals";
import TaskCtrl from "./task/TaskCtrl";
import FreeJewCtrl from "./child/FreeJewCtrl";
import RankCtrl from "./child/RankCtrl";
import AttentionCtrl from "./child/AttentionCtrl";
import DayMissionCtrl from "./child/DayMissionCtrl";
import InviteCtrl from "./child/InviteCtrl";
import { channelConfig, channelHelper } from "./ChannelHelper";
import { nativeAd } from "./NativeAd";
import GiftBagCtrl from "./giftBag/GiftBagCtrl";
import HttpLoader from "./HttpLoader";
import { reporter, ReporterPoints } from "./Reporter";
import oneDollarCtrl from "./child/oneDollarCtrl";
import QuestionCtrl from "./question/QuestionCtrl";
import VideoTipCtrl from "./tip/VideoTipCtrl";
import FestivalActivityCtrl from "./activity/FestivalActivityCtrl";
class Main {
	constructor() {
		//根据IDE设置初始化引擎		
		// Config.useRetinalCanvas = true;
		// Laya.MiniAdpter.init(true,false);  
		if (window["Laya3D"]) Laya3D.init(GameConfig.width, GameConfig.height);
		else Laya.init(GameConfig.width, GameConfig.height, Laya["WebGL"]);
		Laya["Physics"] && Laya["Physics"].enable();
		Laya["DebugPanel"] && Laya["DebugPanel"].enable();
		Laya.stage.scaleMode = GameConfig.scaleMode;
		Laya.stage.screenMode = GameConfig.screenMode;
		Laya.stage.alignV = GameConfig.alignV;
		Laya.stage.alignH = GameConfig.alignH;

		//兼容微信不支持加载scene后缀场景
		Laya.URL.exportSceneToJson = GameConfig.exportSceneToJson;
		//打开调试面板（通过IDE设置调试模式，或者url地址增加debug=true参数，均可打开调试面板）
		if (GameConfig.debug || Laya.Utils.getQueryString("debug") == "true") Laya.enableDebugPanel();
		if (GameConfig.physicsDebug && Laya["PhysicsDebugDraw"]) Laya["PhysicsDebugDraw"].enable();
		// Laya.Stat.show(0,100);
		// Laya.alertGlobalError(true);
		//Laya.MouseManager.multiTouchEnabled = false; //关闭多点触摸
		

		let client = Laya.LocalStorage.getItem("zoozyClient"); 
        if (!client || client == "") {
            //向服务端请求client
        }

		Laya.stage.bgColor = "#eceff7";
		// Laya.Shader3D.debugMode=true
		ZY.init("qq");

		if (ZY.channel !== "none") {
			if (ZY.channel == "oppo" || ZY.channel == "vivo" || ZY.channel == "bd"){
				ZY.hasRequsetLogin = true;
				ZY.eventBus.event("LOGINFAIL");
			}else{
				ZY.eventBus.once("LOGINED", this, this.onLogined);
				ZY.login("zoo");
			}
		}
		if (ZY.channel == "bd"){
			
			if (Laya.Browser.window.swan.getAntiAddiction) {
				let api = Laya.Browser.window.swan.getAntiAddiction();
				api.onAntiAddiction(this.onBdCallback);
			}
		}
		//激活资源版本控制，version.json由IDE发布功能自动生成，如果没有也不影响后续流程
		Laya.ResourceVersion.enable("version.json", Laya.Handler.create(this, this.onVersionLoaded), Laya.ResourceVersion.FILENAME_VERSION);
	}

	private onBdCallback(state, msg): void {
        if (state == 10001) {
			moduleBridge.videoTipCtrl.showTips(this, this.exitGame, 1);
		} else if (state == 10002) {
			moduleBridge.videoTipCtrl.showTips(this, this.exitGame, 2);
		} else if (state == 10003) {
			moduleBridge.videoTipCtrl.showTips(this, this.exitGame, 3);
		}
	}


	private onClientComplete(dat:any):void {
		// 

		let client;

		Laya.LocalStorage.setItem("zoozyClient", client);
		userData.client = client;
	}

	onVersionLoaded(): void {
		//激活大小图映射，加载小图的时候，如果发现小图在大图合集里面，则优先加载大图合集，而不是小图
		Laya.AtlasInfoManager.enable("fileconfig.json", Laya.Handler.create(this, this.onConfigLoaded));
	}

	private zjError():void{
		reporter.upload(ReporterPoints.oppo_fail);
	}

	onConfigLoaded(): void {
		if (ZY.channel == "oppo"){
			reporter.upload(ReporterPoints.yx_launch);
			(window as any).qg.onError(this.zjError.bind(this));
			ZY.eventBus.on("FAILED_LOAD",this,()=>{
				reporter.upload(ReporterPoints.down_fail);
			})
		} else if (ZY.channel === "bd") {
			ZY.reportEvent("yx_launch");
		}
		ZY.ver = 9;
		ZY.bindSceneUI("zoo", ui.main.ShipUI, [
			"main/spSale.ani",
			"res/atlas/main.atlas",
			"main/button_patch.png",
			"main/button_patchHilight0.png",
			"main/button_patchHilight1.png",
			"main/button_patchHilight2.png",
			"main/button_patchHilight3.png",
			"main/button_patchHilight4.png",
			"main/button_patchHilight5.png"]);
		ZY.bindSceneUI("game", ui.main.GameUI, ["res/atlas/main.atlas"]);
		ZY.bindSceneUI("teach", ui.main.TeachUI, ["res/atlas/preload.atlas"]);
		// SavannahWorld.json
		let list = [
			// "cfg/SavannahWorld.json",
			// "cfg/JungleWorld.json",
			"cfgFile.json",
			"preload/bg1.png",
			"preload/bg2.png",
			"preload/bg3.png",
			"preload/img_bg_1.png",
			"preload/img_star.png",
			"preload/lo.png"
		];
		if (ZY.channel == "zj" || ZY.channel == "none") {
			list.push("preload/muji.png");
			list.push("preload/black.png");
		} else if (ZY.channel == "oppo" || ZY.channel == "vivo") {
			list.push("preload/oppo_vivo.png");
			list.push("preload/white.png");
		}
		ZY.preload(list, this, this.onPreloadLoaded, new LoadingPage());
		this.zjLiay()
		////////控制器///////
		systemCtrl.start();
		resLoadRule.start();
		soundStation.start();

		moduleBridge.shipCtrl.start();
		moduleBridge.gameCtrl.start();
		moduleBridge.upgradeCtrl.start();
		moduleBridge.animalMoveCtrl.start();
		moduleBridge.teachCtrl.start();
		moduleBridge.mapCtrl.start();
		moduleBridge.touristMoveCtrl.start();
		moduleBridge.shopCtrl.start();
		moduleBridge.hatCtrl.start();
		moduleBridge.settingCtrl.start();
		moduleBridge.prizeCtrl.start();
		moduleBridge.tipsCtrl.start();
		moduleBridge.endangeredAnimals.start();
		moduleBridge.freeJewCtrl.start();
		moduleBridge.dayMissionCtrl.start();
		moduleBridge.giftbagCtrl.start();
		moduleBridge.onedollarctrl.start();
		moduleBridge.questionCtrl.start();
		moduleBridge.videoTipCtrl.start();
		moduleBridge.festivalActivityCtrl.start();
		if (ZY.channel != ZY.Channel.zj) {
			moduleBridge.rankCtrl.start();
			moduleBridge.attentionCtrl.start();
			moduleBridge.inviteCtrl.start();
			RankCtrl;
			AttentionCtrl;
			InviteCtrl;
		}

		ShipCtrl;
		GameCtrl;
		UpgradeCtrl;
		AnimalMoveCtrl;
		TeachCtrl;
		MapCtrl;
		TouristMoveCtrl;
		ShopCtrl;
		HatCtrl;
		SettingCtrl;
		PrizeCtrl;
		TipsCtrl;
		EndangeredAnimals;
		FreeJewCtrl;
		DayMissionCtrl;
		GiftBagCtrl;
		oneDollarCtrl;
		QuestionCtrl;
		VideoTipCtrl;
		FestivalActivityCtrl;

		moduleBridge.taskCtrl = new TaskCtrl();
	}

	private enterFirstScene(): void {
		ZY.stage.start("teach", [
			"cfg/cfgWorld.json",
			"cfg/cfgAnimals.json",
			"cfg/cfgEnc.json",
			"cfg/cfgGround.json",
			"cfg/cfgZoneDef.json",
			"cfg/cfgUpgradeKey.json",
			"cfg/cfgUpgrade.json",
			"cfg/cfgHats.json",
			"cfg/cfgMission.json",
			"cfg/cfgSpawn.json",
			"cfg/cfgQuestion.json"
		], this, () => {
			ZY.eventBus.once("USERDATA_INIT",this,this.checkZjLiar)
			userData.init();
			UpgradeManager.instance.init();
			moduleBridge.taskCtrl.init();

			if (userData.get("TEACH", 0) == 1) {
				ZY.stage.firstScene = "zoo";
			}
		})
	}

	private zjLiay(): void {
		if (ZY.channel !== "zj") return
		let hr: Laya.HttpRequest = new Laya.HttpRequest();
		hr.http.timeout = 5000;
		hr.once(Laya.Event.ERROR, this, (e: any) => {
			GameCtrl.zjLiarFHLimit = 0
			GameCtrl.zjLiarFHVal = 1

			GameCtrl.zjLiarMGLimit = 0
			GameCtrl.zjLiarMGVal = 1

			GameCtrl.zjGameInsert = 0
			ShipCtrl.zjQuestionInsert = 0

			QuestionCtrl.zjQuestion = 1
		});
		hr.once(Laya.Event.COMPLETE, this, (data: any) => {
			console.log(data)
			if (data.code == 0 && data.data && data.data.ver == "1.0.1") {
				data = data.data;
				GameCtrl.zjLiarFHVal = data.A
				GameCtrl.zjLiarFHLimit = data.B

				GameCtrl.zjLiarMGVal = data.C
				GameCtrl.zjLiarMGLimit = data.D

				GameCtrl.zjGameInsert = data.F || 0
				ShipCtrl.zjQuestionInsert = data.G || 0

				QuestionCtrl.zjQuestion = data.H || 1
			} else {
				GameCtrl.zjLiarFHLimit = 0
				GameCtrl.zjLiarFHVal = 1

				GameCtrl.zjLiarMGLimit = 0
				GameCtrl.zjLiarMGVal = 1

				GameCtrl.zjGameInsert = 0
				ShipCtrl.zjQuestionInsert = 0

				QuestionCtrl.zjQuestion = data.H || 1
			}
		});
		// hr.send("https://api.smartjoygames.com/zy/" + "scampoint?", "", "get", "json");
		hr.send("https://zy-1257315588.file.myqcloud.com/zoo/zj/log.json" + "?time=" + Laya.Browser.now(), "", "get", "json");
	}

	private initTm(): void {
		let power
		if (ZY.channel == "wx") {
			power = wx
		}else if (ZY.channel == "qq"){
			power = (window as any).qq;
		}
		power.tmSDK.init({
			hideRequestLog: false,
			appVersion: '1.0.0'
		});
		if (power.tmSDK.getAppJSONConfig) {
			power.tmSDK.getAppJSONConfig("banner_config").then(res => {
				console.log(res)
				if (res && res.daymission) {
					DayMissionCtrl.dayOpen = res.daymission.open || 0
					DayMissionCtrl.dayTime = res.daymission.time || 0.3
					DayMissionCtrl.dayNum = res.daymission.num || 2
				}
				if (res && res.restart) {
					GameCtrl.restartOpen = res.restart.open || 0
					GameCtrl.restartTime = res.restart.time || 0.3
					GameCtrl.restartNum = res.restart.num || 2
				}
			})
			power.tmSDK.getAppJSONConfig("video_config").then(res => {
				console.log(res)
				if (res.A && res.B && res.C && res.D) {
					GameCtrl.zjLiarFHVal = res.A
					GameCtrl.zjLiarFHLimit = res.B
					GameCtrl.zjLiarMGVal = res.C
					GameCtrl.zjLiarMGLimit = res.D
				} else {
					GameCtrl.zjLiarFHLimit = 0
					GameCtrl.zjLiarFHVal = 1
					GameCtrl.zjLiarMGLimit = 0
					GameCtrl.zjLiarMGVal = 1
				}
			})
			power.tmSDK.getAppJSONConfig("city_config").then(data => {
				if (power.tmSDK.login) {
					power.tmSDK.login().then(res => {
						console.log(res)
						if (res && res.login_city) {
							let city = res.login_city
							console.log(data)
							if (data.open == 1){
								if (data.city && data.city.length > 0){
									for (let i = 0; i < data.city.length; i++) {
										if (data.city[i] == city){
											console.log("被清理了",city)
											DayMissionCtrl.dayOpen = 0;
											GameCtrl.restartOpen = 0;
											GameCtrl.zjLiarFHLimit = 0;
											GameCtrl.zjLiarMGLimit = 0;
											break
										}
									}
								}
							}
						}
					})
				}
			})
		}

	}

    private zjLiar():void{
        userData.set("zjLiarFHBig",0)
        userData.set("zjLiarFHSmall",1)
        userData.set("zjLiarMGBig",0)
        userData.set("zjLiarMGSmall",1)
	}
	
	//登陆事件侦听
	private _data:any;
	private _logined:boolean = false
	private onLogined(data){
		this._data = data
		this._logined = true

		if (ZY.channel == "hw") {
			if (Laya.Browser.window.superSDK.getCustomData) {
				Laya.Browser.window.superSDK.getCustomData("getPlayerExtraInfo", {}, function(result) {
					if (result.code == 1) {
						if (result.msg) {
							if (!result.msg.isRealName) {
								//引导实名认证
								ZY.msg.show("请进行实名认证，否则将进行防沉迷限制");
							} else {
								if (!result.msg.isAdult) {
									if (result.msg.playerDuration >= 5400) {
										moduleBridge.videoTipCtrl.showTips(this, this.exitGame, 1);
									} else {
										Laya.systemTimer.loop(900000, this, this.getPlayerExtraInfo);
									}
								}
							}
						}
					}
				})
			}
		}
	}

	private getPlayerExtraInfo(): void {
		if (Laya.Browser.window.superSDK.getCustomData) {
			Laya.Browser.window.superSDK.getCustomData("getPlayerExtraInfo", {}, function(result) {
				if (result.code == 1) {
					if (result.msg) {
						if (!result.msg.isAdult) {
							if (result.msg.playerDuration >= 5400) {
								Laya.systemTimer.clear(this, this.getPlayerExtraInfo);
								moduleBridge.videoTipCtrl.showTips(this, this.exitGame, 1);
							} 
						}	
					}
				}
			})
		}
	}

	private exitGame(): void {
		if (ZY.channel == "hw") {
			if (Laya.Browser.window.conch) {
				Laya.Browser.window.conch.exit();
			}
		} else if (ZY.channel == "bd") {
			if (Laya.Browser.window.swan.exit) {
				Laya.Browser.window.swan.exit({
					success: function() {
						console.log("退出游戏");
					}
				})
			}
		}
	}

	private checkZjLiar():void{
		//试玩礼包
		userData.set("tryGift",0)
		if (this._logined && this._data){
			//字节骗点
			this.zjLiar();
			if (ZY.channel == "wx" || ZY.channel == "qq"){
				let r = (wx as any).getLaunchOptionsSync()
				if (r && r.scene){
					ZY.TmCtrl.instance.tmSendEvent("scene",{value:r.scene})
				}
				if (this._data.child !== null && this._data.reward !== null){
					userData.set("childnum",Number(this._data.child))
					userData.set("childreward",Number(this._data.reward))
				}
			}
			this._data = null
			this._logined = null
		}
	}

	private onPreloadLoaded(): void {
		if (ZY.channel == "oppo"){
			qg.loadingComplete({});
			reporter.init()
		}
		//微信天幕
		if (ZY.channel == "wx" || ZY.channel == "qq") {
			this.initTm()
		}
		//转发接口
		ZY.showShareMenu()

		if(ZY.channel == "oppo" || ZY.channel == "vivo") {
			nativeAd.init();
		}

		ZY.assetsManger.init(Laya.loader.getRes("cfgFile.json"), "");
		ZY.assetsManger.setUrlBase();
		this.enterFirstScene();		

	}
}
//激活启动类
new Main();
