import Animal from "./Animal";
import { AnimalState, moduleBridge, PlayerState } from "../Interface";
import Helper2D from "../Helper2D";
import AnimationCurve from "../AnimationCurve";
import GameObject = Laya.Sprite3D;
import { UpgradeKeys } from "../UpgradeKeys";
import PfxHelper from "../game/PfxHelper";

export default class AnimalStopper extends Animal {

	// [Header("Stopper")]
    protected energy:number = 0;
    public energyMax:number;
    public energyDecayRate:number;
    public energyGainRate:number;
    public _energyChargeThreshold:number;

	public chargeBaseSpeedMultiplier:number = 2;
    public decayRateIncrease:number;
	public maxDecayRateMultiplier:number = 20;
    protected additionalDecayRate:number = 0;
    public speedDropOffCurve:AnimationCurve;
	// public chargeEffectPrefab:EffectsEvent;
	// public chargeTrailPrefab:EffectsEvent;

	// private _chargeEffect:EffectsEvent;
	// private _chargeTrail:EffectsEvent;

    private initialSmashStrength:number = 3;
    private chargeSmashStrength:number = 7;
	public dontUseChargeSmashProperties:boolean = false;
	public onAngryThrow:boolean = false;
	public goinWildTimeScale:number;

	private coinTimerMax:number = 0.3;
    private coinTimer:number = 0;

	private defaultRunSpeedModifier:number;


    public init(id:string, obj:GameObject):void{
        super.init(id, obj)
		let info = Laya.loader.getRes("cfg/cfgAttribute.json")[this.encId];
		this.defaultRunSpeedModifier = this.runSpeedModifier;
		this.energyMax = info.energyMax
		this.energyDecayRate = info.energyDecayRate
		this.energyGainRate = info.energyGainRate
		this._energyChargeThreshold = info._energyChargeThreshold
	
		this.chargeBaseSpeedMultiplier = info.chargeBaseSpeedMultiplier
		this.decayRateIncrease = info.decayRateIncrease
		this.maxDecayRateMultiplier = info.maxDecayRateMultiplier
		this.dontUseChargeSmashProperties = info.dontUseChargeSmashProperties
		this.onAngryThrow = info.onAngryThrow
		this.goinWildTimeScale = info.goinWildTimeScale;

		let curve = new AnimationCurve();
		if (info.speedDropOffCurve && info.speedDropOffCurve.length > 0){
			for (let i = 0 ; i < info.speedDropOffCurve.length ; i ++) {
				curve.addKeyFrame(info.speedDropOffCurve[i][0],info.speedDropOffCurve[i][1],info.speedDropOffCurve[i][2],info.speedDropOffCurve[i][3],info.speedDropOffCurve[i][4],info.speedDropOffCurve[i][5])
			}
		}
		this.speedDropOffCurve = curve
		this.initValues()
    }


	public get energyChargeThreshold():number {
		// UpgradeValue stopperChargeEnergyThresholdModifier = animalUpgrades.GetUpgrade(UpgradeKeys.stopperChargeEnergyThresholdModifier);
		let stopperChargeEnergyThresholdModifier = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.stopperChargeEnergyThresholdModifier)
		return this._energyChargeThreshold - stopperChargeEnergyThresholdModifier;
	}

	protected get energyDeclineRateMultiplier():number{
		let stopperEnergyDeclineRateMultiplier = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.stopperEnergyDeclineRateMultiplier)
		// let stopperEnergyDeclineRateMultiplier = 1
		return stopperEnergyDeclineRateMultiplier > 0 ? 1 / stopperEnergyDeclineRateMultiplier : 1;
	}


	public onUpdate() {
		let specialCoins = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.specialCoins)
        // let specialCoins = 0

        if (this.state == AnimalState.ridden && moduleBridge.player.state != PlayerState.waitingForStart) {

			this.additionalDecayRate = Math.min(this.additionalDecayRate + this.decayRateIncrease * Laya.timer.delta/1000 * this.energyDeclineRateMultiplier, this.maxDecayRateMultiplier);


            if (this.getEnergyCheck() && this.angerStage < 2) {
				this.energy = Helper2D.Clamp(this.energy + this.energyGainRate * Laya.timer.delta/1000, 0, this.energyMax);
                if (specialCoins > 0) {
                    this.coinTimer -= Laya.timer.delta/1000;
                    if (this.coinTimer < 0) {
						this.coinTimer = this.coinTimerMax;
						PfxHelper.instance.show("pfx_money_1",this.transform.position);
						moduleBridge.gameCtrl.addGold(specialCoins,1007)
                        // CoinEffectsManager.Instance.SpawnCurrency(specialCoins.Value, transform.position + new Vector3(0, 3, 2), "animal" + animalVariantMeta.genusName.ToString());
                    }
                }
            } else {
				this.energy = Helper2D.Clamp(this.energy - this.energyDecayRate * this.energyDeclineRateMultiplier * Laya.timer.delta/1000, 0, this.energyMax);
            }

			if (this.getEnergyCheck() && this.angerStage < 2) {
				// if (!_chargeTrail.particleSystem.isPlaying) {
				// 	_chargeTrail.particleSystem.Play();
				// }
			} else {
				// if (_chargeTrail.particleSystem.isPlaying) {
				// 	_chargeTrail.particleSystem.Stop();
				// }
			}

            if (this.energy > this.energyChargeThreshold) {
				// animal is charging
                // if (!_chargeEffect.particleSystem.isPlaying) {
                //     _chargeEffect.particleSystem.Play();
                // }
                // CameraController.Instance.lerpSpeed = Helper2D.Map(this.energy, this.energyMax, energyChargeThreshold - 0.02f, 0.095f, CameraController.Instance.defaultLerpSpeed);
				if (!this.dontUseChargeSmashProperties) {
					this.smashAnimals = true;
					this.smashStrength = this.chargeSmashStrength;
				}
				this.playerJumpSpeedMultiplier = Math.max(0.7, this.energy * 1.4);

			} else if (this.energy > this.energyChargeThreshold - 0.02) {
				// animal is finishing charge
                // if (_chargeEffect.particleSystem.isPlaying) {
                //     _chargeEffect.particleSystem.Stop();
                //     _chargeEffect.particleSystem.Clear();
                // }
				if (!this.dontUseChargeSmashProperties) {
					this.smashAnimals = true;
					this.smashStrength = this.chargeSmashStrength;
				}
                this.playerJumpSpeedMultiplier = Math.max(0.7, this.energy);
            } else {
				// animal is not charging
				// if (_chargeEffect.particleSystem.isPlaying) {
				// 	_chargeEffect.particleSystem.Stop();
				// 	_chargeEffect.particleSystem.Clear();
				// }
				if (!this.dontUseChargeSmashProperties) {
					this.smashAnimals = false;
					this.smashStrength = this.initialSmashStrength;
				}
                //additional decay for diminishing returns type effect while not charging
                if (!this.getEnergyCheck() || this.angerStage >= 2) {
					this.energy = Helper2D.Clamp(this.energy - (this.additionalDecayRate * Laya.timer.delta/1000 * moduleBridge.player.speedScale), 0, this.energyMax);
                }
                this.playerJumpSpeedMultiplier = Math.max(0.7, this.energy);
            }
        }
        super.onUpdate();
    }

    protected updateAngerTimer() {
		this._angerTimer = this.energy / (this.energyDecayRate * this.energyDeclineRateMultiplier); //  + this.additionalDecayRate * moduleBridge.player.speedScale
        if (this.angerStage <= 0) {
            if (this._angerTimer < this.warningLength + this.goingWildLength) {
				this.angerStage = 1;
				this.PlayWarning();
                // anim.SetTrigger(animTriggerWarning);
            }
        } else if (this.angerStage == 1) {
            if (this._angerTimer > this.warningLength + this.goingWildLength) {
                this.angerStage = 0;
				// anim.SetTrigger(animTriggerResetShaders);
            } else if (this._angerTimer < this.goingWildLength) {
				this.angerStage = 2;
				this._warning.hideWarning();
                this.enterGoingWild();
            }
        } else if (this.angerStage == 2) {
            if (this._angerTimer <= 0) {
                this.getAngry();
            }
        }
    }


    protected getAngry() {
		if (this.onAngryThrow) {
			this.throwPlayer();
		} else {
			moduleBridge.player.eatenCrash();
		}
    }

	protected throwPlayer () {
		
	}

	protected enterGoingWild() {
		if (!this.onAngryThrow) {
			// AudioGlobal.Instance.PlayerThrown();
			moduleBridge.player.blockJumping(this.goingWildLength + 1);
			super.enterGoingWild();
		} else {
			// frontCollider.enabled = false;
			moduleBridge.player.blockJumping(this.goingWildLength);
			// anim.updateMode = AnimatorUpdateMode.UnscaledTime;
			let _lastTimeScale = Laya.timer.scale
			Laya.timer.once(this.goingWildLength,this,() =>{
				this.runSpeedModifier = 0;
				super.enterGoingWild();
				Laya.timer.scale = _lastTimeScale
			}) 
			// StartCoroutine(Auto.Sleep(goingWildLength, goinWildTimeScale));

		}
    }

	protected GetRunSpeedBeforeModifiers() {
        if (this.energy > this.energyChargeThreshold) {
            return super.GetRunSpeedBeforeModifiers() * this.chargeBaseSpeedMultiplier;
        } else {
			return super.GetRunSpeedBeforeModifiers() * this.speedDropOffCurve.evaluate(this.energy) * 1;
        }
    }

    public initValues():void {
		if (!this.dontUseChargeSmashProperties) {
			this.smashStrength = this.initialSmashStrength;
		}
        this.additionalDecayRate = 0;
		this.runSpeedModifier = this.defaultRunSpeedModifier;
	}

    public ride(startingAnimal:boolean,fhtime ?:number,fhdis ?:number):void {
		// UpgradeValue special = animalUpgrades.GetUpgrade(UpgradeKeys.special);
        let special = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.special)
        
		// _chargeEffect = chargeEffectPrefab.Spawn(transform, new Vector3(0, 1, 1), chargeEffectPrefab.transform.rotation);
		// _chargeEffect.TriggerEffect(this, 999);

		// _chargeTrail = chargeTrailPrefab.Spawn(transform, new Vector3(0, 1, -1), chargeTrailPrefab.transform.rotation);
		// _chargeTrail.TriggerEffect(null, 999);
        this.additionalDecayRate = 0;
        if (special > 0) {
            this.energy = this.energyMax;
        } else {
            this.energy = this.energyChargeThreshold - 0.01;
		}
		this.runSpeedModifier = this.defaultRunSpeedModifier;
		super.ride(startingAnimal,fhtime,fhdis);
    }

	public leaveAnimal(crashed:boolean = false, playCrashAnim:boolean = true)
	{
		// _chargeEffect.gameObject.Recycle();
		// _chargeTrail.gameObject.Recycle();
		// CameraController.Instance.lerpSpeed = CameraController.Instance.defaultLerpSpeed;
		super.leaveAnimal (crashed, playCrashAnim);
	}

	protected getEnergyCheck() {
		return this.wetness > 0.5;
	}
}
