            this._view = null;
            this._scene = null;
            this._enclosures = null;
            this._camAni = null;
            this._cam = null;
            this._camOffY = null;
            this._camOffX = null;
            this._select = null;
            this._selectBig = null;
            this._btnMap.clear();
            this._btnMap = null;
            this._btnShop.clear();
            this._btnShop = null;
            this._btnDanger.clear();
            this._btnDanger = null;
            this._btnHat.clear();
            this._btnHat = null;
            this._btnOpen.clear();
            this._btnOpen = null;
            this._btnSoon.clear();
            this._btnSoon = null;
            this._btnStart.clear();
            this._btnStart = null;
            this._btnOpenNow.clear();
            this._btnOpenNow = null;
            this._btnMoreVisitor.clear();
