            this._btnRank.clear ();
            this._btnRank = null;
            this._btnInvi.clear ();
            this._btnInvi = null;
            this._btnKn.clear();
            this._btnKn = null;
            this._btnGiftSure.clear()
            this._btnGiftSure = null;
            // this._enclosureList = null;
