    private calComplete(name:string,orderId:string):void{
        let list = {
            "1000金币和象伯爵" : ["143",1000],
            "5000金币和电子斑马" : ["126",5000],
            "24000金币和所有高级动物" : [24000],
            "摇钱牛" : ["10"],
            "任务狂螺" : ["13"],
            "复活渡鸦" : ["11"],
            "幸运羔羊" : ["12"]
        }
        if (name == "摇钱牛" || name == "任务狂螺" || name == "复活渡鸦" || name == "幸运羔羊" || name == "1000金币和象伯爵" || name == "5000金币和电子斑马"){
            if (userData.getAnimals(userData.cfgAnimal[list[name][0]].encId) && userData.getAnimals(userData.cfgAnimal[list[name][0]].encId).indexOf(list[name][0]) < 0 ) {
                moduleBridge.shopCtrl.giveAward(name,orderId)
            }
        }else{
            let idArr = this.calcFreshIds ();
            for (let i:number = 0;i<idArr.length;i++) {
                let id = idArr[i];
                let cfg = userData.cfgAnimal[id];
                if(cfg) {
                    if(userData.getAnimals(userData.cfgAnimal[id].encId).indexOf(id) > -1 ) {
                        // ZY.msg.show("你已经拥有"+cfg.name+"了");
                    } else {
                        moduleBridge.shopCtrl.giveAward(name,orderId)
                        break
                    }
                }
            }
        }
    }

    private calcFreshIds():number[] {
        let idArr = [];
        let lessName = ["lion8", "gorilla8", "llama8", "emu8"];
        let cfg = userData.cfgSpawn;
        for (var key in cfg) {
            let arr = cfg[key].animal;
            for (let i:number = 0;i<arr.length;i++) {
                let d = arr[i];
                if (d.change <= 0) continue;
                if (lessName.indexOf (d.name) > -1) continue;
                if (d.hasOwnProperty("onlySpawnsIfPurchased") && d.onlySpawnsIfPurchased) continue;
                idArr.push (d.id);
            }
        }
        return idArr;
    }


