            this._camAni.play("default");
            this._cam = ZY.findChildByPath(cam, "CameraOrbit.SelfieOffsetY.SelfieOffsetX.AnimOffset.AnimDolly.ShakeOffset.Main Camera.Camera");
            this._cam.fieldOfView = 35;
            // let root = Laya.loader.getRes("ship/Conventional/AirshipRoot.lh");
