            this._aniShip = this._shipRoot.getComponent(Laya.Animator);
            this._scene.addChild(this._shipRoot);
            
            //计算中间块数量
            let enclosure = userData.enclosure;
            let smallEnc = [];
            let bigEnc = [];
            let staticEnc = [];
            let worldSmlCnt = [];
            for (const id in enclosure) {
                if (enclosure.hasOwnProperty(id)) {
                    const lvl = enclosure[id];
                    const info = userData.cfgEnclosure[id];
                    if(info.path) {
                        staticEnc.push(info);
                    } else {
                        if(info.size === 1) {
                            worldSmlCnt[info.world] = worldSmlCnt[info.world] || 0;
                            worldSmlCnt[info.world] = worldSmlCnt[info.world] + 1;
                            smallEnc.push(info);
                        } else {
                            bigEnc.push(info);
                        }
                    }
                }
            }

            let midCnt = 0;
            for (let i = 0; i < worldSmlCnt.length; i++) {
                midCnt += worldSmlCnt[i] + 1 >> 1;
            }
            midCnt += bigEnc.length;

            let front = this._shipRoot.getChildByName("ship_front") as Sprite3D;
            front.transform.localPositionZ = (midCnt - 1)*28;
            let cannon = front.getChildByName("cannon") as Sprite3D;
            cannon.addComponent(Cannon);
            let cannonAni = cannon.getComponent(Laya.Animator) as Laya.Animator;
