    private onBtnCheckShow(): void {
        let isShowNotice: boolean = userData.get("isShowNotice", 1);
        if (isShowNotice) {
            userData.set("isShowNotice", 0);
            (this._view.btnCheckShow.getChildAt(0) as Laya.Image).visible = true;
        } else {
            userData.set("isShowNotice", 1);
            (this._view.btnCheckShow.getChildAt(0) as Laya.Image).visible = false;
        }
    }

    private onBtnUpdateTip(): void {
        if (ZY.channel === "wx") {
            if (Laya.Browser.window.wx.requestSubscribeMessage) {
                let self = this;
                Laya.Browser.window.wx.requestSubscribeMessage({
                    tmplIds:["ErO_Yw0X3nCApo4SuvgX5-3Rl0watEDGa4SrPWNVJf8"],
                    success(res) {
                        console.log(res);
                        ZY.msg.show("更新消息订阅成功！");
                        self.sendMessageToServer();
                    },
                    fail(res) {
                        console.log(res);
                        ZY.msg.show("更新消息订阅失败！");
                    }
                })
            }  
        } else if (ZY.channel === "qq") {
            if (Laya.Browser.window.qq.subscribeAppMsg) {
                let self = this;
                Laya.Browser.window.qq.subscribeAppMsg({
                    tmplIds:["aabb391c66d8c6818a66169c2a30c93b"],
                    subscribe: true,
                    success(res) {
                        console.log(res);
                        ZY.msg.show("更新消息订阅成功！");
                        self.sendMessageToServer();
                    },
                    fail(res) {
                        console.log(res);
                        ZY.msg.show("更新消息订阅失败！");
                    }
                })
            }
        }
    }

    private sendMessageToServer(): void { 
        let hr: Laya.HttpRequest = new Laya.HttpRequest();
        hr.http.timeout = 5000;
        hr.once(Laya.Event.COMPLETE, this, (data: any) => {
             if (data) {
                 console.log("订阅成功！");
             }
        });

        hr.send("http://192.168.170.240:9000/api/wxSubscribeSave?channel=" + ZY.channel + "&game=zoo&gid=" + ZY.gid, "", "get", "json");
    }
    
    private prevX: number;
    private prevY: number;
    private startScrollText(): void {
        this.prevX = this._view.notice_word.mouseX;
        this.prevY = this._view.notice_word.mouseY;

        this._view.notice_word.on(Laya.Event.MOUSE_MOVE, this, this.scrollText);
        this._view.notice_word.on(Laya.Event.MOUSE_UP, this, this.finishScrollText);
    }
    private scrollText(e: Laya.Event): void {
        var nowX: number = this._view.notice_word.mouseX;
        var nowY: number = this._view.notice_word.mouseY;
        this._view.notice_word.textField.scrollX += this.prevX - nowX;
        this._view.notice_word.textField.scrollY += this.prevY - nowY;
        this.prevX = nowX;
        this.prevY = nowY;
    }

    private finishScrollText(e: Laya.Event): void {
        this._view.notice_word.off(Laya.Event.MOUSE_MOVE, this, this.scrollText);
        this._view.notice_word.off(Laya.Event.MOUSE_UP, this, this.finishScrollText);
    }

