    private _notice:any;
    private _firstShowNotice: boolean;
    private notice():void{
        let ver = userData.get("notice_ver",0)
        if (this._notice && this.status == 1 && moduleBridge.teachCtrl.status == 0 && this._notice.ver !== ver){
            this._view.notice.visible = true
            this._view.notice_word.text = this._notice.notice
            userData.set("notice_ver",this._notice.ver)
            this._notice = null
        }else{
            //this._view.notice.visible = true;
            
            let hr: Laya.HttpRequest = new Laya.HttpRequest();
            hr.http.timeout = 5000;
            hr.once(Laya.Event.COMPLETE, this, (data: any) => {
                if (data.code == 0 && data.data /*&& data.data.ver !== ver*/ && data.data.notice && moduleBridge.teachCtrl.status == 0) {
                    if (this.status == 1){
                        this._firstShowNotice = true;
                        this._view.notice.visible = true
                        this._view.notice_word.text = data.data.notice
                        userData.set("notice_ver",data.data.ver)
                    }else{
                        this._notice = data.data
                    }
                }
            });
            //ZY.channel
            // hr.send("https://api.smartjoygames.com/zy/" + "scampoint?", "", "get", "json");
            hr.send("https://zy-1257315588.file.myqcloud.com/zoo/"+ZY.channel+"/notice.json" + "?time=" + Laya.Browser.now(), "", "get", "json");
        }
    }
