    private initFudai(): void {
        let getRewardDay: string = userData.get("getFdRewardDay","");
        let date: Date = new Date();
        let year: number = date.getFullYear();
        let month: number = date.getMonth() + 1;
        let day: number = date.getDate();
        let str: string = year + "-" + month + "-" + day;

        if (getRewardDay != "" && getRewardDay.indexOf(str) != -1) {
            return;
        }
        

        if ((month == 9 && day >= 18) || (month == 10 && day <= 11)) {
            this._view.btnFudai.visible = true;
            
            let rand: number = Math.random() * 100;
            //console.log("随机数：" + rand);
            this._view.btnFudai.x = rand < 50 ? -this._view.btnFudai.width : Laya.stage.width;
            this._view.btnFudai.y = Laya.stage.height * (Math.random() * 0.5 + 0.25);

            if (this._view.btnFudai.x < 0) {
                this._moveDirX = 1;
            } else {
                this._moveDirX = -1;
            }
            rand = Math.random() * 100;
            //console.log("随机数2：" + rand);
            this._moveDirY = rand < 50 ? 1 : -1;

            this._view.imgLeftWing.rotation = 10;
            this._view.imgRightWing.rotation = -10;

            Laya.Tween.clearAll(this._view.imgLeftWing);
            Laya.Tween.clearAll(this._view.imgRightWing);

            let tween1: Laya.Tween = Laya.Tween.to(this._view.imgLeftWing, {rotation: -25}, 800, null);
            tween1.repeat = 0;
            let tween2: Laya.Tween = Laya.Tween.to(this._view.imgRightWing, {rotation: 25}, 800, null);
            tween2.repeat = 0;
            
            this._stayTime = 0;
            if (!userData.get("isFirstInGame", 0)) {
                this._intervalTime = 15000;
                userData.set("isFirstInGame", 1);
            } else {
                this._intervalTime = 0;
            }
            this._isMove = false;
            Laya.systemTimer.loop(1, this, this.fudaiMove);
        }
    }
    
    private _moveSpeed: number = 60;
    private _moveDirX: number;
    private _moveDirY: number;
    private _stayTime: number;
    private _intervalTime: number;
    private _isMove: boolean;
    private fudaiMove(): void {
        if (this._isMove) {
            this._stayTime += ZY.timerManager.delta;
            if ((this._stayTime < 10000 || this._stayTime > 13000) && (this._stayTime < 20000 || this._stayTime > 23000)) {
                this._view.btnFudai.x += this._moveSpeed * ZY.timerManager.delta / 1000 * this._moveDirX;
                this._view.btnFudai.y += this._moveSpeed * ZY.timerManager.delta / 1000 * this._moveDirY;
            }
   
            if (this._stayTime < 30000) {
               if (this._view.btnFudai.x > Laya.stage.width - this._view.btnFudai.width) {
                   this._moveDirX = -1;
               }
      
               if (this._view.btnFudai.x < 0) {
                   this._moveDirX = 1;
               }
      
               if (this._view.btnFudai.y < 0) {
                   this._moveDirY = 1;
               }
      
               if (this._view.btnFudai.y > Laya.stage.height - this._view.btnFudai.height) {
                   this._moveDirY = -1;
               }
            } else {
                if (this._view.btnFudai.x > Laya.stage.width ||
                    this._view.btnFudai.x < -this._view.btnFudai.width ||
                    this._view.btnFudai.y > Laya.stage.height ||
                    this._view.btnFudai.y < -this._view.btnFudai.height ) {
                    this._isMove = false;
                    this._intervalTime = 0;
                }
            } 
        } else {
            this._intervalTime += ZY.timerManager.delta;
            if (this._intervalTime > 30000) {
                this._isMove = true;
                this._stayTime = 0;

                if (this._view.btnFudai.x < 0) {
                    this._moveDirX = 1;
                } else {
                    this._moveDirX = -1;
                }

                if (this._view.btnFudai.y < 0) {
                    this._moveDirY = 1;
                } else {
                    this._moveDirY = -1;
                }
            }
        }
         


    }

