    public changeSex(sex:number, saveData = true):void {
        let chest = this._sexPoint.getChildByName ("CHESTGIRL") as Laya.SkinnedMeshSprite3D;
        let hair = this._sexPoint.getChildByName ("HAIRGIRL") as Laya.SkinnedMeshSprite3D;
        let skirt = this._sexPoint.getChildByName ("skirt") as Laya.SkinnedMeshSprite3D;
        let cape = this._sexPoint.getChildByName ("cape") as Laya.SkinnedMeshSprite3D;
        if (sex == 1) { 
            if(saveData) userData.sex = 1
            chest.active = false;
            hair.active = false;
        } else if (sex == 2) {
            chest.active = true;
            hair.active = true;
            if(saveData) userData.sex = 2
        }
    }

    private setClothes(index:number):void {
        let chest = this._sexPoint.getChildByName ("CHESTGIRL") as Laya.SkinnedMeshSprite3D;
        let mat = chest.skinnedMeshRenderer.material as Laya.PBRSpecularMaterial;
        this._skinIndex = index;
        this._tilingOffset.z = this._skinIndexList[this._skinIndex] * 0.0625;
        this._clothes.tilingOffset = this._tilingOffset;
        mat.tilingOffset = this._tilingOffset;
    }

    private _skinIndexList = [1,2,3,4,5];
    public changeClothes(dir:string):void {
        
        if (dir == "up") {
            let index = this._skinIndex - 1;
            if (index < 0) index = 4;
            this._skinIndex = index;
        } else if (dir == "down") {
            this._skinIndex = (this._skinIndex + 1) % this._skinIndexList.length;
        }
        // let chest = this._sexPoint.getChildByName ("CHESTGIRL") as Laya.SkinnedMeshSprite3D;
        // let mat = chest.skinnedMeshRenderer.material as Laya.PBRSpecularMaterial;
        // this._tilingOffset.z = this._skinIndexList[this._skinIndex] * 0.0625;
        // this._clothes.tilingOffset = this._tilingOffset;
        // mat.tilingOffset = this._tilingOffset;

        userData.clothes = this._skinIndex;
        this.setClothes (this._skinIndex);
    }

