    private onHatUp(e:Laya.Event):void {
        if (this.status != 5) return;
        if(!this._mouseDown) return;
        this._mouseDown = false;
        let x = -1009 + 1.3 * this._lastHatIndex;
        Laya.Tween.to(this._hatCube.transform, {localPositionX:x},200,Laya.Ease.cubicOut,null, 0, false);
    }

