    public backToShip(from:string = "",index ?:number):void {
        this.changeStatus (2);
        if (from == "hat") {
            this._camOffX.localPosition = this._beforeCam[0];
            this._camOffY.localPosition = this._beforeCam[1];
            this._cameraNode.transform.localPositionX = this._beforeHat[0];
            this._cameraNode.transform.localPositionY = this._beforeHat[1];
            this._cameraNode.transform.localPositionZ = this._beforeHat[2];
            this.removeEventHats ();
            userData.curHatId = this._curHatId;
            // userData.curHatId(this._curHatId)
        }
        if (from == "prize") {
            this.initBuff ();
        }else if (from == "gift1") {
            this.checkSignin()
        }else if (from == "gift2") {
            this.initActive();
        }else if (from == "shop") {
            this.checkSignin()
        }else if (from == "dollar") {
            this.initDollar()
        }else if (from == "start") {
            this.trySkin(index)
        }else if (from == "festival") {
            this.initFudai();
        }
        this.mapDian()
        this.initKnowledgeIcon();
        this._view.visible = true;
    }

