    private buffTimeOut():void {
        let label = this._view.labelBuffTime as Laya.Label;
        let m = Math.floor (this._buffTime / 60).toString ();
        let s = Math.floor (this._buffTime % 60).toString ();
        m = m.length > 1 ? m : "0" + m;
        s = s.length > 1 ? s : "0" + s;
        label.text = m + ":" + s;
        this._buffTime --;
        if (this._buffTime < 0) {
            this._view.btnBuff.visible = false;
            let todayTimes = userData.get ("todayPrizeTimes", 3);
            if (todayTimes <= 0) {
                this._view.btnPrize.visible = false;
            } else {
                this._view.btnPrize.visible = true;
            }
            Laya.systemTimer.clear (this, this.buffTimeOut);
        }
        
    }


