    private onMouseUp(e:Laya.Event):void {
        if (this._view.signMask.visible) return
        if(!this._mouseDown || this.status == 5) return;
        if(moduleBridge.teachCtrl.status > 1) return;
        this._mouseDown = false;
        if(this._hasMove) {
            if(this.status == 2) {
                Laya.Tween.clearAll(this._cameraNode.transform);
                //惯性
                let y = Laya.MouseManager.instance.mouseY;
                let t = Laya.Browser.now()-this._mouseDownTime;
                let v = (y-this._mouseDownY)/t*1000;
                // if(v < 100) return;
                // console.log("v="+v);
                let a;
                let s;
                let v0 = v;
                if(v > 0) {
                    a = -4000;
                } else {
                    a = 4000;
                }
                // console.log("v="+v);
                // this.startFrameLoop();
                Laya.timer.clearAll(this);
                Laya.timer.frameLoop(1, this, ()=>{
                    t = Laya.timer.delta/1000;
                    v = v + a * t;
                    // console.log(v,v0);
                    if(v * v0 < 0) {
                        Laya.timer.clearAll(this);
                        if(this._camOffX.localPositionZ < -65) {
                            Laya.Tween.clearAll(this._camOffX);
                            Laya.Tween.to(this._camOffX,{localPositionZ:-65},500,Laya.Ease.cubicInOut);
                        } else if(this._camOffX.localPositionZ > this._topPosZ) {
                            Laya.Tween.clearAll(this._camOffX);
                            Laya.Tween.to(this._camOffX,{localPositionZ:this._topPosZ},500,Laya.Ease.cubicInOut);
                        }
                    } else {
                        s = v * t + 0.5 * a * t * t;
                        this._camOffX.localPositionZ += s/20;
                        if(this._camOffX.localPositionZ < -65) {
                            a = -4000 * (this._camOffX.localPositionZ + 65);
                        } else if(this._camOffX.localPositionZ > this._topPosZ) {
                            a = -4000 * (this._camOffX.localPositionZ - this._topPosZ);
                        }
                    }
                })
            }
        } else {
            //click
            if(this.status == 2) {
                this._point.x = Laya.MouseManager.instance.mouseX;
                this._point.y = Laya.MouseManager.instance.mouseY;
                //产生射线
                this._cam.viewportPointToRay(this._point,this._ray);
                //拿到射线碰撞的物体
                this._scene.physicsSimulation.rayCast(this._ray,this._outHitResult);
                //如果碰撞到物体
                if (this._outHitResult.succeeded){
                    // console.log(this._outHitResult.collider.owner.name);
                    let sp = this._outHitResult.collider.owner as Sprite3D;
                    let pageIndex = 0;
                    let animal;
                    if(sp.getChildByName("StampedeAI") && moduleBridge.teachCtrl.status < 1) {
                        animal = sp;
                        pageIndex = 2;
                    }
                    sp = this.findParent(sp, "_Enclosure");
                    if(sp) {
                        let name = sp.name.substr(0, sp.name.indexOf("_Enclosure"));
                        console.log("碰撞到了" + name);
                        let info = userData.cfgEnclosure[name];
                        if(moduleBridge.teachCtrl.status === 1) {
                            moduleBridge.teachCtrl.selectEnc();
                            if (name == "Buffalo"){
                                if (ZY.channel == "wx" || ZY.channel == "qq"){
                                    ZY.TmCtrl.instance.tmSendEvent("tutorial_5",{})
                                }else if (ZY.channel == "zj" || ZY.channel == "bd"){
                                    ZY.reportEvent("tutorial_5",{})
                                }else if (ZY.channel == "oppo"){
                                    reporter.upload(ReporterPoints.tutorial_5)
                                } 
                            }
                        }
                        this.selectEnc(sp,name,info.size>1, pageIndex);
                        if(animal && moduleBridge.teachCtrl.status < 1) {
                            this.lockAnimal(animal.transform);
                        }
                    } else {
                        this.unselectEnc();
                        sp = this.findParent(this._outHitResult.collider.owner, "ZooShop");
                        if(sp) {
                            if (ZY.channel == "wx" || ZY.channel == "qq"){
                                ZY.TmCtrl.instance.tmSendEvent("gn_mall model")
                            }
                            console.log("选中商店");
                            moduleBridge.shopCtrl.showShop (0);
                        }
                    }
                } else {
                    console.log("没有碰到任何东西");
                    this.unselectEnc();
                }
            }
        }
    }

