    private fingerMove(view:Laya.Image):void{
        view.visible = true
        let curY = -160
        for (let i = 0; i < 1000; i++) {
            Laya.Tween.to(view,{y:curY+40},300, null, null, i * 300, false);                
            Laya.Tween.to(view,{y:curY},300, null, null, 300 * (i+1), false);
            i++
        }
    }

