    private shortCut():void{
        userData.loop()
        if (ZY.channel == "oppo"){
            if (moduleBridge.teachCtrl.status == 0 && userData.get("oppoShortCut", 0) == 0 && userData.get("desktop1",0) < 2){
                userData.set("desktop1",userData.get("desktop1",0) + 1)
                let power = (window as any).qg
                power.hasShortcutInstalled({
                    success: function(res) {
                      // 判断图标未存在时，创建图标
                      if (res == false) {
                        power.installShortcut({
                          success: function() {
                            // 执行用户创建图标奖励
                          },
                          fail: function(err) {},
                          complete: function() {}
                        })
                      }
                    },
                    fail: function(err) {},
                    complete: function() {}
                  })
            }
        }else if (ZY.channel == "vivo"){
            if (moduleBridge.teachCtrl.status == 0 && userData.get("vivoShortCut", 0) == 0 && userData.get("desktop1",0) < 2){
                let power = (window as any).qg
                let self = this;
                power.hasShortcutInstalled({
                    success: function(status) {
                      if(status) {
                        console.log('已创建')
                      }else{
                        console.log('未创建')
                        power.installShortcut({
                            success: function() {
                                self._view.btnDeskApp.visible = false
                                console.log('创建成功')
                                userData.addGold(100, 1027);
                                ZY.msg.show("恭喜获得100金币");
                                userData.set("vivoShortCut", 1);
                            },
                            fail: function(e) {
                                console.log('创建失败',e)
                              },
                            complete: function() {
                                userData.set("desktop1",userData.get("desktop1",0) + 1)
                            }
                          })
                      }
                    }
                })
            }
        }
    }
