export default class AnimalManager {
    private static _instance:AnimalManager;
    public static get instance():AnimalManager {
        if(!AnimalManager._instance) AnimalManager._instance = new AnimalManager();
        return AnimalManager._instance;
    }
    
    constructor() {
        moduleBridge.animalManager = this;
    }

    private _animalCache:any;
    private _cfgAnimal:any;
    private _cfgAttribute:any;
    private _animalCnt:number = 0;
    private _distSinceLastSpawn:number = 0
    private _timeSinceLastSpawn:number = 0
    private _lastFrameZPos:number = 0
    private _cfgRefresh:any;
    private _cfgSpawn:any;
    private _playerTransform:Laya.Transform3D;
    public start():void {
        this.init();
        // this.createFirst();
        // Laya.timer.loop(1500, this, this.createAnimal);
        this._playerTransform = moduleBridge.player.transform;
        this._currentWorld = userData.get("CURRENT_WORLD", 0);
        this.initialSpawnWave()
        Laya.systemTimer.loop(2, this, this.loop);
    }

    private loop():void {
        this._distSinceLastSpawn += (this._playerTransform.localPositionZ - this._lastFrameZPos);
        this._lastFrameZPos = this._playerTransform.localPositionZ
        this._timeSinceLastSpawn += Laya.timer.delta/1000;
        if (this._distSinceLastSpawn > 16*(2 - Math.cos(0.01*this._playerTransform.localPositionZ)) 
        || (moduleBridge.player.averageSpeed < 10 && this._timeSinceLastSpawn > 1.4) || this._timeSinceLastSpawn > 2) {
            if(this._animalCnt < 7 && ZY.stage.sceneName == "game") {
                this.spawnHerdAnimal();
                this._distSinceLastSpawn = 0;
                this._timeSinceLastSpawn = 0;
            }
        }
    }

    private _initialSpawanWaveIndex:number;
	private initialSpawnWave():void {
        // let i = 0;
        this._initialSpawanWaveIndex = 0;
        Laya.systemTimer.frameLoop(1, this, this.initialSpawnWaveStep);

    }

    private initialSpawnWaveStep():void {
        let i = this._initialSpawanWaveIndex;
        if(i < (this.animalSpawnPosition - this.animalDespawnPosition)) {
            this.spawnHerdAnimal(i, true);
        } else {
            Laya.systemTimer.clear(this, this.initialSpawnWaveStep);
        }
        this._initialSpawanWaveIndex += Helper2D.GetRandom(20, 38,true);
    }


    private _currentWorld;
    private _pathState = {left: 10,right:-10,isValid:false,width:20,zPos:0,isIsland:false};
    public animalDespawnPosition:number = -20;
    public animalSpawnPosition:number = 60;
    private spawnHerdAnimal(zModifier:number = 0, initialSpawnWave:boolean = false){
        //当前大世界
		if (this._currentWorld == null) {
			console.log("LevelSpawner::currentWorld is null");
			return;
		}

		if (initialSpawnWave) { //this is just for the initial spawn wave
            this._pathState.isValid = true;
            this._pathState.isIsland = false
			this._pathState.left = 10;
			this._pathState.right = -10;
            this._pathState.width = 20;
            this._pathState.zPos = Math.random()*60+this._playerTransform.localPositionZ-20;
		} else if (moduleBridge.player.averageSpeed >= 10) {
            let ps = WorldCreater.instance.getPathStateAt(this.animalSpawnPosition + this._playerTransform.localPositionZ)
			this._pathState.isValid = ps.isValid
			this._pathState.isIsland = ps.isIsland
			this._pathState.left = ps.left
			this._pathState.right = ps.right
            this._pathState.width = ps.width
            this._pathState.zPos = this.animalSpawnPosition + this._playerTransform.localPositionZ
		} else {
            let ps = WorldCreater.instance.getPathStateAt(this.animalDespawnPosition + this._playerTransform.localPositionZ);
            this._pathState.isValid = ps.isValid
            this._pathState.isIsland = ps.isIsland
			this._pathState.left = ps.left
			this._pathState.right = ps.right
            this._pathState.width = ps.width
            this._pathState.zPos = this.animalDespawnPosition + this._playerTransform.localPositionZ
		}

		if (this._pathState.isValid == false || this._pathState.isIsland == true) {
			console.log("Invalid path state, no herd spawned");
			return;
		}

		this.spawnHerdAnimal_Default(this._currentWorld, initialSpawnWave);

	}

    private _spawnPosition:Laya.Vector3 = new Laya.Vector3();
    private spawnHerdAnimal_Default(currentWorld:number, initialSpawnWave:boolean) {
		if (currentWorld == null) {
			return;
		}

		if (initialSpawnWave) {
			this._spawnPosition.setValue(Helper2D.GetRandom(this._pathState.right + 2, this._pathState.left - 2,true), 0, this._pathState.zPos);
		} else {
			this._spawnPosition.setValue(Helper2D.GetRandom(this._pathState.right + 2, this._pathState.left - 2,true), 0, this._pathState.zPos);
        }

        //当前小关
        let activeZones = this._cfgRefresh[currentWorld]

        let zoneIdx = 0;
        
		if (initialSpawnWave == false) {
			zoneIdx = this.currentZoneIdx;
		}

		zoneIdx = Helper2D.Clamp(zoneIdx, 0, activeZones.Length - 1);

		let currentZoneDef = activeZones[zoneIdx];
		if (currentZoneDef == null) {
			return;
		}

        let herdAnimalsSpawns = currentZoneDef.animal
        
        let info = userData.cfgWorld[currentWorld].maps[userData.getMapLevelByWorld(currentWorld)].animals

        let possibleAnimalSpawns = []
        for (const key in herdAnimalsSpawns){
            if (herdAnimalsSpawns.hasOwnProperty(key)) {
                if (info.indexOf(herdAnimalsSpawns[key].encId) > -1){
                    possibleAnimalSpawns.push(herdAnimalsSpawns[key])
                }
            }
        }

		if (possibleAnimalSpawns == null || possibleAnimalSpawns.length <= 0) {
			console.log("No possible animals to spawn");
			return;
        }

        // let sum:number = 0;
        // for (let i = 0 ; i < possibleAnimalSpawns.length ; i ++){
        //     sum += possibleAnimalSpawns[i].chance
        // }
        let selectedAnimalSpawn = ZY.getNodeByWeight(possibleAnimalSpawns, "chance");
		// for (const key in possibleAnimalSpawns) {
        //     if (possibleAnimalSpawns.hasOwnProperty(key)) {
        //         if (this.probability(possibleAnimalSpawns[key].chance,sum)){
        //             selectedAnimalSpawn = possibleAnimalSpawns[key]
        //             break
        //         }else{
        //             sum = sum - possibleAnimalSpawns[key].chance
        //         }
        //     }
        // }
        this.spawnAnimal(selectedAnimalSpawn.encId, this._spawnPosition);
    }

	private pickVariantFromAnimalMeta(encId, specificSkin:number = -1, isBoss:boolean = false) {


        if (userData.getAnimals(encId).length <= 0) {
            specificSkin = 0;
        }

        let _PVFAM_animalVariantsMeta = this._cfgSpawn[encId].animal
		if (specificSkin >= 0) {
			if (specificSkin < _PVFAM_animalVariantsMeta.length) {
				return _PVFAM_animalVariantsMeta[specificSkin];
			}
		}

		let _PVFAM_rareSkinChance = userData.getEncUpgradeValue(encId,UpgradeKeys.rareSkinChance)
        
        let _PVFAM_luckyLambMultiplier = 1;
        if (userData.getAnimals(1000) && userData.getAnimals(1000).indexOf(12) > -1){
			_PVFAM_luckyLambMultiplier = 2;
		}

        if (userData.buff && userData.buff.type && userData.buff.type == 5 && userData.buff.time && userData.buff.time > Laya.Browser.now() ){
            _PVFAM_luckyLambMultiplier *= 3
        }

        if (moduleBridge.gameCtrl.upgl){
            let up = Math.floor(Math.random() * 2 + 3)
            if (ZY.channel == "zj"){
                _PVFAM_luckyLambMultiplier *= 10
            }else{
                _PVFAM_luckyLambMultiplier *= up
            }
        }

		let _PVFAM_totalSpawnChance = 0;
		for (let i = 0; i < _PVFAM_animalVariantsMeta.length; i++) {
			if (this.shouldBeCounted(_PVFAM_animalVariantsMeta[i]) && (_PVFAM_animalVariantsMeta[i].dis < moduleBridge.player.transform.localPositionZ || (ZY.channel == "zj" && moduleBridge.gameCtrl.upgl))) {
				if (i > 0) {
					_PVFAM_totalSpawnChance += _PVFAM_animalVariantsMeta[i].chance * Math.max(1,_PVFAM_rareSkinChance) *_PVFAM_luckyLambMultiplier;
				} else {
					_PVFAM_totalSpawnChance += _PVFAM_animalVariantsMeta[i].chance;
				}
			}
		}

		let _PVFAM_selectedIdx = 0;
        let _PVFAM_animalDice = Helper2D.GetRandom(0, _PVFAM_totalSpawnChance,true);
		while (_PVFAM_selectedIdx < _PVFAM_animalVariantsMeta.length && _PVFAM_animalDice > 0) {

			// if (_PVFAM_animalVariantsMeta[_PVFAM_selectedIdx].onlySpawnsIfCaptured || _PVFAM_animalVariantsMeta[_PVFAM_selectedIdx].usedInAOTD || _PVFAM_animalVariantsMeta[_PVFAM_selectedIdx].onlySpawnsIfPurchased) {
			if (this.shouldBeCounted(_PVFAM_animalVariantsMeta[_PVFAM_selectedIdx]) == false) {
				++_PVFAM_selectedIdx;
				continue;
			}

			if ((_PVFAM_animalVariantsMeta[_PVFAM_selectedIdx].dis > moduleBridge.player.transform.localPositionZ)) {
                if ((ZY.channel == "zj" && moduleBridge.gameCtrl.upgl)){

                }else{
                    ++_PVFAM_selectedIdx;
                    continue;
                }
			}

			let _PVFAM_spawnChance = _PVFAM_animalVariantsMeta [_PVFAM_selectedIdx].chance * (Math.max(1, Math.min(_PVFAM_selectedIdx, 1)));
			if (_PVFAM_selectedIdx > 0) {
				_PVFAM_spawnChance *= _PVFAM_luckyLambMultiplier;
			}

			if (_PVFAM_animalDice < _PVFAM_spawnChance) {
				break;
			}

			if (_PVFAM_selectedIdx > 0) {
				_PVFAM_animalDice -= _PVFAM_animalVariantsMeta[_PVFAM_selectedIdx].chance * _PVFAM_luckyLambMultiplier;
			} else {
				_PVFAM_animalDice -= _PVFAM_animalVariantsMeta[_PVFAM_selectedIdx].chance;
			}

			++_PVFAM_selectedIdx;
		}

		if (_PVFAM_selectedIdx >= _PVFAM_animalVariantsMeta.length) {
			_PVFAM_selectedIdx = _PVFAM_animalVariantsMeta.length - 1;
		}

		return _PVFAM_animalVariantsMeta[_PVFAM_selectedIdx];
	}

    private shouldBeCounted(animalMeta):boolean{
        let encId = userData.cfgAnimal[animalMeta.id].encId
		if (animalMeta.usedInAOTD == true) {
			let canBeCaptured = userData.isAvailableForCapture(animalMeta);
            // bool canBeCaptured = AOTDManager.Instance.IsAvailableForCapture(this);
			if (canBeCaptured == true) {
				return true;
			}
			return false;
        }

        if (animalMeta.onlySpawnsIfPurchased){
            return false
        }
        // UpgradeValue additionalRareSkins = animalUpgrades.GetUpgrade(UpgradeKeys.additionalRareSkins);
        // let additionalRareSkins = moduleBridge.gameCtrl.upgradeValue(encId,UpgradeKeys.additionalRareSkins)
        let additionalRareSkins = userData.getEncUpgradeValue(encId,25)
		if (additionalRareSkins >= animalMeta.upgradeLevelRequired) {
			if (!animalMeta.onlySpawnsIfCaptured || userData.getAnimals(encId).indexOf(animalMeta.id) > -1) {
				return true;
			}
		}

		return false;
    }

	private spawnAnimal(encId, pos) {
        // console.log(selectedAnimalSpawn.encId,this.pickVariantFromAnimalMeta(selectedAnimalSpawn.encId))
        let spawnId = this.pickVariantFromAnimalMeta(encId).id
        this.createOne(spawnId,pos);
	}

	public spawnPositionalAnimal(spawnData):void {
        this.spawnAnimal(spawnData.encId, spawnData.spawnPosition);
	}

    // private probability(a,b):boolean{
    //     let x = Helper2D.GetRandom(1,b,false)
    //     if (x <= a){
    //         return true
    //     }else{
    //         return false
    //     }
    // }

    private get currentZoneIdx():number{
        let t = [0,1000,2000];
        let distance = moduleBridge.player.transform.localPositionZ;
        for (let i = t.length - 1 ; i >= 0 ; i --){
            if (distance >= t[i]){
                return i
            }
        }
    }

    public init():void {
        if(!this._animalCache) this._animalCache = {};
        if(!this._cfgAnimal) this._cfgAnimal = userData.cfgAnimal;
        if(!this._cfgAttribute) this._cfgAttribute = Laya.loader.getRes("cfg/cfgAttribute.json");
        if(!this._cfgRefresh) this._cfgRefresh = Laya.loader.getRes("cfg/cfgRefresh.json");
        if(!this._cfgSpawn) this._cfgSpawn = userData.cfgSpawn;
    }

    private createStep1(id:string, obj:Laya.Sprite3D,cls:string, pos:Laya.Vector3, caller:any, callback:Function):void {
        (ZY.stage.scene3D) && ZY.stage.scene3D.addChild(obj);
        let animal:Animal = new AnimalClass[cls]();
        animal.init(id, obj);
        this.setCreateAnimalPos(animal, pos);
        Laya.systemTimer.frameOnce(1,this, this.createStep3,[animal,obj,caller,callback],false);
    }

    private createStep3(animal:Animal, obj:Laya.Sprite3D,caller:any, callback:Function):void {
        obj.addComponentIntance(animal);
        if(caller && callback) {
            callback.call(caller, animal);
        }
    }

    private createStep2(animal:Animal, pos:Laya.Vector3, caller:any, callback:Function):void {
        if (animal && animal.gameObject){
            ZY.stage.scene3D.addChild(animal.gameObject);
            animal.reset();
            this.setCreateAnimalPos(animal, pos);
            if(caller && callback) {
                callback.call(caller, animal);
            }
        }
    }

    private setCreateAnimalPos(animal:Animal, pos:Laya.Vector3):void {
        if(pos) {
            pos.cloneTo(animal.transform.localPosition);
            animal.transform.localPosition = animal.transform.localPosition;
        } else {
            let posZ:number;
            if(moduleBridge.player.state == PlayerState.flying || moduleBridge.player.state == PlayerState.riding || moduleBridge.player.state == PlayerState.landing) {
                posZ = this._playerTransform.localPositionZ+80;
            } else {
                posZ = this._playerTransform.localPositionZ-20;
            }
            const state = WorldCreater.instance.getPathStateAt(posZ);
            let posX;
            if(state.isValid) {
                posX = state.right + 3 + Math.random()*(state.width-6);
            } else {
                posX = 0;
            }
            animal.transform.localPosition.setValue(posX, 0, posZ);
            animal.transform.localPosition = animal.transform.localPosition;
        }
    }

    public createOne(id:string, pos:Laya.Vector3 = null, caller:any=null, callback:Function=null):void {
        this._animalCnt ++;
        let animal:Animal;
        let pool = this._animalCache[id] as Array<Animal>;
        if(pool && pool.length > 0) {
            animal = pool.pop();
            Laya.systemTimer.frameOnce(1,this, this.createStep2, [animal, pos, caller, callback],false);
        } else {
            let cfg = this._cfgAnimal[id];
            if(!cfg) {
                if(caller && callback) {
                    callback.call(caller);
                }
                return;
            }
            let attribute;
            if(cfg.linkEncId) attribute = this._cfgAttribute[cfg.linkEncId];
            else attribute = this._cfgAttribute[cfg.encId];
            if(!attribute) {
                if(caller && callback) {
                    callback.call(caller);
                }
                return;
            }
            let cls = attribute.class;
            ZY.load([cfg.res],this,()=>{
                let obj = Laya.Sprite3D.instantiate(Laya.loader.getRes(cfg.res));
                // ZY.stage.scene3D.addChild(obj);
                // animal = obj.addComponent(AnimalClass[cls]);
                // animal.init(id, obj);
                Laya.systemTimer.frameOnce(1,this, this.createStep1, [id, obj,cls,pos, caller, callback],false);
            })

        }

        
        
        // if(pos) {
        //     animal.transform.localPosition = pos;
        // } else {
        //     let posZ:number;
        //     if(moduleBridge.player.state == PlayerState.flying || moduleBridge.player.state == PlayerState.riding || moduleBridge.player.state == PlayerState.landing) {
        //         posZ = this._playerTransform.localPositionZ+80;
        //     } else {
        //         posZ = this._playerTransform.localPositionZ-20;
        //     }
        //     const state = WorldCreater.instance.getPathStateAt(posZ);
        //     let posX;
        //     if(state.isValid) {
        //         posX = state.right + 3 + Math.random()*(state.width-6);
        //     } else {
        //         posX = 0;
        //     }
        //     animal.transform.localPosition = ZY.Temp.getVector3(posX, 0, posZ);
        // }
    }
    public recover(animal:Animal):void {
        this._animalCnt --;
        if(!this._animalCache[animal.animalId]) this._animalCache[animal.animalId] = [];
        if((this._animalCache[animal.animalId] as Array<any>).indexOf(animal) < 0) this._animalCache[animal.animalId].push(animal);
    }

    public restart():void {
        ZY.eventBus.event("RECOVER_ALL_ANIMAL");
        this._animalCnt = 0;
        // this.createFirst();
        this.initialSpawnWave()
    }

    public stopAndClear():void {
        ZY.eventBus.event("RECOVER_ALL_ANIMAL");

        for (const key in this._animalCache) {
            if (this._animalCache.hasOwnProperty(key)) {
                const pool = this._animalCache[key];
                while(pool.length > 0) {
                    const element = pool.pop() as Animal;
                    if(element) {
                        element.destroy();
                    }
                }
            }
        }
        this._animalCnt = 0;
        Laya.systemTimer.clearAll(this);

        this._playerTransform = null;
    }

}
