import AnimalThrower from "./AnimalThrower";
import Vector3 = Laya.Vector3;
import Helper2D from "../Helper2D";
import { AnimalState, moduleBridge } from "../Interface";
import { UpgradeKeys } from "../UpgradeKeys";
import PfxHelper from "../game/PfxHelper";
import GameObject = Laya.Sprite3D;

export default class AnimalChineseDragon extends AnimalThrower {
	public leavingAscentSpeed:number;
	protected leavingAngle:Vector3;
	public leavingAngleSpeed:number;
	//public leavingForwardSpeed:number;
	public goingWildRunSpeed:number;

    public init(id:string, obj:GameObject):void{
		super.init(id, obj)                      
        let info = Laya.loader.getRes("cfg/cfgAttribute.json")[this.encId];
		this.leavingAscentSpeed = info.leavingAscentSpeed
		this.leavingAngleSpeed = info.leavingAngleSpeed
		this.goingWildRunSpeed = info.goingWildRunSpeed
    }


	protected turnOrBuck ():void {
        Helper2D.SetLocalEulerY(this.transform,this.currentAngle)
		if(this.angerStage <= 3 && this.buckWhenAngry){
			this.buckFunction();
        }
	}

    public reset(){
		super.reset()
		this.leavingAngle = new Vector3(0,0,1);
		this.buckBuildUpCount = 50;
	}

	protected moveAnimal(speed:number):void{
		if (this.state == AnimalState.ignoreAll) {
			let leavingForwardSpeed:number = speed; // currentTargetVelocityFromAI.magnitude * stampedingSpeedModifier;
			Helper2D.MoveTowards(this.leavingAngle.x,0,this.leavingAngleSpeed * Math.floor(Laya.timer.delta))
			Helper2D.MoveTowards(this.leavingAngle.y,2,this.leavingAngleSpeed * Math.floor(Laya.timer.delta))
			Helper2D.MoveTowards(this.leavingAngle.z,1,this.leavingAngleSpeed * Math.floor(Laya.timer.delta))
			// transform.Translate (this.leavingAngle * leavingForwardSpeed * Time.deltaTime);
			this.leavingAngle.x *= leavingForwardSpeed * Math.floor(Laya.timer.delta / 1000)
			this.leavingAngle.y *= leavingForwardSpeed * Math.floor(Laya.timer.delta / 1000)
			this.leavingAngle.z *= leavingForwardSpeed * Math.floor(Laya.timer.delta / 1000)
            this.transform.translate(this.leavingAngle)
            Helper2D.SetLocalEulerZ(this.offset,this.currentAngle * this.leanScale * -1);
            Helper2D.SetLocalEulerX(this.offset,Math.atan2(this.leavingAngle.y, this.leavingAngle.z)*180/Math.PI * -1);
		} else {
			super.moveAnimal (speed);
		}
	}

    protected obstacleCrash(other, immune:boolean = false):void {
		super.obstacleCrash(other, false); // could be set to true, but savannah's big round obstacle is a problem here
	}

	public leaveAnimal(crashed:boolean = false, playCrashAnim:boolean = true):void {
		super.leaveAnimal (crashed, playCrashAnim);
		if(!crashed) {
			this.state = AnimalState.ignoreAll;
		}
		// this.leavingAngle.y = this.currentAngle;
	}

	protected enterGoingWild ():void{
		super.enterGoingWild ();
		this.runSpeedModifier = this.goingWildRunSpeed;
	}

	protected smashObstacle (obstacle):void{
        let specialCoins = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.specialCoins)
		if(specialCoins > 0) {
			PfxHelper.instance.show("pfx_money_1",this.transform.position);
			moduleBridge.gameCtrl.addGold(specialCoins,1007)
			// CoinEffectsManager.Instance.SpawnCurrency(specialCoins.Value, obstacle.transform.position + (Vector3.up * 2), "animal" + animalVariantMeta.genusName.ToString());
		}

		super.smashObstacle(obstacle);
	}
}