import Helper2D from "../Helper2D";
import AnimationCurve from "../AnimationCurve";
import Animal from "./Animal";
import GameObject = Laya.Sprite3D;
import { AnimalState, moduleBridge, PlayerState, FlyingState } from "../Interface";
import { UpgradeKeys } from "../UpgradeKeys";
import PfxHelper from "../game/PfxHelper";

export default class AnimalFlyer extends Animal {
    public flyingState:FlyingState;
	private currentHeight:number = 2;
	private previousHeight:number = 0
	public startHeight:number;
	public cruiseHeight:number;
	public riddenHeight:number;
	public angryDescentSpeed:number;
	public leavingAscentSpeed:number;
	public targetDistance:number;

	private crashFallSpeed:number;
	protected oscilateT:number;
	protected oscilateSpeed:number;
	private hasSwooped:boolean = false;

	private distanceStartBehind:number = 50;
	private flag:boolean = true
	public waitDuration:number;
	private waitTimer:number;

	// [Header("Swoop Curves")]
	public oscilateMax:number;
	public oscilateAccel:number;
	public oscilateAccelUpgrade:number;
	public regularSwoop;
	public bottomTimeRegular:number;

	public startFlyState:FlyingState;

	// public animTriggerFlapping:string = "isFlapping";

	private swoopHeight:number;
	private currDistFromTarget:number;
	private heightDiff:number;
	private heightMultiplier:number = 1;
	public heightMultiplierMinimum:number = 1;
	public heightMultiplierDecayRate:number = 0;
	public heightMultiplierDecayRateUpgrade:number = 0;
	constructor () {
		super();
		this._disableDust = true;
	}

    public init(id:string, obj:GameObject):void{
		super.init(id, obj)
        let info = Laya.loader.getRes("cfg/cfgAttribute.json")[this.encId];
        this.startHeight = info.startHeight
        this.cruiseHeight = info.cruiseHeight
        this.riddenHeight = info.riddenHeight
        this.angryDescentSpeed = info.angryDescentSpeed
        this.leavingAscentSpeed = info.leavingAscentSpeed
        this.targetDistance = info.targetDistance
        this.waitDuration = info.waitDuration
        this.oscilateMax = info.oscilateMax
	    this.oscilateAccel = info.oscilateAccel
        this.oscilateAccelUpgrade = info.oscilateAccelUpgrade
        this.bottomTimeRegular = info.bottomTimeRegular
        this.startFlyState = info.startFlyState
		let curve = new AnimationCurve();
		if (info.regularSwoop && info.regularSwoop.length > 0){
			for (let i = 0 ; i < info.regularSwoop.length ; i ++) {
				curve.addKeyFrame(info.regularSwoop[i][0],info.regularSwoop[i][1],info.regularSwoop[i][2],info.regularSwoop[i][3],info.regularSwoop[i][4],info.regularSwoop[i][5])
			}
		}
		this.regularSwoop = curve
        this.initValues()
    }

	public onUpdate():void {
		if (this.state == AnimalState.ridden) {

			if (this.angerStage >= 3) {

				// let specialCoins = 0
				// let special = 0
				let specialCoins = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.specialCoins)
				let special = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.special)
				let babyFlyingOscilateMultiplier = 1


				this.oscilateT += this.oscilateSpeed * Laya.timer.delta/1000;
				if (this.oscilateT > 1) {
					this.hasSwooped = false;
					this.oscilateT -= 1;
					this.playAni("buck")
					// anim.SetTrigger(animTriggerBucking);
					if (specialCoins > 0 && Helper2D.GetRandom(0, 10 , true) > 4) {
						PfxHelper.instance.show("pfx_money_1",this.transform.position);
						moduleBridge.gameCtrl.addGold(specialCoins,1007)
					}
				}

				if (this.oscilateT > 0.33 && !this.hasSwooped) {
					// AudioGlobal.Instance.AnimalSwoop(gameObject);
					this.hasSwooped = true;
				}

                this.swoopHeight = this.regularSwoop.evaluate(this.oscilateT);
                // console.log(this.swoopHeight,this.oscilateT)
				if (this.oscilateT > this.bottomTimeRegular && this.oscilateT - (this.oscilateSpeed * Laya.timer.delta/1000) <= this.bottomTimeRegular) {
					if (!this.checkAndStartFall()) {
						this.playAni("goingWild")
						// anim.SetTrigger(animTriggerGoingWild);
					}
				}

				this.currentHeight = (this.riddenHeight * this.swoopHeight * this.heightMultiplier) + 0.5;

				if (this.oscilateSpeed < this.oscilateMax * babyFlyingOscilateMultiplier) {
					if (special > 0) {
						this.oscilateSpeed += this.oscilateAccelUpgrade * Laya.timer.delta/1000 * babyFlyingOscilateMultiplier;
					} else {
						this.oscilateSpeed += this.oscilateAccel * Laya.timer.delta/1000 * babyFlyingOscilateMultiplier;
					}
				}

				if (this.heightMultiplier > this.heightMultiplierMinimum) {
					if (special > 0) {
						this.heightMultiplier -= this.heightMultiplierDecayRateUpgrade * Laya.timer.delta/1000;
					} else {
						this.heightMultiplier -= this.heightMultiplierDecayRate * Laya.timer.delta/1000;
					}

				}

			} else {
				this.currentHeight = Helper2D.Lerp(this.currentHeight, this.riddenHeight, 0.02 * 60 * Laya.timer.delta/1000)
			}
		} else if (this.state == AnimalState.crashed) {

		} else if (this.state == AnimalState.falling) {

		} else {
			this.currDistFromTarget = moduleBridge.player.transform.localPositionZ + this.targetDistance - this.transform.localPositionZ;
			if (this.flyingState == FlyingState.idle) {
				this.stampedingSpeedModifier = 0;
				if (this.flag){
					this.currentHeight = this.transform.position.y
					this.flag = false
				}
			} else if (this.flyingState == FlyingState.entering) {
				if (this.currDistFromTarget > 6) {
					if (moduleBridge.player.state == PlayerState.flying) {
						this.stampedingSpeedModifier = 0.8;
					} else {
						if (this.currDistFromTarget > this.targetDistance) {
							this.stampedingSpeedModifier = 4;
						} else {
							this.stampedingSpeedModifier = Helper2D.Clamp (Helper2D.Map (this.currDistFromTarget, this.targetDistance, 0, 4, 1.2), 1.2, 4);
						}
					}
					this.currentHeight = Helper2D.Lerp (this.startHeight, this.cruiseHeight, 1 - (this.currDistFromTarget / this.distanceStartBehind));
				} else {
					this.flyingState = FlyingState.waiting;
					// anim.SetTrigger (animTriggerReset);
				}
				// this.stampedingSpeedModifier *= moduleBridge.player.speedScale;
				// anim.SetBool (animBoolIdle, false);

			} else if (this.flyingState == FlyingState.waiting) {
				if (moduleBridge.player.state == PlayerState.flying) {
					this.stampedingSpeedModifier = Helper2D.Clamp (Helper2D.Map (this.currDistFromTarget, 4 * this.targetDistance, 0, 4, 1), 1.0, 4);
				} else {
					this.stampedingSpeedModifier = Helper2D.Clamp (Helper2D.Map (this.currDistFromTarget, this.targetDistance, 0, 4, 1), 1.0, 4);
				}

				this.waitTimer += Laya.timer.delta/1000;
				if (this.waitTimer > this.waitDuration) {
					this.flyingState = FlyingState.leaving;
					// anim.SetTrigger (animTriggerFlapping);
				}

			} else if (this.flyingState == FlyingState.running) {
				//StampedeBehaviour();
				//MoveAnimal(speed);
			} else if (this.flyingState == FlyingState.leaving) {
				this.currentHeight += this.leavingAscentSpeed * Laya.timer.delta/1000;
				// anim.SetBool(animBoolIdle, false);
			}

		}
			
		super.onUpdate();

        if (this.state != AnimalState.crashed) {
            this.heightDiff = this.currentHeight - this.previousHeight;
            if (Laya.timer.delta/1000 > 0) {
                let a = Math.atan2(this.heightDiff / Laya.timer.delta/1000, this.speed) * (180/Math.PI) * -1;
                Helper2D.SetLocalEulerX(this.offset,a)
                if (this.state == AnimalState.ridden && moduleBridge.player.state != PlayerState.waitingForStart) {
                    // moduleBridge.player.offset.SetLocalEulerX(a);
                    Helper2D.SetLocalEulerX(moduleBridge.player.offset,a)
                }
            }
            this.previousHeight = this.currentHeight;
        }

		if (moduleBridge.player.state != PlayerState.waitingForStart && this.state != AnimalState.falling) {
            Helper2D.SetPosY(this.transform,this.currentHeight)
		}
	}

    protected enterGoingWild() {
        this._angerTimer = this.goingWildLength;
        // anim.SetTrigger(animTriggerGoingWild);
    }

	public ride(startingAnimal:boolean,fhtime ?:number,fhdis ?:number):void {
		this.currentAngle = 0;
		super.ride(startingAnimal,fhtime,fhdis);
		if (startingAnimal) {
			// because flying animals start idle, not running, their idle state has to be reset to false when riding begins
			// anim.SetBool(animBoolIdle, false);
        }
        Helper2D.SetEulerY(this.transform,0)
		// transform.SetEulerY (0);
		this.flyingState = FlyingState.running;
	}

	// public Init(startPos, rotation:number = 0) {
	public initValues() {
		if (!this.startFlyState) return
		if (this.startFlyState == FlyingState.idle && this.isSpawningAndRidden == false) {
			// console.log(this.transform.localPositionY)
			// this.playAni("idle")
			this.state = AnimalState.idle;
		} else {
			this.currentHeight = this.startHeight;
			Helper2D.SetPosY(this.transform,this.currentHeight)
		}
        this.flyingState = this.startFlyState;
        Helper2D.SetLocalEulerX(this.offset,0)

		// this.currentHeight = this.transform.position.y;
		this.flag = true
		this.crashFallSpeed = 0;
		this.oscilateT = 0;
		this.oscilateSpeed = 0;

		if (this.startFlyState && this.startFlyState != FlyingState.running) {
			// anim.SetBool (animBoolIdle, true);
			this.playAni("idle")
		}
		this.waitTimer = 0;

		this.heightMultiplier = 1;
	}

	public reset(){
		super.reset()
		this.initValues()
	}


	public SpawnerPostInit ()
	{
		if (this.startFlyState != FlyingState.idle && this.startFlyState != FlyingState.running) {
            // transform.SetPosZ(moduleBridge.player.transform.localPositionZ - this.distanceStartBehind);
            Helper2D.SetPosZ(this.transform,moduleBridge.player.transform.localPositionZ - this.distanceStartBehind)
		}
		// base.SpawnerPostInit ();
	}

	public leaveAnimal(crashed:boolean = false, playCrashAnim:boolean = true) {
		super.leaveAnimal(crashed, playCrashAnim);
		this.flyingState = FlyingState.leaving;
		this.stampedingSpeedModifier = 0.8;
        Helper2D.SetLocalEulerX(moduleBridge.player.offset,0)
        Helper2D.SetLocalEulerX(this.offset,0)
	}


	public obstacleOnSmashed ()
	{
		if (this.state == AnimalState.ridden) {
			return;
		}

		this.state = AnimalState.running;
		this.flyingState = FlyingState.leaving;
		// base.ObstacleOnSmashed ();
	}


	/*
	 *  OVERRIDES FOR UNWANTED ANIMAL BEHAVIOUR
	 */

	public DryOff(changeYPos) {

	}

	public StartFall (isLeftSide:boolean, fromCollision:boolean, isFrontEdge:boolean = false)
	{
		if (!fromCollision) {
			super.startFall (isLeftSide, fromCollision, isFrontEdge);
		}
	}

	protected  getFallOffset ()
	{
		return 2.5;
	}

	protected fallPunt (isLeft:boolean, isFrontEdge:boolean = false)
	{
		//return base.FallPunt (isLeft);
		while (this.state == AnimalState.falling) {
			if (isLeft) {
				if (this.currentAngle > -45) {
					this.currentAngle -= 45 * Laya.timer.delta/1000;
                    // transform.SetEulerY(this.currentAngle);
                    Helper2D.SetEulerY(this.transform,this.currentAngle)
				}
			} else {
				if (this.currentAngle < 45) {
					this.currentAngle += 45 * Laya.timer.delta/1000;
                    // transform.SetEulerY(this.currentAngle);
                    Helper2D.SetEulerY(this.transform,this.currentAngle)
				}
			}
		}
	}

	protected AlpinePillarCollision (other)
	{
		// moduleBridge.player.MountedCrash(true, SpecialAnalyticManager.HIT_OBSTACLE +":"+ other.gameObject.name.Replace("(Clone)",""));
	}

	public smashAnimal(otherAnimal:Animal,  wasSmashedByPlayer:boolean = true) {
		// let specialCoins = 0
		let specialCoins = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.special)

		super.smashAnimal(otherAnimal, wasSmashedByPlayer);
	}

	protected clearAll():void {
		this.flyingState = null;
		this.currentHeight = null;
		this.previousHeight = null;
		this.startHeight = null;
		this.cruiseHeight = null;
		this.riddenHeight = null;
		this.angryDescentSpeed = null;
		this.leavingAscentSpeed = null;
		this.targetDistance = null;
		this.crashFallSpeed = null;
		this.oscilateT = null;
		this.oscilateSpeed = null;
		this.hasSwooped = null;
		this.distanceStartBehind = null;
		this.waitDuration = null;
		this.waitTimer = null;
		this.oscilateMax = null;
		this.oscilateAccel = null;
		this.oscilateAccelUpgrade = null;
		this.regularSwoop;
		this.bottomTimeRegular = null;
		this.startFlyState = null;
		this.swoopHeight = null;
		this.currDistFromTarget = null;
		this.heightDiff = null;
		this.heightMultiplier = null;
		this.heightMultiplierMinimum = null;
		this.heightMultiplierDecayRate = null;
		this.heightMultiplierDecayRateUpgrade = null;
		this.flag = null
		super.clearAll();
	}
}

moduleBridge.AnimalFlyer = AnimalFlyer;
