import { ZY } from "./ZY";
import { resLoadRule } from "./ResLoadRule";
import { userData } from "./userData/UserData";
import { moduleBridge } from "./Interface";

class SoundStation extends ZY.BaseCtrl {
    constructor() { super(); }

    public start():void {
        super.start();
        ZY.eventBus.on(Laya.Event.FOCUS, this, this.onFocus);
        // ZY.eventBus.on("VIDEO_AD_SHOW", this, this.onStopAllSound);
        // ZY.eventBus.on("VIDEO_AD_CLOSE", this, this.onFocus);
        if (ZY.channel == "wx" || ZY.channel == "qq" || ZY.channel == "bd"){
            ZY.eventBus.on("VIDEO_CLOSE", this, this.onFocus);
        }
    }

    private _ambAirshipSC:ZY.ISoundChannel;
    protected createChildren(sceneName:string):void {
        if(sceneName == "teach") {
            if(ZY.channel === "wx" || ZY.channel === "zj" || ZY.channel === "oppo" || ZY.channel == "vivo" || ZY.channel == "qq" || ZY.channel == "bd") {
                if(resLoadRule.audioFirstLoaded) {
                    this.playTeachSound();
                } else {
                    ZY.eventBus.once("audio_first", this, this.playTeachSound);
                }
            }
        } else if(sceneName == "zoo") {
            ZY.sound.stopMusic();
            this._ambAirshipSC = ZY.sound.playSound("audioGame/Amb_Airship.mp3", 0);
        }
    }

    public onFocus():void {
        if (ZY.ad.isADing) return
        if(this._sceneName == "teach") {
            if(ZY.channel === "wx" || ZY.channel === "zj" || ZY.channel === "oppo" || ZY.channel == "vivo" || ZY.channel == "qq" || ZY.channel == "bd") {
                if(resLoadRule.audioFirstLoaded) {
                    this.playTeachSound();
                } else {
                    ZY.eventBus.once("audio_first", this, this.playTeachSound);
                }
            }
        } else if(this._sceneName == "zoo") {
            ZY.sound.stopMusic();
            this._ambAirshipSC = ZY.sound.playSound("audioGame/Amb_Airship.mp3", 0);
            Laya.timer.once(1000,this,()=>{
                ZY.sound.playMusic("audioGame/Music_Zoo.mp3");
            })
        } else if(this._sceneName == "game") {
            if (userData.get("CURRENT_WORLD", 0) == 0){
                ZY.sound.playMusic("audioGame/Music_Savannah.mp3");
            }else if (userData.get("CURRENT_WORLD", 0) == 1){
                ZY.sound.playMusic("audioGame/Music_Jungle.mp3");
            }
        }
        moduleBridge.shipCtrl.qqGiftSend(true);
    }

    protected beforeEnterScene(sceneName:string):void {
        if(this._sceneName == "teach") {
            if(this._ambFarmSC) {
                this._ambFarmSC.clear();
            }
            this._ambFarmSC = null;
            this._teachSoundList = null;
            Laya.timer.clearAll(this);
            ZY.sound.stopMusic();
        } else if(this._sceneName == "zoo") {
            if(this._ambAirshipSC) {
                this._ambAirshipSC.clear();
                this._ambAirshipSC = null;
            }
        }
    }

    /////////教学场景音乐混合/////////
    private _teachSoundList:Array<string> = ["audio/Amb_Farm_Cow.mp3","audio/Amb_Farm_Rooster.mp3","audio/Amb_Farm_Sheep.mp3"];
    private _ambFarmSC:ZY.ISoundChannel;
    private playTeachSound():void {
        // Laya
        if(this._sceneName != "teach") return;
        Laya.timer.clearAll(this);
        ZY.sound.playMusic("audio/Music/Music_Tutorial.mp3");
        if(!this._teachSoundList) this._teachSoundList = [];
        this._ambFarmSC = ZY.sound.playSound("audio/Amb_Farm.mp3",0);
        // sc = ZY.sound.playSound("audio/Amb_Farm.mp3");
        Laya.timer.once(Math.floor(Math.random()*5000+1000), this, this.onPlayTeachSound);
    }

    private onPlayTeachSound():void {
        if(this._sceneName != "teach") return;
        let src = this._teachSoundList[Math.floor(Math.random()*this._teachSoundList.length)];
        // console.log("播放农场音效:"+src);
        ZY.sound.playSound(src, 1, false, this, ()=>{
            Laya.timer.once(Math.floor(Math.random()*5000+1000), this, this.onPlayTeachSound);
        });
    }
    ///////////////////////////////
}

export const soundStation = new SoundStation();