
export default class AnimationCurve{    
    constructor() {

    }

    private _frameList:Array<{a:number,h:number,k:number,begin:number}> = [];
    /**
     * 增加一个关键帧，其中抛物线顶点坐标为(h,k),(x,y)为抛物线经过的任意另外一点
     * @param h 
     * @param k 
     * @param x 
     * @param y 
     * @param begin 该关键帧起作用的起始点
     * @param end 该关键帧起作用的结束点
     */
    public addKeyFrame(h:number,k:number,x:number,y:number,begin:number,end:number):void {
        let a = (y-k)/(x-h)/(x-h);
        let frame = {a:a,h:h,k:k,begin:begin,end:end};
        this._frameList.push(frame);
    }

    public evaluate(t:number):number {
        if(t < 0) t=0;
        if(t > 1) t=1;

        let frame
        for (let i = 0; i < this._frameList.length; i++) {
            frame = this._frameList[i];
            if(t >= frame.begin && t <= frame.end) {
                return frame.a*(t-frame.h)*(t-frame.h)+frame.k;
            }
        }

        return 0;
    }
}