            this._btnBack.off("click", this, this.onBack);
            this._btnLvup.off("click", this, this.onLevelUp);
            userData.off("UPDATE_GOLD", this, this.onUpdateGold);
            this._btnLvupAd.off("click", this, this.onLevelUp);
            this._view.btnAddCoin.off("click", this, this.openShop);
