    private missionDec(type:number,value:number,target:number){
        let t = {"1001" : "野牛" , "1002" : "斑马" , "1003" : "大象" , "1004" : "鸵鸟" , "1005" : "长颈鹿" , "1006" : "秃鹫" , "1007" : "狮子",
        "1008" : "野猪" , "1009" : "大猩猩" , "1010" : "鳄鱼" , "1011" : "河马" , "1012" : "犀牛" , "1013" : "老虎" , "1014" : "巨嘴鸟" , "0" : "稀树草原" , "1" : "丛林"}
        let tDec = [ "驯服了"+value+"只动物" ,"将"+t[target]+"的栖息地升到"+value+"级" ,"将"+t[target]+"的栖息地升级"+value+"次" , "再完成"+value+"个任务"]
        return tDec[type]
    }

    // 0.驯服了value只动物 1.将target的栖息地升到value级 2.将target的栖息地升级value次 3.再完成value个任务
    private taskClass(type:number,value:number,target:number){
        let reason = {"flag" : false , "dec" : ""};
        let howTodo = [userData.animalsNum , userData.enclosure , userData.uplvlTime , userData.compeletNum]
        if (howTodo[type] != null){
            if (target > -1){
                if (howTodo[type][target] >= value){
                    reason.flag = true;
                }
                reason.dec = this.missionDec(type,value,target)
            }else{
                if(howTodo[type] >= value){
                    reason.flag = true;
                    reason.dec = this.missionDec(type,value,target)
                }else{
                    if (type == 3){
                        reason.dec = this.missionDec(type,value - howTodo[type],target)
                    }else{
                        reason.dec = this.missionDec(type,value,target)
                    }
                }
            }
        }
        return reason
    }

