	public onAwake():void {
		CameraRig.instance = this;
        this.transform = (this.owner as GameObject).transform;
        this.gameObject = this.owner as GameObject;

        this._offsetY = ZY.findChildByPath(this.owner as Laya.Sprite3D, "CameraOrbit.SelfieOffsetY").transform;
        this._ground = (this.owner as Laya.Sprite3D).getChildByName("GroundPlane") as Laya.MeshSprite3D;
        this._ground.active = true;
		this._groundMat = this._ground.meshRenderer.material as Laya.UnlitMaterial;
		this._groundMat.tilingOffsetX = 10;
		this._groundMat.tilingOffsetY = 10;

		this._ground.transform.localPosition = this._offsetY.localPosition = ZY.Temp.getVector3(0,0,userData.startPos+10);

		// let obj = this.gameObject.getChildByName("CameraOrbit") as Laya.Sprite3D;
		// obj.transform.localPosition = new Vector3(0,54,-76);
		// obj.transform.localPositionY = -15;
		
		this._crashPosition = new Vector3();
		this.camera = ZY.findChildByPath(this.gameObject, "CameraOrbit.SelfieOffsetY.SelfieOffsetX.AnimOffset.AnimDolly.ShakeOffset.Main Camera.Camera");
		// this._cameraTarget = new Vector3();

		let worldIndex = userData.get("CURRENT_WORLD", 0);
		let worldName = userData.cfgWorld[worldIndex].worldName;
		let cfg = userData.cfgGround[worldName];
		this._groundColors = cfg.groundColor;

		this._groundMat.albedoColor.fromArray(this._groundColors[0]);
		this._groundMat.albedoColor = this._groundMat.albedoColor;

		ZY.eventBus.on("CHANGE_WORLD", this, this.onChangeWorld);
	}

	private onChangeWorld(index):void {
		if(index < 0 || index > 2) index = 0;
		this._groundMat.albedoColor.fromArray(this._groundColors[index]);
		this._groundMat.albedoColor = this._groundMat.albedoColor;
	}
	
	public destroy():void {
		this._playerTransform = null;
		this.gameObject.destroy(true);
		this.gameObject = null;

		ZY.eventBus.off("CHANGE_WORLD", this, this.onChangeWorld);

		super.destroy();
	}

    public onStart():void {
		this._playerTransform = moduleBridge.player.transform;
		
		Laya.Tween.to(this, {leadOffset:0}, this._leadTransitionDuration*1000, Laya.Ease.cubicOut);

		// if(moduleBridge.teachCtrl.status > 0) {
		// 	this._lead = -10;
		// }
		// Laya.timer.scale = 0.1;
	}
	
	public reset():void {
		this._ground.transform.localPosition = this._offsetY.localPosition = ZY.Temp.getVector3(0,0,userData.startPos+10);
		Laya.Tween.to(this, {leadOffset:0}, this._leadTransitionDuration*1000, Laya.Ease.cubicOut);
		this._groundMat.albedoColor.fromArray(this._groundColors[0]);
		this._groundMat.albedoColor = this._groundMat.albedoColor;
		this._groundMat.tilingOffsetW = 0;
		// _groundMat
	}

	public fuhuo(z:number):void{
		this._ground.transform.localPosition = this._offsetY.localPosition = ZY.Temp.getVector3(0,0,z+10);
	}
    // public crashed():void {
