    private getPosition():Vector3 {
		if (moduleBridge.player.state == PlayerState.crashed || moduleBridge.player.state == PlayerState.UI) {
            return this._crashPosition;
        } else {
            let xPos = this._playerTransform.position.x-1;
            let zPos = this._playerTransform.position.z + this._lead + this.leadOffset;
            let yPos = 0;

			let pathMiddle = WorldCreater.instance.mid;//地图中心点的坐标
			// console.log(pathMiddle);
            xPos = xPos * 0.9 + pathMiddle * 0.1;
            xPos = Helper2D.Clamp(xPos, this._playerTransform.position.x - 3, this._playerTransform.position.x + 3);
            this._prevFrameXPos = xPos;
			this._crashPosition.setValue(xPos, yPos, zPos);
            return this._crashPosition;
        }
    }

