import AnimalZoomer from "./AnimalZoomer";
import Animal from "../animalMove/ZooAnimal";
import { moduleBridge } from "../Interface";
import Player from "./Player";
import { UpgradeKeys } from "../UpgradeKeys";

export default class AnimalZoomerDontCrash extends AnimalZoomer {


    public onAwake():void{
		super.onAwake();
        this.coinsFromGoingMaxSpeed = true;
    }


	protected animalCrash(otherAnimal):void {
		// let special = 0
		let special = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.special)

		if (special > 0 && this.angerStage >= 3) {
			moduleBridge.player.jump(this.transform.localRotationEulerY, true)
			// moduleBridge.player.effectMountedCrash.Spawn(moduleBridge.player.transform.position, moduleBridge.player.effectMountedCrash.transform.rotation).TriggerEffect();
		} else {
            super.animalCrash(otherAnimal);
		}
	}

	protected obstacleCrashVerified(obstacle) {
		// let special = 0
		let special = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.special)
		
		if (special > 0 && this.angerStage >= 3) {
			moduleBridge.player.jump(this.transform.localRotationEulerY,true)
			// Player.instance.effectMountedCrash.Spawn(Player.instance.transform.position, Player.instance.effectMountedCrash.transform.rotation).TriggerEffect();
		} else {
			super.obstacleCrashVerified(obstacle);
		}
	}
}
