import Animal from "./Animal";
import Helper2D from "../Helper2D";
import { moduleBridge } from "../Interface";
import { UpgradeKeys } from "../UpgradeKeys";


export default class AnimalThrower extends Animal {

    protected defaultRunSpeed = 0;
	// [Header("Slap")]
	public slapWhenAngry:boolean = false;
	public slapEffectsPrefab;
    public slapPos;
    public ignoreCoinsOnThrow:boolean;
    public onAwake(){
        this.defaultRunSpeed = Math.max(this.defaultRunSpeed, this.runSpeedModifier);
        // anim.updateMode = AnimatorUpdateMode.Normal;
		this.runSpeedModifier = this.defaultRunSpeed;
		super.onAwake();
    }

	protected getAngry() {
		// frontCollider.enabled = true;
		// anim.updateMode = AnimatorUpdateMode.Normal;
        this.runSpeedModifier = this.defaultRunSpeed;
		//Player.Instance.transform.position = transform.position + (transform.rotation * launchPos);
		moduleBridge.player.jump(this.transform.localRotationEulerY, false, true)
		this.Bark(50)
		this.slapPlayer();
	}

	protected slapPlayer() {
		if(this.slapWhenAngry) {
			if(this.slapEffectsPrefab == null) {
				return;
			}
			// EffectsEvent slapEffect = slapEffectsPrefab.Spawn(slapPos.position, slapEffectsPrefab.transform.rotation);
			// if(slapEffect == null) {
			// 	return;
			// }
			// slapEffect.TriggerEffect();
		}
	}

	protected enterGoingWild ()
	{
		// frontCollider.enabled = false;
		moduleBridge.player.blockJumping(this.goingWildLength);
		// anim.updateMode = AnimatorUpdateMode.UnscaledTime;
		// Auto.Sleep(goingWildLength, goinWildTimeScale)
		this.runSpeedModifier = 0;
		super.enterGoingWild ();
	}


	public jumpOverride (angle:number)
	{
		if (this.angerStage < 2 && moduleBridge.player.isGroundAtLocation(this.transform.position)) {
			this.currentAngle = angle;
            Helper2D.SetLocalEulerY(this.transform,angle);
            Helper2D.SetLocalEulerY(this.offset,0);
			this.angerStage = 2;
			this.enterGoingWild();
		}
	}

	protected moveAnimal(speed:number):void{
		super.moveAnimal(speed);
	}

	protected turnOrBuck ()
	{
		if (this.angerStage < 2) {
			super.turnOrBuck();
		}
	}

	public GamePaused ()
	{
		// anim.updateMode = AnimatorUpdateMode.Normal;
		super.gamePaused ();
	}

	public gameUnpaused ()
	{
		if (this.angerStage == 2) {
			// anim.updateMode = AnimatorUpdateMode.UnscaledTime;
		}
		super.gameUnpaused ();
	}

	public (startingAnimal:boolean,fhtime ?:number,fhdis ?:number):void 
	{
		this.setupRide ();
		super.ride (startingAnimal,fhtime,fhdis);
	}

	protected setupRide ()
	{
		let specialCoins = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.specialCoins)

		if (specialCoins > 0 && !this.ignoreCoinsOnThrow) {
			// moduleBridge.player.SetJumpCoins(specialCoins);
		}
	}















}