    private stopMaoPao():void {
        this._mainView.on (Laya.Event.MOUSE_DOWN, this, (e:Laya.Event)=>{
            e.stopPropagation ();
        })
        for (let i:number = 0; i < this._mainView.numChildren; i ++) {
            let node = this._mainView.getChildAt (i) as Laya.Sprite;
            node.on (Laya.Event.MOUSE_DOWN, this, (e:Laya.Event)=>{
                e.stopPropagation ();
            })
        }
    }

