    protected playAni(name:string,layer:number=0,time:number=0):void {
        ZY.animatorTrigger.stopTrigger(this._ani);
        if(name == "ridden") {
            this._ani.play("FX_ridden", 1, 0);
        } else {
            this._ani.play("empty", 1, 0);
        }
        this._ani.play(name, layer, time);
    }

    protected triggerAni(names:Array<string>):void {
        ZY.animatorTrigger.playTrigger(this._ani, names);
    }

