    private setNodeMat(node:Laya.Sprite3D):void {
        if(node instanceof Laya.SkinnedMeshSprite3D && node.name !== "eye" && node.name !== "biteMesh") {
            (node as Laya.SkinnedMeshSprite3D).skinnedMeshRenderer.material = this._mat;
        }
        if(node.numChildren > 0) {
            for (let i = 0; i < node.numChildren; i++) {
                const element = node.getChildAt(i);
                this.setNodeMat(element as Laya.Sprite3D);
            }
        }
    }

    public isClearForward(layerMask:number):Threat {
        let hit:Laya.HitResult = this._hit;
		let closestThreat = new Threat();
        closestThreat.dist = 100;
        let ray = ZY.Temp.ray;
        let tmp = new Vector3();

        Vector3.transformQuat(ZY.Temp.getVector3(this._boxCollider.sizeX/2 + 0.7, 0.2, 0), this.transform.rotation, tmp);
        ray.origin.setValue(this.transform.position.x+tmp.x,this.transform.position.y+tmp.y,this.transform.position.z+tmp.z);
        Vector3.transformQuat(ZY.Temp.FORWARD, this.transform.rotation, tmp);
        ray.direction = tmp;

        if(ZY.stage.scene3D.physicsSimulation.rayCast(ray, hit, this._boxCollider.sizeZ/2+15, CollisionGroup.ANIMAL, layerMask)){
			closestThreat.dist = Vector3.distance(hit.point, ray.origin)-this._boxCollider.sizeZ/2;//hit.distance - boxCollider.size.z/2;
			closestThreat.transform = (hit.collider.owner as GameObject).transform;
            closestThreat.pointOfRayImpact = hit.point;
        }
        
        Vector3.transformQuat(ZY.Temp.getVector3(-this._boxCollider.sizeX/2 - 0.7, 0.2, 0), this.transform.rotation, tmp);
        ray.origin.setValue(this.transform.position.x+tmp.x,this.transform.position.y+tmp.y,this.transform.position.z+tmp.z);
        Vector3.transformQuat(ZY.Temp.FORWARD, this.transform.rotation, tmp);
        ray.direction = tmp;
		if (ZY.stage.scene3D.physicsSimulation.rayCast(ray, hit, this._boxCollider.sizeZ/2+15, CollisionGroup.ANIMAL, layerMask)) {
			if (Vector3.distance(hit.point, ray.origin)-this._boxCollider.sizeZ/2 < closestThreat.dist) {
				closestThreat.dist = Vector3.distance(hit.point, ray.origin)-this._boxCollider.sizeZ/2;
				closestThreat.transform = (hit.collider.owner as GameObject).transform;
                closestThreat.pointOfRayImpact = hit.point;
            }
        }
        Vector3.transformQuat(ZY.Temp.getVector3(0, 0.2, 0), this.transform.rotation, tmp);
        ray.origin.setValue(this.transform.position.x+tmp.x,this.transform.position.y+tmp.y,this.transform.position.z+tmp.z);
        Vector3.transformQuat(ZY.Temp.FORWARD, this.transform.rotation, tmp);
        ray.direction = tmp;
		if (ZY.stage.scene3D.physicsSimulation.rayCast(ray, hit, this._boxCollider.sizeZ/2+15, CollisionGroup.ANIMAL, layerMask)) {
            if (Vector3.distance(hit.point, ray.origin)-this._boxCollider.sizeZ/2 < closestThreat.dist) {
				closestThreat.dist = Vector3.distance(hit.point, ray.origin)-this._boxCollider.sizeZ/2;
				closestThreat.transform = (hit.collider.owner as GameObject).transform;
                closestThreat.pointOfRayImpact = hit.point;
            } else if ((hit.collider.owner as GameObject).transform == closestThreat.transform) {
                closestThreat.pointOfRayImpact = hit.point;
            }
        }

		return closestThreat;
	}

    public revoverSelf():void {
        Laya.timer.clearAll(this);
        this.state = AnimalState.waitingForRecycle;
        this.stopDustPfx();
        this.gameObject.removeSelf();
        if(this.stampedeAI) this.stampedeAI.init();
        this.hideNew();
        moduleBridge.animalManager.recover(this);
    }

