    public init(id:string, obj:GameObject, resetPos:boolean = true):void{
        this.rideTime = null;
        this.stayDis = null;
        this.stayTime = null;
        this.animalId = id;
        let animalInfo = userData.cfgAnimal[id];
        let linkEncId = animalInfo.linkEncId;
        if(linkEncId) this.encId = linkEncId;
        else this.encId = animalInfo.encId;
        this._skinIndex = animalInfo.skinIndex || 0;
        this._hatExp = animalInfo.exp || 0;
        // this.encId = userData.cfgAnimal[id].encId;
        let info = Laya.loader.getRes("cfg/cfgAttribute.json")[this.encId];
        this.gameObject = obj;
        this.transform = obj.transform;
        this._albedoColor = new Laya.Vector4(1,1,1,1);
        if(!this._mat) {
            this._mat = (this.gameObject as Laya.SkinnedMeshSprite3D).skinnedMeshRenderer.material.clone();
            this._mat.tilingOffset = new Laya.Vector4(1,1,this._skinIndex/16,0);
        }
        
        this.smashStrength = info.smashStrength,            
        this._defaulsmashStrength = info.smashStrength,            
        this.stampedingSpeedModifier = info.stampedingSpeedModifier,
        this.runSpeedModifier = info.runSpeedModifier
        this._defaultrunSpeedModifier = info.runSpeedModifier
        this.playerJumpHeightMultiplier = info.playerJumpHeightMultiplier,
        this.playerJumpSpeedMultiplier = info.playerJumpSpeedMultiplier,
        this.playerLassooBoostMultiplier = info.playerLassooBoostMultiplier,
        this.hasJumpOverride = info.hasJumpOverride,
        this.edible = info.edible,
        this.buckArray = info.buckArray
        this.playerJointOffset = info.playerJointOffset
        this.runLength = info.runLength
        this.goingWildLength = info.goingWildLength
        this.centeringScale = info.centeringScale
        this.waterSpeedMultiplier = info.waterSpeedMultiplier
        this._barkSound = info.bark
        this.leanScale = info.leanScale || 0
        if(id === "900"){
            this.runLength = Number.MAX_VALUE;
            this.stampedingSpeedModifier = 0;
        }
        this.buckWhenAngry = info.buckWhenAngry;
        this.angryRunSpeedModifier = info.angryRunSpeedModifier
        this.smashAnimals = info.smashAnimals,
	    this.smashAnimalStengthLimited = info.smashAnimalStengthLimited,
	    this.immuneToSmashing = info.immuneToSmashing
	    this.immuneToObstaclesWhileBoosting = info.immuneToObstaclesWhileBoosting
	    this.captureLengthArray = info.captureLengthArray
        if (info.buckArc && info.buckArc.length > 0){
            let curve = new AnimationCurve();
            for (let i = 0 ; i < info.buckArc.length ; i ++) {
                curve.addKeyFrame(info.buckArc[i][0],info.buckArc[i][1],info.buckArc[i][2],info.buckArc[i][3],info.buckArc[i][4],info.buckArc[i][5])
            }
            this.buckArc = curve
        }
        this.playerJoint = ZY.findChildByPath(this.gameObject, info.playerJoint).transform;
        this._shadowQuad = (obj.getChildByName("shadowQuad") as GameObject).transform;
        this._ctrlHips = (this.playerJoint.owner.parent.parent as GameObject).transform;
        this.strName = userData.cfgAnimal[id].name;
        this.sex = Math.random() > 0.5 ? 1 : 2

        this.transform.getWorldLossyScale().cloneTo(this._oldScale)
        this._oldScale = this._oldScale
        // let names = Laya.loader.getRes("cfg/cfgNickname.json")[this.sex];
        // this.nickName = names[Helper2D.GetRandom(0,names.length-1,true)]

        this.offset = (this.gameObject.getChildByName("offset") as GameObject).transform;
        this._ani = obj.getComponent(Animator);
        this._stampedingSpeedRandomRangeDeviation = info.stampedingSpeedRandomRangeDeviation;
        if(!this._dust) {
            this._dust = obj.getChildByName("pfx_dust2") as Laya.ShuriKenParticle3D;
        }
        if(!this._dust.parent) {
            this.gameObject.addChild(this._dust);
        }

        this.stampedeAI = this.gameObject.addComponent(StampedeAI);

        let joint = obj.getChildByName("LOC_pfx_above") as GameObject;
        let rigidbody = obj.getComponent(Laya.Rigidbody3D) as Laya.Rigidbody3D;
        if(rigidbody) {
            rigidbody.isTrigger = true;
        }

        if(joint) this._headJoint = joint.transform;
        else this._headJoint = this.playerJoint;
        //速度配置
        this.reset(resetPos)
        // if(moduleBridge.teachCtrl.status > 0) {
        //     this._warning = moduleBridge.teachCtrl.warnInfo;
        // } else {
        //     this._warning = moduleBridge.gameCtrl.warnInfo;
        // }
        this._warning = moduleBridge.warning;
    }

    protected startDustPfx():void {
        if(this._dust && !this._disableDust && !this._dustPlaying) {
            ZY.playPart(this._dust);
            this._dustPlaying = true;
            // if(!this._dust.parent) {
            //     this.gameObject.addChild(this._dust);
            // }
        }
    }

    protected stopDustPfx():void {
        if(this._dust && this._dustPlaying) {
            ZY.stopPart(this._dust);
            this._dustPlaying = false;
            // this._dust.removeSelf();
        }
    }

    public refreshName():void{
        let list = []
        let info = Laya.loader.getRes("cfg/cfgNickname.json")[this.sex];
        let encAnimals = userData.getAnimals(Number(this.encId))
        for (let i = 0; i < encAnimals.length; i++) {
            const name = userData.nickName[encAnimals[i]]
            list.push(name)
        }
        let nickName:number;
        while (true){
            let index = Helper2D.GetRandom(0,info.length-1-list.length,true)
            if (list.indexOf(index) > -1){
                continue
            }else{
                nickName = index
                break
            }
        }
        userData.setNickName(Number(this.animalId),nickName,this.sex)
    }


	public onAwake() {
        this.gameObject = this.owner as Laya.Sprite3D;
        this.transform = this.gameObject.transform;

        let co = this.gameObject.getComponent(Laya.Rigidbody3D) as Laya.Rigidbody3D;

        if(co) {
            co.collisionGroup = CollisionGroup.ANIMAL;
            this._boxCollider = co.colliderShape as Laya.BoxColliderShape;
            co.canCollideWith = CollisionGroup.ANIMAL | CollisionGroup.WORLD_SIDE | CollisionGroup.STAMPEDE_AI | CollisionGroup.OBSTACLE |CollisionGroup.WATER;
        }


        ZY.setCollisionGroupSingle(this.gameObject, "StampedeAI", CollisionGroup.STAMPEDE_AI, CollisionGroup.ANIMAL);
        ZY.setCollisionGroupSingle(this.gameObject, "landingCollider", CollisionGroup.LANDING_COLLIDER, CollisionGroup.LANDING_RANGE);
    }
    
    public colorYellow():void {
        // if(this.state == AnimalState.ridden) console.log("yellow");
        this._albedoColor.fromArray([1,1,0,1]);
        this._mat.emissionColor = this._albedoColor;
        this.setNodeMat(this.gameObject);
    }

