        this.stayTime = 0;
        this.rideTime = Math.floor(Laya.Browser.now()/1000);

        this.stayDis = moduleBridge.player.transform.localPositionZ

        // this._transform.localRotationEulerY = 0
        Helper2D.SetLocalEulerY(this.transform,0)
		// anim.SetTrigger(animTriggerLanded);
        this.aniRidden();
        Laya.systemTimer.clear(this, this.showNew);
