    private _finishedBucking = false;
    protected _buckSize:number;
	protected buckFunction(){
		if (this._finishedBucking == true) {
			return;
		}
		// let buckLengthMultiplier = 1
		// let shockwaveOnBuck = 0
        // let specialCoins = 0
        let buckLengthMultiplier = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.buckLengthMultiplier)
        let shockwaveOnBuck = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.shockwaveOnBuck)
        let specialCoins = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.specialCoins)
		let babyBuckSizeMultiplier = 1

		this.angryBuckTimer -= Laya.timer.delta/1000
        this._buckSize = Math.min(this.buckArray[this.buckArrayCounter].maxBuckLength * buckLengthMultiplier * babyBuckSizeMultiplier, this.buckBuildUpCount * 0.2);
		if (this.angryBuckTimer <= 0) {

            this.buckArrayCounter = (this.buckArrayCounter + 1) % this.buckArray.length;
            if (this.buckArrayCounter == 0 && shockwaveOnBuck > 0) {
                this.stampedeAI.bounce();
            }
            this.buckBuildUpCount *= 1.45;

            Helper2D.SetLocalEulerY(this.transform,this.currentAngle);

            this._buckSize = Math.min(this.buckArray[this.buckArrayCounter].maxBuckLength * buckLengthMultiplier, this.buckBuildUpCount * 0.2);
            this.angryBuckTimer = this._buckSize;
            PfxHelper.instance.show("pfx_dustcloud_sml",this.transform.localPosition)
            if (specialCoins > 0 && Helper2D.GetRandom(0, 10,true) > 4 && this.angerStage > 1) {
                PfxHelper.instance.show("pfx_money_1",this.transform.position);
                moduleBridge.gameCtrl.addGold(specialCoins,1007,true)
            }
		}
		if (!this._finishedBucking) {
            let t = (this._buckSize - this.angryBuckTimer) / this._buckSize;
			this.setBuckAnimTime(t);
			moduleBridge.player.setPlayerBuckAnim(t);
            Helper2D.SetEulerY(this.offset,this.currentAngle);
            Helper2D.SetLocalPosY(this.transform,this.buckArc.evaluate(t) * this._buckSize * this.buckArray[this.buckArrayCounter].heightModifier - this.waterDepth * this.maxWaterDepth)
		}
	}

    private setBuckAnimTime(t){
        // 播放跳跃动画
        if (this.animalId !== "902"){
            this._ani.play("buck", 0 , t);
        }
        // this.playAni("buck",0,t);
    }

	public startFall(isLeftSide :boolean, fromCollision:boolean, isFrontEdge:boolean = false) {
        if (!isFrontEdge) {
            if (isLeftSide) {
                if (this.currentAngle > 0) {
                    this.currentAngle = 0;
                    this.transform.localRotationEulerY = this.currentAngle;
                    // transform.SetEulerY(this.currentAngle);
                }
            } else {
                if (this.currentAngle < 0) {
                    this.currentAngle = 0;
                    this.transform.localRotationEulerY = this.currentAngle;
                    // transform.SetEulerY(this.currentAngle);
                }
            }
        }
		this.state = AnimalState.falling;
		this.fallTimer = 0;
		this.fallSpeed = this.speed;
		// CameraTarget.Instance.Crashed();
		// AnimalCaptureController.Instance.CancelCapturing();
        // AudioGlobal.Instance.StartFallCrash();
		this.fallPunt(isLeftSide, isFrontEdge)
		this.resetWarning()

		if (this.buckWhenAngry && this.angerStage == 3) {
			this._finishedBucking = true;
			// anim.SetBool(animBoolIdle, true);
			// anim.SetTrigger(animTriggerResetIdle);
			this.aniSpeed = 1;
            // transform.SetPosY(0);
            Helper2D.SetLocalPosY(this.transform,0)
		}
		// UIController.Instance.pauseButton.SetActive(false);
    }
    
    protected fallPunt(isLeft:boolean, isFrontEdge:boolean = false) {
		while (this.state == AnimalState.falling) {
            if (isFrontEdge) {
                if (this.transform.localRotation.x < 50) {
                    this.transform.rotate(ZY.Temp.getVector3(90 * Laya.timer.delta,0,0));
                }
                this.transform.translate(ZY.Temp.getVector3(-5 * Laya.timer.delta,0,0));
            } else if (isLeft) {
				if (this.transform.localRotation.z < 50) {
					this.transform.rotate(ZY.Temp.getVector3(0,0,90 * Laya.timer.delta));
				}
				this.transform.translate(ZY.Temp.getVector3(-5 * Laya.timer.delta,0,0));
			} else {
				if (this.transform.localRotation.z > -50) {
					this.transform.rotate(ZY.Temp.getVector3(0,0,-90 * Laya.timer.delta));
				}
                this.transform.translate(ZY.Temp.getVector3(5 * Laya.timer.delta,0,0));
			}
		}
    }
    
    protected resetWarning(){
        Laya.timer.once(1000, this, () => {
            // anim.SetTrigger (animTriggerResetWarning);
        })
    }
    
	protected calculateCurrentAngle() {
		if (this.speed > 0) {

			let clampedSpeed = Math.max(this.speed, 8);

			this.xVelocity += ((clampedSpeed * 0.35) + (25 * (1 - 0.35))) * Math.sin(Math.abs(this._inputthisframe) * Math.PI/180) * Math.sign(this._inputthisframe) * 1.5;
			// this.xVelocity += ((clampedSpeed * 0.35) + (25 * (1 - 0.35))) * Math.sin(Math.abs(inputThisFrame) * Mathf.Deg2Rad) * Mathf.Sign(inputThisFrame) * Player.Instance.GetSteeringSensitivity();
			this.xVelocity = Helper2D.Clamp(this.xVelocity,this.speed * -0.7, this.speed * 0.7)

			this._inputthisframe = 0;

			// console.log(this.xVelocity)
			this.currentAngle = Math.asin(Math.min(Math.abs(this.xVelocity) / clampedSpeed, 1)) * Math.sign(this.xVelocity) * (180/Math.PI)

		}
		this.centerXVelocity();
	}

	private centerXVelocity() {
        this.xVelocity = Helper2D.MoveTowards(this.xVelocity, 0, Math.pow(Math.abs(this.xVelocity), 1.5) * 1.5  * Laya.timer.delta/1000);
	}


