    private _inWater:boolean = false;
    public onTriggerEnter(other:Laya.PhysicsComponent) {
        // console.log(other.owner.name);
        if((other.owner as GameObject).layer == 4) {
            //layer==4是水
            // console.log("进入水中了");
            if (this.wetness < 0.7) {
                this._inWater = true
                this.wetness = 0.5;
                this.waterDepth = 0.65;
                PfxHelper.instance.show("pfx_splashMud_med",this.transform.localPosition);
                this.stopDustPfx();
                Helper2D.SetPosY(this.transform,-this.waterDepth *this.maxWaterDepth)

                let waterIncreaseRideLength = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.waterIncreaseRideLength)

                if (waterIncreaseRideLength > 0 && this.angerStage < 1 && this.state == AnimalState.ridden && moduleBridge.player.state == PlayerState.riding) {
                    this._angerTimer += waterIncreaseRideLength;
                }
                Laya.timer.frameLoop(1,this,this.inWater)
            }
        }
        this.RidingCollision(other);
    }

    private inWater():void{
        if (this._inWater){
            this.wetness = Math.min(this.wetness + 1.1 * Laya.timer.delta/1000, 1);
            this.waterDepth = Math.min(this.waterDepth + 4.2 * Laya.timer.delta/1000, 1);
        }else{
            Laya.timer.clear(this,this.inWater)
        }
    }

    public onTriggerExit(other:Laya.PhysicsComponent) {
        if((other.owner as GameObject).layer == 4) {
            //layer==4是水
            this._inWater = false
            // console.log("离开水了");
        }
    }

	public RidingCollision(other) {

        // let smashStrengthModifier = 0
        let smashStrengthModifier = moduleBridge.gameCtrl.upgradeValue(this.encId,UpgradeKeys.smashStrength);
		//Debug.Log("riding collision 1");
		if (this.state == AnimalState.ridden && moduleBridge.player.state == PlayerState.riding) {
			let otherAnimal = other.owner.getComponent(Animal)
			if (otherAnimal == null) {
				// otherAnimal = other.GetComponentInParent<Animal> ();
			}

			if (otherAnimal) {
				if (otherAnimal.state != AnimalState.crashed) {
					if (this.smashAnimals && (this.smashStrength + smashStrengthModifier > otherAnimal.smashStrength || !this.smashAnimalStengthLimited) && !otherAnimal.immuneToSmashing) {
                        this.smashAnimal(otherAnimal);
                    } else {
						this.animalCrash(otherAnimal);
					}
				}
            }
            let obstacle = (other.owner as Laya.Sprite3D)
            if (obstacle.layer == 8 || obstacle.getComponent(Obstacle) ){
                // this.obstacleCrashVerified(obstacle)
                let obj = obstacle.getComponent(Obstacle)
                if (obj){
                    if (obstacle.name == "coin"){
                        obj.collect(this.encId)
                        userData.add_stat_boxs(1)
                        return
                    }
                    if (!obj.isLevelEdge && this.smashStrength + smashStrengthModifier > obj.smashStrength) {
                        if (!obj.smashed) {
                            this.smashObstacle(obj);
                        }
                    } else {
                        this.obstacleCrash(obj);
                    }
                }else{
                    this.obstacleCrash(obstacle);
                }
            }
		}
	}

	protected obstacleCrashVerified(obstacle) {
        // this.leaveAnimal(true);
        if (this.immuneToObstaclesWhileBoosting && this._immuneToAnimals) {
			if (!obstacle.isLevelEdge && !obstacle.isCenterEdge) {
				return;
			}
		}
        this.state = AnimalState.crashed;
		moduleBridge.player.mountedCrash(true, "HIT_OBSTACLE");
	}

    public smashAnimal (otherAnimal:IAnimal,wasSmashedByPlayer:boolean = true) {
		if (otherAnimal.immuneToSmashing || otherAnimal.encId == "1006") {
			// otherAnimal.shockwaveKnockback(this.transform.localPosition);
		} else {
			if (wasSmashedByPlayer) {
				moduleBridge.player.lastAnimalSmashed = otherAnimal;
            }
            ZY.eventBus.event("TASK_UPDATE", ["CRASH_ANIMAL", 0, 1]);
            ZY.eventBus.event("TASK_UPDATE", ["CRASH_ANIMAL_IN_ONE", 0, 1]);
            ZY.eventBus.event("TASK_UPDATE", ["CRASH_ANIMAL_WITH", this.encId, 1]);
            ZY.eventBus.event("TASK_UPDATE", ["CRASH_ANIMAL_IN_ONE_WITH", this.encId, 1]);
            userData.add_stat_smashAnimals(1)
            if ((ZY.channel == "zj" || ZY.channel == "wx") && this.animalId == "141" && otherAnimal.animalId == "101"){
                // if(this.animalId == "141" && otherAnimal.animalId == "101"){
                this._narrow -= 1
                if (this._narrow < 0) this._narrow = 0
                let proportion = 0.3 + this._narrow * 0.35
                this.transform.setWorldLossyScale(new Laya.Vector3(this._oldScale.x*proportion,this._oldScale.y*proportion,this._oldScale.z*proportion))
            }
            otherAnimal.animalSmashed();
            this.Bark(30)
		}
    }

