    protected smashFinished:boolean;
	protected animalSmashedSequence() {
        this.playAni("smashed")
		let gravity = 210;
        let velocity = new Vector3(Helper2D.GetRandom(-20,20,true),Helper2D.GetRandom(40,55,true),Helper2D.GetRandom(75,95,true))
        let rotationY = Helper2D.GetRandom(600,1600,true)* Helper2D.RandomSign()
        this.smashFinished = false;
        // this.stopDustPfx();
        Laya.timer.frameLoop(1, this, ()=>{
            if (!this.smashFinished){
                this.transform.localPositionX += velocity.x * Laya.timer.delta/1000
                this.transform.localPositionY += velocity.y * Laya.timer.delta/1000
                this.transform.localPositionZ += velocity.z * Laya.timer.delta/1000
                this.offset.localRotationEulerY += rotationY * Laya.timer.delta/1000
                velocity.y -= gravity * Laya.timer.delta/1000
                if (velocity.y < 0 && this.transform.localPositionY < 0) {
                    PfxHelper.instance.show("pfx_dustcloud_sml",this.transform.localPosition)
                    this.smashFinished = true;
                    Laya.timer.once(2,this,() => {
                        this.revoverSelf()
                    })
                }
            }
        })
	}

