	public shockwaveKnockback(shockwavePos:Vector3) {
		if (this.state == AnimalState.ignoreAll) {
			return;
		}
		let dir = this.normalized(new Vector3(this.transform.localPositionX-shockwavePos.x,this.transform.localPositionY-shockwavePos.y,this.transform.localPositionZ-shockwavePos.z))
        let force = Helper2D.Map(Helper2D.GetDistance(new Vector3(shockwavePos.x,shockwavePos.y,shockwavePos.z+2),this.transform.localPosition),0,8,8,3)

		if (this.transform.localPositionZ > shockwavePos.z) {
			if (Math.abs(this.transform.localPositionX - shockwavePos.x) < 2) {
                dir.x = dir.z * Math.sign(dir.x) * 0.6
                dir = this.normalized(dir)
			}
		}
        this.knockback(new Vector3(dir.x * force , dir.y * force , dir.z * force))
	}
    
    private cancelKnockback:boolean = false; //is set on TriggerEnter to stop animal being pushed into obstacle or animals
	public knockback(distance:Vector3, duration:number = 0.35) {
        if(this.state == AnimalState.ridden) return;
		this.state = AnimalState.knockback;
		this.cancelKnockback = false;
		//local vars
		let start = this.transform.localPosition;
        // let target = start + distance;
        let target = new Vector3(start.x+distance.x,start.y+distance.y,start.z+distance.z)
        let ps = moduleBridge.worldCreater.getPathStateAt(start.z+distance.z)
        if (target.x > 0 && target.x > ps.left) target.x = ps.left
        else if (target.x < 0 && target.x < ps.right) target.x = ps.right
		// Easer ease = Ease.FromType(EaseType.QuadOut);
		let elapsed:number = 0;

        Laya.timer.frameLoop(1,this,this.knock,[start,target,duration])
    }
    
    private _elapsed:number;
    private knock(start:Vector3,target:Vector3,duration:number):void{
        this._elapsed = this._elapsed || 0
        if (this._elapsed < duration && !this.cancelKnockback) {
            this._elapsed = Helper2D.MoveTowards(this._elapsed, duration, Laya.timer.delta/1000);
            this.transform.localPositionX = Helper2D.Lerp(start.x,target.x,this._elapsed / duration)
            this.transform.localPositionY = Helper2D.Lerp(start.y,target.y,this._elapsed / duration)
            this.transform.localPositionZ = Helper2D.Lerp(start.z,target.z,this._elapsed / duration)
        }else{
            this._elapsed = null;
            Laya.timer.clearAll(this)
            if (!this.cancelKnockback) {
                this.transform.localPosition = target;
            }
            if (moduleBridge.player.steed == this) {
                this.state = AnimalState.ridden;
            } else {
                this.state = AnimalState.running;
            }
        }
    }



    public dryOff(changeYPos:boolean) {
        this.wetness = Math.max(this.wetness - 0.7*Laya.timer.delta/1000, 0);
        this.waterDepth = Math.max(this.waterDepth - 4 * Laya.timer.delta/1000, 0);
		if ((this.state != AnimalState.ridden || moduleBridge.player.state != PlayerState.waitingForStart) && this.state != AnimalState.launching && this.state != AnimalState.crashed && changeYPos && this.state != AnimalState.ignoreAll && !(this.encId == "1006")) {
            Helper2D.SetLocalPosY(this.transform,Helper2D.Lerp(this.transform.localPositionY, -this.waterDepth * this.maxWaterDepth, 7*Laya.timer.delta/1000))
        }
    }

