import { ZY } from "../ZY";
import { ui } from "../ui/layaMaxUI";
import ZooButton from "../script/ZooButton";
import { userData } from "../userData/UserData";
import { moduleBridge } from "../Interface";

export default class InviteCtrl extends ZY.BaseCtrl {
    constructor() { super(); }
    
    public start():void {
        super.start();
        this.bindAssets("res/atlas/invite.atlas", Laya.Loader.ATLAS, "zoo");
    }

    protected beforeEnterScene(sceneName:string):void {
        if(this._sceneName == "zoo") {
            this._btnBack.off("click", this, this.onBack);
            this._btnBack.clear();
            this._btnBack = null;
            this._btnGain.clear();
            this._btnGain = null;

            this._view.removeSelf();
            this._view.destroy(true);
            this._view = null;        
        }
    }

    private _view:ui.main.InviteFriendsUI;
    private _btnBack:ZooButton;
    private _btnGain:ZooButton;
    private _btnInvi:ZooButton;
    protected createChildren(sceneName:string):void {
        if(sceneName == "zoo") {
            if(!this._view) {
                this._view = new ui.main.InviteFriendsUI();
                this._btnBack = new ZooButton(this._view.btnBack);
                this._btnGain = new ZooButton(this._view.btnGain);
                this._btnInvi = new ZooButton(this._view.btnInvite);
            }
            this._btnBack.on("click", this, this.onBack);
            this._btnGain.on("click", this, this.onGain);
            this._btnInvi.on("click", this, this.onInvite);
            this.stopMaoPao ();
        }
    }

    private onInvite():void {
        // 邀请
        if (ZY.channel == "wx") {
            ZY.tmShare("share_friend")
        }else if (ZY.channel == "qq"){
            if ((window as any).qq) {
                ZY.TmCtrl.instance.tmSendEvent("share_phototeach",{value:"点击"})
                let list = [null,
                    ["开局一条龙，快来试玩吧！","https://zy-1257315588.file.myqcloud.com/zoo/share_img/share7.jpg"],
                    ["园长，快进来看看你的动物怎么样了？","https://zy-1257315588.file.myqcloud.com/zoo/share_img/share8.jpg"],
                    ["太魔性啦！这谁顶得住啊？","https://zy-1257315588.file.myqcloud.com/zoo/share_img/share9.jpg"],
                ]
                let index = Math.floor(Math.random() * 2 + 1);
                let self = this;
                (window as any).qq.shareAppMessage({
                    title: list[index][0],
                    imageUrl: list[index][1],
                    query: "gid="+ZY.gid
                });
            }
        }
    }

    private onGain():void {
        // 领取
		let hr: Laya.HttpRequest = new Laya.HttpRequest();
        hr.http.timeout = 5000;
        hr.once(Laya.Event.ERROR, this, (e: any) => {
            if(num < 5){
                num += 1
                hr.send(url, "", "get", "json");
            }else{
                ZY.msg.show("领取失败,请稍后再试~")
            }
        });
        hr.once(Laya.Event.COMPLETE, this, (data: any) => {
            if (data.code == 0 && data.data) {
                let tData = data.data
                if (tData.reward && tData.child !== null && tData.reward !== null && tData.count !== null){
                    userData.addGold(200*tData.count,1008)
                    userData.addJew(2*tData.count,1010)
                    userData.set("childnum",Number(tData.child));
                    userData.set("childreward",Number(tData.reward));
                    this.showReward()
                    ZY.msg.show("领取成功，"+200*tData.count+"金币，"+2*tData.count+"钻石。")
                }else{
                    ZY.msg.show("领取失败,请稍后再试~")
                }
			}else if (data.code == 1109){
                ZY.msg.show("已领取完全部奖励~")
            }else{
                ZY.msg.show("领取失败,条件不足~")
            }
        });
        let num:number = 0;
        let client = Laya.LocalStorage.getItem("zoozyClient");
        let time = Laya.Browser.now();
        let sha = ZY.md5(ZY.md5(ZY.key + ZY.noice) + time + ":" + client + "::" + ZY.noice);
        let url = "https://api.smartjoygames.com/zy/reward?sha="+sha+"&noice="+ZY.noice+"&time="+time+"&client="+client+"&game=zoo&channel="+ZY.channel+"&gid="+ZY.gid
        hr.send(url, "", "get", "json");
    }
    

    private stopMaoPao():void {
        this._view.on (Laya.Event.MOUSE_DOWN, this, (e:Laya.Event)=>{
            e.stopPropagation ();
        })
        for (let i:number = 0; i < this._view.numChildren; i ++) {
            let node = this._view.getChildAt (i) as Laya.Sprite;
            node.on (Laya.Event.MOUSE_DOWN, this, (e:Laya.Event)=>{
                e.stopPropagation ();
            })
        }
    }

    private _lasttime:number;
    private init():void {
        if (!this._lasttime || Laya.Browser.now()/1000 - this._lasttime >= 60){
            let hr: Laya.HttpRequest = new Laya.HttpRequest();
            hr.http.timeout = 5000;
            hr.once(Laya.Event.COMPLETE, this, (data: any) => {
                if (data.code == 0 && data.data) {
                    let tData = data.data
                    console.log(tData)
                    if (tData.child !== null && tData.reward !== null){
                        userData.set("childnum",Number(tData.child));
                        userData.set("childreward",Number(tData.reward));
                        this.showReward()
                    }
                }
            });
            let num:number = 0;
            let client = Laya.LocalStorage.getItem("zoozyClient");
            let time = Laya.Browser.now();
            let sha = ZY.md5(ZY.md5(ZY.key + ZY.noice) + time + ":" + client + "::" + ZY.noice);
            // let url = "http://192.168.170.240:9000/api/getRewardInfo?game=zoo&channel=qq&gid="+ZY.gid
            let url = "https://api.smartjoygames.com/zy/getRewardInfo?sha="+sha+"&noice="+ZY.noice+"&time="+time+"&client="+client+"&game=zoo&channel="+ZY.channel+"&gid="+ZY.gid
            console.log(url)
            hr.send(url, "", "get", "json");
        }
        this._lasttime = this._lasttime || Laya.Browser.now()/1000
    }


    private showReward():void{
        let child = userData.get("childnum",0);
        let reward = userData.get("childreward",0);
        console.log(child,reward)
        if (child <= reward){
            this._view.btnGain.disabled = true;
        }
        //0.未完成 1.未领取 2.已领取
        this._view.list.array = [
            { hasGain:0 },{ hasGain:0 },{ hasGain:0 },{ hasGain:0 },{ hasGain:0 }
        ];
        for (let i = 0; i < 5; i++) {
            let img = this._view["btnInvi"+i].getChildAt(0).getChildAt(0) as Laya.Image
            if (i < child){
                img.skin = "invite/touxiang.png"
                this._view.list.array[i].hasGain += 1
            }else{
                img.skin = ""
            }
            if (i < reward){
                this._view.list.array[i].hasGain += 1
            }
        }
    }

    private updateView():void {
        this.init ();
        this.showReward()
        this._view.list.renderHandler = new Laya.Handler(this, this.onTest);
        this._view.list.vScrollBarSkin = "";
    }

    private onTest(cell:Laya.Box, index:number):void {
        let imgStatus = cell.getChildByName("imgStatus") as Laya.Image;
        let data = cell.dataSource; // { hasGain:false },
        let list1 = {
            "0" : "invite/img_status0.png",
            "1" : "invite/img_status1.png",
            "2" : "invite/img_status2.png"}
        imgStatus.skin = list1[data.hasGain]

        let list2 = {
            0 : "一" ,1 : "二" ,2 : "三" ,3 : "四" ,4 : "五" 
        }
        let labelTip = cell.getChildByName("labelTip") as Laya.Label
        labelTip.text = "邀请第"+list2[index]+"位好友\n获得200金币 2钻石"
    }

    public showView():void {
        if(this._sceneName == "zoo") ZY.stage.addChild(this._view, ZY.Layer.MIDDLE);
        Laya.timer.callLater(this, this.updateView);
    }

    private onBack():void {
        this._view.removeSelf();
    }

}

moduleBridge.inviteCtrl = new InviteCtrl();