import { moduleBridge } from "../Interface";
import { ZY } from "../ZY";
import CollisionGroup from "../CollisionGroup";

export default class Chunk extends Laya.Script3D {
    constructor() {
        super();

        ZY.eventBus.on("RECOVER_CHUNK", this, this.recover);
    }
    
    public transform:Laya.Transform3D;
    public gameObject:Laya.Sprite3D;
    private _playerTransform:Laya.Transform3D;
    public name:string;
    public length:number;
    public init(obj:Laya.Sprite3D, name:string, isIsland:boolean = false):void {
        this.gameObject = obj;
        this.transform = obj.transform;
        this._playerTransform = moduleBridge.player.transform;
        this.name = name;
        if(isIsland) {
            ZY.setCollisionGroupSingle(obj, null, CollisionGroup.WORLD_SIDE, CollisionGroup.ANIMAL|CollisionGroup.PLAYER);
            obj.name = "island";
        } else {
            ZY.setCollisionGroupSingle(obj.getChildByName("collider") as Laya.Sprite3D, null, CollisionGroup.WORLD_SIDE, CollisionGroup.ANIMAL|CollisionGroup.PLAYER);
        }
        let river = obj.getChildByName("river") as Laya.Sprite3D;
        if(river) {
            ZY.setCollisionGroupSingle(river, null, CollisionGroup.WATER, CollisionGroup.ANIMAL);
        }

        // this.resetMat(moduleBridge.worldCreater.matBase, moduleBridge.worldCreater.matTop);
    }

    public resetMat(matBase:Laya.PBRStandardMaterial, matTop:Laya.PBRStandardMaterial, rock:Laya.PBRStandardMaterial):void {
        if(!matTop || !matBase) return;
        let c;
        let obj = this.gameObject;
        for (let i = 0; i < obj.numChildren; i++) {
            c = obj.getChildAt(i) as Laya.MeshSprite3D;
            if(c && c instanceof Laya.MeshSprite3D) {
                if(c.name.indexOf("_top") > 0) {
                    c.meshRenderer.material = matTop;
                } else if(c.name.indexOf("_base") > 0) {
                    c.meshRenderer.material = matBase;
                } else if(c.name.indexOf("rock") >= 0) {
                    c.meshRenderer.material = rock;
                }
            }
        }
    }

    public isRight:boolean = false;
    private _inUse:boolean;
    public reset(isRight:boolean, len:number, index:number):void {
        this.isRight = isRight;
        let co = this.gameObject.getChildByName("collider");
        if(co) {
            if(isRight) {
                co.name = "right";
            } else {
                co.name = "left";
            }
        }
        this._inUse = true;
        this.length = len;

        let w = moduleBridge.worldCreater;
        this.resetMat(w.matBase[index], w.matTop[index], w.matRock[index]);
    }

    public destroy():void {
        if(this.gameObject) {
            this.gameObject.removeSelf();
            this.gameObject.destroy(true);
        }
        this.gameObject = null;
        this.transform = null;
        this._playerTransform = null;
        this.isRight = null;
        this.length = null;
        this.name = null;
        this._inUse = null;

        super.destroy();
    }

    private recover():void {
        if(this._inUse) {
            this.gameObject.removeSelf();
            moduleBridge.worldCreater.recoverChunk(this);
            this._inUse = false;
        }
    }

    public onUpdate():void {
        if(this.transform.localPositionZ < this._playerTransform.localPositionZ-48) {
            this.recover();
        }
    }
}