    public firstTime: number;
    protected afterEnterScene(sceneName: string): void {
        if (sceneName == "game") {
            if (userData.get("CURRENT_WORLD", 0) == 0) {
                ZY.sound.playMusic("audioGame/Music_Savannah.mp3");
            } else if (userData.get("CURRENT_WORLD", 0) == 1) {
                ZY.sound.playMusic("audioGame/Music_Jungle.mp3");
            }
            if (userData.get("openOne", 0) == 0) {
                if (ZY.channel == "wx" || ZY.channel == "qq") {
                    ZY.TmCtrl.instance.tmSendEvent("tutorial_8", {})
                } else if (ZY.channel == "zj" || ZY.channel == "bd") {
                    ZY.reportEvent("tutorial_8", {})
                } else if (ZY.channel == "oppo") {
                    this.firstTime = Laya.Browser.now()
                    reporter.upload(ReporterPoints.tutorial_8)
                }
                userData.set("openOne", 1)
            } else if (userData.get("openOne", 0) == 1 && (ZY.channel == "wx" || ZY.channel == "qq")) {
                ZY.TmCtrl.instance.tmSendEvent("yd_run_2")
                userData.set("openOne", 2)
            } else if (userData.get("openOne", 0) == 2 && (ZY.channel == "wx" || ZY.channel == "qq")) {
                ZY.TmCtrl.instance.tmSendEvent("yd_run_3")
                userData.set("openOne", 3)
            } else if (userData.get("openOne", 0) == 1 && ZY.channel == "oppo") {
                reporter.upload(ReporterPoints.yd_run_2)
                userData.set("openOne", 2)
            } else if (userData.get("openOne", 0) == 2 && ZY.channel == "oppo") {
                reporter.upload(ReporterPoints.yd_run_3)
                userData.set("openOne", 3)
            } else if (userData.get("openOne", 0) == 1 && ZY.channel === "bd") {
                ZY.reportEvent("yd_run_2");
                userData.set("openOne", 2);
            } else if (userData.get("openOne", 0) == 2 && ZY.channel === "bd") {
                ZY.reportEvent("yd_run_3");
                userData.set("openOne", 3);
            }
        }
    }

    private changeSkin(p): void {
        //性別
        let meshes = ZY.findChildByPath(p, "offset.MESHES.DO_NOT_BATCH");

        let chest = meshes.getChildByName("CHESTGIRL") as Laya.SkinnedMeshSprite3D;
        let hair = meshes.getChildByName("HAIRGIRL") as Laya.SkinnedMeshSprite3D;
        if (userData.sex == 1) {
            chest.active = false
            hair.active = false
        } else if (userData.sex == 2) {
            chest.active = true
            hair.active = true
        }
        //帽子
        let roleHead = ZY.findChildByPath(p, "offset.ctrl_main.ctrl_hips.HEAD 1.SKELETON 3.joint_head.joint_HAT.Hat_attach");
        let curHat = Laya.loader.getRes(userData.cfgHats[userData.curHatId].res);
        roleHead.addChild(curHat);
        //皮肤
        let sexPoint = ZY.findChildByPath(p, "offset.MESHES.DO_NOT_BATCH") as Laya.Sprite3D;

        let skinIndexList = [1, 2, 3, 4, 5];
        let meshBody = p.getChildByName("MESH_BODY") as Laya.SkinnedMeshSprite3D;
        let meterial = meshBody.skinnedMeshRenderer.material as Laya.PBRStandardMaterial;
        let mat = chest.skinnedMeshRenderer.material as Laya.PBRSpecularMaterial;

        let tilingOffset: Laya.Vector4 = new Laya.Vector4(1, 1, 0, -0.0625)
        tilingOffset.z = skinIndexList[userData.clothes] * 0.0625;
        let clothes = (p.getChildByName("MESH_BODY") as Laya.SkinnedMeshSprite3D).skinnedMeshRenderer.material as Laya.PBRStandardMaterial;
        clothes.tilingOffset = tilingOffset;
        mat.tilingOffset = tilingOffset;
    }

