    private shareComplet(e: any): void {
        if (e.code == 0) {
            if (e.param[0] && e.param[1]) {
                this.addGold(e.param[0], e.param[1])
            }
            if (e.param[1] == 1013) {
                this.onContinue()
            } else if (e.param[1] == 1008) {
                this.closeRecord()
            } else if (e.param[1] == 1014) {
                this._view.zjLuxiang.visible = false
            }
            console.log("分享成功", e)
            if (ZY.channel == "wx" || ZY.channel == "qq") {
                ZY.TmCtrl.instance.tmSendEvent("recording_succ", {})
            } else if (ZY.channel == "zj") {
                ZY.reportEvent("recording_succ", {})
            }
        }
    }

