    private afterReborn(): void {
        //完成每日任务
        userData.setDailyTask(2, 1)
        this._view.botReborn.visible = false
        this._view.oppoActive4.visible = false;
        this._view.gailvUp.visible = false
        let worldIndex = userData.get("CURRENT_WORLD", 0);
        let fareset: number = userData.getFarest(worldIndex)
        if (fareset < this._distance) {
            userData.setFarest(worldIndex, this._distance)
            let text = this._distance - fareset
            if (ZY.channel == "qq") {
                ZY.TmCtrl.instance.tmSendEvent("share_highscore", { value: "曝光" })
                this._view.botRecordZj.visible = false
                this._view.botRecord.visible = true
                this._view.botRecordQg.visible = false
                this._view.btnClose.visible = false
                this._view.txtRecord.text = "提高了" + text + "米"
                Laya.timer.once(2 * 1000, this, () => {
                    this._view.btnClose.visible = true
                })
            } else if (ZY.channel == "zj") {
                this._view.botRecord.visible = false
                this._view.botRecordZj.visible = true
                this._view.botRecordQg.visible = false
                this._view.closeRecordZj.visible = false
                this._view.txtRecordZj.text = "提高了" + text + "米"
                Laya.timer.once(2 * 1000, this, () => {
                    this._view.closeRecordZj.visible = true
                })
                RecordVideo.stopRecordVideo()
            } else if (ZY.channel == "wx" || ZY.channel == "oppo" || ZY.channel == "vivo" || ZY.channel == "hw" || ZY.channel == "bd" || ZY.channel == "none") {
                if (this._distance >= 200) {
                    let list = [
                        [200, 500, 1], [500, 1000, 2], [1000, 1500, 3], [1500, 2000, 4], [2000, 2500, 5],
                        [2500, 3000, 6], [3000, 3500, 7], [3500, 4000, 8], [4000, 4500, 9], [4500, 5000, 10],
                        [5000, null, 11]
                    ]
                    this._view.botRecordZj.visible = false
                    this._view.botRecord.visible = false
                    this._view.botRecordQg.visible = true
                    if (ZY.channel == "oppo" && this.firstTime) {
                        // reporter.upload(ReporterPoints.show_record)
                    } else if (ZY.channel == "wx") {
                        ZY.TmCtrl.instance.tmSendEvent("share_highscore", { value: "曝光" })
                    }else if (ZY.channel == "hw"){
                        this._view.btnVideoQg.visible = false;
                        this._view.btnJewQg.centerX = 0;
                        this._view.btnJewQg.left = NaN;
                    } else if (ZY.channel === "bd" && this.firstTime) {
                        ZY.reportEvent("show_record");
                    }
                    // this._view.btnCloseQg.visible = false
                    this._view.txtRecordQg.text = "提高了" + text + "米"
                    if (ZY.channel == "oppo") {
                        this._view.btnCloseQg.visible = true
                        // ZY.ad.showQGbanner(0)
                        // reporter.upload(ReporterPoints.ad_zuigao, 2)
                    } else {
                        Laya.timer.once(2 * 1000, this, () => {
                            this._view.btnCloseQg.visible = true
                        })
                    }
                    for (let i = 0; i < list.length; i++) {
                        const element = list[i];
                        if (element[0] <= this._distance && ((element[1] && element[1] > this._distance) || !element[1])) {
                            this._qgJew = element[2] * (worldIndex + 1)
                            this._qgGold = Math.floor((this._distance - 200) * (worldIndex + 1) * 0.2 + 50)
                            this._view.oneQg.text = String(this._qgGold)
                            this._view.doubleQg.text = "+" + String(this._qgGold * 2)
                            this._view.fiveQg.text = "+" + String(this._qgGold * 5)
                            this._view.cutJewQg.text = "X" + String(this._qgJew)
                            break
                        }
                    }
                } else {
                    this.closeRecord()
                }
            } else {
                this._view.botRecord.visible = false
                this._view.botRecordZj.visible = false
                this._view.botRecordQg.visible = true
                this._view.closeRecordZj.visible = false
                this._view.txtRecordZj.text = "提高了" + text + "米"
                Laya.timer.once(2 * 1000, this, () => {
                    this._view.closeRecordZj.visible = true
                })
            }
        } else {
            this.closeRecord()
        }
    }


    //关闭重生
    private closeReborn(flag: boolean = false): void {
        if (ZY.channel == "vivo") {
            ZY.ad.hideBanner()
        }
        if ((ZY.channel == "zj" || ZY.channel == "wx" || ZY.channel == "qq" || ZY.channel == "bd") && !flag) {
            if (this._rebornGou) {
                this._rebornGou = null;
                this.closeRebornAd()
                return
            }
            if (ZY.channel == "zj") {
                RecordVideo.stopRecordVideo()
            }
        }
        if (ZY.channel == "qq" || ZY.channel == "vivo" || ZY.channel == "wx"){
            ZY.ad.showInsertAd()
        }else if (ZY.channel == "zj" && GameCtrl.zjGameInsert == 1){
            ZY.ad.showZjInsertAd()
        }
        let iphone: boolean = false
        if ((ZY.channel == "wx" || ZY.channel == "qq" || ZY.channel == "bd") && !Laya.Browser.onAndroid) {
            iphone = true
        }
        if (this._giftBag % 2 == 0 && (userData.getAnimals(1000) && userData.getAnimals(1000).indexOf(11) < 0) && !iphone && ZY.channel !== "zj") {
            this._view.visible = false;
            moduleBridge.giftbagCtrl.showGiftBag(0)
            if (ZY.channel == "oppo" && this.firstTime) {
                // reporter.upload(ReporterPoints.show_noreborn)
            } else if (ZY.channel === "bd" && this.firstTime) {
                ZY.reportEvent("show_noreborn");
            }
        } else {
            this.afterReborn()
        }
        this._giftBag += 1
    }

