    private _completTask: any;
    private completTask(e: any): void {
        if (moduleBridge.teachCtrl.status > 0) return
        this._view.gou.visible = true
        this._view.taskTarget.visible = false
        Laya.timer.once(1000, this, () => {
            let data = moduleBridge.taskCtrl.getTaskInfo();
            if (data.length <= 0) this.displayTarget(false)
        })

        if (this._completTask) {
            this._completTask = this._completTask || []
        } else {
            this._completTask = []
        }
        if (this._completTask.length >= 3) {
            this._completTask.splice(0, 1)
        }
        let list = {
            "0": { 0: 30, 1: 60, 2: 90 },
            "1": { 0: 120, 1: 240, 2: 360 }
        }
        let gold = list[userData.get("CURRENT_WORLD", 0)][e.difficulty]
        this._completTask.push([e.dec, e.difficulty, gold])
        this.addGold(gold, 1006, false)
        ZY.sound.playSound("audio/UI_Mission_Complete.mp3");
        if (ZY.channel == "wx" || ZY.channel == "qq") {
            ZY.TmCtrl.instance.tmSendEvent("finish_task", { value: e.id })
        } else if (ZY.channel == "zj") {
            ZY.reportEvent("finish_task", { value: e.id })
        }
        // this._view.completTask.visible = true
        // this._view.whichTask.text = e.dec

        // const element = this._view.completTask.getChildByName("difficult3"+e.difficulty) as Laya.Image
        // element.visible = true

        // Laya.timer.once(2000,this,()=>{
        //     this._view.completTask.visible = false
        //     const element = this._view.completTask.getChildByName("difficult3"+e.difficulty) as Laya.Image
        //     element.visible = false
        // })
    }


