    //字节骗子
    private zjLiar(index: number): void {
        if (ZY.channel !== "zj" && ZY.channel !== "wx" && ZY.channel !== "qq" && ZY.channel !== "bd") return
        if (index == 1) {
            // 复活
            let fh_limit = GameCtrl.zjLiarFHLimit
            let fh_val = GameCtrl.zjLiarFHVal
            let fh_big = userData.get("zjLiarFHBig", 0)
            let fh_small = userData.get("zjLiarFHSmall", 1)
            console.log(fh_limit, fh_val, fh_big, fh_small)
            //未达到上限
            if (fh_big < fh_limit) {
                if (fh_val <= fh_small) { //要显示奇怪的东西了
                    Laya.timer.once(2 * 1000, this, () => {
                        this._view.zjLiarFH.visible = true;
                    });
                    (this._view.btnZjLiarFH.getChildAt(0) as Laya.Image).visible = true;
                    this._rebornGou = true;
                    userData.set("zjLiarFHSmall", 1)
                    userData.set("zjLiarFHBig", fh_big + 1)
                } else {
                    userData.set("zjLiarFHSmall", fh_small + 1)
                    this._view.zjLiarFH.visible = false
                    this._rebornGou = null;
                }
            } else {
                this._rebornGou = null;
                this._view.zjLiarFH.visible = false
            }
        } else if (index == 2) {
            //5倍部分
            let mg_limit = GameCtrl.zjLiarMGLimit
            let mg_val = GameCtrl.zjLiarMGVal

            let mg_big = userData.get("zjLiarMGBig", 0)
            let mg_small = userData.get("zjLiarMGSmall", 1)
            console.log(mg_limit, mg_big, mg_big, mg_small)
            //未达到上限
            if (mg_big < mg_limit) {
                if (mg_val <= mg_small) { //要显示奇怪的东西了
                    Laya.timer.once(2 * 1000, this, () => {
                        this._view.ZjLiarMG.visible = true;
                    });
                    this._view.ZjLiarMG.bottom = ZY.ad.bannerHeight + 180;
                    (this._view.btnZjLiarMG.getChildAt(0) as Laya.Image).visible = true;
                    this._fiveGou = true;
                    userData.set("zjLiarMGSmall", 1)
                    userData.set("zjLiarMGBig", mg_big + 1);
                } else {
                    userData.set("zjLiarMGSmall", mg_small + 1)
                    this._view.ZjLiarMG.visible = false
                    this._fiveGou = null;
                }
            } else {
                this._fiveGou = null;
                this._view.ZjLiarMG.visible = false
            }
        }
    }

