    /**
     * 1 复活
     * 2 3倍钱
     */
    private onFocus(index: number): void {
        let now = Laya.Browser.now()
        if (this._shareTime && (now - this._shareTime > 2.5 * 1000)) {
            if (index == 1) {
                ZY.TmCtrl.instance.tmSendEvent("share_revive", { value: "完成" })
                userData.set("rebornAd", 1)
                this.btnlegent()
            } else if (index == 2) {
                ZY.TmCtrl.instance.tmSendEvent("share_5gold", { value: "完成" })
                userData.addGold(this._goldAdd * 2, 1015)
                let now_gold = Math.floor(userData.gold)
                this._view.gold1.text = now_gold.toString()
                this._view.gold2.text = now_gold.toString()
                this._view.gold3.text = "(+" + this._goldAdd * 2 + ")"
                this._view.gold4.text = "(+" + this._goldAdd * 2 + ")"
                this._view.gold3.visible = true
                this._view.gold4.visible = true
                Laya.timer.once(1500, this, () => {
                    this._view.gold3.visible = false
                    this._view.gold4.visible = false
                })
                this._goldAdd = 0
                userData.set("fiveGoldAd", 0)
                ZY.ad.hideBanner()
                this.lastUI()
            } else if (index == 3) {
                this.addGold(100, 1008)
                ZY.TmCtrl.instance.tmSendEvent("share_highscore", { value: "完成" })
                this.closeRecord()
            }
        } else {
            let list = [null,
                "请分享到不同的群",
                "分享失败，请分享给不同好友试试",
                "不要频繁分享到同一个群，换个群试试",
                "该群已分享过，请换个群试试"]
            ZY.msg.show(list[Math.floor(Math.random() * 3 + 1)])
        }
        ZY.eventBus.off(Laya.Event.FOCUS, this, this.onFocus);
        this._shareTime = null
    }

