    private rebornAnimalCreate(animal: IAnimal): void {
        let pos = moduleBridge.player.findSafeSpawnPlace()

        ZY.posSprite3D(this._player as any, pos.x, pos.y, pos.z, false);
        ZY.posSprite3D(animal as any, pos.x, pos.y, pos.z, false);

        let lastAnimalRidden = moduleBridge.player.lastAnimalRidden
        let time = lastAnimalRidden.stayTime
        let distance = Math.floor(moduleBridge.player.transform.localPositionZ - lastAnimalRidden.stayDis)

        if (this._camRig) this._camRig.fuhuo(pos.z);
        // this.displayTarget(true)
        if (moduleBridge.paused == true) return;
        let taskData = moduleBridge.taskCtrl.getTaskInfo();
        if (taskData && taskData.length > 0) {
            let task = taskData[0];
            let progress = task.progress || 0
            if (task.type == "RIDE_STAY" && task.target == Number(lastAnimalRidden.encId)) {
                this._view.taskTarget.text = (time) + " / " + task.value;
                this._player.ride(animal, true, time);
            } else if (task.type == "RUN_NO_JUMP") {
                this._view.taskTarget.text = (distance) + " / " + task.value;
                this._player.ride(animal, true, 0, distance);
            } else if (task.type == "RIDE_SAME_CNT") {
                let count = moduleBridge.player.rideSameCnt
                if (Number(this._player.lastAnimalRidden.encId) !== task.target) {
                    count = 0
                }
                this._view.taskTarget.text = task.dec + " 目前进度:" + (count || 0) + "/" + task.value
                this._player.ride(animal, true);
            } else {
                this._view.taskTarget.text = progress + " / " + task.value;
                this._player.ride(animal, true);
            }
            this._view.completTask.visible = true;
            this._view.whichTask.text = task.dec;
            // this._view.taskTarget.y = 100 + ZY.stage.notchHeight;
            this._view.taskTarget.visible = true;
            this._view.gou.visible = false;
            (this._view.completTask.getChildByName("difficult30") as Laya.Image).visible = false;
            (this._view.completTask.getChildByName("difficult31") as Laya.Image).visible = false;
            (this._view.completTask.getChildByName("difficult32") as Laya.Image).visible = false;
            (this._view.completTask.getChildByName("difficult3" + task.difficulty) as Laya.Image).visible = true
        } else {
            this._player.ride(animal, true);
            this._view.completTask.visible = false;
            this._view.taskTarget.visible = false;
        }
        this._player.fuhuo()
        Laya.systemTimer.frameOnce(1, this, () => {
            this._view.tapInfo.bottom = 250
            this._view.tapInfo.visible = true;
            this._view.btnPause.visible = true;
            moduleBridge.paused = true
            if (ZY.channel == "zj") {
                RecordVideo.pauseRecordVideo()
            }
            Laya.stage.once(Laya.Event.MOUSE_DOWN, this, () => {
                moduleBridge.paused = false;
                this._view.tapInfo.visible = false;
            })
        });
    }


