    private _upgradeList = {}
    private creatUpgrade() {
        let worldIndex = userData.get("CURRENT_WORLD", 0);
        let mapLevel = userData.getMapLevelByWorld(worldIndex);
        let encIds = []
        for (let i = 0; i < userData.cfgWorld[worldIndex].maps[mapLevel].animals.length; i++) {
            encIds.push(userData.cfgWorld[worldIndex].maps[mapLevel].animals[i])
        }
        let chosenAnimal:number;
        if (moduleBridge.endangeredAnimals.curTryId == 0){
            chosenAnimal = userData.startAnimal
        }else{
            chosenAnimal = moduleBridge.endangeredAnimals.curTryId
        }
        let cfgKey = userData.cfgUpgradeKey;
        if (chosenAnimal > 999 && encIds.indexOf(chosenAnimal) < 0) {
            if (chosenAnimal > 1000) {
                encIds.push(chosenAnimal)
            }
        } else if (userData.cfgAnimal && chosenAnimal && userData.cfgAnimal[chosenAnimal] && userData.cfgAnimal[chosenAnimal].encId) {
            let startEncId = userData.cfgAnimal[chosenAnimal].linkEncId ? userData.cfgAnimal[chosenAnimal].linkEncId : userData.cfgAnimal[chosenAnimal].encId

            if (!(encIds.indexOf(startEncId) > -1)) encIds.push(startEncId)
        }

        this._upgradeList = this._upgradeList || {}
        for (let i = 0; i < encIds.length; i++) {
            const encId = encIds[i];
            let level = userData.enclosure[encId] || -1
            // const cfgLevel = userData.cfgLevel[encId]
            let res:string;
            if (userData.cfgEnclosure[encId] && userData.cfgEnclosure[encId].res){
                res = userData.cfgEnclosure[encId].res
            }else if (encId == "1015" || encId == "1016" || encId == "1017"){
                let list = {"1015" : "AnimalCuddler" , "1016" : "AnimalChineseDragon" , "1017" : "AnimalVaulter"}
                res = list[encId]
                level = 9
            }
            if (res){
                const cfgLevel = userData.cfgUpgrade[res].level
                this._upgradeList[encId] = this._upgradeList[encId] || []
                for (let j = 0; j < cfgLevel.length; j++) {
                    const a = cfgLevel[j].key
                    if (level > j) {
                        this._upgradeList[encId][a] = this._upgradeList[encId][a] || cfgKey[a].default
                        this._upgradeList[encId][a] = this.operationUp(this._upgradeList[encId][a], cfgLevel[j].value, cfgKey[a].opt)
                    } else {
                        this._upgradeList[encId][a] = cfgKey[a].default
                    }
                }
            }
        }
    }

    public upgradeValue(encId: string, key: number): number {
        if (this._upgradeList && this._upgradeList[encId]) {
            if (this._upgradeList[encId][key] && (moduleBridge.player.steed && moduleBridge.player.steed.encId && moduleBridge.player.steed.encId == encId)) {
                return this._upgradeList[encId][key]
            } else {
                return userData.cfgUpgradeKey[key].default
            }
        } else {
            this._upgradeList = this._upgradeList || {}
            this._upgradeList[encId] = this._upgradeList[encId] || []
            this._upgradeList[encId][key] = userData.cfgUpgradeKey[key].default
            return this._upgradeList[encId][key]
        }
    }


    private operationUp(v1: number, v2: number, opt: number): number {
        if (opt == 1) {
            return v1 + v2
        } else if (opt == 2) {
            return v2
        }
    }


