        Laya.stage.off(Laya.Event.MOUSE_DOWN, this, this.realGame)
        moduleBridge.taskCtrl.changeScene(this._sceneName)
        ZY.ad.hideBanner()
        let id = this._defaultAnimalId;
        WorldCreater.instance.reset();
        AnimalManager.instance.restart();
        AnimalManager.instance.createOne(id.toString(), ZY.Temp.getVector3(0, 50, 9), this, this.onStartAnimalCreate);
        if (this._camRig) this._camRig.reset();
        if (this._view.tapInfo) this._view.tapInfo.visible = false;
        if (this._view.restartTask) this._view.restartTask.visible = false;
