    private _wxPd: number = 0;
    public restartGame(): void {
        this._view.visible = true;
        this.clearView()
        Laya.Tween.clearAll(this._view.finger);
        this._reborn = null;
        this._completTask = null
        this._goldAdd = null
        this._taskGold = null
        this.firstTime = null;

        this._view.complet0.visible = this._view.complet1.visible = this._view.complet2.visible = false;
        this._view.tapInfo.visible = true;
        this._view.btnPause.visible = false;
        this._view.gailvUp.visible = false;
        this.restartTask()
        if (ZY.channel == "vivo") {
            //ZY.ad.showQGbanner(0)
            this._view.tapInfo.bottom = 250;//ZY.ad.bannerHeight + 50
        } else if (ZY.channel == "oppo") {
            this._view.tapInfo.bottom = 250
        } else if (ZY.channel == "zj") {
            //if ((window as any).tt.getSystemInfoSync().appName == "Douyin"){
            // this._view.tapInfo.bottom = 250
            //}else{
            ZY.ad.showTTBanner(0)
            this._view.tapInfo.bottom = ZY.ad.bannerHeight + 50
            //}
        } else if (ZY.channel == "wx" || ZY.channel == "qq") {
            GameCtrl.restartOpen = GameCtrl.restartOpen || 0
            GameCtrl.restartNum = GameCtrl.restartNum || 2
            GameCtrl.restartTime = GameCtrl.restartTime || 0.3
            this._wxPd = this._wxPd || 0
            if (GameCtrl.restartOpen == 1 && this._wxPd == (GameCtrl.restartNum - 1)) {
                this._view.tapInfo.bottom = 100
                Laya.timer.once(GameCtrl.restartTime * 1000, this, () => {
                    if (this._view.tapInfo.visible == true) {
                        ZY.ad.showTTBanner(0)
                        this._view.tapInfo.bottom = ZY.ad.bannerHeight + 50
                    }
                })
            } else {
                ZY.ad.showTTBanner(0)
                this._view.tapInfo.bottom = ZY.ad.bannerHeight + 50
            }
            this._wxPd = (this._wxPd + 1) % GameCtrl.restartNum
        } else if (ZY.channel == "bd") {
            //ZY.ad.showBDbanner(0);
            this._view.tapInfo.bottom = ZY.ad.bannerHeight + 50;
        }
        if (moduleBridge.endangeredAnimals.curTryId > 0){
            this._defaultAnimalId = moduleBridge.endangeredAnimals.curTryId
        }
        Laya.stage.on(Laya.Event.MOUSE_DOWN, this, this.realGame)
    }

    private restartTask(): void {
        let taskData = moduleBridge.taskCtrl.getTaskInfo();
        if (taskData && taskData.length > 0) {
            let task = taskData[0];
            this._view.restart.visible = true
            this._view.restartTask.visible = true;
            this._view.restartText.text = task.dec;
            (this._view.restartTask.getChildByName("difficult0") as Laya.Image).visible = false;
            (this._view.restartTask.getChildByName("difficult1") as Laya.Image).visible = false;
            (this._view.restartTask.getChildByName("difficult2") as Laya.Image).visible = false;
            (this._view.restartTask.getChildByName("difficult" + task.difficulty) as Laya.Image).visible = true;
            if (task.target > 100) {
                let target = task.target
                if (task.target < 1000 && userData.cfgAnimal[target]) {
                    target = userData.cfgAnimal[target].encId
                }
                let current_world = userData.get("CURRENT_WORLD", 0)
                let info = userData.cfgWorld[current_world].maps[userData.getMapLevelByWorld(current_world)].animals
                if (info.indexOf(target) > -1) {
                    this._view.changeTaskStart.visible = true;
                    this._view.lockMap.visible = false
                    this._view.changeTaskStart.y = 650
                } else {
                    this._view.changeTaskStart.visible = false;
                    this._view.lockMap.visible = true;
                    this._view.lockMap.y = 650
                }
            } else {
                if (Number(task.area) > userData.worldLevel) {
                    this._view.changeTaskStart.visible = false;
                    this._view.lockMap.visible = true
                    this._view.lockMap.y = 650
                } else {
                    this._view.changeTaskStart.visible = true;
                    this._view.lockMap.visible = false
                    this._view.changeTaskStart.y = 650
                }
            }
            if (ZY.channel == "wx" || ZY.channel == "qq") {
                ZY.TmCtrl.instance.tmSendEvent("ad_changeTask", { value: "曝光" })
            } else if (ZY.channel == "zj") {
                ZY.reportEvent("ad_changeTask", { value: "曝光" })
            } else if (ZY.channel == "oppo") {
                // reporter.upload(ReporterPoints.ad_changeTask, 2)

                //nativeAd.showAsBanner2("207986", this._view.oppoActive2);
                ZY.ad.showQGbanner(0);
            } else if (ZY.channel == "vivo") {
                nativeAd.showAsBanner2("abea873d8eef4a7d904c27e005acebea", this._view.oppoActive2);
            }
            this._view.restartTask.y = 500
            if (ZY.channel !== "hw"){
                this._view.btnMoreXy.visible = true
            }
            this._view.btnMoreXy.y = 800
            this.teachGl()
        } else {
            this._view.restartTask.visible = false;
            if (ZY.channel !== "hw"){
                this._view.btnMoreXy.visible = true
            }
            this._view.btnMoreXy.y = 600
            this.teachGl()
        }
        this.upgl = false;
    }

    private teachGl(): void {
        if (userData.get("teachGl", 0) == 0) {
            userData.set("teachGl", 1)
            this._view.teachGlMask.visible = true
            this._view.teachLast.visible = true
            this._view.btnMoreXy.skin = "main/img_xiyou.png"
            this._view.fingerXy.visible = true
            let img = this._view.fingerXy;
            let curY = -160
            for (let i = 0; i < 1000; i++) {
                if (this._view.fingerXy.visible == true) {
                    Laya.Tween.to(img, { y: curY + 40 }, 300, null, null, i * 300, false);
                    Laya.Tween.to(img, { y: curY }, 300, null, null, 300 * (i + 1), false);
                    i++
                }
            }
        } else {
            this._view.fingerXy.visible = false
            if (ZY.channel == "wx" || ZY.channel == "qq") {
                ZY.TmCtrl.instance.tmSendEvent("ad_moregl", { value: "曝光" })
            } else if (ZY.channel == "zj") {
                ZY.reportEvent("ad_moregl", { value: "曝光" })
            }
            this._view.btnMoreXy.skin = "main/img_xiyou1.png"
        }
    }

    private moreGl(e: Laya.Event): void {
        if (userData.get("teachGl", 0) == 1) {
            userData.set("teachGl", 2)
            this.upgl = true;
            this._view.tapInfo.visible = false;
            this._view.tapinfo2.visible = true;
            this._view.btnMoreXy.visible = false;
            this._view.teachGlMask.visible = false
            this._view.teachLast.visible = false
            return
        }
        if (ZY.channel == "wx") {
            ZY.ad.showVideoAd(this, this.moreGl2, ZY.wxAd.moregl, "ad_moregl");
        } else if (ZY.channel == "zj") {
            ZY.ad.showVideoAd(this, this.moreGl2, 0, "ad_moregl");
        } else if (ZY.channel == "vivo") {
            ZY.ad.showVivoVideo(this, this.moreGl2, ZY.vivoAd.moregl, "ad_moregl");
        } else if (ZY.channel == "oppo") {
            ZY.ad.showOPPOVideo(this, this.moreGl2);
        } else {
            ZY.ad.showVideoAd(this, this.moreGl2, 0, "ad_moregl");
        }
    }

