    private _distance: number = 0;
    protected frameLoop(): void {
        // console.log(this._player.transform.position)
        // console.log(this._player.transform)
        if (Math.floor(this._player.transform.localPositionZ) != this._distance) {
            let abs = Math.abs(this._player.transform.localPositionZ - this._distance)
            if (abs < 10) {
                ZY.eventBus.event("TASK_UPDATE", ["RUN_IN_ONE", 0, Math.floor(abs)]);
                ZY.eventBus.event("TASK_UPDATE", ["RUN", 0, Math.floor(abs)]);
            } else {
                if (this._player.transform.localPositionZ < 15) {
                    ZY.eventBus.event("TASK_UPDATE", ["RUN_IN_ONE", 0, Math.floor(this._player.transform.localPositionZ)]);
                    ZY.eventBus.event("TASK_UPDATE", ["RUN", 0, Math.floor(this._player.transform.localPositionZ)]);
                } else {
                    ZY.eventBus.event("TASK_UPDATE", ["RUN_IN_ONE", 0, Math.floor(abs)]);
                    ZY.eventBus.event("TASK_UPDATE", ["RUN", 0, Math.floor(abs)]);
                }
            }
            this._distance = Math.floor(this._player.transform.localPositionZ);
            this._view.distance.text = this._view.distance2.text = this._distance.toString();
            this._view.mi.x = this._view.distance.textWidth + 40;
            if (this._distance >= 1000) {
                userData.setDailyTask(3, 1)
            }
        }
    }

    public changeAnimal(id: string): void {
        if (moduleBridge.paused == true) return
        let list = {
            "207": "game/Conventional/vulture6.lh",
            "348": "game/Conventional/tiger7.lh"
        }
        let steed = moduleBridge.player.steed
        let pos: Laya.Vector3 = new Laya.Vector3();
        steed.transform.localPosition.cloneTo(pos)
        pos = pos
        steed.leaveAnimal()
        steed.revoverSelf()
        ZY.stage.load([list[id]], this, () => {
            AnimalManager.instance.createOne(id, pos, this, this.changeAnimal2);
        })
    }

    private changeAnimal2(animal: IAnimal): void {
        this._player.changeAnimal(animal);
        this._player.fuhuo()
    }

    private shortCut(): void {
        if (ZY.channel == "oppo") {
            if (moduleBridge.teachCtrl.status == 0 && userData.get("desktop2", 0) < 2) {
                userData.set("desktop2", userData.get("desktop2", 0) + 1)
                let power = (window as any).qg
                power.hasShortcutInstalled({
                    success: function (res) {
                        // 判断图标未存在时，创建图标
                        if (res == false) {
                            power.installShortcut({
                                success: function () {
                                    // 执行用户创建图标奖励
                                },
                                fail: function (err) { },
                                complete: function () { }
                            })
                        }
                    },
                    fail: function (err) { },
                    complete: function () { }
                })
            }
        } else if (ZY.channel == "vivo") {
            if (moduleBridge.teachCtrl.status == 0 && userData.get("vivoShortCut", 0) == 0 && userData.get("desktop2", 0) < 2) {
                let power = (window as any).qg
                power.hasShortcutInstalled({
                    success: function (status) {
                        if (status) {
                            console.log('已创建')
                        } else {
                            console.log('未创建')
                            power.installShortcut({
                                success: function () {
                                    console.log('创建成功')
                                    userData.addGold(100, 1027);
                                    ZY.msg.show("恭喜获得100金币");
                                    userData.set("vivoShortCut", 1);
                                },
                                fail: function (e) {
                                    console.log('创建失败', e)
                                },
                                complete: function () {
                                    userData.set("desktop2", userData.get("desktop2", 0) + 1)
                                }
                            })
                        }
                    }
                })
            }
        }
    }
